export async function* transformStream(
  body: ReadableStream<Uint8Array> | null,
) {
  if (!body) {
    return;
  }
  let accumulatedValue = "";
  const reader = body.getReader();
  let done, value;
  while (!done) {
    ({ value, done } = await reader.read());
    if (done) {
      break;
    } else {
      const str = new TextDecoder().decode(value);
      accumulatedValue += str;
      const parts = accumulatedValue.split("\n");
      for (let i = 0; i < parts.length - 1; i++) {
        yield parts[i];
      }
      accumulatedValue = parts[parts.length - 1];
    }
  }
}

async function getCustomInstructions(sys: DevToolsSys, basePath: string) {
  const customInstructions: CustomInstruction[] = [];
  async function tryAddCustomInstructions(path: string) {
    try {
      const ignorePath = sys.resolve(basePath, path);
      if (await sys.exists(ignorePath)) {
        const customInstructionsContent = await sys.readFile(ignorePath);
        if (customInstructionsContent) {
          const id = "hash-" + (await sys.hash(customInstructionsContent));
          customInstructions.push({
            id: id,
            name: path,
            content: customInstructionsContent,
          });
        }
      }
    } catch (error) {
      console.debug(`No ${path} found or error reading it:`, error);
    }
  }
  await tryAddCustomInstructions(".cursorrules");
  await tryAddCustomInstructions(".builderrules");
  await tryAddCustomInstructions(".windsurfrules");
  await tryAddCustomInstructions(".github/copilot-instructions.md");

  return customInstructions;
}

