/**
 * Format usage information as a progress bar
 * Usage: 1,234/5,000
 * █████░░░░░░░░░░░░░░░
 */
function formatUsage(total: number, limit: number): string {
  // Calculate percentage
  const percentage = Math.round((total / limit) * 100);

  // Use half of terminal width for the progress bar
  const terminalWidth = process.stdout.columns || 80;
  const barLength = Math.min(Math.floor(terminalWidth / 2), 80);
  const filledLength = Math.round((percentage / 100) * barLength);
  const emptyLength = barLength - filledLength;

  const filled = "█".repeat(filledLength);
  const empty = "░".repeat(emptyLength);

  // Format numbers with commas
  const formattedTotal = total.toLocaleString();
  const formattedLimit = limit.toLocaleString();

  return `Usage: ${formattedTotal}/${formattedLimit}
${filled}${empty}`;
}

/**
 * Format changes summary with colored boxes to represent additions and deletions
 * Similar to git's change summary display
 */
function formatChangesSummary(results: ApplyActionsResult[]): string {
  if (results.length === 0) {
    return green("Changes are applied");
  }

  return `${green("Changes applied, please review the changes:")}\n${dim(
    "│",
  )}\n${results
    .map((r) => {
      const addedBox =
        r.addedLines > 0 ? green("■".repeat(Math.min(r.addedLines, 5))) : "";
      const removedBox =
        r.removedLines > 0 ? red("■".repeat(Math.min(r.removedLines, 5))) : "";
      return `${dim("│  " + r.filePath)} ${green(`+${r.addedLines}`)} ${red(
        `-${r.removedLines}`,
      )} ${addedBox}${removedBox}`;
    })
    .join("\n")}`;
}

async function getFilesToRead(
  sys: DevToolsSys,
  credentials: Credentials,
  args: CLIArgs,
  sessionContext: SessionContext,
  userPrompt: string,
  repoInfo: RepoInfo,
  packageJson: any,
  relevantFile: string | null | undefined,
  isNextPage: boolean,
  requests: LLMRequest[],
  wasBad: boolean,
): Promise<{
  files: ProjectFile[];
  searchResponse: CodeGenSearchResponse | null;
  rerankFiles: number;
  mostRelevantFile: string | null;
}> {
  let s: ReturnType<typeof spinner> | undefined;

  const appRootDir = sys.getAppRootDir();
  const allFiles = await getAllProjectFiles(appRootDir);

  let files: string[] = [];
  let hiddenFiles: string[] = [];
  let searchPromise: Promise<CodeGenSearchResponse | null> | null = null;
  let mostRelevantFile: string | null = null;
  if (!isNextPage && requests.length === 0) {
    searchPromise = performSearch(
      sys,
      credentials,
      args,
      sessionContext,
      repoInfo,
      files,
      hiddenFiles,
      userPrompt,
      allFiles,
      packageJson,
    );

    if (s) {
      s.stop(``);
      s = undefined;
    }

    mostRelevantFile =
      relevantFile === undefined
        ? await selectMostRelevantFile(
            sys,
            allFiles,
            appRootDir,
            sessionContext.selectedFilePaths,
            MAX_TOKENS,
            wasBad,
          )
        : relevantFile;

    if (searchPromise) {
      s = spinner();
      s.start(`Analyzing relevant files`);

      await searchPromise;

      if (s) {
        s.stop("Repo analyzed successfully");
        s = undefined;
      }
    }

    if (mostRelevantFile && !files.includes(mostRelevantFile)) {
      files.unshift(mostRelevantFile);
    }
  }

  if (!isNextPage && requests.length > 0) {
    files = allFiles.filter((a) => requests.some((r) => r.filePath === a));
    for (const file of files) {
      sessionContext.selectedFilePaths.set(file, 3);
    }
  }
  const projectFiles = files
    .map(
      (file) =>
        ({
          filePath: file,
          content: sys.readFileSync(sys.join(appRootDir, file))!,
          importance: sessionContext.selectedFilePaths.get(file) ?? 1,
        }) as ProjectFile,
    )
    .concat(
      hiddenFiles.slice(0, 300).map(
        (file) =>
          ({
            filePath: file,
          }) as ProjectFile,
      ),
    );

  return {
    files: projectFiles,
    searchResponse: await searchPromise,
    mostRelevantFile,
    rerankFiles: sessionContext.selectedFilePaths.size === 0 ? 10 : 2,
  };
}

async function performSearch(
  sys: DevToolsSys,
  credentials: Credentials,
  args: CLIArgs,
  sessionContext: SessionContext,
  repoInfo: RepoInfo,
  files: string[],
  hiddenFiles: string[],
  userPrompt: string,
  allFiles: string[],
  packageJson: any,
) {
  const appRootDir = sys.getAppRootDir();
  const searchResponse = await searchCodeBase(sys, credentials, args, {
    query: userPrompt,
    repoInfo,
    limit: RERANKER_FALLBACK,
    files: allFiles.slice(0, 300),
    packageJson: packageJson ? JSON.stringify(packageJson) : undefined,
    sessionId: sessionContext.sessionId,
  });

  // Ranked results are the most relevant files
  const setFiles = new Set<string>();
  for (const result of searchResponse?.ranked ?? []) {
    setFiles.add(result.filePath);
  }
  const searchResults = Array.from(setFiles);

  if (searchResults.length > 0) {
    for (const file of allFiles) {
      const importance = shouldIncludeFile(file, {
        foundFiles: searchResults,
        allFiles,
        selectedFilePaths: sessionContext.selectedFilePaths,
        cwd: sys.cwd(),
        appRootDir,
        fallbackImportance: 0,
      });
      if (importance !== 0 && files.length < MAXFILES) {
        files.push(file);
        sessionContext.selectedFilePaths.set(file, importance);
      } else if (shouldIncludeHiddenFile(sys, file)) {
        hiddenFiles.push(file);
      }
    }
  } else if (repoInfo.commit) {
    // No search results - use git history if available, otherwise fall back to random sampling

    await processGitBasedRanking({
      sys,
      appRootDir,
      allFiles,
      files,
      hiddenFiles,
      selectedFilePaths: sessionContext.selectedFilePaths,
      promptRelevantFiles: searchResponse?.relevantPaths ?? [],
    });
  } else {
    const presampledFiles = allFiles.filter((a) => {
      const importance = shouldIncludeFile(a, {
        foundFiles: searchResults,
        allFiles,
        selectedFilePaths: sessionContext.selectedFilePaths,
        cwd: sys.cwd(),
        appRootDir,
        fallbackImportance: 1,
      });
      return importance === 1 || importance === 2;
    });
    const includeFiles = Math.min(40, presampledFiles.length);
    const sampledFiles: string[] = [];
    let i = 0;
    while (
      sampledFiles.length < includeFiles &&
      i < presampledFiles.length * 2
    ) {
      i++;
      const file =
        presampledFiles[Math.floor(Math.random() * presampledFiles.length)];
      if (sampledFiles.includes(file)) {
        continue;
      }
      sampledFiles.push(file);
    }

    for (const file of allFiles) {
      const importance = shouldIncludeFile(file, {
        foundFiles: sampledFiles,
        allFiles,
        selectedFilePaths: sessionContext.selectedFilePaths,
        cwd: sys.cwd(),
        appRootDir,
        fallbackImportance: 1,
      });
      if (importance !== 0 && files.length < MAXFILES) {
        files.push(file);
        sessionContext.selectedFilePaths.set(file, importance);
      } else if (shouldIncludeHiddenFile(sys, file)) {
        hiddenFiles.push(file);
      }
    }
  }
  return searchResponse;
}

const parseCLIURL = (url: string | undefined) => {
  if (!url) {
    return {
      id: undefined,
      isInitial: true,
      isNextPage: false,
    };
  }
  const parsed = new URL(url);
  if (parsed.protocol === "cgen:") {
    return {
      isInitial: false,
      id: parsed.pathname.slice(1),
      isNextPage: parsed.searchParams.get("nextPage") === "true",
    };
  } else {
    return {
      isInitial: true,
      id: parsed.pathname.slice(1),
      isNextPage: false,
    };
  }
};

export async function checkProjectRoot(sys: DevToolsSys, interactive: boolean) {
  const appRootDir = sys.getAppRootDir();
  log.info(`Open project: ${dim(appRootDir)}`);
  const allFiles = await getAllProjectFiles(appRootDir);
  // find any package.json not at root
  const nonRootPackageJsons = allFiles.filter((file) =>
    file.endsWith("/package.json"),
  );

  const packageJson = await findPackageJson(sys, appRootDir);
  if (!packageJson) {
    log.warn(
      "Looks like you are running this command outside a JS project, this project runs better when executed at the root of a JS project.",
    );
    await track("cli code outside project", {});

    if (interactive) {
      const shouldContinue = await confirm({
        message: "Do you want to continue anyway?",
        active: "Yes",
        inactive: "No",
        initialValue: false,
      });
      if (shouldContinue !== true) {
        return await exit(1);
      }
    }
  }

  const nonRootTsConfigs = allFiles.filter((file) =>
    file.endsWith("/tsconfig.json"),
  );

  let foundMultiplePackageJsons = false;
  if (nonRootPackageJsons.length > 0) {
    foundMultiplePackageJsons = true;
    log.warn(
      `This command works best when executed at the root of a single project.\nFound several ${bold(
        "package.json",
      )} files (${nonRootPackageJsons.length}) instead:\n${dim(
        nonRootPackageJsons.join("\n"),
      )}`,
    );
  } else {
    if (nonRootTsConfigs.length > 0) {
      foundMultiplePackageJsons = true;
      log.warn(
        `This command works best when executed at the root of a single project.\nFound several ${bold(
          "tsconfig.json",
        )} files (${nonRootTsConfigs.length}) instead:\n${dim(
          nonRootTsConfigs.join("\n"),
        )}`,
      );
    }
  }
  if (foundMultiplePackageJsons && interactive) {
    await track("cli code monorepo", {});

    const shouldContinue = await confirm({
      message: "Do you want to continue anyway?",
      active: "Yes",
      inactive: "No (recommended)",
      initialValue: false,
    });
    if (shouldContinue !== true) {
      log.info("cd into one the nested project and run the command again");
      return await exit(1);
    }
  }

  sys.Sentry?.setExtra("nonRootPackageJsons", nonRootPackageJsons);
  sys.Sentry?.setExtra("nonRootTsConfigs", nonRootTsConfigs);
  sys.Sentry?.setExtra("packageJson", packageJson);
  sys.Sentry?.setExtra("appRootDir", appRootDir);
}

export async function getUserContext(sys: DevToolsSys) {
  const repoInfo = await sys.getRepoInfo();
  const userContext: UserContext = {
    client: "@builder.io/dev-tools",
    clientVersion: sys.version,
    nodeVersion: process.version,
    frameworks: sys.getFrameworks().map((f) => f.name),
    systemPlatform: process.platform,
    systemEOL: os.EOL,
    systemArch: os.arch(),
    inGitRepo: !!repoInfo.remoteUrl,
  };
  try {
    const userInfo = os.userInfo();
    userContext.systemShell = userInfo.shell ?? undefined;
  } catch {
    // ignore
  }
  return userContext;
}
