import isUnicodeSupported from "is-unicode-supported";
import color from "picocolors";
import { cursor, erase } from "sisteransi";
import type { Key } from "node:readline";

import { stdin, stdout } from "node:process";
import * as readline from "node:readline";
import { green, red } from "picocolors";

export const spinner = () => {
  const frames = unicode ? ["◒", "◐", "◓", "◑"] : ["•", "o", "O", "0"];
  const delay = unicode ? 80 : 120;

  let unblock: () => void;
  let loop: NodeJS.Timeout;
  let isSpinnerActive: boolean = false;
  let _message: string = "";
  let _extra: string = "";
  let _lastMessage: string = "";

  const handleExit = (code: number) => {
    const msg = code > 1 ? "Something went wrong" : "Canceled";
    if (isSpinnerActive) stop(msg, code);
  };

  const errorEventHandler = (err: Error, origin: string | Promise<unknown>) => {
    console.error(origin, err);
    handleExit(2);
  };
  const signalEventHandler = () => handleExit(1);

  const registerHooks = () => {
    // Reference: https://nodejs.org/api/process.html#event-uncaughtexception
    process.on("uncaughtExceptionMonitor", errorEventHandler);
    // Reference: https://nodejs.org/api/process.html#event-unhandledrejection
    process.on("unhandledRejection", errorEventHandler);
    // Reference Signal Events: https://nodejs.org/api/process.html#signal-events
    process.on("SIGINT", signalEventHandler);
    process.on("SIGTERM", signalEventHandler);
    process.on("exit", handleExit);
  };

  const clearHooks = () => {
    process.removeListener("uncaughtExceptionMonitor", errorEventHandler);
    process.removeListener("unhandledRejection", errorEventHandler);
    process.removeListener("SIGINT", signalEventHandler);
    process.removeListener("SIGTERM", signalEventHandler);
    process.removeListener("exit", handleExit);
  };

  const start = (msg: string = ""): void => {
    isSpinnerActive = true;
    unblock = block();
    _message = msg.replace(/\.+$/, "");
    process.stdout.write(`${color.gray(S_BAR)}\n`);
    let frameIndex = 0;
    let dotsTimer = 0;
    registerHooks();
    if (!process.env.CI) {
      loop = setInterval(() => {
        const frame = color.magenta(frames[frameIndex]);
        const loadingDots = ".".repeat(Math.floor(dotsTimer)).slice(0, 3);
        process.stdout.write(
          cursor.move(-999, -_lastMessage.split("\n").length + 1),
        );
        process.stdout.write(erase.down(_lastMessage.split("\n").length));
        const toWrite = `${frame}  ${_message}${loadingDots}${_extra}`;
        process.stdout.write(toWrite);
        frameIndex = frameIndex + 1 < frames.length ? frameIndex + 1 : 0;
        dotsTimer = dotsTimer < frames.length ? dotsTimer + 0.125 : 0;
        _lastMessage = toWrite;
      }, delay) as any;
    }
  };

  const stop = (msg?: string, code: number = 0): void => {
    _message = msg ?? _message;
    isSpinnerActive = false;
    clearInterval(loop);
    const step =
      code === 0
        ? green(S_STEP_SUBMIT)
        : code === 1
          ? red(S_STEP_CANCEL)
          : red(S_STEP_ERROR);
    process.stdout.write(
      cursor.move(-999, -_lastMessage.split("\n").length + 1),
    );
    process.stdout.write(erase.down(_lastMessage.split("\n").length));
    process.stdout.write(`${step}  ${_message}\n`);
    _lastMessage = _message;
    clearHooks();
    unblock();
  };

  const message = (msg: string = ""): void => {
    _message = msg ?? _message;
  };

  const extra = (msg: string = ""): void => {
    _extra = msg;
  };

  return {
    start,
    stop,
    message,
    extra,
  };
};

const unicode = isUnicodeSupported();
const s = (c: string, fallback: string) => (unicode ? c : fallback);

const S_BAR = s("│", "|");

const S_STEP_CANCEL = s("■", "x");
const S_STEP_ERROR = s("▲", "x");
const S_STEP_SUBMIT = s("◇", "o");

const isWindows = globalThis.process.platform.startsWith("win");

export function block({
  input = stdin,
  output = stdout,
  overwrite = true,
  hideCursor = true,
} = {}) {
  const rl = readline.createInterface({
    input,
    output,
    prompt: "",
    tabSize: 1,
  });
  readline.emitKeypressEvents(input, rl);
  if (input.isTTY) input.setRawMode(true);

  const clear = (data: Buffer, { name }: Key) => {
    const str = String(data);
    if (str === "\x03") {
      if (hideCursor) output.write(cursor.show);
      process.exit(0);
      return;
    }
    if (!overwrite) return;
    let dx = name === "return" ? 0 : -1;
    let dy = name === "return" ? -1 : 0;

    readline.moveCursor(output, dx, dy, () => {
      readline.clearLine(output, 1, () => {
        input.once("keypress", clear);
      });
    });
  };
  if (hideCursor) output.write(cursor.hide);
  input.once("keypress", clear);

  return () => {
    input.off("keypress", clear);
    if (hideCursor) output.write(cursor.show);

    // Prevent Windows specific issues: https://github.com/natemoo-re/clack/issues/176
    if (input.isTTY && !isWindows) input.setRawMode(false);

    // @ts-expect-error fix for https://github.com/nodejs/node/issues/31762#issuecomment-1441223907
    rl.terminal = false;
    rl.close();
  };
}
