import type { CLIArgs } from "./index";
import { mkdir, writeFile } from "fs/promises";
import { prettierFormat } from "./prettier";
import { getCredentials } from "./credentials";
import type { DevToolsSys } from "../types";
import { findAllMappingFiles } from "./figma-publish";
import {
  cancel,
  intro,
  isCancel,
  outro,
  spinner,
  text,
  log,
  confirm,
} from "@clack/prompts";
import {
  bold,
  bgCyan,
  dim,
  yellow,
  cyan,
  green,
  bgGreen,
  white,
  magenta,
} from "picocolors";
import { extname, isAbsolute, relative } from "node:path";
import { exit } from "./exit";
import { track } from "./track";
import { safeFetch } from "./fetch";
import { pkgVersion } from "./version";
import { getFigmaComponentName, type FigmaBuilderLink } from "./figma-utils";

interface KVGetRequest {
  privateKey: string;
  spaceId: string;
}

const apiHost = "https://cdn.builder.io";

export const runFigmaMigrate = async (sys: DevToolsSys, args: CLIArgs) => {
  console.log("");
  intro(bgCyan(bold(white(" figma migrate "))) + `  ` + dim(pkgVersion));

  const { builderPrivateKey: privateKey, builderPublicKey: spaceId } =
    await getCredentials(sys, args, {
      forceSpaceId: args.spaceId,
      builderPrivateKey: true,
      builderPublicKey: true,
    });

  await track("figma publish started", {
    spaceId,
  });
  const mappingFiles = await findAllMappingFiles(sys);

  if (mappingFiles.length > 0) {
    await track("figma migrate with existing files", {
      spaceId,
    });
    log.warning(
      `${yellow("This repo already has figma mappings in:")}\n
${mappingFiles.map((a) => ` - ${relative(sys.getAppRootDir(), a)}`).join("\n")}

Migration is going to create new files, possibly duplicates.`,
    );
    const shouldContinue = await confirm({
      message: "Do you want to continue?",
      initialValue: false,
    });
    if (!shouldContinue || isCancel(shouldContinue)) {
      cancel("Migration cancelled");
      return await exit(1);
    }
  }

  log.message(
    `We are about to import your existing figma mappings into this local repo.`,
  );

  let s = spinner();
  s.start("Fetching figma mappings");
  const links = await getBuilderSpaceFigmaLinks({
    privateKey: privateKey!,
    spaceId: spaceId!,
  });

  s.stop(`Fetched ${links.length} figma mappings:`);
  log.message(
    links
      .map((l) => {
        switch (l.figmaKey) {
          // add icon
          case "design-tokens":
            return cyan(`❇︎ Design tokens mapper`);
          case "*": // generic mapper
            return yellow(`● Generic mapper`);
          default:
            return `${magenta(
              "❖" + " " + getFigmaComponentName(l.figmaName),
            )} ${dim("→")} ${l.builderName}`;
        }
      })
      .join("\n"),
  );

  const mappingsDir = await text({
    message: `Where do you want to save the figma mappings? ${dim(
      "(Ctrl+C to exit)",
    )}`,
    initialValue: "./src/mappings",
    defaultValue: "./src/mappings",
    placeholder: "./src/mappings",
    validate: (value) => {
      if (extname(value) !== "") {
        return "It must be a valid directory path, not a file";
      }
      if (isAbsolute(value)) {
        return "It must be a relative path";
      }
    },
  });
  if (isCancel(mappingsDir)) {
    cancel("Migration cancelled");
    return await exit(1);
  }

  await mkdir(mappingsDir, { recursive: true });

  await Promise.all(
    links.map(async (link) => {
      const tsSource = await convertFigmaBuilderLinkToMapping(link);
      let fileName;
      switch (link.figmaKey) {
        case "design-tokens":
          fileName = "design-tokens";
          break;
        case "*":
          fileName = "generic";
          break;
        default:
          fileName = getFigmaComponentName(link.figmaName);
      }
      const filePath = mappingsDir + "/" + fileName + ".mapper.tsx";
      log.info(green("created ") + dim(filePath));
      return writeFile(filePath, tsSource);
    }),
  );
  log.info(
    "Check out the docs for following steps: https://builder.io/docs/figma/figma-mappings",
  );
  outro(
    bgGreen(bold(white(" Done! 🎉 "))) + ` ${links.length} mappings imported`,
  );

  await track("figma migrate success", {
    spaceId,
    mappingFiles: links.length,
  });
};

export async function getBuilderSpaceFigmaLinks(kvReq: KVGetRequest) {
  const rsp = await safeFetch(
    `${apiHost}/api/internal/kv/${kvReq.spaceId}/figma-builder?apiKey=${kvReq.spaceId}`,
    {
      headers: {
        Authorization: `Bearer ${kvReq.privateKey}`,
      },
    },
  );

  if (!rsp.ok) {
    throw new Error(`Failed to fetch figma-builder links: ${rsp.statusText}`);
  }

  const result = await rsp.json();

  if (!(result instanceof Object && "figmaBuilderLinks" in result)) {
    throw new Error("Invalid response");
  }

  return result.figmaBuilderLinks as FigmaBuilderLink[];
}

async function convertFigmaBuilderLinkToMapping(link: FigmaBuilderLink) {
  // TODO: get TS type for arg
  const fn = "figmaMapping",
    arg = link.originalInputMapper?.match(
      /function Mapper\(\s*([a-zA-Z0-9_$]+)\s*(?::[^)])?\s*\)/,
    )?.[1],
    body = link.originalInputMapper?.match(
      /function Mapper\([^)]*\)(\s*{([^]*))$/,
    )?.[1];

  let src;
  switch (link.figmaKey) {
    case "design-tokens":
      src = `${fn}({
  designTokenMapper(${arg || "designToken"})${body}
})`;
      break;
    case "*":
      src = `${fn}({
  genericMapper(${arg || "figma"})${body}
})`;
      break;
    default:
      src = `${fn}({${
        link.figmaUrl ? `\n  url: ${JSON.stringify(link.figmaUrl)},` : ""
      }
  componentKey: ${JSON.stringify(link.figmaKey)}${
    body ? `,\n  mapper(${arg || "figma"})${body}` : ""
  }
})`;
  }

  return prettierFormat(
    `import { figmaMapping } from '@builder.io/dev-tools/figma';
${componentImportLine(link)}

${src}`,
    "typescript",
    undefined,
  );
}

function componentImportLine(link: FigmaBuilderLink) {
  const { exportType, importName, importPath } = link;
  if (importName && importPath) {
    const path = JSON.stringify(importPath);
    if (exportType === "named") {
      return `import { ${importName} } from ${path};`;
    } else {
      return `import ${importName} from ${path};`;
    }
  }
  return "";
}
