import type { AngularDevToolsSys } from ".";

export async function angularAddRoute(
  sys: AngularDevToolsSys,
  path: string,
  componentName: string,
  componentPath: string,
) {
  const fileExtension = sys.typescriptEnabled ? ".ts" : ".js";
  const fileName = `app.routes${fileExtension}`;
  const appRoutesPath = sys.join(sys.appDir, fileName);
  try {
    if (sys.magicast) {
      const mod = await sys.magicast.loadFile(appRoutesPath);

      // Get exports or create new if none exist
      const exportKeys = Object.keys(mod.exports);
      if (exportKeys.length === 0) {
        // Create new export named 'routes' if no exports exist
        mod.exports.routes = [];
      }

      // Get the first export (either existing or newly created)
      const exportKey = Object.keys(mod.exports)[0];
      const routes = mod.exports[exportKey];

      // Don't add the same route twice
      if (routes.find((r: any) => r.path === path)) {
        return;
      }
      const newEntry = sys.magicast.builders.raw("{}");
      newEntry.path = path;
      newEntry.component = sys.magicast.builders.raw(componentName);
      routes.push(newEntry);

      // Don't add the same import twice
      if (mod.imports.$items.find((i) => i.imported === componentName)) {
        return;
      }
      mod.imports.$append({
        imported: componentName,
        from: componentPath,
      });
      await sys.magicast.writeFile(mod, appRoutesPath);
    }
  } catch (e) {
    let content: string | null = null;
    try {
      content = sys.readFileSync(appRoutesPath);
    } catch (e) {
      sys.Sentry?.captureException(e, {
        level: "warning",
      });
    }
    sys.Sentry?.captureException(e, {
      extra: {
        content: content,
        appRoutesPath,
        componentName,
        componentPath,
      },
    });
  }
}
