import type ts from "typescript";
import type { DevToolsSys } from "../../types";
import { getTextOfPropertyName } from "./convert-values";

export function normalizeImports(sys: DevToolsSys, sourceFile: ts.SourceFile) {
  const externalImportStatements: ts.ImportDeclaration[] = [];
  const importStatements: ts.ImportDeclaration[] = [];
  const unknownImportStatements: ts.ImportDeclaration[] = [];
  const statements: ts.Statement[] = [];

  for (const statement of sourceFile.statements) {
    if (sys.ts.isImportDeclaration(statement)) {
      if (sys.ts.isStringLiteral(statement.moduleSpecifier)) {
        const importPath = statement.moduleSpecifier.text;
        if (!importPath.startsWith(".") && !importPath.startsWith("/")) {
          externalImportStatements.push(statement);
          continue;
        }
      }

      const importClause = statement.importClause;
      if (importClause) {
        const namedBindings = importClause.namedBindings;
        if (namedBindings) {
          importStatements.push(statement);
          continue;
        }

        if (importClause.name) {
          importStatements.push(statement);
          continue;
        }
      }

      unknownImportStatements.push(statement);
      continue;
    }

    statements.push(statement);
  }

  return sys.ts.factory.updateSourceFile(sourceFile, [
    ...sortImports(sys, externalImportStatements),
    ...sortImports(sys, importStatements),
    ...sortImports(sys, unknownImportStatements),
    ...statements,
  ]);
}

export function normalizeLineBreaks(code: string) {
  const lines = code.split("\n").map((l) => l.trimEnd());

  code = "";
  let foundImport = false;
  for (let i = lines.length - 1; i >= 0; i--) {
    let line = lines[i];

    if (!foundImport && line.startsWith("import ")) {
      foundImport = true;
      line = line + "\n";
    }

    if (/^builder.init(.*)$/.test(line)) {
      line = line + "\n";
    }

    if (lines[i - 1] && !lines[i - 1].includes("*/")) {
      if (line.trim().startsWith("Builder.registerComponent")) {
        line = "\n" + line;
      }

      if (line.trim().startsWith("/**")) {
        line = "\n" + line;
      }

      if (line.trim().startsWith("module.exports")) {
        line = "\n" + line;
      }

      if (line.trim().startsWith("export ")) {
        line = "\n" + line;
      }
    }

    code = line + "\n" + code;
  }

  return code.trim() + "\n";
}

function sortImports(
  sys: DevToolsSys,
  importStatements: ts.ImportDeclaration[],
) {
  return importStatements.sort((a, b) => {
    const aText = getImportName(sys, a);
    const bText = getImportName(sys, b);
    return aText.toLocaleLowerCase().localeCompare(bText.toLocaleLowerCase());
  });
}

function getImportName(sys: DevToolsSys, s: ts.ImportDeclaration) {
  const importClause = s.importClause;
  if (importClause) {
    const namedBindings = importClause.namedBindings;
    if (namedBindings && sys.ts.isNamedImports(namedBindings)) {
      if (namedBindings.elements.length > 0) {
        return namedBindings.elements[0].name.text;
      }
    }
    if (importClause.name) {
      return importClause.name.text;
    }
  }
  if (s.moduleSpecifier && sys.ts.isStringLiteral(s.moduleSpecifier)) {
    return s.moduleSpecifier.text;
  }
  return "";
}

export function ensureUseClientStringLiteral(code: string) {
  if (
    typeof code === "string" &&
    !code.includes(`'use client'`) &&
    !code.includes(`"use client"'`)
  ) {
    code = `"use client"\n${code}`;
  }
  return code;
}

export function removeUseClientStringLiteral(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
) {
  const statements = sourceFile.statements.filter((s) => {
    if (
      sys.ts.isExpressionStatement(s) &&
      sys.ts.isStringLiteral(s.expression)
    ) {
      if (sys.ts.isStringLiteral(s.expression)) {
        if (s.expression.text === "use client") {
          return false;
        }
      }
    }
    return true;
  });
  return sys.ts.factory.updateSourceFile(sourceFile, statements);
}

export function normalizeRegisteredArrayElements(
  sys: DevToolsSys,
  elements: ts.Expression[],
) {
  return elements.sort((a, b) => {
    if (
      sys.ts.isObjectLiteralExpression(a) &&
      sys.ts.isObjectLiteralExpression(b)
    ) {
      const aName = getNameProperty(sys, a);
      const bName = getNameProperty(sys, b);
      return aName.toLocaleLowerCase().localeCompare(bName.toLocaleLowerCase());
    }
    return 1;
  });
}

function getNameProperty(sys: DevToolsSys, obj: ts.ObjectLiteralExpression) {
  const prop = obj.properties.find((p) => {
    return getTextOfPropertyName(sys, p) === "name";
  });

  if (
    prop &&
    sys.ts.isPropertyAssignment(prop) &&
    sys.ts.isStringLiteral(prop.initializer)
  ) {
    return prop.initializer.text;
  }

  return "zzzz";
}
