import { debug } from "node:console";
import { request as httpRequest } from "node:http";
import { request as httpsRequest } from "node:https";

export function requestJSON<T>(opts: RequestOptions) {
  const startTime = Date.now();

  return new Promise<T>((resolve, reject) => {
    const request = getRequestModule(opts.url);
    const req = request(
      {
        protocol: opts.url.protocol,
        host: opts.url.host,
        port: opts.url.port,
        path: opts.url.pathname + opts.url.search,
        method: opts.method,
        headers: opts.headers,
      },
      (res) => {
        let data = "";
        res.on("data", (chunk) => {
          data += chunk;
        });

        res.on("end", () => {
          debug(
            `${opts.method} ${opts.url.href}, duration: ${
              Date.now() - startTime
            }ms`,
          );

          if (
            typeof res.statusCode === "number" &&
            res.statusCode >= 200 &&
            res.statusCode < 300
          ) {
            if (
              typeof res.headers["content-type"] !== "string" ||
              !res.headers["content-type"].includes("application/json")
            ) {
              reject(
                `Response from ${opts.url.href} content-type is ${res.headers["content-type"]}`,
              );
            } else {
              try {
                resolve(JSON.parse(data));
              } catch (err) {
                reject(
                  `Response from ${opts.url.href} is not valid JSON: ${data}\n${err}`,
                );
              }
            }
          } else {
            reject(
              `Request to ${opts.url.href} failed with status ${res.statusCode}: ${data}`,
            );
          }
        });
      },
    ).on("error", (e) => {
      reject(`Error fetching ${opts.url.href}, ${e}`);
    });

    if (opts.body) {
      req.setHeader("Content-Type", "application/json");
      req.write(opts.body);
    }

    req.end();
  });
}

function getRequestModule(url: URL) {
  if (url.protocol === "https:") {
    return httpsRequest;
  } else if (url.protocol === "http:") {
    return httpRequest;
  } else {
    throw new Error(`Unsupported protocol ${url.protocol}`);
  }
}

export interface RequestOptions {
  url: URL;
  headers?: Record<string, string>;
  method: string;
  body?: any;
}
