import type { DevToolsSys } from "../../index";
import { createTsProgram } from "../../../common/typescript";

export async function createQwikTsProgram(sys: DevToolsSys) {
  const entryPaths = new Set<string>();

  const recursive = async (childDir: string) => {
    const itemNames = await sys.readdir(childDir);

    await Promise.all(
      itemNames.map(async (itemName) => {
        if (itemName.startsWith(".") || itemName === "node_modules") {
          return;
        }

        const itemPath = sys.join(childDir, itemName);
        const ext = sys.extname(itemName).toLowerCase();
        if (ext === ".tsx" || ext === ".ts") {
          entryPaths.add(itemPath);
          return;
        }

        const stat = await sys.stat(itemPath);
        if (stat.isDirectory()) {
          await recursive(itemPath);
        }
      }),
    );
  };

  await recursive(sys.getAppRootDir());

  const tsProgram = await createTsProgram(sys, [...entryPaths]);
  return tsProgram;
}
