import type { ApiResponse, DevToolsServerContext } from "../types";
import type {
  IncomingMessage,
  OutgoingHttpHeaders,
  Server,
  ServerResponse,
} from "http";
import {
  PREVIEW_URL_QS,
  BUILDER_AUTH_CONNECT_PATH,
  BUILDER_AUTH_RETURN_PATH,
  DEV_TOOLS_API_PATH,
  DEV_TOOLS_CLIENT_SCRIPT_PATH,
  DEV_TOOLS_SERVER_CLOSE_PATH,
  DEV_TOOLS_MAPPINGS_PATH,
} from "../common/constants";
import { handleDevApiRequest } from "./dev-tools-api";
import { getClientScript, getConnectedStepHtml } from "./client-script";
import { getBuilderAuthConnectUrl } from "./auth";
import { getCredentials } from "../cli/credentials";
import {
  findAllMappingFiles,
  findMappingsFromFiles,
} from "../cli/figma-publish";
import { pkgVersion } from "../cli/version";

export async function handleDevRequest(
  ctx: DevToolsServerContext | null,
  server: Server,
  req: IncomingMessage,
  res: ServerResponse,
) {
  try {
    if (!ctx) {
      return text(req, res, 400, `Builder context closed`);
    }

    const url = getNodeHttpUrl(req);

    if (url.pathname === DEV_TOOLS_API_PATH) {
      if (req.method === "POST") {
        const body = await getRequestBody(req);
        if (body) {
          try {
            const apiResult = await handleDevApiRequest(ctx, JSON.parse(body));
            return json(req, res, 200, apiResult);
          } catch (e: any) {
            ctx.debug(`Dev API Error`, e.stack || e.message || e);
            const errorResponse: ApiResponse = {
              errors: [String(e.message || e)],
            };
            return json(req, res, 500, errorResponse);
          }
        }
      } else if (req.method === "OPTIONS") {
        const headers: OutgoingHttpHeaders = {
          "Access-Control-Allow-Methods": "POST, OPTIONS",
          "Access-Control-Allow-Headers":
            "Content-Type, Cache-Control, X-Builder-Devtools-Version",
        };

        const reqOrigin = req.headers.origin;
        if (reqOrigin && reqOrigin !== "null") {
          if (isValidRequestOrigin(new URL(reqOrigin))) {
            headers["Access-Control-Allow-Origin"] = reqOrigin;
          }
        } else {
          headers["Access-Control-Allow-Origin"] = "*";
        }

        // console.log("OPTIONS response", headers);
        res.writeHead(200, headers);
        res.end();
        return;
      }

      return text(
        req,
        res,
        405,
        `${DEV_TOOLS_API_PATH} is an API endpoint used only during development by the Builder.io Devtools client-side script`,
      );
    }

    if (url.pathname === DEV_TOOLS_CLIENT_SCRIPT_PATH) {
      return javascript(req, res, 200, await getClientScript(ctx));
    }

    if (url.pathname === BUILDER_AUTH_CONNECT_PATH) {
      const previewUrl = url.searchParams.get(PREVIEW_URL_QS);
      if (!previewUrl) {
        return text(req, res, 400, `Missing ${PREVIEW_URL_QS} querystring`);
      }
      return redirect(
        res,
        302,
        await getBuilderAuthConnectUrl(ctx, previewUrl),
      );
    }

    if (url.pathname === BUILDER_AUTH_RETURN_PATH) {
      return html(req, res, 200, await getConnectedStepHtml(ctx));
    }

    if (url.pathname === DEV_TOOLS_MAPPINGS_PATH) {
      await getCredentials(ctx, { _: [] }, {});

      const mappingFiles = await findAllMappingFiles(ctx);

      const { figmaBuilderLinks, diagnostics } = await findMappingsFromFiles({
        mappingFiles,
        sys: ctx,
      });

      return response(
        req,
        res,
        200,
        "application/json",
        JSON.stringify(
          {
            figmaBuilderLinks,
            foundErrors: diagnostics.length > 0,
            diagnostics,
            version: 2,
            local: true,
          },
          null,
          2,
        ),
      );
    }

    if (url.pathname === DEV_TOOLS_SERVER_CLOSE_PATH) {
      ctx.debug(`Close Dev Tools Server Requested: ${url.href}`);
      setTimeout(() => {
        try {
          server.close((err) => {
            ctx.debug(`Dev Tools Server Closed Error: ${err}`);
          });
        } catch (e) {
          ctx.debug(`Error closing devtools server: ${e}`);
        }
      }, 20);
      return text(req, res, 200, `Closing Dev Tools Server`);
    }

    return text(req, res, 404, `Not found`);
  } catch (e: any) {
    ctx?.Sentry?.captureException(e);
    return text(req, res, 500, `Dev Server Error: ${e}`);
  }
}

function json(
  req: IncomingMessage,
  res: ServerResponse,
  status: number,
  apiRsp: ApiResponse,
) {
  response(req, res, status, "application/json", JSON.stringify(apiRsp));
}

function javascript(
  req: IncomingMessage,
  res: ServerResponse,
  status: number,
  body: string,
) {
  response(req, res, status, "application/javascript", body);
}

function html(
  req: IncomingMessage,
  res: ServerResponse,
  status: number,
  body: string,
) {
  response(req, res, status, "text/html", body);
}

function text(
  req: IncomingMessage,
  res: ServerResponse,
  status: number,
  body: string,
) {
  response(req, res, status, "text/plain", body);
}

function response(
  req: IncomingMessage,
  res: ServerResponse,
  status: number,
  contentType:
    | "text/plain"
    | "text/html"
    | "application/javascript"
    | "application/json",
  body: string,
) {
  const headers: OutgoingHttpHeaders = {
    "Content-Type": `${contentType}; charset=utf-8`,
    "Cache-Control": "no-cache, no-store, must-revalidate",
    "X-Builder-Devtools-Version": pkgVersion,
  };

  const reqOrigin = req.headers.origin;
  if (reqOrigin && reqOrigin !== "null") {
    if (isValidRequestOrigin(new URL(reqOrigin))) {
      headers["Access-Control-Allow-Origin"] = reqOrigin;
    }
  } else {
    headers["Access-Control-Allow-Origin"] = "*";
  }

  res.writeHead(status, headers);
  res.end(body);
}

function isValidRequestOrigin(o: URL) {
  if (o.hostname === "localhost") {
    return true;
  }
  if (
    o.origin === "https://builder.io" ||
    o.origin === "https://www.builder.io" ||
    o.origin.endsWith(".builder.io")
  ) {
    return true;
  }
  if (o.port === "") {
    return false;
  }
  if (o.port !== "80" && o.port !== "443") {
    return true;
  }
  return false;
}

function redirect(
  response: ServerResponse,
  status: number,
  redirectTo: string,
) {
  response.writeHead(status, {
    Location: redirectTo,
  });
  response.end();
}

async function getRequestBody(request: IncomingMessage) {
  let body = "";
  for await (const chunk of request as any) {
    body += chunk;
  }
  return body;
}

function getNodeHttpUrl(req: IncomingMessage) {
  const path = req.url || "/";
  return new URL(path, `http://${req.headers.host}`);
}
