import type ts from "typescript";
import type { EnsureConfigResult } from "../../../types";
import type { DevToolsSys } from "../..";
import { codeToSource, sourceToCode } from "../../../common/ast/transform";
import { ensureImport } from "../../../common/ast/ensure-imports";
import { getExportedStatements } from "../../../common/ast/exported-statements";
import { getTextOfPropertyName } from "../../../common/ast/convert-values";

export async function viteEnsureConfigPlugin(
  sys: DevToolsSys,
  configFilePath: string,
  configContent: string,
) {
  try {
    let sourceFile = codeToSource(sys, configContent);

    const esmEsports = getExportedStatements(sys, sourceFile);
    if (esmEsports.length !== 1) {
      throw new Error(`Expected an export from ${configFilePath}`);
    }

    const defaultExport = esmEsports[0];
    if (!defaultExport.isDefaultExport) {
      throw new Error(`Expected a default export from ${configFilePath}`);
    }

    const exportAssignment = defaultExport.statement;
    if (!sys.ts.isExportAssignment(exportAssignment)) {
      throw new Error(`Expected an export assignment from ${configFilePath}`);
    }

    const result: EnsureConfigResult = {
      content: configContent,
      filePath: configFilePath,
      fileName: sys.basename(configFilePath),
      hasContentUpdates: false,
    };

    if (
      sys.ts.isCallExpression(defaultExport.node) &&
      sys.ts.isIdentifier(defaultExport.node.expression) &&
      defaultExport.node.expression.text === "defineConfig"
    ) {
      const defineConfigArgs = [...defaultExport.node.arguments];
      if (defineConfigArgs.length === 0) {
        throw new Error(`Expected an argument in defineConfig()`);
      }

      const defineConfigArg = defineConfigArgs[0];
      if (sys.ts.isArrowFunction(defineConfigArg)) {
        const block = updateDefineConfigReturnStatement(sys, defineConfigArg);
        if (!block) {
          return result;
        }
        defineConfigArgs[0] = sys.ts.factory.updateArrowFunction(
          defineConfigArg,
          defineConfigArg.modifiers,
          defineConfigArg.typeParameters,
          defineConfigArg.parameters,
          defineConfigArg.type,
          defineConfigArg.equalsGreaterThanToken,
          block,
        );

        const statements = [...sourceFile.statements];
        const index = statements.indexOf(exportAssignment);
        statements[index] = sys.ts.factory.updateExportAssignment(
          exportAssignment,
          undefined,
          sys.ts.factory.updateCallExpression(
            defaultExport.node,
            defaultExport.node.expression,
            defaultExport.node.typeArguments,
            defineConfigArgs,
          ),
        );

        sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

        sourceFile = ensureImport(sys, sourceFile, {
          namedImports: ["builderDevTools"],
          importPath: "@builder.io/dev-tools/vite",
        });

        result.content = sourceToCode(sys, sourceFile);
        result.hasContentUpdates = true;
        return result;
      }

      if (sys.ts.isObjectLiteralExpression(defineConfigArg)) {
        const configObj = updateConfigObjectLiteral(sys, defineConfigArg);
        if (!configObj) {
          return result;
        }

        const statements = [...sourceFile.statements];
        const index = statements.indexOf(exportAssignment);
        statements[index] = sys.ts.factory.updateExportAssignment(
          exportAssignment,
          undefined,
          sys.ts.factory.updateCallExpression(
            defaultExport.node,
            defaultExport.node.expression,
            defaultExport.node.typeArguments,
            [configObj],
          ),
        );

        sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

        sourceFile = ensureImport(sys, sourceFile, {
          namedImports: ["builderDevTools"],
          importPath: "@builder.io/dev-tools/vite",
        });

        result.content = sourceToCode(sys, sourceFile);
        result.hasContentUpdates = true;
        return result;
      }

      throw new Error(`Unsupported argument in defineConfig()`);
    }

    if (sys.ts.isObjectLiteralExpression(exportAssignment.expression)) {
      const configObj = updateConfigObjectLiteral(
        sys,
        exportAssignment.expression,
      );
      if (!configObj) {
        return result;
      }

      const statements = [...sourceFile.statements];
      const index = statements.indexOf(exportAssignment);
      statements[index] = sys.ts.factory.updateExportAssignment(
        exportAssignment,
        exportAssignment.modifiers,
        configObj,
      );

      sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

      sourceFile = ensureImport(sys, sourceFile, {
        namedImports: ["builderDevTools"],
        importPath: "@builder.io/dev-tools/vite",
      });

      result.content = sourceToCode(sys, sourceFile);
      result.hasContentUpdates = true;
      return result;
    }

    throw new Error(`Unable to parse ${configFilePath}`);
  } catch (e) {
    sys.Sentry?.captureException(e, {
      extra: {
        configFilePath,
        configContent,
      },
      level: "warning",
    });
    throw e;
  }
}

function updateDefineConfigReturnStatement(
  sys: DevToolsSys,
  defineConfigArg: ts.ArrowFunction | ts.FunctionExpression,
) {
  let fnBody = defineConfigArg.body;
  if (!sys.ts.isBlock(fnBody)) {
    return null;
  }

  const bodyStatements = [...fnBody.statements];
  let rIndex = bodyStatements.findIndex((s) => {
    return sys.ts.isReturnStatement(s);
  });

  if (rIndex === -1) {
    const rtnStatement = sys.ts.factory.createReturnStatement(
      sys.ts.factory.createObjectLiteralExpression(),
    );
    bodyStatements.push(rtnStatement);
    rIndex = bodyStatements.length - 1;
  }

  const rtnStatement = bodyStatements[rIndex] as ts.ReturnStatement;
  if (
    !rtnStatement.expression ||
    !sys.ts.isObjectLiteralExpression(rtnStatement.expression)
  ) {
    return null;
  }

  const configObj = updateConfigObjectLiteral(sys, rtnStatement.expression);
  if (configObj) {
    bodyStatements[rIndex] = sys.ts.factory.updateReturnStatement(
      rtnStatement,
      configObj,
    );
    return sys.ts.factory.updateBlock(fnBody, bodyStatements);
  }

  return null;
}

function updateConfigObjectLiteral(
  sys: DevToolsSys,
  config: ts.ObjectLiteralExpression,
) {
  if (isRemixFramework(sys)) {
    const remixConfig = updateCommonJsLibrary(sys, config);
    if (remixConfig) {
      config = remixConfig;
    }
  }
  const properties = [...config.properties];

  const pIndex = properties.findIndex((p) => {
    const n = getTextOfPropertyName(sys, p);
    return n === "plugins";
  });

  if (pIndex > -1) {
    const pluginsProp = properties[pIndex];
    if (
      sys.ts.isPropertyAssignment(pluginsProp) &&
      pluginsProp.initializer &&
      sys.ts.isArrayLiteralExpression(pluginsProp.initializer)
    ) {
      const pluginsArr = updatePluginsArray(
        sys,
        pluginsProp.initializer as ts.ArrayLiteralExpression,
      );
      if (pluginsArr) {
        properties[pIndex] = sys.ts.factory.updatePropertyAssignment(
          pluginsProp,
          pluginsProp.name,
          pluginsArr,
        );
        return sys.ts.factory.updateObjectLiteralExpression(config, properties);
      }
    }
    return null;
  }

  const pluginsArr = updatePluginsArray(
    sys,
    sys.ts.factory.createArrayLiteralExpression([], true),
  );
  if (pluginsArr) {
    return sys.ts.factory.updateObjectLiteralExpression(config, [
      ...properties,
      sys.ts.factory.createPropertyAssignment("plugins", pluginsArr),
    ]);
  }

  return null;
}

function updatePluginsArray(
  sys: DevToolsSys,
  pluginsArr: ts.ArrayLiteralExpression,
) {
  const hasPlugin = pluginsArr.elements.some((e) => {
    if (sys.ts.isCallExpression(e) && sys.ts.isIdentifier(e.expression)) {
      return e.expression.text === "builderDevTools";
    }
    return false;
  });

  if (hasPlugin) {
    return null;
  }

  return sys.ts.factory.updateArrayLiteralExpression(pluginsArr, [
    ...pluginsArr.elements,
    sys.ts.factory.createCallExpression(
      sys.ts.factory.createIdentifier("builderDevTools"),
      undefined,
      [],
    ),
  ]);
}

function updateCommonJsLibrary(
  sys: DevToolsSys,
  config: ts.ObjectLiteralExpression,
) {
  const properties = [...config.properties];

  const pIndex = properties.findIndex((p) => {
    const n = getTextOfPropertyName(sys, p);
    return n === "ssr";
  });

  if (pIndex > -1) {
    // if ssr object exists, update it
    const ssrProp = properties[pIndex];
    if (
      sys.ts.isPropertyAssignment(ssrProp) &&
      ssrProp.initializer &&
      sys.ts.isObjectLiteralExpression(ssrProp.initializer)
    ) {
      const ssrObj = updateExistingSsrProperty(
        sys,
        ssrProp.initializer as ts.ObjectLiteralExpression,
      );
      if (ssrObj) {
        properties[pIndex] = sys.ts.factory.updatePropertyAssignment(
          ssrProp,
          ssrProp.name,
          ssrObj,
        );
        return sys.ts.factory.updateObjectLiteralExpression(config, properties);
      }
    }
    return null;
  }

  // If no ssr object with noExternal array, create one
  const dependenciesArr = updateExternalDependenciesArray(
    sys,
    sys.ts.factory.createArrayLiteralExpression([], true),
  );
  if (dependenciesArr) {
    const ssrObjectExpression = sys.ts.factory.createObjectLiteralExpression([
      sys.ts.factory.createPropertyAssignment("noExternal", dependenciesArr),
    ]);
    return sys.ts.factory.updateObjectLiteralExpression(config, [
      ...properties,
      sys.ts.factory.createPropertyAssignment("ssr", ssrObjectExpression),
    ]);
  }

  return null;
}

function updateExistingSsrProperty(
  sys: DevToolsSys,
  ssrObj: ts.ObjectLiteralExpression,
) {
  const properties = [...ssrObj.properties];

  const pIndex = properties.findIndex((p) => {
    const n = getTextOfPropertyName(sys, p);
    return n === "noExternal";
  });

  if (pIndex > -1) {
    const dependencyArrayProp = properties[pIndex];
    if (
      sys.ts.isPropertyAssignment(dependencyArrayProp) &&
      dependencyArrayProp.initializer &&
      sys.ts.isArrayLiteralExpression(dependencyArrayProp.initializer)
    ) {
      const dependenciesArr = updateExternalDependenciesArray(
        sys,
        dependencyArrayProp.initializer as ts.ArrayLiteralExpression,
      );
      if (dependenciesArr) {
        properties[pIndex] = sys.ts.factory.updatePropertyAssignment(
          dependencyArrayProp,
          dependencyArrayProp.name,
          dependenciesArr,
        );
        return sys.ts.factory.updateObjectLiteralExpression(ssrObj, properties);
      }
    }
    return null;
  }

  const dependenciesArr = updateExternalDependenciesArray(
    sys,
    sys.ts.factory.createArrayLiteralExpression([], true),
  );
  if (dependenciesArr) {
    return sys.ts.factory.updateObjectLiteralExpression(ssrObj, [
      ...properties,
      sys.ts.factory.createPropertyAssignment("noExternal", dependenciesArr),
    ]);
  }
}

function updateExternalDependenciesArray(
  sys: DevToolsSys,
  dependenciesArr: ts.ArrayLiteralExpression,
) {
  const hasDependency = dependenciesArr.elements.some((e) => {
    if (sys.ts.isStringLiteral(e)) {
      return e.text === "@builder.io/react";
    }
    return false;
  });

  if (hasDependency) {
    return null;
  }

  return sys.ts.factory.updateArrayLiteralExpression(dependenciesArr, [
    ...dependenciesArr.elements,
    sys.ts.factory.createStringLiteral("@builder.io/react"),
  ]);
}

function isRemixFramework(sys: DevToolsSys) {
  return sys.getFrameworks().some((f) => f.name === "@remix-run/react");
}
