import type ts from "typescript";
import { SDK_VERSIONS, type ComponentInfo } from "../../../types";
import type { NextDevToolsSys } from "./index";
import { codeToSource } from "../../../common/ast/transform";
import {
  resolveComponentPath,
  findArrayStatementInitializer,
  getFilePathFromIdentifier,
} from "../../../common/ast/utils";
import {
  getComponentDisplayName,
  getComponentDisplayNameFromFilePath,
  getComponentId,
  getComponentImportPath,
  getDisplayFilePath,
  sortComponents,
} from "../../../common/utils";
import { getTextOfPropertyName } from "../../../common/ast/convert-values";
import {
  parseRegisteredComponentInfo,
  componentHasAcceptChildrenProp,
} from "../../../common/ast/component-info";
import { NEXT_BUILDER_API_KEY_ENV } from "./next-api-key";

export async function parseNextRegistryFromFile(sys: NextDevToolsSys) {
  const code = await sys.readFile(sys.registryPath);
  if (code) {
    return parseNextRegistryFromCode(sys, code);
  }
  return { components: [], sourceFile: codeToSource(sys, "") };
}

export async function parseNextRegistryFromCode(
  sys: NextDevToolsSys,
  code: string,
) {
  const sourceFile = codeToSource(sys, code);
  if (sys.sdkVersion === SDK_VERSIONS.gen2) {
    return parseNextRegistryFromSourceSdkGen2(sys, sourceFile);
  }
  return parseNextRegistryFromSource(sys, sourceFile);
}

async function parseNextRegistryFromSource(
  sys: NextDevToolsSys,
  sourceFile: ts.SourceFile,
) {
  const cmps: ComponentInfo[] = [];

  for (let i = 0; i < sourceFile.statements.length; i++) {
    const s = sourceFile.statements[i];
    if (!sys.ts.isExpressionStatement(s)) {
      continue;
    }
    const exp = s.expression;
    if (!sys.ts.isCallExpression(exp)) {
      continue;
    }
    const propAccessExp = exp.expression;
    if (!sys.ts.isPropertyAccessExpression(propAccessExp)) {
      continue;
    }
    const propAccessExpIdentifier = propAccessExp.expression;
    if (!sys.ts.isIdentifier(propAccessExpIdentifier)) {
      continue;
    }
    if (propAccessExpIdentifier.text !== "Builder") {
      continue;
    }
    const propAccessExpName = propAccessExp.name.text;
    if (propAccessExpName !== "registerComponent") {
      continue;
    }
    const args = exp.arguments;
    if (args.length < 2) {
      continue;
    }
    const infoArg = args[1];
    if (!sys.ts.isObjectLiteralExpression(infoArg)) {
      continue;
    }

    const cmpArg = args[0];
    if (sys.ts.isCallExpression(cmpArg)) {
      const fnName =
        sys.ts.isIdentifier(cmpArg.expression) && cmpArg.expression.text;

      if (fnName === "withChildren") {
        // Builder.registerComponent( withChildren( ?? ), {...})

        const wrappedCmpArg = checkWrapperFunction(sys, cmpArg.arguments[0]);

        if (sys.ts.isCallExpression(wrappedCmpArg)) {
          // Builder.registerComponent( withChildren(dynamic( .. )), {...})
          const cmp = await parseNextRegisteredDynamicComponent(
            sys,
            i,
            wrappedCmpArg,
            infoArg,
            true,
          );
          if (cmp) {
            cmps.push(cmp);
          }
        } else {
          // Builder.registerComponent( withChildren(Component), {...})
          const cmpArgArgs = wrappedCmpArg;
          if (sys.ts.isIdentifier(cmpArgArgs)) {
            const cmp = await parseNextRegisteredIdentifierComponent(
              sys,
              sourceFile,
              i,
              cmpArgArgs,
              infoArg,
              true,
            );
            if (cmp) {
              cmps.push(cmp);
            }
          }
        }
        continue;
      }

      if (fnName === "dynamic") {
        // Builder.registerComponent(dynamic(() => import('./components/heading')), {...})
        const cmp = await parseNextRegisteredDynamicComponent(
          sys,
          i,
          cmpArg,
          infoArg,
          false,
        );
        if (cmp) {
          cmps.push(cmp);
        }
        continue;
      }

      const wrappedCmpArg = checkWrapperFunction(sys, cmpArg.arguments[0]);

      if (
        sys.ts.isCallExpression(wrappedCmpArg) &&
        sys.ts.isIdentifier(wrappedCmpArg.expression) &&
        wrappedCmpArg.expression.text === "dynamic"
      ) {
        // Builder.registerComponent(forwardProps(dynamic(() => import('./components/heading'))), {...})
        const cmp = await parseNextRegisteredDynamicComponent(
          sys,
          i,
          wrappedCmpArg,
          infoArg,
          false,
        );
        if (cmp) {
          cmps.push(cmp);
        }
        continue;
      }

      if (sys.ts.isIdentifier(wrappedCmpArg)) {
        // Builder.registerComponent(forwardProps(Component), {...} )
        const cmp = await parseNextRegisteredIdentifierComponent(
          sys,
          sourceFile,
          i,
          wrappedCmpArg,
          infoArg,
          false,
        );
        if (cmp) {
          cmps.push(cmp);
        }
        continue;
      }

      continue;
    }

    if (sys.ts.isIdentifier(cmpArg)) {
      // Builder.registerComponent(Component, {...} )
      const cmp = await parseNextRegisteredIdentifierComponent(
        sys,
        sourceFile,
        i,
        cmpArg,
        infoArg,
        false,
      );
      if (cmp) {
        cmps.push(cmp);
      }
      continue;
    }
  }

  return { sourceFile, components: sortComponents(cmps) };
}

function checkWrapperFunction(
  sys: NextDevToolsSys,
  node: ts.Expression,
): ts.Expression {
  if (sys.ts.isCallExpression(node)) {
    const fnName = sys.ts.isIdentifier(node.expression) && node.expression.text;
    if (fnName === "dynamic") {
      return node;
    } else {
      return checkWrapperFunction(sys, node.arguments[0]);
    }
  }
  return node;
}

async function parseNextRegisteredDynamicComponent(
  sys: NextDevToolsSys,
  statementIndex: number,
  dynamicArg: ts.CallExpression,
  infoArg: ts.ObjectLiteralExpression,
  withChildrenWrapper: boolean,
) {
  let cmp: ComponentInfo = {
    id: "",
    name: "",
    displayFilePath: "",
    exportName: "",
    importName: "",
    filePath: "",
    relFilePath: "",
    importPath: "",
    inputs: [],
    nodeIndex: statementIndex,
    acceptsChildren: withChildrenWrapper,
    framework: "react",
  };

  const importArgArgs = dynamicArg.arguments;
  if (importArgArgs.length === 0) {
    return null;
  }

  const importArgArg = importArgArgs[0];
  if (!sys.ts.isArrowFunction(importArgArg)) {
    return null;
  }

  const importBody = importArgArg.body;
  if (sys.ts.isCallExpression(importBody)) {
    // probably default export
    // dynamic(() => import('./components/heading')),
    const importExp = importBody.expression;
    if (importExp.kind !== sys.ts.SyntaxKind.ImportKeyword) {
      return null;
    }

    if (importBody.arguments.length !== 1) {
      return null;
    }

    const importPathArg = importBody.arguments[0];
    if (!sys.ts.isStringLiteral(importPathArg)) {
      return null;
    }

    const importPath = importPathArg.text;

    const resolvedPath = await resolveNextImportPath(sys, importPath);
    cmp.filePath = resolvedPath;
    cmp.relFilePath = sys.relative(sys.getAppRootDir(), cmp.filePath);

    if (await sys.exists(resolvedPath)) {
      cmp.importPath = getComponentImportPath(sys, cmp.filePath);
    } else {
      // If path doesn't exist, it's an external module
      cmp.externalImportPath = importPath;
      cmp.importPath = cmp.externalImportPath;
    }

    cmp.name = getComponentDisplayNameFromFilePath(sys, cmp.filePath);
    cmp.importName = "default";
    cmp.exportName = "default";
    cmp.exportType = "default";
  } else if (sys.ts.isPropertyAccessExpression(importBody)) {
    // probably named export
    // dynamic(async () => (await import("./components/title")).Title),
    const paranthesizedExp = importBody.expression;
    if (!sys.ts.isParenthesizedExpression(paranthesizedExp)) {
      return null;
    }

    const awaitExpr = paranthesizedExp.expression;
    if (!sys.ts.isAwaitExpression(awaitExpr)) {
      return null;
    }

    const callExp = awaitExpr.expression;
    if (!sys.ts.isCallExpression(callExp)) {
      return null;
    }

    const importExp = callExp.expression;
    if (importExp.kind !== sys.ts.SyntaxKind.ImportKeyword) {
      return null;
    }

    if (callExp.arguments.length !== 1) {
      return null;
    }

    const importPathArg = callExp.arguments[0];
    if (!sys.ts.isStringLiteral(importPathArg)) {
      return null;
    }

    if (!sys.ts.isIdentifier(importBody.name)) {
      return null;
    }

    const importPath = importPathArg.text;

    const resolvedPath = await resolveNextImportPath(sys, importPath);
    cmp.filePath = resolvedPath;
    cmp.relFilePath = sys.relative(sys.getAppRootDir(), cmp.filePath);

    if (await sys.exists(resolvedPath)) {
      cmp.importPath = getComponentImportPath(sys, cmp.filePath);
    } else {
      // If path doesn't exist, it's an external module
      cmp.externalImportPath = importPath;
      cmp.importPath = cmp.externalImportPath;
    }

    cmp.name = getComponentDisplayName(importBody.name.text);
    cmp.importName = importBody.name.text;
    cmp.exportName = importBody.name.text;
    cmp.exportType = "named";
  } else {
    return null;
  }

  cmp = parseRegisteredComponentInfo(sys, cmp, infoArg);

  cmp.displayFilePath = getDisplayFilePath(sys, cmp.filePath);

  if (cmp.filePath && cmp.exportName) {
    cmp.id = await getComponentId(
      sys,
      cmp.filePath,
      cmp.exportName,
      cmp.externalImportPath,
    );
  }
  return cmp;
}

async function parseNextRegisteredIdentifierComponent(
  sys: NextDevToolsSys,
  sourceFile: ts.SourceFile,
  statementIndex: number,
  identifierArg: ts.Identifier,
  infoArg: ts.ObjectLiteralExpression,
  withChildrenWrapper: boolean,
) {
  let cmp: ComponentInfo = {
    id: "",
    name: "",
    displayFilePath: "",
    exportName: "",
    importName: "",
    filePath: "",
    relFilePath: "",
    importPath: "",
    inputs: [],
    nodeIndex: statementIndex,
    acceptsChildren: withChildrenWrapper,
    framework: "react",
  };

  const importDeclarations = sourceFile.statements.filter((s) =>
    sys.ts.isImportDeclaration(s),
  ) as ts.ImportDeclaration[];

  const defaultImportDeclaration = importDeclarations.find(
    (s) => s.importClause && s.importClause.name?.text === identifierArg.text,
  );

  if (defaultImportDeclaration) {
    const importPath = defaultImportDeclaration.moduleSpecifier;
    if (!sys.ts.isStringLiteral(importPath)) {
      return null;
    }
    const resolvedPath = await resolveNextImportPath(sys, importPath.text);
    cmp.filePath = resolvedPath;
    cmp.relFilePath = sys.relative(sys.getAppRootDir(), cmp.filePath);

    if (await sys.exists(resolvedPath)) {
      cmp.importPath = getComponentImportPath(sys, cmp.filePath);
    } else {
      // If path doesn't exist, it's an external module
      cmp.externalImportPath = importPath.text;
      cmp.importPath = cmp.externalImportPath;
    }

    cmp.name = getComponentDisplayNameFromFilePath(sys, cmp.filePath);
    cmp.importName = identifierArg.text;
    cmp.exportName = "default";
    cmp.exportType = "default";
  } else {
    const namedImportDeclaration = importDeclarations.find((s) => {
      if (!s.importClause) {
        return false;
      }
      if (!s.importClause.namedBindings) {
        return false;
      }
      if (!sys.ts.isNamedImports(s.importClause.namedBindings)) {
        return false;
      }
      return s.importClause.namedBindings.elements.some(
        (e) => e.name.text === identifierArg.text,
      );
    });
    if (!namedImportDeclaration) {
      return null;
    }
    const importPath = namedImportDeclaration.moduleSpecifier;
    if (!sys.ts.isStringLiteral(importPath)) {
      return null;
    }

    const resolvedPath = await resolveNextImportPath(sys, importPath.text);
    cmp.filePath = resolvedPath;
    cmp.relFilePath = sys.relative(sys.getAppRootDir(), cmp.filePath);

    // If path doesn't exist, it's an external module
    if (await sys.exists(resolvedPath)) {
      cmp.importPath = getComponentImportPath(sys, cmp.filePath);
    } else {
      cmp.externalImportPath = importPath.text;
      cmp.importPath = cmp.externalImportPath;
    }

    cmp.name = getComponentDisplayName(identifierArg.text);
    cmp.importName = identifierArg.text;
    cmp.exportName = identifierArg.text;
    cmp.exportType = "named";
  }

  cmp = parseRegisteredComponentInfo(sys, cmp, infoArg);

  cmp.displayFilePath = getDisplayFilePath(sys, cmp.filePath);

  if (cmp.filePath && cmp.exportName) {
    cmp.id = await getComponentId(
      sys,
      cmp.filePath,
      cmp.exportName,
      cmp.externalImportPath,
    );
  }
  return cmp;
}

function resolveNextImportPath(sys: NextDevToolsSys, importPath: string) {
  return resolveComponentPath(
    sys,
    sys.normalize(sys.resolve(sys.dirname(sys.registryPath), importPath)),
  );
}

export function ensureBuilderIsInitialized(
  sys: NextDevToolsSys,
  sourceFile: ts.SourceFile,
  typescriptEnabled: boolean,
) {
  let isApiKeyInitialized = false;
  let lastImportIndex = 0;

  const statements = [...sourceFile.statements];

  for (let i = 0; i < statements.length; i++) {
    const s = statements[i];
    if (sys.ts.isImportDeclaration(s)) {
      lastImportIndex = i;
      continue;
    }
    if (!sys.ts.isExpressionStatement(s)) {
      continue;
    }
    const exp = s.expression;
    if (!sys.ts.isCallExpression(exp)) {
      continue;
    }
    const propAccessExp = exp.expression;
    if (!sys.ts.isPropertyAccessExpression(propAccessExp)) {
      continue;
    }
    const propAccessExpIdentifier = propAccessExp.expression;
    if (!sys.ts.isIdentifier(propAccessExpIdentifier)) {
      continue;
    }
    if (propAccessExpIdentifier.text !== "builder") {
      continue;
    }
    const propAccessExpName = propAccessExp.name.text;
    if (propAccessExpName == "init") {
      isApiKeyInitialized = true;
      break;
    } else {
      continue;
    }
  }

  if (isApiKeyInitialized) {
    return sourceFile;
  }

  // process.env.NEXT_PUBLIC_BUILDER_API_KEY
  const envKeyExpression = sys.ts.factory.createPropertyAccessExpression(
    sys.ts.factory.createPropertyAccessExpression(
      sys.ts.factory.createIdentifier("process"),
      sys.ts.factory.createIdentifier("env"),
    ),
    sys.ts.factory.createIdentifier(NEXT_BUILDER_API_KEY_ENV),
  );

  const nonNullEnvKeyExpression = typescriptEnabled
    ? sys.ts.factory.createNonNullExpression(envKeyExpression)
    : envKeyExpression;

  const initializeBuilderApiKey = sys.ts.factory.createExpressionStatement(
    sys.ts.factory.createCallExpression(
      sys.ts.factory.createPropertyAccessExpression(
        sys.ts.factory.createIdentifier("builder"),
        sys.ts.factory.createIdentifier("init"),
      ),
      undefined,
      [nonNullEnvKeyExpression],
    ),
  );

  statements.splice(lastImportIndex + 1, 0, initializeBuilderApiKey);

  return sys.ts.factory.updateSourceFile(sourceFile, statements);
}

async function parseNextRegistryFromSourceSdkGen2(
  sys: NextDevToolsSys,
  sourceFile: ts.SourceFile,
) {
  const cmps: ComponentInfo[] = [];

  const s = findArrayStatementInitializer(sys, sourceFile, "CUSTOM_COMPONENTS");

  if (s) {
    await Promise.all(
      s.arrayLiteralExp.elements.map(async (cmpElm, i) => {
        const cmp = await parseNextRegisteredComponentSdkGen2(
          sys,
          sourceFile,
          cmpElm,
        );
        if (cmp) {
          cmp.nodeIndex = i;
          cmps.push(cmp);
        }
      }),
    );
  }

  return { sourceFile, components: sortComponents(cmps) };
}

export async function parseNextRegisteredComponentSdkGen2(
  sys: NextDevToolsSys,
  sourceFile: ts.SourceFile,
  cmpObjLit: ts.Node,
) {
  if (!sys.ts.isObjectLiteralExpression(cmpObjLit)) {
    return null;
  }

  let cmp: ComponentInfo = {
    id: "",
    name: "",
    displayFilePath: "",
    exportName: "",
    importName: "",
    filePath: "",
    relFilePath: "",
    importPath: "",
    inputs: [],
    acceptsChildren: false,
    framework: "react",
  };

  const acceptsChildren = componentHasAcceptChildrenProp(sys, cmpObjLit);

  if (acceptsChildren) {
    cmp.acceptsChildren = true;
  }

  for (const p of cmpObjLit.properties) {
    if (!sys.ts.isPropertyAssignment(p) || !sys.ts.isIdentifier(p.name)) {
      continue;
    }
    const propName = getTextOfPropertyName(sys, p);

    if (propName === "component") {
      let identifierName: string;
      const { initializer } = p;

      if (sys.ts.isCallExpression(initializer)) {
        const fnName =
          sys.ts.isIdentifier(initializer.expression) &&
          initializer.expression.text;

        if (fnName === "dynamic") {
          // component: dynamic(() => import("./components/Counter/Counter")),

          const importArgArgs = initializer.arguments;
          if (importArgArgs.length === 0) {
            return null;
          }

          const importArgArg = importArgArgs[0];
          if (!sys.ts.isArrowFunction(importArgArg)) {
            return null;
          }

          const importBody = importArgArg.body;
          if (sys.ts.isCallExpression(importBody)) {
            // probably default export
            // dynamic(() => import('./components/heading')),
            const importExp = importBody.expression;
            if (importExp.kind !== sys.ts.SyntaxKind.ImportKeyword) {
              return null;
            }

            if (importBody.arguments.length !== 1) {
              return null;
            }

            const importPathArg = importBody.arguments[0];
            if (!sys.ts.isStringLiteral(importPathArg)) {
              return null;
            }

            const importPath = importPathArg.text;

            const resolvedPath = await resolveNextImportPath(sys, importPath);
            cmp.filePath = resolvedPath;
            cmp.relFilePath = sys.relative(sys.getAppRootDir(), cmp.filePath);

            if (await sys.exists(resolvedPath)) {
              cmp.importPath = getComponentImportPath(sys, cmp.filePath);
            } else {
              // If path doesn't exist, it's an external module
              cmp.externalImportPath = importPath;
              cmp.importPath = cmp.externalImportPath;
            }

            cmp.name = getComponentDisplayNameFromFilePath(sys, cmp.filePath);
            cmp.importName = "default";
            cmp.exportName = "default";
            cmp.exportType = "default";
          } else if (sys.ts.isPropertyAccessExpression(importBody)) {
            // probably named export
            // dynamic(async () => (await import("./components/title")).Title),
            const paranthesizedExp = importBody.expression;
            if (!sys.ts.isParenthesizedExpression(paranthesizedExp)) {
              return null;
            }

            const awaitExpr = paranthesizedExp.expression;
            if (!sys.ts.isAwaitExpression(awaitExpr)) {
              return null;
            }

            const callExp = awaitExpr.expression;
            if (!sys.ts.isCallExpression(callExp)) {
              return null;
            }

            const importExp = callExp.expression;
            if (importExp.kind !== sys.ts.SyntaxKind.ImportKeyword) {
              return null;
            }

            if (callExp.arguments.length !== 1) {
              return null;
            }

            const importPathArg = callExp.arguments[0];
            if (!sys.ts.isStringLiteral(importPathArg)) {
              return null;
            }

            if (!sys.ts.isIdentifier(importBody.name)) {
              return null;
            }

            const importPath = importPathArg.text;

            const resolvedPath = await resolveNextImportPath(sys, importPath);
            cmp.filePath = resolvedPath;
            cmp.relFilePath = sys.relative(sys.getAppRootDir(), cmp.filePath);

            if (await sys.exists(resolvedPath)) {
              cmp.importPath = getComponentImportPath(sys, cmp.filePath);
            } else {
              // If path doesn't exist, it's an external module
              cmp.externalImportPath = importPath;
              cmp.importPath = cmp.externalImportPath;
            }

            cmp.name = getComponentDisplayName(importBody.name.text);
            cmp.importName = importBody.name.text;
            cmp.exportName = importBody.name.text;
            cmp.exportType = "named";
          } else {
            return null;
          }

          cmp = parseRegisteredComponentInfo(sys, cmp, cmpObjLit);

          cmp.displayFilePath = getDisplayFilePath(sys, cmp.filePath);

          if (cmp.filePath && cmp.exportName) {
            cmp.id = await getComponentId(
              sys,
              cmp.filePath,
              cmp.exportName,
              cmp.externalImportPath,
            );
          }
          return cmp;
        }
      }

      if (sys.ts.isIdentifier(initializer)) {
        identifierName = initializer.text;
      }

      //@ts-ignore
      if (!identifierName) {
        continue;
      }

      if (cmp.name === "") {
        cmp.name = getComponentDisplayName(identifierName);
      }
      cmp.importName = identifierName;

      const i = await getFilePathFromIdentifier(
        sys,
        sys.registryPath,
        sourceFile,
        identifierName,
      );
      if (!i) {
        continue;
      }

      cmp.filePath = i.filePath;
      cmp.relFilePath = i.relFilePath;

      if (await sys.exists(i.filePath)) {
        cmp.importPath = getComponentImportPath(sys, i.filePath);
      } else {
        // If path doesn't exist, it's an external module
        cmp.externalImportPath = i.importPath;
        cmp.importPath = cmp.externalImportPath;
      }

      cmp.importPath = getComponentImportPath(sys, i.filePath);
      cmp.displayFilePath = i.displayFilePath;
      cmp.exportType = i.exportType;

      const importDeclarations = sourceFile.statements.filter((s) =>
        sys.ts.isImportDeclaration(s),
      ) as ts.ImportDeclaration[];

      const defaultImportDeclaration = importDeclarations.find(
        (s) => s.importClause && s.importClause.name?.text === identifierName,
      );

      if (defaultImportDeclaration) {
        const importPath = defaultImportDeclaration.moduleSpecifier;
        if (!sys.ts.isStringLiteral(importPath)) {
          return null;
        }
        cmp.exportName = "default";
      } else {
        const namedImportDeclaration = importDeclarations.find((s) => {
          if (!s.importClause) {
            return false;
          }
          if (!s.importClause.namedBindings) {
            return false;
          }
          if (!sys.ts.isNamedImports(s.importClause.namedBindings)) {
            return false;
          }
          return s.importClause.namedBindings.elements.some(
            (e) => e.name.text === identifierName,
          );
        });
        if (!namedImportDeclaration) {
          return null;
        }
        const importPath = namedImportDeclaration.moduleSpecifier;
        if (!sys.ts.isStringLiteral(importPath)) {
          return null;
        }

        cmp.exportName = identifierName;
      }

      continue;
    }
  }

  cmp = parseRegisteredComponentInfo(sys, cmp, cmpObjLit);

  if (cmp.filePath && cmp.exportName) {
    cmp.id = await getComponentId(
      sys,
      cmp.filePath,
      cmp.exportName,
      cmp.externalImportPath,
    );
  }

  return cmp;
}
