import type {
  CreateDevToolsOptions,
  DevToolsAdapter,
  DevToolsSys,
} from "../../../types";
import { getBuilderCache, setBuilderCache } from "../../../common/cache";
import { getRemixApiKey, setRemixApiKey } from "./remix-api-key";
import {
  findBuilderRegistry,
  hasTsconfig,
  recursiveFindDir,
} from "../../../common/fs";
import {
  remixEnsureBuilderSetup,
  remixEnsureFigmaImportSetup,
} from "./remix-ensure-builder-setup";
import { remixComponentRegistry } from "./remix-registry";
import { SDK_VERSIONS } from "../../../types";
import { detectReactSdkVersion } from "../react/react-builder-sdk-setup";

export async function createRemixDevTools(sys: CreateDevToolsOptions) {
  const remixSys = await createRemixDevToolsSys(sys);

  const devTools: DevToolsAdapter = {
    builderPageOptions: async () => {
      const pathname = "/builder-demo";
      const localePathname = remixSys.hasLocaleParam
        ? `/en-us${pathname}`
        : undefined;

      return {
        templateContentId: "a5c9eef7909d4fc99554264a17de22c9",
        pathname,
        localePathname,
        title: "Builder + Remix Demo Page",
      };
    },
    ensureBuilderSetup: () => {
      return remixEnsureBuilderSetup(remixSys);
    },
    ensureFigmaImportPage: () => {
      return remixEnsureFigmaImportSetup(remixSys);
    },
    getRegistryPath: () => {
      return remixSys.registryPath;
    },
    getRegistry: (opts) => {
      return remixComponentRegistry(
        remixSys,
        null,
        false,
        false,
        null,
        null,
        false,
        !!opts?.readAllInputTypes,
      );
    },
    addExternalPackage: (pkgName: string) => {
      remixSys.addExternalPackage(pkgName);
    },
    loadComponent: async (opts) => {
      const r = await remixComponentRegistry(
        remixSys,
        opts.cmpId,
        false,
        false,
        null,
        null,
        false,
        false,
      );
      return {
        ...r,
        component: r.components.find((c) => c.id === opts.cmpId)!,
      };
    },
    registerComponent: (opts) => {
      return remixComponentRegistry(
        remixSys,
        opts.cmpId,
        true,
        false,
        null,
        null,
        true,
        false,
      );
    },
    setRegisteredComponentInfo: (opts) => {
      return remixComponentRegistry(
        remixSys,
        opts.cmpId,
        false,
        false,
        opts,
        null,
        true,
        false,
      );
    },
    setRegisteredComponentInput: (opts) => {
      return remixComponentRegistry(
        remixSys,
        opts.cmpId,
        false,
        false,
        null,
        opts,
        true,
        false,
      );
    },
    unregisterComponent: (opts) => {
      return remixComponentRegistry(
        remixSys,
        opts.cmpId,
        false,
        true,
        null,
        null,
        true,
        false,
      );
    },
    getPublicApiKey: () => {
      return getRemixApiKey(remixSys);
    },
    setPublicApiKey: (opts) => {
      return setRemixApiKey(remixSys, opts.publicApiKey);
    },
    getDependencies: (opts) => {
      if (opts.sdkVersion === SDK_VERSIONS.gen2) {
        return [
          { name: "@builder.io/dev-tools" },
          { name: "@builder.io/sdk-react" },
        ];
      }
      return [
        { name: "@builder.io/dev-tools" },
        { name: "@builder.io/react" },
        { name: "@builder.io/sdk" },
      ];
    },
    getDevRunCommand: () => {
      return "dev";
    },
    getCache: () => getBuilderCache(remixSys),
    setCache: (cache) => setBuilderCache(remixSys, cache),
  };

  return devTools;
}

export async function createRemixDevToolsSys(sys: DevToolsSys) {
  const remixConfigPath = await getRemixConfigPath(sys);
  const rootDir = sys.getAppRootDir();
  const routesDir = await getRemixRoutesDir(sys);
  const appDir = sys.dirname(routesDir);
  const componentsDir = sys.join(appDir, "components");
  const typescriptEnabled = await hasTsconfig(sys);
  const registryPath = await findBuilderRegistry(sys, appDir);
  const externalPackages: Record<string, string> = {};

  const remixSys: RemixDevToolsSys = {
    ...sys,
    registryPath,
    remixConfigPath,
    typescriptEnabled,
    appDir,
    componentsDir,
    routesDir,
    hasLocaleParam: await hasLocaleParam(sys, routesDir),
    addExternalPackage: (pkgName: string) => {
      externalPackages[sys.join(rootDir, pkgName, "index.ts")] =
        `export * from "${pkgName}";`;
    },
    readFileSync: (path: string) =>
      externalPackages[path] ?? sys.readFileSync(path),

    existsSync: (path: string) =>
      !!externalPackages[path] || sys.existsSync(path),

    readdirSync: (path: string) => {
      const realFiles = sys.readdirSync(path);
      if (path === rootDir) {
        return [
          ...realFiles,
          ...Object.keys(externalPackages).map((p) =>
            p.slice(rootDir.length + 1),
          ),
        ];
      }
      return realFiles;
    },
    readdir: async (path: string) => {
      const realFiles = await sys.readdir(path);
      if (path === rootDir) {
        return [
          ...realFiles,
          ...Object.keys(externalPackages).map((p) =>
            p.slice(rootDir.length + 1),
          ),
        ];
      }
      return realFiles;
    },
  };

  if (sys.sdkVersion) {
    remixSys.sdkVersion = sys.sdkVersion;
  } else {
    remixSys.sdkVersion = await detectReactSdkVersion(remixSys);
  }

  return remixSys;
}

async function hasLocaleParam(sys: DevToolsSys, routesDir: string) {
  const routesDirFiles = await sys.readdir(routesDir);
  return routesDirFiles.some((f) => f.startsWith(`($locale).`));
}

async function getRemixConfigPath(sys: DevToolsSys) {
  const configs = [
    "remix.config.ts",
    "remix.config.js",
    "vite.config.ts",
    "vite.config.js",
  ];
  for (const configName of configs) {
    const configPath = sys.join(sys.getAppRootDir(), configName);
    if (await sys.exists(configPath)) {
      return configPath;
    }
  }
  if (sys.ignoreMissingConfig) {
    return "";
  }

  throw new Error(
    `Could not find Remix config file. Searched for ${configs.join(
      ", ",
    )} in "${sys.getAppRootDir()}".`,
  );
}

async function getRemixRoutesDir(sys: DevToolsSys) {
  const routesDir = await recursiveFindDir(sys, sys.getAppRootDir(), "routes");
  if (routesDir) {
    return routesDir;
  }
  throw new Error(
    `Could not find Remix "routes" directory in "${sys.getAppRootDir()}". Please ensure this command is ran from within your Remix project's directory.`,
  );
}

export interface RemixDevToolsSys extends DevToolsSys {
  appDir: string;
  componentsDir: string;
  registryPath: string;
  remixConfigPath: string;
  typescriptEnabled: boolean;
  routesDir: string;
  hasLocaleParam: boolean;
  addExternalPackage: (pkgName: string) => void;
}
