import type { AppDependency, ComponentInfo, DevToolsSys } from "../../types";
import type ts from "typescript";
import { normalizePathSlash } from "../utils";

export function getSourceDependencies(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker | null,
  fnNode: ts.Node,
) {
  if (!fnNode || !typeChecker) {
    return undefined;
  }

  const symbol = typeChecker.getSymbolAtLocation(fnNode);
  if (!symbol) {
    return undefined;
  }

  const valueDeclaration = symbol.valueDeclaration;
  if (!valueDeclaration) {
    return undefined;
  }

  const filePath = valueDeclaration.getSourceFile().fileName;
  const moduleName = getModuleNameFromPath(sys, filePath);
  if (!moduleName) {
    return undefined;
  }

  const dep: AppDependency = {
    name: moduleName,
  };
  return [dep];
}

function getModuleNameFromPath(sys: DevToolsSys, filePath: string) {
  if (typeof filePath === "string") {
    filePath = sys.normalize(normalizePathSlash(filePath));
    const parts = filePath.split("/");
    const nodeModuleIndex = parts.lastIndexOf("node_modules");
    if (nodeModuleIndex > -1) {
      const moduleParts = parts.slice(nodeModuleIndex + 1);
      let moduleName = moduleParts[0];
      if (moduleName) {
        if (moduleName.startsWith("@") && moduleParts[1]) {
          moduleName += "/" + moduleParts[1];
        }
        return moduleName;
      }
    }
  }
  return undefined;
}

export function getAppDependencies(cmps: ComponentInfo[]) {
  const deps: AppDependency[] = [];
  for (const cmp of cmps) {
    if (cmp.dependencies) {
      for (const dep of cmp.dependencies) {
        if (!deps.some((d) => d.name === dep.name)) {
          deps.push({ ...dep });
        }
      }
    }
  }
  return deps.sort((a, b) => a.name.localeCompare(b.name));
}
