import type { RemixDevToolsSys } from "./index";
import type ts from "typescript";
import {
  getReactComponentsFromSourceFile,
  type ReactComponentSource,
} from "../react/react-components";
import { shouldSkipFolder } from "../../../common/utils";

const SKIP_FOLDERS = new Set(["dist", "node_modules"]);

export async function getRemixSourceComponentsFromDir(
  sys: RemixDevToolsSys,
  tsProgram: ts.Program,
) {
  const cmps: ReactComponentSource[] = [];
  const sourceFiles = tsProgram.getSourceFiles();
  const typeChecker = tsProgram.getTypeChecker();

  const buildDir = sys.join(sys.getAppRootDir(), "build");

  await Promise.all(
    sourceFiles.map(async (sourceFile) => {
      const fileName = sys.basename(sourceFile.fileName);
      if (
        fileName.startsWith(".") ||
        fileName === "node_modules" ||
        shouldSkipFolder(sys, SKIP_FOLDERS, sourceFile.fileName) ||
        SKIP_APP_FILES.has(fileName) ||
        fileName.includes("$.")
      ) {
        return;
      }

      const filePath = sys.normalize(sourceFile.fileName);
      if (filePath === sys.remixConfigPath || filePath === buildDir) {
        return;
      }

      const fileCmps = await getReactComponentsFromSourceFile(
        sys,
        typeChecker,
        filePath,
        sourceFile,
        filePath === sys.registryPath,
      );
      cmps.push(...fileCmps);
    }),
  );

  return cmps;
}

// https://remix.run/docs/en/main/file-conventions/route-files-v2
const SKIP_APP_FILES = new Set([
  "root.tsx",
  "root.ts",
  "root.jsx",
  "root.js",
  "_index.tsx",
  "_index.jsx",
  "_index.ts",
  "_index.js",
]);
