import type ts from "typescript";
import type { AppDependency, ComponentInfo, DevToolsSys } from "../../../types";
import { codeToSource } from "../../../common/ast/transform";
import {
  getComponentDisplayName,
  getComponentId,
  getComponentDisplayNameFromFilePath,
  getComponentImportNameFilePath,
  getDisplayFilePath,
  isValidJsxTag,
  getComponentImportPath,
  shouldSkipFolder,
} from "../../../common/utils";
import {
  getExportedStatements,
  type ExportedStatement,
} from "../../../common/ast/exported-statements";
import { hasJsxNode } from "../../../common/ast/utils";
import { createTsProgram, getTypeChecker } from "../../../common/typescript";
import { parseComponentSourcePropsFromNode } from "../../../common/ast/component-input-types";
import { getSourceDependencies } from "../../../common/ast/app-dependencies";
import type { ReactDevToolsSys } from "./index";
import { getImportedStatements } from "../../../common/ast/imported-statements";

export async function parseReactAllComponentSourceInputs(
  sys: DevToolsSys,
  components: ComponentInfo[],
) {
  const hasUnregistered = components.some((c) => !c.isRegistered);
  if (!hasUnregistered) {
    return components;
  }

  const tsProgram = await createTsProgram(
    sys,
    components.map((c) => c.filePath),
  );
  const typeChecker = tsProgram.getTypeChecker();

  const componentsWithTypes = await Promise.all(
    components.map(async (cmp) => {
      const sourceFile = tsProgram.getSourceFile(cmp.filePath);
      if (sourceFile) {
        const typedSrcCmps = await getReactComponentsFromSourceFile(
          sys,
          typeChecker,
          cmp.filePath,
          sourceFile,
          false,
        );
        const typedSrcCmp = typedSrcCmps.find((t) => t.cmpInfo.id === cmp.id);
        if (typedSrcCmp) {
          cmp.inputs = parseComponentSourcePropsFromNode(
            sys,
            typeChecker,
            typedSrcCmp.node.fnNode,
          );
        }
      }
      return cmp;
    }),
  );

  return componentsWithTypes;
}

export async function parseReactComponentSourceInputs(
  sys: DevToolsSys,
  srcCmp: ReactComponentSource,
  tsProgram: ts.Program | null,
  includeImports: boolean,
) {
  if (srcCmp.node.fnNode) {
    const { sourceFile, typeChecker } = await getTypeChecker(
      sys,
      srcCmp.cmpInfo.filePath,
      tsProgram,
    );

    const typedSrcCmps = await getReactComponentsFromSourceFile(
      sys,
      typeChecker,
      srcCmp.cmpInfo.filePath,
      sourceFile,
      includeImports,
    );
    const typedSrcCmp = typedSrcCmps.find(
      (t) => t.cmpInfo.id === srcCmp.cmpInfo.id,
    );
    if (!typedSrcCmp) {
      throw new Error(
        `createComponentInputs() Could not find component ${srcCmp.cmpInfo.name} in ${srcCmp.cmpInfo.filePath}`,
      );
    }

    return parseComponentSourcePropsFromNode(
      sys,
      typeChecker,
      typedSrcCmp.node.fnNode,
    );
  }
  return [];
}

export async function getReactComponentsFromPath(
  sys: DevToolsSys,
  filePath: string,
  includeImports: boolean,
) {
  const ext = sys.extname(filePath).toLowerCase();
  if (ext === ".tsx" || ext === ".jsx" || ext === ".ts" || ext === ".js") {
    const code = await sys.readFile(filePath);
    if (code) {
      return getReactComponentsFromCode(sys, filePath, code, includeImports);
    }
  }
  return null;
}

export function getReactComponentsFromCode(
  sys: DevToolsSys,
  filePath: string,
  code: string,
  includeImports: boolean = false,
) {
  const sourceFile = codeToSource(sys, code);
  return getReactComponentsFromSourceFile(
    sys,
    null,
    filePath,
    sourceFile,
    includeImports,
  );
}

export async function getReactComponentsFromSourceFile(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker | null,
  filePath: string,
  sourceFile: ts.SourceFile,
  includeImports: boolean,
) {
  const fileCmps: ReactComponentSource[] = [];

  for (const s of sourceFile.statements) {
    if (
      sys.ts.isExpressionStatement(s) &&
      sys.ts.isStringLiteral(s.expression)
    ) {
      if (s.expression.text === "use server") {
        // react server component files should be ignored
        return fileCmps;
      }
    }
  }

  const exports = getExportedStatements(sys, sourceFile);
  if (includeImports) {
    exports.push(...getImportedStatements(sys, sourceFile));
  }
  filePath = sys.normalize(filePath);

  await Promise.all(
    exports.map(async (exp) => {
      const sCmps = await getReactComponentSource(
        sys,
        typeChecker,
        filePath,
        exp,
      );
      fileCmps.push(...sCmps);
    }),
  );

  return fileCmps;
}

async function getReactComponentSource(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker | null,
  filePath: string,
  exp: ExportedStatement,
): Promise<ReactComponentSource[]> {
  const srcCmps: ReactComponentSource[] = [];
  const relFilePath = sys.relative(sys.getAppRootDir(), filePath);
  const importPath = getComponentImportPath(sys, filePath);

  if (sys.ts.isVariableStatement(exp.node)) {
    for (const declaration of exp.node.declarationList.declarations) {
      if (!sys.ts.isIdentifier(declaration.name)) {
        continue;
      }
      if (!isValidJsxTag(declaration.name.text)) {
        continue;
      }

      const cmpExp = getReactComponentExpression(
        sys,
        typeChecker,
        declaration.initializer,
      );
      if (cmpExp) {
        const cmpName = cmpExp.name || declaration.name.text;

        const exportName = exp.isDefaultExport ? "default" : cmpName;
        const exportType = exp.isDefaultExport ? "default" : "named";

        srcCmps.push({
          cmpInfo: {
            id: await getComponentId(sys, filePath, exportName),
            displayFilePath: getDisplayFilePath(sys, filePath),
            name: getComponentDisplayName(cmpName),
            inputs: [],
            exportName,
            exportType,
            importName: cmpName,
            filePath,
            relFilePath,
            importPath,
            dependencies: cmpExp.deps,
            framework: "react",
          },
          node: cmpExp,
        });
      }
    }
    return srcCmps;
  }

  if (sys.ts.isArrowFunction(exp.node)) {
    const cmpExp = getReactComponentExpression(sys, typeChecker, exp.node);
    if (cmpExp) {
      const cmpName = getComponentDisplayNameFromFilePath(sys, filePath);

      const exportName = exp.isDefaultExport ? "default" : cmpName;
      const exportType = exp.isDefaultExport ? "default" : "named";

      srcCmps.push({
        cmpInfo: {
          id: await getComponentId(sys, filePath, exportName),
          displayFilePath: getDisplayFilePath(sys, filePath),
          name: cmpName,
          inputs: [],
          exportName,
          exportType,
          importName: getComponentImportNameFilePath(sys, filePath),
          filePath,
          relFilePath,
          importPath,
          dependencies: cmpExp.deps,
          framework: "react",
        },
        node: cmpExp,
      });
    }
    return srcCmps;
  }

  if (
    sys.ts.isFunctionExpression(exp.node) ||
    sys.ts.isFunctionDeclaration(exp.node)
  ) {
    const fnName = exp.node.name?.text;
    if (fnName && !isValidJsxTag(fnName)) {
      return srcCmps;
    }

    const cmpExp = getReactComponentExpression(sys, typeChecker, exp.node);
    if (cmpExp) {
      const cmpName =
        cmpExp.name || getComponentDisplayNameFromFilePath(sys, filePath);

      const exportName =
        exp.isDefaultExport || !cmpExp.name ? "default" : cmpExp.name;
      const exportType = exp.isDefaultExport ? "default" : "named";

      srcCmps.push({
        cmpInfo: {
          id: await getComponentId(sys, filePath, exportName),
          displayFilePath: getDisplayFilePath(sys, filePath),
          name: cmpName,
          inputs: [],
          exportName,
          exportType,
          importName:
            cmpExp.name || getComponentImportNameFilePath(sys, filePath),
          filePath,
          relFilePath,
          importPath,
          dependencies: cmpExp.deps,
          framework: "react",
        },
        node: cmpExp,
      });
    }
    return srcCmps;
  }

  if (
    (sys.ts.isExportSpecifier(exp.node) || sys.ts.isIdentifier(exp.node)) &&
    isValidJsxTag(exp.node.getText())
  ) {
    let modulePath;
    const cmpExp = getReactComponentExpression(sys, typeChecker, exp.node);
    if (cmpExp) {
      const cmpName =
        cmpExp.name || getComponentDisplayNameFromFilePath(sys, filePath);
      const exportName =
        exp.isDefaultExport || !cmpExp.name ? "default" : cmpExp.name;
      const exportType = exp.isDefaultExport ? "default" : "named";

      const moduleSpecifier =
        closestExportDeclaration(sys, exp.node)?.moduleSpecifier ??
        closestImportDeclaration(sys, exp.node)?.moduleSpecifier;

      if (moduleSpecifier && sys.ts.isStringLiteral(moduleSpecifier)) {
        const symbol = typeChecker?.getSymbolAtLocation(moduleSpecifier);
        if (symbol && symbol.declarations && symbol.declarations.length > 0) {
          const declaration = symbol.declarations[0];
          if (
            !sys.ts.isSourceFile(declaration) ||
            (sys.ts.isSourceFile(declaration) &&
              declaration.fileName.includes("node_modules"))
          ) {
            modulePath = moduleSpecifier.text;
          }
        }
      }

      srcCmps.push({
        cmpInfo: {
          id: await getComponentId(sys, filePath, exportName, modulePath),
          displayFilePath: getDisplayFilePath(sys, filePath),
          name: cmpName,
          inputs: [],
          exportName,
          exportType,
          importName:
            cmpExp.name || getComponentImportNameFilePath(sys, filePath),
          filePath,
          relFilePath,
          importPath: modulePath ? modulePath : importPath,
          externalImportPath: modulePath,
          dependencies: cmpExp.deps,
          framework: "react",
        },
        node: cmpExp,
      });
    }
    return srcCmps;
  }

  // export * from "@cloudscape-design/components";
  if (
    sys.ts.isExportDeclaration(exp.node) &&
    exp.node.exportClause === undefined
  ) {
    const moduleSpecifier = exp.node.moduleSpecifier;

    if (moduleSpecifier && sys.ts.isStringLiteral(moduleSpecifier)) {
      const exportedComponentNames = await extractReactComponentsFromModule(
        sys,
        typeChecker,
        filePath,
        moduleSpecifier,
      );
      srcCmps.push(...exportedComponentNames);
    }
    return srcCmps;
  }

  return srcCmps;
}

function closestExportDeclaration(
  sys: DevToolsSys,
  node: ts.Node,
): ts.ExportDeclaration | null {
  while (node) {
    if (sys.ts.isExportDeclaration(node)) {
      return node;
    }
    node = node.parent;
  }
  return null;
}

function closestImportDeclaration(
  sys: DevToolsSys,
  node: ts.Node,
): ts.ImportDeclaration | null {
  while (node) {
    if (sys.ts.isImportDeclaration(node)) {
      return node;
    }
    node = node.parent;
  }
  return null;
}

function getReactComponentExpression(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker | null,
  x:
    | ts.Expression
    | ts.FunctionDeclaration
    | ts.PropertyAccessExpression
    | ts.ExportSpecifier
    | ts.Identifier
    | ts.Declaration
    | undefined,
): ReactComponentSourceNode | null {
  if (!x) {
    return null;
  }
  if (sys.ts.isArrowFunction(x)) {
    if (hasJsxNode(sys, x.body)) {
      return {
        name: "",
        fnNode: x,
        deps: getSourceDependencies(sys, typeChecker, x),
      };
    }
    return null;
  }

  if (sys.ts.isFunctionExpression(x) || sys.ts.isFunctionDeclaration(x)) {
    if (hasJsxNode(sys, x.body)) {
      return {
        name: x.name?.text || "",
        fnNode: x,
        deps: getSourceDependencies(sys, typeChecker, x),
      };
    }
    return null;
  }

  if (sys.ts.isCallExpression(x)) {
    const exp = x.expression;
    if (sys.ts.isPropertyAccessExpression(exp)) {
      const args = x.arguments;
      if (args.length === 0) {
        return null;
      }

      const reactIdentifier = exp.expression;
      if (!sys.ts.isIdentifier(reactIdentifier)) {
        return null;
      }

      if (reactIdentifier.text !== "React") {
        return null;
      }

      const forwardRefIdentifier = exp.name;
      if (!sys.ts.isIdentifier(forwardRefIdentifier)) {
        return null;
      }

      if (forwardRefIdentifier.text !== "forwardRef") {
        return null;
      }

      return getReactComponentExpression(sys, typeChecker, args[0]);
    } else if (sys.ts.isIdentifier(exp) && exp.text === "observer") {
      const args = x.arguments;
      if (args.length === 0) {
        return null;
      }

      return getReactComponentExpression(sys, typeChecker, args[0]);
    }

    return null;
  }

  if (!typeChecker) {
    return null;
  }

  if (sys.ts.isPropertyAccessExpression(x)) {
    // import * as AccordionPrimitive from "@radix-ui/react-accordion";
    // const Accordion = AccordionPrimitive.Root;

    const expIdentifier = x.expression;
    const nameIdentifier = x.name;
    if (
      !sys.ts.isIdentifier(expIdentifier) ||
      !sys.ts.isIdentifier(nameIdentifier)
    ) {
      return null;
    }

    if (!isValidJsxTag(nameIdentifier.text)) {
      // Root
      return null;
    }

    const type = typeChecker.getTypeAtLocation(nameIdentifier);
    const callSignatures = type.getCallSignatures();
    if (callSignatures.length > 0) {
      const params = callSignatures[0].getParameters();
      if (params.length > 0) {
        return {
          name: "",
          fnNode: nameIdentifier,
          deps: getSourceDependencies(sys, typeChecker, nameIdentifier),
        };
      }
    }
  }

  if (sys.ts.isExportSpecifier(x)) {
    const nameIdentifier = x.name;
    return {
      name: nameIdentifier.text || "",
      fnNode: nameIdentifier,
      deps: getSourceDependencies(sys, typeChecker, nameIdentifier),
    };
  }

  if (sys.ts.isIdentifier(x)) {
    return {
      name: x.text || "",
      fnNode: x,
      deps: getSourceDependencies(sys, typeChecker, x),
    };
  }

  // Handle declaration statements with 'as default'
  if (sys.ts.isExpressionStatement(x)) {
    const expression = x.expression;
    if (
      sys.ts.isAsExpression(expression) &&
      sys.ts.isIdentifier(expression.type) &&
      expression.type.text === "default"
    ) {
      return getReactComponentExpression(
        sys,
        typeChecker,
        expression.expression,
      );
    }
  }

  // Handle variable declarations with 'as default'
  if (sys.ts.isVariableStatement(x)) {
    for (const declaration of x.declarationList.declarations) {
      if (
        declaration.initializer &&
        sys.ts.isAsExpression(declaration.initializer)
      ) {
        const asExpression = declaration.initializer;
        if (
          sys.ts.isIdentifier(asExpression.type) &&
          asExpression.type.text === "default"
        ) {
          const componentExp = getReactComponentExpression(
            sys,
            typeChecker,
            asExpression.expression,
          );
          if (componentExp && sys.ts.isIdentifier(declaration.name)) {
            return {
              name: declaration.name.text,
              fnNode: componentExp.fnNode,
              deps: componentExp.deps,
            };
          }
        }
      }
    }
  }

  return null;
}

export async function getReactSourceComponentsFromDir(
  sys: ReactDevToolsSys,
  tsProgram: ts.Program,
) {
  const cmps: ReactComponentSource[] = [];
  const sourceFiles = tsProgram.getSourceFiles();
  const typeChecker = tsProgram.getTypeChecker();

  await Promise.all(
    sourceFiles.map(async (sourceFile) => {
      const filePath = sys.normalize(sourceFile.fileName);
      if (shouldSkipFolder(sys, SKIP_FOLDERS, sourceFile.fileName)) {
        return;
      }

      if (
        filePath === sys.reactConfigPath ||
        filePath === sys.builderComponentPath
      ) {
        return;
      }

      const fileCmps = await getReactComponentsFromSourceFile(
        sys,
        typeChecker,
        filePath,
        sourceFile,
        filePath === sys.registryPath,
      );
      cmps.push(...fileCmps);
    }),
  );

  return cmps.sort((a, b) => a.cmpInfo.name.localeCompare(b.cmpInfo.name));
}

export interface ReactComponentSourceNode {
  name: string;
  fnNode: ts.Node;
  deps: AppDependency[] | undefined;
}

export interface ReactComponentSource {
  cmpInfo: ComponentInfo;
  node: ReactComponentSourceNode;
}

const SKIP_FOLDERS = new Set(["node_modules", "dist"]);

async function extractReactComponentsFromModule(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker | null,
  filePath: string,
  moduleSpecifier: ts.StringLiteral,
): Promise<ReactComponentSource[]> {
  const components: ReactComponentSource[] = [];
  const modulePath = moduleSpecifier.text;

  if (!typeChecker) {
    return components;
  }

  try {
    const moduleSymbol = typeChecker.getSymbolAtLocation(moduleSpecifier);
    if (!moduleSymbol) {
      return components;
    }

    const exportedSymbols = typeChecker.getExportsOfModule(moduleSymbol);

    for (const symbol of exportedSymbols) {
      const declaration = symbol.valueDeclaration || symbol.declarations?.[0];
      if (!declaration) continue;

      const exportName = symbol.name;

      if (!isValidJsxTag(exportName) && exportName !== "default") {
        continue;
      }

      if (!isLikelyReactComponent(sys, typeChecker, declaration)) {
        continue;
      }

      const cmpExp = getReactComponentExpression(sys, typeChecker, declaration);
      if (cmpExp) {
        const exportName = symbol.name;
        const exportType = exportName === "default" ? "default" : "named";
        const cmpName = getComponentDisplayName(exportName);
        const importName =
          exportName === "default"
            ? getComponentImportNameFilePath(sys, filePath)
            : exportName;

        components.push({
          cmpInfo: {
            id: await getComponentId(sys, filePath, exportName, modulePath),
            displayFilePath: getDisplayFilePath(sys, filePath),
            name: cmpName,
            inputs: [],
            exportName,
            exportType,
            importName,
            filePath,
            relFilePath: sys.relative(sys.getAppRootDir(), filePath),
            importPath: modulePath,
            externalImportPath: modulePath,
            dependencies: cmpExp.deps,
            framework: "react",
          },
          node: cmpExp,
        });
      }
    }
  } catch (error) {
    sys.Sentry?.captureException(
      new Error(`Error processing module ${modulePath}:`, { cause: error }),
      {
        extra: {
          modulePath,
          filePath,
        },
      },
    );
  }

  return components;
}

function isLikelyReactComponent(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker,
  declaration: ts.Declaration,
): boolean {
  if (
    sys.ts.isInterfaceDeclaration(declaration) ||
    sys.ts.isTypeAliasDeclaration(declaration)
  ) {
    return false;
  }

  if (sys.ts.isExportSpecifier(declaration)) {
    const symbol = typeChecker.getSymbolAtLocation(declaration.name);
    if (symbol) {
      const aliasedSymbol = typeChecker.getAliasedSymbol(symbol);
      const aliasedDeclaration = aliasedSymbol.declarations?.[0];
      if (aliasedDeclaration) {
        return isLikelyReactComponent(sys, typeChecker, aliasedDeclaration);
      }
    }
    return false;
  }

  if (
    sys.ts.isFunctionDeclaration(declaration) ||
    sys.ts.isArrowFunction(declaration) ||
    sys.ts.isFunctionExpression(declaration)
  ) {
    const signature = typeChecker.getSignatureFromDeclaration(declaration);
    if (signature) {
      const returnType = typeChecker.getReturnTypeOfSignature(signature);

      const result = isJsxElement(returnType, typeChecker);
      return result;
    }
  }

  if (sys.ts.isClassDeclaration(declaration)) {
    const result =
      declaration.heritageClauses?.some((clause) =>
        clause.types.some(
          (type) =>
            type.expression.getText() === "React.Component" ||
            type.expression.getText() === "Component",
        ),
      ) ?? false;
    return result;
  }

  if (sys.ts.isVariableDeclaration(declaration)) {
    const type = typeChecker.getTypeAtLocation(declaration);
    const result = type
      .getCallSignatures()
      .some((sig) =>
        isJsxElement(typeChecker.getReturnTypeOfSignature(sig), typeChecker),
      );
    return result;
  }

  return false;
}

function isJsxElement(type: ts.Type, typeChecker: ts.TypeChecker): boolean {
  const typeString = typeChecker.typeToString(type);
  return (
    typeString === "JSX.Element" ||
    typeString === "Element" ||
    typeString.includes("ReactElement") ||
    typeString.includes("React.ReactElement") ||
    typeString.includes("ReactNode")
  );
}
