import type { ComponentInfo, DevToolsSys } from "../types";
import detectPackageManager from "which-pm-runs";

export function isMapperFile(fileName: string) {
  return (
    fileName.endsWith(".mapper.tsx") ||
    fileName.endsWith(".mapper.jsx") ||
    fileName.endsWith(".mapper.ts") ||
    fileName.endsWith(".mapper.js") ||
    fileName.endsWith(".mapper.mjs")
  );
}

export function getImportPath(
  sys: DevToolsSys,
  containingModulePath: string,
  moduleToImportPath: string,
) {
  containingModulePath = normalizePathSlash(containingModulePath);
  moduleToImportPath = normalizePathSlash(moduleToImportPath);

  containingModulePath = sys.dirname(containingModulePath);

  const fileName = sys.basename(
    moduleToImportPath,
    sys.extname(moduleToImportPath),
  );

  moduleToImportPath = sys.dirname(moduleToImportPath);

  let p: string;
  if (fileName === "index") {
    p = sys.relative(containingModulePath, moduleToImportPath);
  } else {
    p = sys.relative(
      containingModulePath,
      sys.join(moduleToImportPath, fileName),
    );
  }
  p = normalizePathSlash(sys.normalize(p));

  if (!p.startsWith(".")) {
    p = "./" + p;
  }

  return p;
}

export function normalizePathSlash(path: string) {
  const isExtendedLengthPath = path.startsWith("\\\\?\\");
  if (isExtendedLengthPath) {
    return path;
  }
  return path.replace(/\\/g, "/");
}

export function getComponentImportNameFilePath(
  sys: DevToolsSys,
  filePath: string,
) {
  const ext = sys.extname(filePath);
  const parts = sys.basename(filePath, ext).split(/[\/\\]/);
  const fileName = parts[parts.length - 1];
  if (fileName === "index") {
    return getComponentImportName(sys.basename(sys.dirname(filePath)));
  }
  return getComponentImportName(fileName);
}

export function getComponentImportName(str: string) {
  return getComponentDisplayName(str).replace(/ /g, "");
}

export function getComponentDisplayNameFromFilePath(
  sys: DevToolsSys,
  filePath: string,
) {
  const ext = sys.extname(filePath);

  const fileName = normalizePathSlash(sys.basename(filePath, ext))
    .split("/")
    .pop()!;
  if (fileName === "index") {
    return getComponentDisplayName(sys.basename(sys.dirname(filePath)));
  }
  return getComponentDisplayName(fileName);
}

export function getComponentImportPath(sys: DevToolsSys, absFilePath: string) {
  let relFilePath = normalizePathSlash(
    sys.relative(sys.getAppRootDir(), absFilePath),
  );

  relFilePath = relFilePath
    .split("/")
    .filter((p) => p !== "." && p !== ".." && p !== "")
    .join("/");

  if (relFilePath.endsWith(".tsx") || relFilePath.endsWith(".jsx")) {
    relFilePath = relFilePath.slice(0, -4);
  } else if (relFilePath.endsWith(".ts") || relFilePath.endsWith(".js")) {
    relFilePath = relFilePath.slice(0, -3);
  }

  const frameworks = sys.getFrameworks();

  if (relFilePath.startsWith("src/")) {
    relFilePath = relFilePath.slice(4);
  }

  if (frameworks.some((f) => f.name.includes("next"))) {
    return "@/" + relFilePath;
  }

  return "~/" + relFilePath;
}

export function getDisplayFilePath(sys: DevToolsSys, filePath: string) {
  let path = filePath;
  let parts: string[] = [];
  for (let i = 0; i < 2; i++) {
    const part = sys.basename(path);
    if (!part || part === "components") {
      break;
    }
    parts.unshift(part);
    path = sys.dirname(path);
  }
  return parts.join("/");
}

export function getComponentDisplayName(str: string) {
  str = str
    .replace(/([A-Z])/g, " $1")
    .replace(/\./g, " ")
    .replace(/_/g, " ")
    .replace(/-/g, " ")
    .replace(/\s/g, " ")
    .trim();

  while (str.includes("  ")) {
    str = str.replace(/  /g, " ");
  }

  return str
    .split(" ")
    .map((p) => {
      if (p.length > 2) {
        return p[0].toUpperCase() + p.slice(1);
      }
      return p;
    })
    .join("")
    .trim();
}

export function sortComponents(cmps: ComponentInfo[]) {
  return cmps.sort((a, b) =>
    a.name.toLocaleLowerCase().localeCompare(b.name.toLocaleLowerCase()),
  );
}

export async function getComponentId(
  sys: DevToolsSys,
  filePath: string,
  exportName: string,
  externalImportPath?: string,
) {
  if (typeof exportName !== "string" || exportName === "") {
    throw new Error(
      `getComponentId(), exportName must be a non-empty string, received: ${exportName}, ${filePath}`,
    );
  }

  if (externalImportPath) {
    filePath = externalImportPath;
  }

  filePath = sys.normalize(filePath);

  const hash = await sys.hash(filePath + "#" + exportName);
  return hash.substring(0, 8);
}

export async function getLocalAppId(sys: DevToolsSys) {
  const hash = await sys.hash(sys.getAppRootDir());
  return hash.substring(0, 8);
}

export function isNumber(v: any): v is number {
  return typeof v === "number";
}

export function isBoolean(v: any): v is boolean {
  return typeof v === "boolean";
}

export function isString(v: any): v is string {
  return typeof v === "string";
}

export function isValidJsxTag(tag: string) {
  if (typeof tag === "string") {
    return /^[A-Z][A-Za-z0-9]*$/.test(tag);
  }
  return false;
}

export function clone<T>(obj: T): T {
  return JSON.parse(JSON.stringify(obj));
}

export function shouldSkipFolder(
  sys: DevToolsSys,
  skipFolders: Set<string>,
  fileName: string,
): boolean {
  for (const folder of skipFolders) {
    if (sys.dirname(fileName).includes(folder)) {
      return true;
    }
  }
  return false;
}

export function getPackageManager() {
  try {
    const detected = detectPackageManager();
    if (detected?.name) {
      return detected.name;
    }
  } catch (e) {}

  return "npm";
}

export function isWindows() {
  return process.platform.startsWith("win");
}

export function builderNpxPackage() {
  if (isWindows()) {
    return '"@builder.io/dev-tools"';
  } else {
    return "builder.io";
  }
}
