import type { SDKFrameworks, SDKVersionInfo } from "../../types";

import { SDK_VERSIONS } from "../../types";

export const SUPPORTED_FRAMEWORKS = {
  react: "react",
  remix: "remix",
  angular: "angular",
  next: "next",
  qwik: "qwik",
};

const SDK_PER_FRAMEWORK: SDKFrameworks = {
  [SUPPORTED_FRAMEWORKS.react]: [
    {
      version: SDK_VERSIONS.gen1,
      recommended: true,
    },
    {
      version: SDK_VERSIONS.gen2,
      recommended: false,
    },
  ],
  [SUPPORTED_FRAMEWORKS.remix]: [
    {
      version: SDK_VERSIONS.gen1,
      recommended: false,
    },
    {
      version: SDK_VERSIONS.gen2,
      recommended: true,
    },
  ],
  [SUPPORTED_FRAMEWORKS.angular]: [
    {
      version: SDK_VERSIONS.gen2,
      recommended: true,
    },
  ],
  [SUPPORTED_FRAMEWORKS.next]: [
    {
      version: SDK_VERSIONS.gen1,
      recommended: true,
    },
    {
      version: SDK_VERSIONS.gen2,
      recommended: false,
    },
  ],
  [SUPPORTED_FRAMEWORKS.qwik]: [
    {
      version: SDK_VERSIONS.gen2,
      recommended: true,
    },
  ],
};

export function getSDKDetailsForFramework(framework: string): SDKVersionInfo[] {
  return SDK_PER_FRAMEWORK[framework];
}
