import type { EnvInfo } from "../../../types";
import type { QwikDevToolsSys } from ".";
import { getDotEnvValue, setDotEnvVar } from "../../../common/dotenv";

export async function getQwikApiKey(sys: QwikDevToolsSys) {
  const dotEnvFileNames = [".env"];

  const env = await getDotEnvValue(
    sys,
    dotEnvFileNames,
    QWIK_BUILDER_API_KEY_ENV,
  );
  return env;
}

export async function setQwikApiKey(
  sys: QwikDevToolsSys,
  publicApiKey: string,
) {
  setBuilderPublicApiKeyEnvVar(publicApiKey);

  const env = await getQwikApiKey(sys);

  const modifiedType = await setDotEnvVar(
    sys,
    env.file,
    env.envKey,
    publicApiKey,
  );

  const updatedEnv: EnvInfo = {
    ...env,
    modifiedType,
  };
  return updatedEnv;
}

function setBuilderPublicApiKeyEnvVar(publicApiKey: string) {
  if (typeof process !== "undefined") {
    process.env[QWIK_BUILDER_API_KEY_ENV] = publicApiKey;
  }
}

const QWIK_BUILDER_API_KEY_ENV = `PUBLIC_BUILDER_API_KEY`;
