import type ts from "typescript";
import type { ComponentInfo } from "../../../types";
import type { NextDevToolsSys } from ".";
import { codeToStatement } from "../../../common/ast/transform";
import { getImportPath } from "../../../common/utils";
import { updateComponentObjLiteralInfoProperties } from "../../../common/ast/component-info";

export function createNextRegisteredComponent(
  sys: NextDevToolsSys,
  cmpInfo: ComponentInfo,
) {
  let code = `Builder.registerComponent(\n`;

  if (sys.appDir) {
    // app dir

    if (cmpInfo.acceptsChildren) {
      code += `  withChildren(${cmpInfo.importName}),\n`;
    } else {
      code += `  ${cmpInfo.importName},\n`;
    }
  } else {
    // not app dir
    // use dynamic imports
    const importPath = cmpInfo.externalImportPath
      ? cmpInfo.externalImportPath
      : getImportPath(sys, sys.registryPath, cmpInfo.filePath);
    if (cmpInfo.exportName === "default") {
      if (cmpInfo.acceptsChildren) {
        code += `  withChildren(dynamic(() => import(${JSON.stringify(
          importPath,
        )}))),\n`;
      } else {
        code += `  dynamic(() => import(${JSON.stringify(importPath)})),\n`;
      }
    } else {
      if (cmpInfo.acceptsChildren) {
        code += `  withChildren(dynamic(async () => (await import(${JSON.stringify(
          importPath,
        )})).${cmpInfo.exportName})),\n`;
      } else {
        code += `  dynamic(async () => (await import(${JSON.stringify(
          importPath,
        )})).${cmpInfo.exportName}),\n`;
      }
    }
  }

  code += `  {
    name: "${cmpInfo.name}",
  }\n`;
  code += `);`;

  const registerStatement = codeToStatement(sys, code);

  return updateNextRegisteredComponent(sys, registerStatement, cmpInfo);
}

export function updateNextRegisteredComponent(
  sys: NextDevToolsSys,
  registerStatement: ts.Statement,
  cmpInfo: ComponentInfo,
) {
  if (!sys.ts.isExpressionStatement(registerStatement)) {
    return registerStatement;
  }

  const callExp = registerStatement.expression;
  if (!sys.ts.isCallExpression(callExp)) {
    return registerStatement;
  }

  const args = [...callExp.arguments];
  if (args.length < 2) {
    return registerStatement;
  }

  if (cmpInfo.acceptsChildren) {
    // Builder.registerComponent( withChildren(Component, { ... }) )
    if (
      sys.ts.isCallExpression(args[0]) &&
      sys.ts.isIdentifier(args[0].expression) &&
      args[0].expression.text === "withChildren"
    ) {
      // withChildren() already wrapping first arg
      // do nothing
    } else {
      // withChildren() not wrapping first arg, wrap it
      args[0] = sys.ts.factory.createCallExpression(
        sys.ts.factory.createIdentifier("withChildren"),
        undefined,
        [args[0]],
      );
    }
  } else {
    // Builder.registerComponent( Component, { ... } )
    if (
      sys.ts.isCallExpression(args[0]) &&
      sys.ts.isIdentifier(args[0].expression) &&
      args[0].expression.text === "withChildren"
    ) {
      // withChildren() wrapping first arg, unwrap it
      args[0] = args[0].arguments[0];
    } else {
      // withChildren() not wrapping first arg
      // do nothing
    }
  }

  const cmpObjExp = args[1];
  if (!sys.ts.isObjectLiteralExpression(cmpObjExp)) {
    return registerStatement;
  }

  args[1] = updateComponentObjLiteralInfoProperties(sys, cmpObjExp, cmpInfo);

  return sys.ts.factory.updateExpressionStatement(
    registerStatement,
    sys.ts.factory.updateCallExpression(
      callExp,
      callExp.expression,
      callExp.typeArguments,
      args,
    ),
  );
}
