import type ts from "typescript";
import type { AngularDevToolsSys } from ".";
import { codeToSource, sourceToCode } from "../../../common/ast/transform";
import { ensureImport } from "../../../common/ast/ensure-imports";
import { getImportPath } from "../../../common/utils";
import { getTextOfPropertyName } from "../../../common/ast/convert-values";

export async function angularUpdateAppModule(
  sys: AngularDevToolsSys,
  cmpName: string,
  cmpPath: string,
) {
  const fileExtension = sys.typescriptEnabled ? ".ts" : ".js";
  const fileName = `app.module${fileExtension}`;

  const appModulePath = sys.join(sys.appDir, fileName);
  const appModuleCode = await sys.readFile(appModulePath);

  if (!appModuleCode) {
    sys.Sentry?.captureException(
      new Error(
        `Unable to read app.module.${fileExtension} at ${appModulePath}`,
      ),
      {
        extra: {
          appModuleCode,
          appModulePath,
        },
      },
    );
    return;
  }

  const updatedCode = await angularEnsureAppModuleImports(
    sys,
    appModulePath,
    cmpName,
    cmpPath,
    appModuleCode,
  );

  if (!updatedCode) {
    sys.Sentry?.captureException(
      new Error(
        `Unable to update app.module.${fileExtension} at ${appModulePath}`,
      ),
      {
        extra: {
          appModuleCode,
          appModulePath,
        },
      },
    );
    return;
  }

  await sys.writeFile(appModulePath, updatedCode);

  return appModulePath;
}

export async function angularEnsureAppModuleImports(
  sys: AngularDevToolsSys,
  appModulePath: string,
  cmpName: string,
  cmpPath: string,
  code: string,
) {
  let content = code;
  let sourceFile = codeToSource(sys, code);

  const cmpRelPath = getImportPath(sys, appModulePath, cmpPath);

  sourceFile = ensureImport(sys, sourceFile, {
    namedImports: [cmpName],
    importPath: cmpRelPath,
  });

  sourceFile = ensureImport(sys, sourceFile, {
    namedImports: ["Content"],
    importPath: "@builder.io/sdk-angular",
  });

  const updatedStatements: ts.Statement[] = [];

  for (const s of sourceFile.statements) {
    if (sys.ts.isClassDeclaration(s) && s.name?.text === "AppModule") {
      const updatedClassDeclaration = modifyNgModuleDecorator(sys, cmpName, s);
      updatedStatements.push(updatedClassDeclaration);
    } else {
      updatedStatements.push(s);
    }
  }

  sourceFile = sys.ts.factory.updateSourceFile(sourceFile, updatedStatements);

  content = sourceToCode(sys, sourceFile);
  return content;
}

function modifyNgModuleDecorator(
  sys: AngularDevToolsSys,
  cmpName: string,
  node: ts.ClassDeclaration,
): ts.ClassDeclaration {
  const decorators = sys.ts.getDecorators(node);

  if (decorators) {
    for (const decorator of decorators) {
      if (
        sys.ts.isCallExpression(decorator.expression) &&
        sys.ts.isIdentifier(decorator.expression.expression) &&
        decorator.expression.expression.text === "NgModule"
      ) {
        const ngModuleArgs = decorator.expression.arguments;
        if (
          ngModuleArgs.length === 1 &&
          sys.ts.isObjectLiteralExpression(ngModuleArgs[0])
        ) {
          let updatedObjectLiteral = appendArrayProperty(
            sys,
            ngModuleArgs[0],
            "declarations",
            cmpName,
          );

          if (updatedObjectLiteral) {
            const updatedObjectLiteralWithContent = appendArrayProperty(
              sys,
              updatedObjectLiteral,
              "imports",
              "Content",
            );

            if (updatedObjectLiteralWithContent) {
              updatedObjectLiteral = updatedObjectLiteralWithContent;
            }

            if (updatedObjectLiteral) {
              const updatedDecorator = sys.ts.factory.updateDecorator(
                decorator,
                sys.ts.factory.updateCallExpression(
                  decorator.expression,
                  decorator.expression.expression,
                  decorator.expression.typeArguments,
                  [updatedObjectLiteral],
                ),
              );

              const updatedDecorators = decorators.map((d) =>
                d === decorator ? updatedDecorator : d,
              );

              const modifiers = [
                ...updatedDecorators,
                ...(sys.ts.getModifiers(node) || []),
              ];

              return sys.ts.factory.updateClassDeclaration(
                node,
                modifiers,
                node.name,
                node.typeParameters,
                node.heritageClauses,
                node.members,
              );
            }
          }
        }
      }
    }
  }
  return node;
}

function appendArrayProperty(
  sys: AngularDevToolsSys,
  objectExpression: ts.ObjectLiteralExpression,
  propertyName: string,
  itemName: string,
) {
  const properties = [...objectExpression.properties];

  const pIndex = properties.findIndex((p) => {
    const n = getTextOfPropertyName(sys, p);
    return n === propertyName;
  });

  if (pIndex > -1) {
    const prop = properties[pIndex];
    if (
      sys.ts.isPropertyAssignment(prop) &&
      prop.initializer &&
      sys.ts.isArrayLiteralExpression(prop.initializer)
    ) {
      const updatedList = updateArrayItems(
        sys,
        prop.initializer as ts.ArrayLiteralExpression,
        itemName,
      );
      if (updatedList) {
        properties[pIndex] = sys.ts.factory.updatePropertyAssignment(
          prop,
          prop.name,
          updatedList,
        );
        return sys.ts.factory.updateObjectLiteralExpression(
          objectExpression,
          properties,
        );
      }
      // if item is already present in array
      return null;
    }
    // if prop is present but it's not an array
    return null;
  }

  // if prop is not present
  const prop = updateArrayItems(
    sys,
    sys.ts.factory.createArrayLiteralExpression([], true),
    itemName,
  );
  if (prop) {
    return sys.ts.factory.updateObjectLiteralExpression(objectExpression, [
      ...properties,
      sys.ts.factory.createPropertyAssignment(propertyName, prop),
    ]);
  }

  return null;
}

function updateArrayItems(
  sys: AngularDevToolsSys,
  arrayExpression: ts.ArrayLiteralExpression,
  itemToBeInserted: string,
) {
  const hasItem = arrayExpression.elements.some((e) => {
    if (sys.ts.isIdentifier(e)) {
      return e.text === itemToBeInserted;
    }
    return false;
  });

  if (hasItem) {
    return null;
  }

  return sys.ts.factory.updateArrayLiteralExpression(arrayExpression, [
    ...arrayExpression.elements,
    sys.ts.factory.createIdentifier(itemToBeInserted),
  ]);
}
