import type { DevToolsSys, EnvInfo } from "../../../types";
import { codeToSource, sourceToCode } from "../../../common/ast/transform";
import type ts from "typescript";
import { findVariableStatement } from "../../../common/ast/utils";

export async function getAngularApiKey(sys: DevToolsSys) {
  const envFileNames = ["environment.ts"];

  const env = await getEnvValue(sys, envFileNames, ANGULAR_BUILDER_API_KEY_ENV);
  return env;
}

export async function setAngularApiKey(sys: DevToolsSys, publicApiKey: string) {
  const env = await getAngularApiKey(sys);

  const modifiedType = await setEnvValue(
    sys,
    env.file,
    env.envKey,
    publicApiKey,
  );

  const updatedEnv: EnvInfo = {
    ...env,
    modifiedType,
  };

  return updatedEnv;
}

export async function getEnvValue(
  sys: DevToolsSys,
  envFileNames: string[],
  envKey: string,
) {
  const envInfo: EnvInfo = {
    envKey,
    envValue: null,
    file: "",
  };

  for (const envFileName of envFileNames) {
    const envPath = sys.join(
      sys.getAppRootDir(),
      "src",
      "environments",
      envFileName,
    );
    const value = await parseEnvFile(sys, envPath);

    if (
      typeof value === "string" &&
      value.length > 0 &&
      value !== "YOUR_API_KEY"
    ) {
      envInfo.envValue = value;
      envInfo.file = envPath;
      return envInfo;
    }
  }

  envInfo.file = sys.join(
    sys.getAppRootDir(),
    "src",
    "environments",
    envFileNames[envFileNames.length - 1],
  );
  return envInfo;
}

export async function parseEnvFile(sys: DevToolsSys, envPath: string) {
  const envContent = await sys.readFile(envPath);
  if (envContent) {
    return parseEnvContent(sys, envContent);
  }
  return "";
}

export function parseEnvContent(sys: DevToolsSys, envContent: string) {
  const sourceFile = codeToSource(sys, envContent);
  let extractedApiKey: string = "";

  const variableName = "environment";

  const s = findVariableStatement(sys, sourceFile, variableName);
  if (s && s.statement.declarationList.declarations.length > 0) {
    const declaration = s.statement.declarationList.declarations[0];
    const initializer = declaration.initializer;
    if (initializer && sys.ts.isObjectLiteralExpression(initializer)) {
      initializer.properties.forEach((prop) => {
        if (
          sys.ts.isPropertyAssignment(prop) &&
          sys.ts.isIdentifier(prop.name) &&
          prop.name.text === ANGULAR_BUILDER_API_KEY_ENV &&
          sys.ts.isStringLiteral(prop.initializer)
        ) {
          extractedApiKey = prop.initializer.text;
        }
      });
    }
  }

  return extractedApiKey;
}

export async function appendApiKey(
  sys: DevToolsSys,
  envContent: string,
  key: string,
  value: string,
): Promise<ts.VariableStatement | null> {
  const sourceFile = codeToSource(sys, envContent);

  const variableName = "environment";

  const s = findVariableStatement(sys, sourceFile, variableName);

  if (s && s.statement.declarationList.declarations.length > 0) {
    const declarationList = s.statement.declarationList;
    const declaration = declarationList.declarations[0];

    const initializer = declaration.initializer;

    if (initializer && sys.ts.isObjectLiteralExpression(initializer)) {
      const newProperty = sys.ts.factory.createPropertyAssignment(
        sys.ts.factory.createIdentifier(key),
        sys.ts.factory.createStringLiteral(value),
      );
      const newProperties = sys.ts.factory.createNodeArray([
        ...initializer.properties,
        newProperty,
      ]);

      const newInitializer = sys.ts.factory.updateObjectLiteralExpression(
        initializer,
        newProperties,
      );

      const newDeclaration = sys.ts.factory.updateVariableDeclaration(
        declaration,
        declaration.name,
        declaration.exclamationToken,
        declaration.type,
        newInitializer,
      );

      const newDeclarationList = sys.ts.factory.updateVariableDeclarationList(
        declarationList,
        [newDeclaration],
      );

      const newVariableStatement = sys.ts.factory.updateVariableStatement(
        s.statement,
        s.statement.modifiers,
        newDeclarationList,
      );

      return newVariableStatement;
    }
    return null;
  }
  return null;
}

export async function setEnvValue(
  sys: DevToolsSys,
  envPath: string,
  key: string,
  value: string,
): Promise<"create" | "update" | null> {
  let envContent = await sys.readFile(envPath);

  // check if we already have an existing file
  if (envContent) {
    // read the existing file
    if (!envContent.includes(key)) {
      // existing env does not have a builder api key
      sys.debug(`Add "${key}" to ${envPath}`);
      const updatedEnvNode = await appendApiKey(sys, envContent, key, value);
      if (updatedEnvNode) {
        const updatedEnvContent = sourceToCode(sys, updatedEnvNode);
        const code = await sys.formatCode(envPath, updatedEnvContent);
        await sys.writeFile(envPath, code);
      }

      return "update";
    } else {
      sys.debug(`"${key}" already set in ${envPath}`);
    }
  } else {
    // create a new env file since it doesn't exist yet
    sys.debug(`Creating new env file with "${key}" at ${envPath}`);

    const envContent = `
    // https://www.builder.io/c/docs/using-your-api-key
    export const environment = {
      production: false,
      builderApiKey: "${value}",
    };
    `.trimStart();
    const code = await sys.formatCode(envPath, envContent);
    await sys.writeFile(envPath, code);
    return "create";
  }

  return null;
}

export const ANGULAR_BUILDER_API_KEY_ENV = "builderApiKey";
