import { SDK_VERSIONS, type ModifiedFile } from "../../../types";
import type { NextDevToolsSys } from ".";
import { getImportPath } from "../../../common/utils";
import { NEXT_BUILDER_API_KEY_ENV } from "./next-api-key";
import { nextComponentRegistry } from "./next-registry";

// https://www.builder.io/c/docs/quickstart

export async function nextEnsureBuilderSetup(sys: NextDevToolsSys) {
  const modifiedFiles: ModifiedFile[] = [];

  const counterCmpPath = await nextEnsureCounterComponent(sys, modifiedFiles);
  await nextEnsureBuilderRegistry(sys, modifiedFiles, counterCmpPath);

  if (sys.appDir) {
    sys.debug(`ensure builder setup for next.js app ${sys.appDir}`);
    await nextAppEnsureBuilderSetup(sys, sys.appDir, modifiedFiles);
  } else if (sys.pagesDir) {
    sys.debug(`ensure builder setup for next.js pages ${sys.pagesDir}`);
    await nextPagesEnsureBuilderSetup(sys, sys.pagesDir, modifiedFiles);
  }

  return modifiedFiles;
}

export async function nextEnsureFigmaImportPageSetup(sys: NextDevToolsSys) {
  const modifiedFiles: ModifiedFile[] = [];

  await nextEnsureBuilderRegistry(sys, modifiedFiles);

  if (sys.appDir) {
    sys.debug(`ensure figma-imports setup for next.js app ${sys.appDir}`);
    await nextAppEnsureFigmaImportSetup(sys, sys.appDir, modifiedFiles);
  } else if (sys.pagesDir) {
    sys.debug(`ensure figma-imports setup for next.js pages ${sys.pagesDir}`);
    await nextPagesEnsureFigmaImportSetup(sys, sys.pagesDir, modifiedFiles);
  }

  return modifiedFiles;
}

async function nextAppEnsureFigmaImportSetup(
  sys: NextDevToolsSys,
  appDir: string,
  modifiedFiles: ModifiedFile[],
) {
  const routeFileName = `page.${sys.typescriptEnabled ? "tsx" : "jsx"}`;
  const routePath = sys.join(appDir, `figma-imports`, routeFileName);
  if (!(await sys.exists(routePath))) {
    const code = await sys.formatCode(
      routePath,
      getNextAppRoute({ sys, routePath, modelName: "figma-imports" }),
    );
    await sys.writeFile(routePath, code);
    modifiedFiles.push({
      filePath: routePath,
      modifiedType: "create",
    });
  }

  if (!(await sys.exists(sys.builderComponentPath))) {
    const code = await sys.formatCode(
      sys.builderComponentPath,
      getNextAppBuilderComponent(sys),
    );
    await sys.writeFile(sys.builderComponentPath, code);
    modifiedFiles.push({
      filePath: sys.builderComponentPath,
      modifiedType: "create",
    });
  }
}

async function nextAppEnsureBuilderSetup(
  sys: NextDevToolsSys,
  appDir: string,
  modifiedFiles: ModifiedFile[],
) {
  const routeFileName = `page.${sys.typescriptEnabled ? "tsx" : "jsx"}`;
  const routePath = sys.join(appDir, `[...page]`, routeFileName);
  if (!(await sys.exists(routePath))) {
    const code = await sys.formatCode(
      routePath,
      getNextAppRoute({ sys, routePath, modelName: "page" }),
    );
    await sys.writeFile(routePath, code);
    modifiedFiles.push({
      filePath: routePath,
      modifiedType: "create",
    });
  }

  if (!(await sys.exists(sys.builderComponentPath))) {
    const code = await sys.formatCode(
      sys.builderComponentPath,
      getNextAppBuilderComponent(sys),
    );
    await sys.writeFile(sys.builderComponentPath, code);
    modifiedFiles.push({
      filePath: sys.builderComponentPath,
      modifiedType: "create",
    });
  }
}

function getNextAppBuilderComponent(sys: NextDevToolsSys) {
  const ts = sys.typescriptEnabled;

  if (sys.sdkVersion === SDK_VERSIONS.gen2) {
    return `
  "use client";
  import { ${
    ts ? "BuilderContent, " : ""
  } isPreviewing, Content } from "@builder.io/sdk-react";
  import DefaultErrorPage from "next/error";
  import { CUSTOM_COMPONENTS } from ${JSON.stringify(
    getImportPath(sys, sys.builderComponentPath, sys.registryPath),
  )};
  ${
    ts
      ? `\ninterface BuilderPageProps {
      content: BuilderContent | null;
      model: string;
      }`
      : ""
  }

  // Builder Public API Key set in .env file
  const builderApiKey = process.env.${NEXT_BUILDER_API_KEY_ENV}${ts ? "!" : ""};

  export function RenderBuilderContent({ content, model }${
    ts ? ": BuilderPageProps" : ""
  }) {
    // Call the isPreviewing hook to determine if
    // the page is being previewed in Builder
    // If "content" has a value or the page is being previewed in Builder,
    // render the BuilderComponent with the specified content and model props.
    if (content || isPreviewing()) {
      return <Content
        content={content}
        apiKey={builderApiKey}
        model={model}
        customComponents={CUSTOM_COMPONENTS}
      />;
    }
    // If the "content" is falsy and the page is
    // not being previewed in Builder, render the
    // DefaultErrorPage with a 404.
    return <DefaultErrorPage statusCode={404} />;
  }
  `.trimStart();
  }

  return `
"use client";
${ts ? 'import { ComponentProps } from "react";' : ""}
import { BuilderComponent, useIsPreviewing } from "@builder.io/react";
import { ${ts ? "BuilderContent, " : ""}builder } from '@builder.io/sdk';
import DefaultErrorPage from "next/error";
import ${JSON.stringify(
    getImportPath(sys, sys.builderComponentPath, sys.registryPath),
  )};
${
  ts ? `\ntype BuilderPageProps = ComponentProps<typeof BuilderComponent>;` : ""
}

// Builder Public API Key set in .env file
builder.init(process.env.${NEXT_BUILDER_API_KEY_ENV}${ts ? "!" : ""});

export function RenderBuilderContent({ content, model }${
    ts ? ": BuilderPageProps" : ""
  }) {
  // Call the useIsPreviewing hook to determine if
  // the page is being previewed in Builder
  const isPreviewing = useIsPreviewing();
  // If "content" has a value or the page is being previewed in Builder,
  // render the BuilderComponent with the specified content and model props.
  if (content || isPreviewing) {
    return <BuilderComponent content={content} model={model} />;
  }
  // If the "content" is falsy and the page is
  // not being previewed in Builder, render the
  // DefaultErrorPage with a 404.
  return <DefaultErrorPage statusCode={404} />;
}
`.trimStart();
}

function generatePagePropsType(sys: NextDevToolsSys) {
  if (sys.features.asyncParams) {
    return `\ninterface PageProps {
      params: Promise<{
        page: string[];
      }>;
    }`;
  } else {
    return `\ninterface PageProps {
      params: {
        page: string[];
      };
    }`;
  }
}

function getNextAppRoute({
  sys,
  routePath,
  modelName,
}: {
  sys: NextDevToolsSys;
  routePath: string;
  modelName: "page" | "figma-imports";
}) {
  const ts = sys.typescriptEnabled;
  const buildContentImport = getImportPath(
    sys,
    routePath,
    sys.builderComponentPath,
  );

  if (sys.sdkVersion === SDK_VERSIONS.gen2) {
    return `
import { fetchOneEntry } from "@builder.io/sdk-react";
import { RenderBuilderContent } from ${JSON.stringify(buildContentImport)};

// Builder Public API Key set in .env file
const PUBLIC_API_KEY = process.env.${NEXT_BUILDER_API_KEY_ENV}${ts ? "!" : ""};

${ts ? generatePagePropsType(sys) : ""}

export default async function Page(props${ts ? `: PageProps` : ``}) {
  const builderModelName = "${modelName}";

  // Use the page path specified in the URL to fetch the content
  const urlPath = "/" + (${
    sys.features.asyncParams ? "(await props?.params)" : "props?.params"
  }?.page?.join("/") || "");

  const content = await fetchOneEntry({
   // Get the page content from Builder with the specified options
    apiKey: PUBLIC_API_KEY,
    model: builderModelName,
    userAttributes: { urlPath },
  });

  return (
    <>
      {/* Render the Builder page */}
      <RenderBuilderContent content={content} model={builderModelName} />
    </>
  );
}
`.trimStart();
  }

  return `
import { builder } from "@builder.io/sdk";
import { RenderBuilderContent } from ${JSON.stringify(buildContentImport)};

// Builder Public API Key set in .env file
builder.init(process.env.${NEXT_BUILDER_API_KEY_ENV}${ts ? "!" : ""});
${ts ? generatePagePropsType(sys) : ""}

export default async function Page(props${ts ? `: PageProps` : ``}) {
  const builderModelName = "${modelName}";

  const content = await builder
    // Get the page content from Builder with the specified options
    .get(builderModelName, {
      userAttributes: {
        // Use the page path specified in the URL to fetch the content
        urlPath: "/" + (${
          sys.features.asyncParams ? "(await props?.params)" : "props?.params"
        }?.page?.join("/") || ""),
      },
    })
    // Convert the result to a promise
    .toPromise();

  return (
    <>
      {/* Render the Builder page */}
      <RenderBuilderContent content={content} model={builderModelName} />
    </>
  );
}
`.trimStart();
}

