function getNextPagesRoute({
  sys,
  routePath,
  modelName,
}: {
  sys: NextDevToolsSys;
  routePath: string;
  modelName: "page" | "figma-imports";
}) {
  const ts = sys.typescriptEnabled;

  if (sys.sdkVersion === SDK_VERSIONS.gen2) {
    return `
import React from "react";
import { useRouter } from "next/router";
${ts ? 'import type { BuilderContent } from "@builder.io/sdk-react";' : ""}
import {
  Content,
  fetchEntries,
  fetchOneEntry,
  isPreviewing,
} from "@builder.io/sdk-react";
import DefaultErrorPage from "next/error";
import Head from "next/head";
${ts ? 'import { GetStaticProps } from "next";' : ""}
import { CUSTOM_COMPONENTS } from ${JSON.stringify(
      getImportPath(sys, sys.builderComponentPath, sys.registryPath),
    )};

const BUILDER_API_KEY = process.env.${NEXT_BUILDER_API_KEY_ENV}${ts ? "!" : ""};

// Define a function that fetches the Builder
// content for a given page
export const getStaticProps${
      ts ? ": GetStaticProps" : ""
    } = async ({ params }) => {
  ${sys.features.asyncParams ? "await params;" : ""}
  const urlPath =
    "/" +
    (Array.isArray(params?.page) ? params.page.join("/") : params?.page || "");

  // Fetch the builder content for the given page
  const page = await fetchOneEntry({
    apiKey: BUILDER_API_KEY,
    model: "${modelName}",
    userAttributes: { urlPath },
  });

  return {
    // Return the page content as props
    props: { page },
    // Revalidate the content every 5 seconds
    revalidate: 5,
  };
};

${
  modelName === "page"
    ? `
// Define a function that generates the
// static paths for all pages in Builder
export async function getStaticPaths() {
  // Get a list of all pages in Builder
  const pages = await fetchEntries({
    apiKey: BUILDER_API_KEY,
    model: "page",
    // We only need the URL field
    fields: "data.url",
    options: { noTargeting: true },
  });
  // Generate the static paths for all pages in Builder
  return {
    paths: pages.map((page) => \`\${page.data?.url}\`),
    fallback: "blocking",
  };
}
`
    : ""
}

// Define the Page component
export default function Page(props${
      ts ? ": { page: BuilderContent | null }" : ""
    }) {
  const router = useRouter();
  const canShowContent = props.page || isPreviewing(router.asPath);

  // If the page content is not available
  // and not in preview/editing mode, show a 404 error page
  if (!canShowContent) {
    return <DefaultErrorPage statusCode={404} />;
  }

  // If the page content is available, render
  // the BuilderComponent with the page content
  return (
    <>
      <Head>
        <title>{props.page?.data?.title}</title>
      </Head>
      {/* Render the Builder page */}
      <Content
        model="${modelName}"
        content={props.page}
        apiKey={BUILDER_API_KEY}
        customComponents={CUSTOM_COMPONENTS}
      />
    </>
  );
}

    `;
  }

  return `
import React from "react";
import { useRouter } from "next/router";
import { BuilderComponent, builder, useIsPreviewing } from "@builder.io/react";
import DefaultErrorPage from "next/error";
import Head from "next/head";${
    ts ? '\nimport { BuilderContent } from "@builder.io/sdk";' : ""
  }${ts ? '\nimport { GetStaticProps } from "next";' : ""}
import ${JSON.stringify(getImportPath(sys, routePath, sys.registryPath))};

builder.init(process.env.${NEXT_BUILDER_API_KEY_ENV}${ts ? "!" : ""});

// Define a function that fetches the Builder
// content for a given page
export const getStaticProps${
    ts ? ": GetStaticProps" : ""
  } = async ({ params }) => {
  ${sys.features.asyncParams ? "await params;" : ""}
  // Fetch the builder content for the given page
  const page = await builder
    .get("${modelName}", {
      userAttributes: {
        urlPath: "/" + ((params?.page${
          ts ? ` as string[]` : ``
        })?.join("/") || ""),
      },
    })
    .toPromise();

  // Return the page content as props
  return {
    props: {
      page: page || null,
    },
    // Revalidate the content every 5 seconds
    revalidate: 5,
  };
};

${
  modelName === "page"
    ? `// Define a function that generates the
// static paths for all pages in Builder
export async function getStaticPaths() {
  // Get a list of all pages in Builder
  const pages = await builder.getAll("page", {
    // We only need the URL field
    fields: "data.url",
    options: { noTargeting: true },
  });

  // Generate the static paths for all pages in Builder
  return {
    paths: pages.map((page) => String(page.data?.url)).filter(url => url !== '/'),
    fallback: 'blocking',
  };
}`
    : ``
}

// Define the Page component
export default function Page({ page }${
    ts ? ": { page: BuilderContent | null }" : ""
  }) {
  const router = useRouter();
  const isPreviewing = useIsPreviewing();

  // If the page content is not available
  // and not in preview mode, show a 404 error page
  if (!page && !isPreviewing) {
    return <DefaultErrorPage statusCode={404} />;
  }

  // If the page content is available, render
  // the BuilderComponent with the page content
  return (
    <>
      <Head>
        <title>{page?.data?.title}</title>
      </Head>
      {/* Render the Builder page */}
      <BuilderComponent model="${modelName}" content={page || undefined} />
    </>
  );
}
`.trimStart();
}

async function nextEnsureCounterComponent(
  sys: NextDevToolsSys,
  modifiedFiles: ModifiedFile[],
) {
  const ts = sys.typescriptEnabled;
  const ext = ts ? "tsx" : "jsx";

  const counterCmpDir = sys.join(sys.componentsDir, `Counter`);
  const counterCmpPath = sys.join(counterCmpDir, `Counter.${ext}`);
  const counterStylesPath = sys.join(counterCmpDir, `styles.module.css`);

  const { component, styles } = getCounterComponent(sys);

  if (!(await sys.exists(counterCmpPath))) {
    const code = await sys.formatCode(counterCmpPath, component);
    await sys.writeFile(counterCmpPath, code);
    modifiedFiles.push({
      filePath: counterCmpPath,
      modifiedType: "create",
    });
  }

  if (!(await sys.exists(counterStylesPath))) {
    await sys.writeFile(counterStylesPath, styles);
    modifiedFiles.push({
      filePath: counterStylesPath,
      modifiedType: "create",
    });
  }

  return counterCmpPath;
}

function getCounterComponent(sys: NextDevToolsSys) {
  const ts = sys.typescriptEnabled;
  const component = `
"use client"
import React, { useState } from "react";
import styles from "./styles.module.css";
${
  ts
    ? `
interface CounterProps {
  initialCount?: number;
}`
    : ``
}

function Counter({ initialCount = 99 }${ts ? `: CounterProps` : ``}) {
  const [count, setCount] = useState(initialCount);

  const increment = () => {
    setCount((prevCount) => prevCount + 1);
  };

  const decrement = () => {
    setCount((prevCount) => prevCount - 1);
  };

  return (
    <div className={styles.counter}>
      <button className={styles.btn} onClick={decrement}>
        -
      </button>
      <span className={styles.count}>{count}</span>
      <button className={styles.btn} onClick={increment}>
        +
      </button>
    </div>
  );
}

export default Counter;
`.trimStart();

  const styles = `
.counter {
  margin: 32px auto;
  display: flex;
  width: 100%;
  max-width: 190px;
}

.btn {
  width: 42px;
  font-size: 32px;
  font-weight: bold;
  background-color: #1c6bd1;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  line-height: 1.4;
}

.btn:hover {
  opacity: 0.8;
}

.count {
  flex: 1;
  font-size: 42px;
  text-align: center;
}
`.trimStart();

  return { component, styles };
}

async function nextEnsureBuilderRegistry(
  sys: NextDevToolsSys,
  modifiedFiles: ModifiedFile[],
  counterCmpPath?: string,
) {
  if (!(await sys.exists(sys.registryPath))) {
    if (sys.sdkVersion === SDK_VERSIONS.gen2) {
      const code = getNextBuilderRegistryReactGen2(sys);
      await sys.writeFile(sys.registryPath, code);
      // else it is gen1
    } else {
      await sys.writeFile(sys.registryPath, "");
    }

    modifiedFiles.push({
      filePath: sys.registryPath,
      modifiedType: "create",
    });
  }

  if (!counterCmpPath) {
    return;
  }

  const registry = await nextComponentRegistry(
    sys,
    null,
    false,
    false,
    null,
    null,
    false,
    false,
  );

  const counterCmp = registry.components.find((c) => {
    return c.filePath === counterCmpPath;
  });
  if (!counterCmp) {
    return;
  }

  // Register the counter component
  await nextComponentRegistry(
    sys,
    counterCmp.id,
    true,
    false,
    null,
    null,
    true,
    false,
  );
}

function getNextBuilderRegistryReactGen2(sys: NextDevToolsSys) {
  if (sys.typescriptEnabled) {
    return `import type { RegisteredComponent } from "@builder.io/sdk-react";

export const CUSTOM_COMPONENTS: RegisteredComponent[] = [
];`;
  }

  return `export const CUSTOM_COMPONENTS = [
  ];
  `;
}
