import * as ts from "typescript";
import * as path from "path";
import * as crypto from "crypto";

/**
 * Performs an incremental type-check (no emit) for the given project directory.
 * It reads tsconfig.json (just like the tsc CLI does) and enforces the `noEmit` flag.
 *
 * @param projectDir - The path to the project (where tsconfig.json is located).
 * @param oldProgram - Optionally, the previous builder program for incremental builds.
 * @returns An object containing the new builder program and collected diagnostics.
 */
export interface CheckpointData {
  program: ts.EmitAndSemanticDiagnosticsBuilderProgram;
  diagnostics: ts.Diagnostic[];
}

export type Checkpoint = CheckpointData | null;

export function runCheckpoint(
  projectDir: string,
  oldProgram?: ts.EmitAndSemanticDiagnosticsBuilderProgram,
): Checkpoint {
  try {
    // Locate tsconfig.json.
    const configPath = ts.findConfigFile(
      projectDir,
      ts.sys.fileExists,
      "tsconfig.json",
    );
    if (!configPath) {
      return null;
    }

    // Read and parse tsconfig.json.
    const configFile = ts.readConfigFile(configPath, ts.sys.readFile);
    if (configFile.error) {
      return null;
    }

    const parsedConfig = ts.parseJsonConfigFileContent(
      configFile.config,
      ts.sys,
      path.dirname(configPath),
    );

    // Enforce noEmit so that no files are output.
    parsedConfig.options.noEmit = true;

    // Create an incremental compiler host that correctly sets source file versions.
    const host = ts.createIncrementalCompilerHost(parsedConfig.options);

    // Create an incremental builder program, reusing the previous program if available.
    const builderProgram = ts.createEmitAndSemanticDiagnosticsBuilderProgram(
      parsedConfig.fileNames,
      parsedConfig.options,
      host,
      oldProgram,
      parsedConfig.errors,
      parsedConfig.projectReferences,
    );

    // Get all diagnostics (syntactic, semantic, etc.) without emitting files.
    const diagnostics = ts.getPreEmitDiagnostics(builderProgram.getProgram());

    return {
      program: builderProgram,
      diagnostics: diagnostics.filter(filterDiagnostic),
    };
  } catch (e) {
    return null;
  }
}

export function filterDiagnostic(c: ts.Diagnostic) {
  if (c.category !== ts.DiagnosticCategory.Error) {
    return false;
  }
  if (!c.file) {
    return false;
  }
  // usused parameter
  if (
    c.code === 6133 ||
    c.code === 6138 ||
    c.code === 6305 ||
    c.code === 6196 ||
    c.code === 6192 ||
    c.code === 6198 ||
    c.code === 6199 ||
    c.code === 6205 ||
    c.code === 1449 ||
    c.code === 2578 ||
    c.code === 7006 ||
    c.code === 5074 ||
    c.code === 7026 ||
    c.code === 2503 ||
    c.code === 2695
  ) {
    return false;
  }

  // Omit prop type mismatch
  // like when the component expects an enum but the value is a string
  // TODO: this is actually a legitimate type error and the correct way would be to use `as EnumType` in the mapper
  if (c.code === 2322) {
    return false;
  }

  return true;
}

/**
 * Produces a fingerprint (hash) for a given diagnostic.
 * Combines file name, position, code, category, and message into a unique identifier.
 */
function fingerprintDiagnostic(diag: ts.Diagnostic): string {
  // Use 'global' for diagnostics not tied to a file.
  const fileName = diag.file ? diag.file.fileName : "global";
  const pos =
    diag.file && diag.start !== undefined
      ? diag.file.getLineAndCharacterOfPosition(diag.start)
      : { line: 0, character: 0 };
  const code = diag.code;
  const category = diag.category;
  const message = ts.flattenDiagnosticMessageText(diag.messageText, "\n");
  // Create a raw fingerprint string.
  const rawFingerprint = `${fileName}:${pos.line}:${pos.character}:${code}:${category}:${message}`;
  // Hash the raw fingerprint for a concise representation.
  return crypto.createHash("sha256").update(rawFingerprint).digest("hex");
}

/**
 * Given a new list of diagnostics and a baseline list, filter out diagnostics
 * that were already present in the baseline.
 */
export function filterBaselineDiagnostics(
  baselineFingerprints: Set<string>,
  checkpoint: Checkpoint,
): ts.Diagnostic[] {
  if (checkpoint?.diagnostics) {
    return checkpoint.diagnostics.filter(
      (d) => !baselineFingerprints.has(fingerprintDiagnostic(d)),
    );
  }
  return [];
}

export function createFingerprintSet(checkpoint: Checkpoint): Set<string> {
  if (checkpoint?.diagnostics) {
    return new Set(checkpoint.diagnostics.map((d) => fingerprintDiagnostic(d)));
  }
  return new Set();
}

/**
 * Returns a FormatDiagnosticsHost suitable for formatting diagnostics.
 */
const diagnosticHost: ts.FormatDiagnosticsHost = {
  getCanonicalFileName: (fileName) => fileName,
  getCurrentDirectory: () => ts.sys.getCurrentDirectory(),
  getNewLine: () => ts.sys.newLine,
};

/**
 * Pretty prints diagnostics as a plain text string.
 *
 * @param diagnostics - An array of ts.Diagnostic objects.
 * @returns A formatted string that describes the diagnostics.
 */
export function prettyPrintDiagnostics(diagnostics: ts.Diagnostic[]): string {
  return ts.formatDiagnostics(diagnostics, diagnosticHost);
}

export function prettyPrintDiagnosticsWithContext(
  diagnostics: ts.Diagnostic[],
): string {
  return ts.formatDiagnosticsWithColorAndContext(diagnostics, diagnosticHost);
}
