export const runFigmaGenerate = async (sys: DevToolsSys, args: CLIArgs) => {
  const userContext = await getUserContext(sys);
  sys.Sentry?.setContext("userContext", userContext);

  const interactive =
    !args.componentName || !args.mappingOutput || !args.skipInstallation;
  if (interactive) {
    if (!process.stdout.isTTY && process.env.FORCE_TTY !== "1") {
      await track("tty error", {});
      cancel(
        "This command requires a TTY-enabled terminal.\nIf you are using Windows Git Bash, try a different terminal or wrap the command with winpty.",
      );
      return await exit(1);
    }
  }
  console.log("");
  intro(bgMagenta(bold(white(" figma generate "))) + `  ` + dim(pkgVersion));

  log.message(
    dim(`This command will help you create new mapper files to connect Figma components to your code components.
Check out `) +
      bold(cyan("https://www.builder.io/c/docs/cli-mapping")) +
      dim(" for more details."),
  );

  if (args.componentName && !args.mappingOutput) {
    log.error(
      "Mapping output must be provided when mapping a single component",
    );
    return await exit(1);
  }
  if (typeof args.mappingOutput === "string") {
    if (!isMapperFile(args.mappingOutput)) {
      log.error(
        "Mapping output must end with .mapper.{tsx, ts, jsx, js, mjs}, e.g. my-component.mapper.tsx",
      );
      return await exit(1);
    }
  }

  await checkProjectRoot(sys, interactive);

  let s: ReturnType<typeof spinner> | undefined;
  s = spinner();
  s.start("Loading type information");

  let devTools: DevTools | undefined;
  try {
    devTools = await createDevTools(sys);
  } catch (e) {
    sys.Sentry?.captureException(e);
    s.stop(`Repo's framework is not supported`);
    await track("figma generate, framework not supported", {
      frameworks: sys.getFrameworks().map((f) => f.name),
    });
    s.message(
      `We can not generate mappers automatically, but it's possible to do it manually:
https://www.builder.io/c/docs/mapping-functions`,
    );
    return await exit(1);
  }
  let importData: (readonly [string, FigmaComponentInfo])[] = [];
  let credentials: Credentials;
  if (args.token) {
    // empty line
    log.message();

    credentials = await getCredentials(sys, args, {});
    importData = await getImportDataFromToken(
      credentials,
      args.token,
      !!args.verbose,
    );
  } else {
    if (s) {
      s.stop(`Component metadata processed`);
      s = undefined;
    }
    const figmaUrls = args._.slice(2);
    if (!figmaUrls.length) {
      cancel(
        'No Figma URL provided, use:\n  npx builder.io figma generate "https://..."',
      );
      printHelp();
      return await exit(1);
    }

    credentials = await getCredentials(sys, args, {
      builderPrivateKey: true,
      builderPublicKey: true,
      figmaAuth: needsFigmaAuth(figmaUrls),
      forceSpaceId: args.spaceId,
    });
    if (!credentials.builderPrivateKey || !credentials.builderPublicKey) {
      cancel("Missing builder.io credentials");
      return await exit(1);
    }
    const codegenUsage = await getBuilderCodegenUsage(
      credentials.builderPublicKey,
      credentials.builderPrivateKey,
    );
    const links = await getBuilderSpaceFigmaLinks({
      privateKey: credentials.builderPrivateKey,
      spaceId: credentials.builderPublicKey,
    });
    if (
      !codegenUsage.features?.includes("component-mapping") &&
      links.length === 0
    ) {
      cancel(
        "Component mapping is an enterprise feature. Please visit " +
          bold(cyan("https://www.builder.io/unlock-enterprise")) +
          " to start your trial.",
      );
      return await exit(1);
    }

    s = spinner();
    s.start("Gathering Figma component info");
    importData = await getFigmaNodeDataFromURLs(
      sys,
      args,
      credentials.figmaAuth,
      {
        privateKey: credentials.builderPrivateKey,
        spaceId: credentials.builderPublicKey,
      },
      figmaUrls,
    );
  }

  await track("figma generate started", {
    spaceId: credentials.builderPublicKey,
    mode: args.componentName ? "non-interactive" : "interactive",
  });
  const highlight = createEmphasize(common);

  let mappingPath: string | undefined;
  let filePath: string | undefined;
  let registry = await devTools.getRegistry({
    readAllInputTypes: true,
  });

  if (s) {
    s.stop(`Figma's component metadata loaded`);
    s = undefined;
  }

  // empty line
  log.message();

  const mappingFiles = await findAllMappingFiles(sys);

  const { figmaBuilderLinks, figmaLinksToResolve } =
    await findMappingsFromFiles({
      sys,
      mappingFiles,
    });

  if (figmaLinksToResolve.length > 0 && credentials.figmaAuth) {
    await inPlaceResolveFigmaURLs(
      sys,
      args,
      credentials.figmaAuth,
      figmaBuilderLinks,
      figmaLinksToResolve,
    );
  }

  // select components to map, skip components already in registry by default but allow to override
  const selectedComponents: string[] = await selectFigmaComponents(
    importData,
    figmaBuilderLinks,
  );

  importData = importData.filter(([, c]) => selectedComponents.includes(c.key));

  const savedFiled: string[] = [];
  for (let i = 0; i < importData.length; i++) {
    const [figmaUrl, figmaComponentInfo] = importData[i];

    if (i > 0) {
      log.info(`Mapped ${i} / ${importData.length} components:\n
${importData
  .map(([, c], index) => {
    if (i > index) {
      if (c.documentName === "-") {
        return dim(` ❖ ${c.name}`) + " " + yellow("(skipped)");
      } else {
        return green(` ✔ ${c.name} → <${c.documentName} />`);
      }
    } else if (i === index) {
      return underline(` → ${c.name}`);
    } else {
      return dim(` ❖ ${c.name}`);
    }
  })
  .filter(Boolean)
  .join("\n")}\n`);
    }

    let registeredCmp: ComponentInfo | undefined;
    let lastCode: string | undefined;
    let lastCompletion: string | undefined;
    let lastFileName: string | undefined;

    const dependencyTree = await devTools.findAllDependencies();
    // Filter out known non-component packages
    const componentPackages = dependencyTree.filter(
      (pkg) => !isNonComponentPackages(pkg.name),
    );
    const resultComponent = args.componentName
      ? registry.components.find((cmp) => cmp.name === args.componentName)
      : await askComponent(
          registry.components,
          figmaComponentInfo.name,
          componentPackages.length > 0,
        );
    if (resultComponent === "external package") {
      const importPath = await askPackageImportPath(componentPackages);

      devTools.addExternalPackage(importPath);

      const newRegistry = await devTools.getRegistry({
        readAllInputTypes: true,
      });
      if (newRegistry) {
        registry = newRegistry;
      }
      const resultComponent = await askComponent(
        registry?.components.filter((cmp) => cmp.importPath === importPath),
        figmaComponentInfo.name,
        true,
      );
      if (resultComponent === "skip" || resultComponent === "scaffold") {
        registeredCmp = undefined;
      } else {
        registeredCmp = resultComponent as ComponentInfo;
      }
    } else if (resultComponent === "skip" || resultComponent === "scaffold") {
      registeredCmp = undefined;
    } else {
      registeredCmp = resultComponent as ComponentInfo;
    }

    if (!registeredCmp) {
      if (resultComponent === "scaffold") {
        log.info("Scaffolding mapper for " + figmaComponentInfo.name);
      } else {
        figmaComponentInfo.documentName = "-";
        log.warning("Skipping mapping for " + figmaComponentInfo.name);
        continue;
      }
    }
    if (!registeredCmp) {
      const isAngular = sys
        .getFrameworks()
        .some((f) => f.name === "@angular/core");
      registeredCmp = {
        id: "",
        name: getFigmaComponentName(figmaComponentInfo.name),
        importName: getFigmaComponentName(figmaComponentInfo.name),
        isRegistered: false,
        importPath: "",
        filePath: "",
        relFilePath: "",
        inputs: [],
        exportName: "",
        framework: isAngular ? "angular" : "react",
      };
    }
    const docsUrl = await askDocsUrl(registeredCmp);

    let prompt: string | undefined;

    let body: MappingCodeV3 = {
      registry,
      figmaNode: figmaComponentInfo,
      figmaUrl,
      registeredCmp,
      token: args.token,
      docsUrl,
      userContext,
    };

    try {
      while (true) {
        body.previousCompletion = lastCompletion;
        body.userPrompt = prompt;
        const stream = completionStream(credentials, body, !!args.verbose);
        for await (const chunk of stream) {
          if (chunk.type === "step") {
            if (s) {
              s.stop();
            }
            s = spinner();
            s.start(chunk.title);
          }
          if (chunk.type === "done") {
            lastCode = chunk.code;
            lastCompletion = chunk.completion;
            lastFileName = chunk.fileName;
          }
        }

        if (lastCode) {
          if (s) {
            s.stop("Generated mapping");
          }
          log.message(highlight.highlight("typescript", lastCode).value);
        } else {
          if (s) {
            s.stop("Failed to generate mapping", 1);
          }
          await track("figma generate, LLM failed", {
            spaceId: credentials.builderPublicKey,
            componentName: figmaComponentInfo.name,
          });
          return await exit(1);
        }

        if (args.componentName) {
          break;
        }

        const next = await askHowGenerationLooks();
        if (next.trim() === "good") {
          break;
        }
        await track("figma add follow up prompt", {
          spaceId: credentials.builderPublicKey,
          prompt: next,
        });
        prompt = next as string;
      }
    } catch (e) {
      sys.Sentry?.captureException(e);
      cancel("LLM failed:" + String(e));
      return await exit(1);
    }

    const baseName =
      lastFileName ??
      `${getFigmaComponentName(figmaComponentInfo.name)}.mapper.tsx`;

    // Save in memory the mapping
    figmaComponentInfo.documentName = registeredCmp.importName;

    // Save to disk
    if (lastCode) {
      if (args.mappingOutput) {
        filePath = args.mappingOutput;
        mappingPath = sys.dirname(filePath);
        fs.mkdirSync(mappingPath, { recursive: true });
      } else if (!mappingPath) {
        filePath = await getMappingPath(baseName);
        mappingPath = sys.dirname(filePath);
        fs.mkdirSync(mappingPath, { recursive: true });
      } else {
        filePath = sys.join(mappingPath, baseName);
      }
      fs.writeFileSync(
        filePath,
        await prettierFormat(lastCode, undefined, filePath),
      );
      savedFiled.push(filePath);
    }
    if (!registeredCmp.isRegistered && registeredCmp.id) {
      if (s) {
        s.stop();
      }
      s = spinner();
      s.start("Registering builder component in registry");
      await devTools.registerComponent({
        cmpId: registeredCmp.id,
      });
      s.stop(`<${registeredCmp.importName}> added to the builder registry`);
    }
  }
  if (credentials.builderPublicKey) {
    await setPublicKey(sys, credentials.builderPublicKey, devTools, true);
  }

  if (mappingPath) {
    if (savedFiled.length === 1) {
      log.success(
        `${bgGreen(bold(white(" Done! 🎉 ")))} New mapper file saved in: ${dim(
          savedFiled[0],
        )}`,
      );
    } else {
      log.success(
        `${bgGreen(bold(white(" Done! 🎉 ")))} Created ${
          savedFiled.length
        } new mapping files:\n${savedFiled.map((f) => `  - ${f}`).join("\n")}`,
      );
    }

    outro(
      dim(
        "Figma mappings need to be published to come into effect. You can do this by running:\n\n   ",
      ) +
        bgBlack(white(bold(` npx ${builderNpxPackage()} figma publish `))) +
        "\n\n   " +
        dim(
          "Read more: https://www.builder.io/c/docs/cli-mapping#publishing-a-new-mapping",
        ),
    );

    await track("figma add success", {
      spaceId: credentials.builderPublicKey,
      mappingPath,
      mode: args.componentName ? "non-interactive" : "interactive",
    });
  } else {
    outro("No new mappings were created.");
  }
};

async function getMappingPath(baseName: string) {
  const usesSrc = fs.existsSync("src");
  const defaultValue = `${usesSrc ? "src/" : ""}mappings/${baseName}`;

  const mappingPath = await text({
    message: `Where do you want to save the new mapping? ${dim(
      "(Ctrl+C to exit)",
    )}`,
    defaultValue,
    placeholder: defaultValue,
    initialValue: defaultValue,
    validate(value) {
      if (isAbsolute(value)) {
        return `Please enter a relative path, not absolute`;
      }
      if (!isMapperFile(value)) {
        return `Please enter a path that ends with '.mapper.{tsx,jsx,ts,js,mjs}'`;
      }
      if (fs.existsSync(value)) {
        return `File already exists, please choose a different name`;
      }
    },
  });
  if (isCancel(mappingPath)) {
    cancel("Mapping not saved, goodbye!");
    return await exit(1);
  }
  return mappingPath;
}

export interface MappingCodeV3 {
  figmaNode?: FigmaComponentInfo;
  registry?: ComponentRegistry;
  userContext?: UserContext;
  figmaUrl?: string;
  docsUrl?: string;
  registeredCmp?: ComponentInfo;
  previousCompletion?: string;
  userPrompt?: string;
  token?: string;
  pragma?: string;
}

async function selectFigmaComponents(
  importData: (readonly [string, FigmaComponentInfo])[],
  figmaBuilderLinks: FigmaBuilderLink[],
): Promise<string[]> {
  // If only one component, no selection needed
  if (importData.length <= 1) {
    return importData.map(([, c]) => c.key);
  }

  // Otherwise, show selection UI
  return await askSelectedComponents(importData, figmaBuilderLinks);
}

async function askHowGenerationLooks() {
  const next = await text({
    message: `How does the mapping look? ${dim(
      'Reply "good", or provide feedback (Ctrl+C to exit)',
    )}`,
    defaultValue: "good",
    placeholder: "good",
  });
  if (isCancel(next)) {
    cancel("Mapping not saved, goodbye!");
    return await exit(1);
  }
  return next;
}

async function askDocsUrl(registeredCmp: ComponentInfo) {
  const docsUrl = await text({
    message: `Providing a docs URL for ${
      registeredCmp.name
    } can drastically improve results: ${dim("(Ctrl+C to skip)")}`,
    placeholder: "https://",
    validate(value) {
      value = value.trim();
      if (value && !value.startsWith("https://")) {
        return "Please enter a valid URL";
      }
      return undefined;
    },
  });
  if (isCancel(docsUrl)) {
    return "";
  }
  return docsUrl;
}

async function askSelectedComponents(
  importData: (readonly [string, FigmaComponentInfo])[],
  figmaBuilderLinks: FigmaBuilderLink[],
) {
  const selectedComponents = await multiselect({
    message: `Pick which ${magenta(
      "❖",
    )} Figma components you want to connect to code:`,
    required: true,
    options: importData.map(([, c]) => ({
      label: `${c.name}${
        figmaBuilderLinks.some((link) => link.figmaKey === c.key)
          ? dim(" (already mapped)")
          : ""
      }`,
      value: c.key,
    })),
    initialValues: importData
      .map(([, c]) => c.key)
      .filter((key) => {
        return !figmaBuilderLinks.some((link) => link.figmaKey === key);
      }),
  });

  if (isCancel(selectedComponents)) {
    cancel("Mapping not saved, goodbye!");
    return await exit(1);
  }

  return selectedComponents;
}

async function askPackageImportPath(
  componentPackages: DependencyTree | undefined,
) {
  if (!componentPackages || componentPackages.length === 0) {
    return null;
  }

  if (componentPackages.length === 0) {
    return null;
  }

  const choices = componentPackages.map((pkg) => ({
    title: pkg.name,
    value: pkg.name,
  }));

  const packageResult = await prompts({
    type: "autocomplete",
    name: "package",
    message: `Select an installed external package:`,
    hint: "Type or use arrow keys. Press Enter to select",
    choices: choices,
    async suggest(input, choices) {
      input = input.toLowerCase();
      const suggestions = choices
        .map((c) => {
          const entryText = c.value.trim().toLowerCase();
          let d = 0;
          if (input.length === 0) {
            d = 0;
          } else {
            d = distance(input, entryText);
          }
          return {
            ...c,
            distance: d,
          };
        })
        .sort((a, b) => a.distance - b.distance)
        .filter((c) => {
          return c.title.toLowerCase().includes(input);
        });
      return suggestions;
    },
  });

  if (!packageResult.package) {
    cancel("Package selection cancelled");
    return await exit(1);
  }

  let importPath = packageResult.package;
  const selectedPackageInfo = componentPackages.find(
    (pkg) => pkg.name === packageResult.package,
  );
  function startsWithCapital(str: string): boolean {
    return /^[A-Z]/.test(str);
  }

  if (selectedPackageInfo && selectedPackageInfo.subPackages.length > 0) {
    // Filter out known non-component subpackages
    const componentSubPackages = selectedPackageInfo.subPackages.filter(
      (sub) => !NON_COMPONENT_PACKAGES.has(sub) && startsWithCapital(sub),
    );

    if (componentSubPackages.length > 0) {
      const choices = [
        {
          title: `${selectedPackageInfo.name} ${dim("(Ctrl+C)")}`,
          value: null,
        },
        ...componentSubPackages.map((sub) => ({
          title: `${dim(selectedPackageInfo.name)}/${sub}`,
          value: sub,
        })),
      ];
      const subPackageResult = await prompts({
        type: "autocomplete",
        name: "subPackage",
        message: `Select a component subpackage (optional):`,
        hint: "Type or use arrow keys. Press Enter to select",
        async suggest(input, choices) {
          input = input.toLowerCase().replace(selectedPackageInfo.name, "");
          const suggestions = choices
            .map((c) => {
              if (c.value === null) {
                return {
                  ...c,
                  distance: 0,
                };
              }
              const entryText = c.value
                ? c.value.trim().toLowerCase()
                : selectedPackageInfo.name;
              let d = 0;
              if (input.length === 0) {
                d = 0;
              } else {
                d = distance(input, entryText);
              }
              return {
                ...c,
                distance: d,
              };
            })
            .sort((a, b) => a.distance - b.distance)
            .filter((c) => {
              return c.title.toLowerCase().includes(input);
            });
          return suggestions;
        },
        choices,
      });

      if (
        subPackageResult.subPackage !==
        `${selectedPackageInfo.name} ${dim("(Ctrl+C)")}`
      ) {
        importPath = `${packageResult.package}/${subPackageResult.subPackage}`;
      }
    }
  }
  return importPath;
}

const NON_COMPONENT_PACKAGES = new Set([
  "react",
  "react-dom",
  "next",
  "nextjs",
  "typescript",
  "babel",
  "webpack",
  "eslint",
  "prettier",
  "jest",
  "lodash",
  "axios",
  "express",
  "moment",
  "redux",
  "react-redux",
  "styled-components",
  "emotion",
  "@types/react",
  "@types/node",
  // Common subpackages that aren't components
  "utils",
  "helpers",
  "date-fns",
  "constants",
  "types",
  "interfaces",
  "hooks",
  "gray-matter",
  "context",
  "providers",
  "store",
  "reducers",
  "actions",
  "services",
  "api",
  "clsx",
  "class-variance-authority",
  "lucide-react",
  "dotenv",
  "classnames",
]);

const isNonComponentPackages = (packageName: string) => {
  if (packageName.startsWith("@types")) {
    return true;
  }
  if (packageName.startsWith("@emotion")) {
    return true;
  }
  if (packageName.startsWith("@builder.io")) {
    return true;
  }
  if (packageName.startsWith("eslint")) {
    return true;
  }
  if (packageName.startsWith("prettier")) {
    return true;
  }
  if (packageName.startsWith("jest")) {
    return true;
  }
  if (packageName.startsWith("lodash")) {
    return true;
  }
  if (packageName.startsWith("remark")) {
    return true;
  }
  if (packageName.includes("autoprefixer")) {
    return true;
  }
  if (packageName.includes("tailwind")) {
    return true;
  }
  if (packageName.includes("postcss")) {
    return true;
  }
  if (packageName.includes("webpack")) {
    return true;
  }
  if (packageName.includes("babel")) {
    return true;
  }
  if (packageName.includes("typescript")) {
    return true;
  }
  if (packageName.includes("types")) {
    return true;
  }
  if (packageName.includes("rollup")) {
    return true;
  }
  if (packageName.includes("vite")) {
    return true;
  }
  if (packageName.includes("esbuild")) {
    return true;
  }
  if (packageName.includes("storybook")) {
    return true;
  }
  if (packageName.includes("testing-library")) {
    return true;
  }
  if (packageName.includes("cypress")) {
    return true;
  }
  return NON_COMPONENT_PACKAGES.has(packageName);
};

async function askComponent(
  components: ComponentInfo[],
  componentNameRef: string,
  addExternalPackage: boolean,
) {
  const indent = "";
  const bestMatch = componentNameRef.toLowerCase();
  function isGoodMatch(c: ComponentInfo) {
    const cmpName = c.importName.toLowerCase();
    let d = distance(cmpName, bestMatch);
    if (cmpName.includes(bestMatch)) {
      d -= bestMatch.length;
    }
    return d;
  }
  const choices = components
    .slice()
    .sort((a, b) => {
      const aDistance = isGoodMatch(a);
      const bDistance = isGoodMatch(b);

      if (aDistance === bDistance) {
        return a.importName.localeCompare(b.importName);
      }
      return aDistance - bDistance;
    })
    .map((c) => {
      let title = `${c.importName}`;
      if (isGoodMatch(c) < 3) {
        title = bold(yellow("★ ") + title);
      } else {
        title = title;
      }
      return {
        title: indent + title + dim(` from '${c.importPath}'`),
        value: c as ComponentInfo | string,
      };
    });

  choices.push({
    title: indent + "⏭️  Skip " + dim("(Ctrl+C)"),
    value: "skip",
  });
  choices.push({
    title: indent + "🏗️  Scaffold Mapper " + dim("(Generate interface only)"),
    value: "scaffold",
  });
  if (addExternalPackage) {
    choices.push({
      title: indent + "📦 External npm package",
      value: "external package",
    });
  }
  const result = await prompts({
    type: "autocomplete",
    name: "component",
    message: ` Select a code component to map ${bold(
      magenta(`❖ ${componentNameRef}`),
    )}`,
    hint: "Type or use arrow keys. Press Enter to select",
    async suggest(input, choices) {
      input = input.toLowerCase();
      const distanceInput = input.length === 0 ? bestMatch : input;
      const suggestions = choices
        .map((c) => {
          const entryText = (
            typeof c.value === "string" ? c.value : c.value.importName
          )
            .trim()
            .toLowerCase();
          let d = 0;
          if (input.length === 0 && typeof c.value === "string") {
            d = 2;
          } else {
            d = distance(distanceInput, entryText);
            if (entryText.includes(bestMatch)) {
              d -= bestMatch.length;
            }
            if (typeof c.value === "string") {
              d += 1000;
            }
          }
          return {
            ...c,
            distance: d,
          };
        })
        .sort((a, b) => a.distance - b.distance)
        .filter((c) => {
          if (c.value === "external package") {
            return true;
          }
          if (c.value === "skip") {
            return true;
          }
          return c.title.toLowerCase().includes(input);
        });

      return suggestions;
    },
    choices: choices,
  });
  return result.component;
}

interface MappingItemDone {
  type: "done";
  code: string;
  completion: string;
  fileName: string;
}

interface MappingItemCode {
  type: "code";
  delta: string;
}

interface MappingItemStep {
  type: "step";
  title: string;
}

type MappingItem = MappingItemDone | MappingItemCode | MappingItemStep;

async function* completionStream(
  credentials: Credentials,
  body: MappingCodeV3,
  verbose: boolean,
) {
  const json = JSON.stringify(body);
  const url = new URL(`${apiAirV2Host}/mappings/completions/v3`);
  if (credentials.builderPublicKey) {
    url.searchParams.set("apiKey", credentials.builderPublicKey);
  }
  if (credentials.userId) {
    url.searchParams.set("userId", credentials.userId);
  }
  if (verbose) {
    console.log("FETCH", url, json);
  }
  const headers: Record<string, string> = {
    "Content-Type": "application/json",
  };
  if (credentials.builderPrivateKey) {
    headers.Authorization = `Bearer ${credentials.builderPrivateKey}`;
  }
  const res = await safeFetch(url, {
    method: "POST",
    body: json,
    headers,
  });
  if (!res.ok) {
    throw new Error("Failed to generate completion:\n" + (await res.text()));
  }
  const stream = transformStream(res.body);
  for await (const value of stream) {
    if (value.length === 0) {
      continue;
    }
    const json = JSON.parse(value) as MappingItem;
    yield json;
  }
}
