import type { NextDevToolsSys } from "./index";
import type ts from "typescript";
import {
  getReactComponentsFromSourceFile,
  type ReactComponentSource,
} from "../react/react-components";
import { isMapperFile } from "../../../common/utils";

export async function getNextSourceComponentsFromDir(
  sys: NextDevToolsSys,
  tsProgram: ts.Program,
) {
  const cmps: ReactComponentSource[] = [];
  const sourceFiles = tsProgram.getSourceFiles();
  const typeChecker = tsProgram.getTypeChecker();

  await Promise.all(
    sourceFiles.map(async (sourceFile) => {
      const fileName = sys.basename(sourceFile.fileName);
      if (sys.appDir && SKIP_APP_FILES.has(fileName)) {
        return;
      }
      if (sys.dirname(sourceFile.fileName).includes("node_modules")) {
        return;
      }

      const filePath = sys.normalize(sourceFile.fileName);
      if (
        filePath === sys.nextConfigPath ||
        filePath === sys.builderComponentPath
      ) {
        return;
      }

      const fileCmps = await getReactComponentsFromSourceFile(
        sys,
        typeChecker,
        filePath,
        sourceFile,
        filePath === sys.registryPath || isMapperFile(filePath),
      );
      cmps.push(...fileCmps);
    }),
  );

  return cmps.sort((a, b) => a.cmpInfo.name.localeCompare(b.cmpInfo.name));
}

// https://nextjs.org/docs/app/api-reference/file-conventions
const SKIP_APP_FILES = new Set([
  "default.tsx",
  "default.ts",
  "default.jsx",
  "default.js",
  "error.tsx",
  "error.ts",
  "error.jsx",
  "error.js",
  "layout.tsx",
  "layout.ts",
  "layout.jsx",
  "layout.js",
  "loading.tsx",
  "loading.ts",
  "loading.jsx",
  "loading.js",
  "not-found.tsx",
  "not-found.ts",
  "not-found.jsx",
  "not-found.js",
  "page.tsx",
  "page.ts",
  "page.jsx",
  "page.js",
  "route.tsx",
  "route.ts",
  "route.jsx",
  "route.js",
  "template.tsx",
  "template.ts",
  "template.jsx",
  "template.js",
]);
