import type { EnsureConfigResult } from "../../../types";
import type { DevToolsSys } from "../..";

export async function webpackEnsureConfigPlugin(
  _sys: DevToolsSys,
  _configFilePath: string,
  _configContent: string,
): Promise<EnsureConfigResult> {
  _sys.debug(`Webpack config updates are currently in progress...`);
  return {
    content: _configContent,
    fileName: _sys.basename(_configFilePath),
    filePath: _configFilePath,
    hasContentUpdates: true,
  };
}
