import type ts from "typescript";
import type { DevToolsSys } from "../../types";
import { findVariableStatement } from "./utils";

export function ensureArrayStatement(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  opts: EnsureArrayStatementOptions,
) {
  const variableStatement = findVariableStatement(
    sys,
    sourceFile,
    opts.variableName,
  );
  if (variableStatement) {
    return sourceFile;
  }
  return appendArrayStatement(sys, sourceFile, opts);
}

function appendArrayStatement(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  opts: EnsureArrayStatementOptions,
) {
  const arrayType =
    typeof opts.arrayType === "string"
      ? sys.ts.factory.createArrayTypeNode(
          sys.ts.factory.createTypeReferenceNode(opts.arrayType, undefined),
        )
      : undefined;

  const initializer = sys.ts.factory.createArrayLiteralExpression([]);

  const variableDeclaration = sys.ts.factory.createVariableDeclaration(
    opts.variableName,
    undefined,
    arrayType,
    initializer,
  );

  const variableDeclarationList = sys.ts.factory.createVariableDeclarationList(
    [variableDeclaration],
    sys.ts.NodeFlags.Const,
  );

  const exportModifier = sys.ts.factory.createModifier(
    sys.ts.SyntaxKind.ExportKeyword,
  );

  const variableStatement = sys.ts.factory.createVariableStatement(
    [exportModifier],
    variableDeclarationList,
  );

  const statements = [...sourceFile.statements, variableStatement];
  return sys.ts.factory.updateSourceFile(sourceFile, statements);
}

export interface EnsureArrayStatementOptions {
  variableName: string;
  arrayType?: string;
}
