import { SDK_VERSIONS, type DevToolsSys } from "../../../types";
import { findPackageJson } from "../../../common/fs";

export async function detectReactSdkVersion(sys: DevToolsSys) {
  const pkgJson = await findPackageJson(sys, sys.getRepoRootDir());
  if (pkgJson && typeof pkgJson === "object") {
    const depNames = new Set(
      Object.keys({
        ...pkgJson.dependencies,
        ...pkgJson.devDependencies,
      }),
    );

    if (depNames.has("@builder.io/sdk-react")) {
      return SDK_VERSIONS.gen2;
    }
    if (depNames.has("@builder.io/react")) {
      return SDK_VERSIONS.gen1;
    }
  }

  return null;
}
