import type { DevToolsSys, EnvInfo } from "../../../types";
import { getDotEnvValue, setDotEnvVar } from "../../../common/dotenv";

export async function getNextApiKey(sys: DevToolsSys) {
  const dotEnvFileNames = [
    ".env.local",
    ".env.development.local",
    ".env.production.local",
    ".env.development",
    ".env.production",
    ".env",
  ];

  const env = await getDotEnvValue(
    sys,
    dotEnvFileNames,
    NEXT_BUILDER_API_KEY_ENV,
  );
  return env;
}

export async function setNextApiKey(sys: DevToolsSys, publicApiKey: string) {
  const env = await getNextApiKey(sys);

  const modifiedType = await setDotEnvVar(
    sys,
    env.file,
    env.envKey,
    publicApiKey,
  );

  const updatedEnv: EnvInfo = {
    ...env,
    modifiedType,
  };
  return updatedEnv;
}

export const NEXT_BUILDER_API_KEY_ENV = `NEXT_PUBLIC_BUILDER_API_KEY`;
