import type ts from "typescript";
import type { DevToolsSys } from "../../types";

export function setObjectExpressionProperty(
  sys: DevToolsSys,
  obj: ts.ObjectLiteralExpression,
  propName: string,
  propValue: any,
  deleteIfNullOrUndefined = true,
) {
  const props = obj.properties.filter((p) =>
    sys.ts.isPropertyAssignment(p),
  ) as ts.PropertyAssignment[];

  const i = getObjLiteralPropertyIndex(sys, obj, propName);
  if (i > -1) {
    if (propValue == null && deleteIfNullOrUndefined) {
      return sys.ts.factory.updateObjectLiteralExpression(
        obj,
        props.filter((_, j) => j !== i),
      );
    }

    props[i] = sys.ts.factory.updatePropertyAssignment(
      props[i],
      props[i].name,
      valueToExpression(sys, propValue),
    );

    return sys.ts.factory.updateObjectLiteralExpression(obj, props);
  }

  if (propValue != null) {
    props.push(
      sys.ts.factory.createPropertyAssignment(
        propName,
        valueToExpression(sys, propValue),
      ),
    );
  }

  return sys.ts.factory.updateObjectLiteralExpression(obj, props);
}

export function getObjLiteralPropertyIndex(
  sys: DevToolsSys,
  obj: ts.ObjectLiteralExpression,
  propName: string,
) {
  return obj.properties.findIndex((p) => {
    return getTextOfPropertyName(sys, p) === propName;
  });
}

export function expressionToValue(sys: DevToolsSys, exp: ts.Expression): any {
  switch (exp.kind) {
    case sys.ts.SyntaxKind.ArrayLiteralExpression:
      return convertArrayExpressionToJsArray(
        sys,
        exp as ts.ArrayLiteralExpression,
      );

    case sys.ts.SyntaxKind.ObjectLiteralExpression:
      return objectExpressionToObjectValue(
        sys,
        exp as ts.ObjectLiteralExpression,
      );

    case sys.ts.SyntaxKind.StringLiteral:
      return (exp as ts.StringLiteral).text;

    case sys.ts.SyntaxKind.NumericLiteral:
      return Number((exp as ts.NumericLiteral).text);

    case sys.ts.SyntaxKind.NoSubstitutionTemplateLiteral:
      return (exp as ts.StringLiteral).text;

    case sys.ts.SyntaxKind.TrueKeyword:
      return true;

    case sys.ts.SyntaxKind.FalseKeyword:
      return false;

    case sys.ts.SyntaxKind.Identifier:
      switch ((exp as ts.Identifier).escapedText) {
        case "undefined":
          return undefined;
        case "null":
          return null;
        case "String":
          return String;
        case "Number":
          return Number;
        case "Boolean":
          return Boolean;
        case "NaN":
          return NaN;
      }
  }
  return undefined;
}

export function objectExpressionToObjectValue(
  sys: DevToolsSys,
  objectLiteral: ts.ObjectLiteralExpression,
): { [key: string]: any } {
  const obj: { [key: string]: any } = {};

  if (objectLiteral && sys.ts.isObjectLiteralExpression(objectLiteral)) {
    for (const prop of objectLiteral.properties) {
      if (sys.ts.isPropertyAssignment(prop)) {
        const attrName = getTextOfPropertyName(sys, prop);
        if (attrName) {
          obj[attrName] = expressionToValue(sys, prop.initializer);
        }
      }
    }
  }

  return obj;
}

export function convertArrayExpressionToJsArray(
  sys: DevToolsSys,
  arr: ts.ArrayLiteralExpression,
) {
  if (arr && sys.ts.isArrayLiteralExpression(arr)) {
    return arr.elements.map((el) => expressionToValue(sys, el));
  }
  return [];
}

export function getTextOfPropertyName(
  sys: DevToolsSys,
  prop: ts.PropertyAssignment | ts.ObjectLiteralElementLike | undefined,
): string | undefined {
  if (prop && sys.ts.isPropertyAssignment(prop)) {
    const propName = prop.name;
    switch (propName.kind) {
      case sys.ts.SyntaxKind.Identifier:
        return (<ts.Identifier>propName).text;
      case sys.ts.SyntaxKind.StringLiteral:
      case sys.ts.SyntaxKind.NumericLiteral:
        return (<ts.LiteralExpression>propName).text;
      case sys.ts.SyntaxKind.ComputedPropertyName:
        const expression = (<ts.ComputedPropertyName>propName).expression;
        if (
          sys.ts.isStringLiteral(expression) ||
          sys.ts.isNumericLiteral(expression)
        ) {
          return (<ts.LiteralExpression>(
            (<ts.ComputedPropertyName>propName).expression
          )).text;
        }
    }
  }
  return undefined;
}

export function valueToExpression(sys: DevToolsSys, val: any) {
  return valueToTsExpression(sys, val, new WeakSet());
}

function valueToTsExpression(sys: DevToolsSys, val: any, refs: WeakSet<any>) {
  if (val === undefined) {
    return sys.ts.factory.createIdentifier("undefined");
  }
  if (val === null) {
    return sys.ts.factory.createIdentifier("null");
  }
  if (typeof val === "string") {
    return sys.ts.factory.createStringLiteral(val);
  }
  if (typeof val === "number") {
    if (isNaN(val)) {
      return sys.ts.factory.createIdentifier("NaN");
    }
    return sys.ts.factory.createNumericLiteral(val);
  }
  if (val === true) {
    return sys.ts.factory.createTrue();
  }
  if (val === false) {
    return sys.ts.factory.createFalse();
  }
  if (val === String) {
    return sys.ts.factory.createIdentifier("String");
  }
  if (val === Number) {
    return sys.ts.factory.createIdentifier("Number");
  }
  if (val === Boolean) {
    return sys.ts.factory.createIdentifier("Boolean");
  }
  if (Array.isArray(val)) {
    return arrayToArrayExpression(sys, val, refs);
  }
  if (typeof val === "object") {
    return objectToObjectLiteral(sys, val, refs);
  }
  return sys.ts.factory.createIdentifier(val);
}

function arrayToArrayExpression(
  sys: DevToolsSys,
  list: any[],
  refs: WeakSet<any>,
): ts.ArrayLiteralExpression {
  const newList: any[] = list.map((l) => {
    return valueToTsExpression(sys, l, refs);
  });
  return sys.ts.factory.createArrayLiteralExpression(newList, true);
}

function objectToObjectLiteral(
  sys: DevToolsSys,
  obj: { [key: string]: any },
  refs: WeakSet<any>,
): ts.ObjectLiteralExpression {
  if (refs.has(obj)) {
    return sys.ts.factory.createIdentifier("undefined") as any;
  }

  refs.add(obj);

  const newProperties: ts.ObjectLiteralElementLike[] = Object.keys(obj).map(
    (key) => {
      return sys.ts.factory.createPropertyAssignment(
        key,
        valueToTsExpression(sys, obj[key], refs) as ts.Expression,
      );
    },
  );

  return sys.ts.factory.createObjectLiteralExpression(newProperties, true);
}
