import type { QwikDevToolsSys } from ".";
import {
  getQwikComponentsFromSourceFile,
  type QwikComponentSource,
} from "./qwik-components";
import { getTypeChecker } from "../../../common/typescript";
import { parseComponentSourcePropsFromNode } from "../../../common/ast/component-input-types";

export async function parseQwikComponentSourceInputs(
  sys: QwikDevToolsSys,
  srcCmp: QwikComponentSource,
) {
  const fnNode = srcCmp.node.fnNode;

  if (sys.ts.isArrowFunction(fnNode) || sys.ts.isFunctionExpression(fnNode)) {
    const fnParams = fnNode.parameters;
    if (fnParams.length > 0) {
      const { sourceFile, typeChecker } = await getTypeChecker(
        sys,
        srcCmp.cmpInfo.filePath,
        null,
      );
      const typedSrcCmps = await getQwikComponentsFromSourceFile(
        sys,
        typeChecker,
        srcCmp.cmpInfo.filePath,
        sourceFile,
      );
      const typedSrcCmp = typedSrcCmps.find(
        (t) => t.cmpInfo.id === srcCmp.cmpInfo.id,
      );
      if (!typedSrcCmp) {
        throw new Error(
          `createComponentInputs() Could not find component ${srcCmp.cmpInfo.name} in ${srcCmp.cmpInfo.filePath}`,
        );
      }
      return parseComponentSourcePropsFromNode(sys, typeChecker, fnNode);
    }
  }
  return [];
}
