import prettier from "prettier";

export async function prettierFormat(
  code: string,
  parser?: string,
  filePath?: string,
) {
  try {
    if (filePath) {
      const file = await prettier.getFileInfo(filePath);
      if (file.ignored || file.inferredParser == null) {
        return code;
      }
    }
    const configFile = await prettier.resolveConfigFile(process.cwd());
    if (configFile) {
      const config = await prettier.resolveConfig(configFile);
      const formattedCode = await prettier.format(code, {
        ...config,
        filepath: filePath,
        parser,
      });
      return formattedCode;
    } else {
      const formattedCode = await prettier.format(code, {
        filepath: filePath,
        parser,
      });
      return formattedCode;
    }
  } catch (error) {
    console.warn("Error formatting code", error);
    return code;
  }
}
