import { isMapperFile } from "../common/utils";

export interface ArtifactItem {
  type: "shell" | "file" | "text" | "delta" | "done";
  id?: string;
  content: string;
  filePath?: string;
  artifactTitle?: string;
  incomplete?: boolean;
}

interface CodebaseIndexingProgress {
  type: "progress";
  chunksProcessed: number;
  totalChunks: number;
}

interface Diagnostic {
  type: "embedding-error" | "extra-context-error";
  files: string[];
}

interface IndexingContext {
  type: "stats";
  inputTokens: number;
  cachedInputTokens: number;
  outputTokens: number;
  totalChunks: number;
  embeddingTokens: number;
  writtenVectors: number;
  dbWrites: number;
  diagnostics: Diagnostic[];
}

const INDEXING_POOL = 16;
const INDEXING_MAX_REQUEST_SIZE = 200000;
const INDEXING_MAX_FILE_SIZE = 300000;

type CodebaseDataEvent = CodebaseIndexingProgress | IndexingContext;

