export const codeIndexing = async (
  sys: DevToolsSys,
  credentials: Credentials,
) => {
  let s = spinner();
  s.start(`Indexing files...`);

  const appRootDir = sys.getAppRootDir();
  const files = await getAllProjectFiles(appRootDir);

  const allFileContents = files
    .filter(shouldIncludeFile)
    .map((file) => ({
      filePath: file,
      content: sys.readFileSync(sys.join(appRootDir, file))!,
    }))
    .filter((file) => file.content.length < INDEXING_MAX_FILE_SIZE);

  const fileGroups: {
    filePath: string;
    content: string;
  }[][] = [];

  let currentGroup: {
    filePath: string;
    content: string;
  }[] = [];
  fileGroups.push(currentGroup);

  let currentSize = 0;
  for (const file of allFileContents) {
    if (currentSize + file.content.length > INDEXING_MAX_REQUEST_SIZE) {
      currentGroup = [];
      fileGroups.push(currentGroup);
      currentSize = 0;
    }
    currentGroup.push(file);
    currentSize += file.content.length;
  }

  let currentStep = 0;
  const totalBatch = fileGroups.length;
  let startTime = Date.now();

  function updateProgress(delta: number) {
    currentStep += delta;
    const progressBarChar = "░";
    const maxLength = 60;
    const progress = currentStep / totalBatch;
    const progressBar = progressBarChar.repeat(
      Math.floor(progress * maxLength),
    );
    const progressBarEmpty = " ".repeat(maxLength - progressBar.length);
    const progressPercentage = `${Math.round(progress * 100)}%`;

    const elapsedMs = Date.now() - startTime;
    const estimatedTotalMs = progress > 0 ? elapsedMs / progress : 0;
    const remainingMs = Math.max(0, estimatedTotalMs - elapsedMs);
    const remainingTime = new Date(remainingMs).toISOString().substr(11, 8);

    s.extra(
      `\n\n[${progressBar}${progressBarEmpty}] ${progressPercentage} (${remainingTime} remaining)`,
    );
  }

  updateProgress(0);

  const sessionId = crypto.randomUUID();
  const repoInfo = await sys.getRepoInfo();
  await Promise.all(
    Array.from({ length: INDEXING_POOL }).map(async () => {
      while (true) {
        const fileContents = fileGroups.shift();
        if (!fileContents) {
          break;
        }
        const url = new URL(`${apiAirV2Host}/codegen/indexing`);
        url.searchParams.set("apiKey", credentials.builderPublicKey!);
        url.searchParams.set("userId", credentials.userId!);
        const response = await safeFetch(url, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${credentials.builderPrivateKey}`,
          },
          body: JSON.stringify({
            repoInfo,
            files: fileContents,
            sessionId: sessionId,
            addExtraContext: true,
          }),
        });
        if (!response.ok) {
          throw new Error("Failed to index codebase");
        }
        const stream = transformStream(response.body);
        let lastProgress = 0;
        for await (const chunk of stream) {
          if (chunk.length === 0) {
            continue;
          }
          const json = JSON.parse(chunk) as CodebaseDataEvent;
          if (json.type === "progress") {
            const progress = json.chunksProcessed / json.totalChunks;
            updateProgress(progress - lastProgress);
            lastProgress = progress;
          }
        }
      }
    }),
  );

  s.stop("Codebase indexed");
};

function shouldIncludeFile(file: string) {
  if (file.endsWith("package.json")) {
    return true;
  }

  // not include if not code file (.ts, .tsx, .js, .jsx, .md, .html)
  if (
    !file.endsWith(".ts") &&
    !file.endsWith(".tsx") &&
    !file.endsWith(".js") &&
    !file.endsWith(".jsx") &&
    !file.endsWith(".html")
  ) {
    return false;
  }
  if (isMapperFile(file)) {
    return false;
  }
  if (file.endsWith("package-lock.json")) {
    return false;
  }

  return true;
}
