import { createServer, IncomingMessage, ServerResponse } from "http";
import type { DevToolsSys } from "../types";
import {
  BUILDER_AUTH_RETURN_PATH,
  FRAMEWORK_QS,
  NODE_VERSION_QS,
  PLATFORM_QS,
} from "../common/constants";
import { platform } from "node:os";
import { join } from "node:path";
import { existsSync, mkdirSync, readFileSync, writeFileSync } from "node:fs";
import { confirm, log } from "@clack/prompts";
import { yellow, green, dim } from "picocolors";
import type { CLIArgs } from "./index";
import { setPublicKey } from "./figma-publish";
import { setUserId } from "./track";
import { randomUUID } from "crypto";
import { safeFetch } from "./fetch";
import { spinner } from "./spinner";
import { printHelp } from "./help";
import type { AddressInfo } from "net";

declare const OAUTH_DONE_HTML: string;

// Default starting ports
const DEFAULT_FIGMA_PORT = 5294;
const DEFAULT_BUILDER_PORT = 5284;
const MAX_PORT_ATTEMPTS = 4;

export interface CredentialsOptions {
  forceSpaceId?: string;
  builderPublicKey?: boolean;
  builderPrivateKey?: boolean;
  figmaAuth?: boolean;
  force?: boolean;
}

export interface FigmaAuth {
  access_token: string;
  oauth: boolean;
}

export type Feature = "component-mapping";
export interface BuilderCodegenUsage {
  total: number | undefined;
  fast: number | undefined;
  quality: number | undefined;
  features?: Feature[];
  limits?: {
    aiGeneration: number;
    aiGenerationContextWindow: number;
  };
}

export interface BuilderAuth {
  privateKey: string;
  spaceId: string;
  spaceName: string;
  userId: string;
}

export interface Credentials {
  figmaAuth?: FigmaAuth;
  builderPublicKey?: string;
  builderPrivateKey?: string;
  spaceName?: string;
  userId?: string;
}

export const isCI = (args: CLIArgs) => {
  if (process.env.CI) {
    return true;
  }
  if (args.ci) {
    return true;
  }
  return false;
};

export interface Credentials {
  figmaAuth?: FigmaAuth;
  builderPublicKey?: string;
  builderPrivateKey?: string;
  spaceName?: string;
  userId?: string;
}

export const getCredentials = async (
  sys: DevToolsSys,
  args: CLIArgs,
  opts: CredentialsOptions,
): Promise<Credentials> => {
  const storedCredentials = isCI(args) ? {} : loadCredentials(sys);

  let spaceName: string | undefined = storedCredentials.spaceName;
  let userId: string | undefined = storedCredentials.userId;
  let figmaAuth: FigmaAuth | undefined;
  let builderPublicKey: string | undefined =
    process.env.BUILDER_PUBLIC_KEY ?? storedCredentials.builderPublicKey;
  let builderPrivateKey: string | undefined =
    process.env.BUILDER_PRIVATE_KEY ?? storedCredentials.builderPrivateKey;
  const figmaPersonalToken =
    process.env.FIGMA_PERSONAL_TOKEN ?? args.figmaToken;
  if (figmaPersonalToken) {
    figmaAuth = { access_token: figmaPersonalToken, oauth: false };
  } else {
    figmaAuth = storedCredentials.figmaAuth;
  }
  if (
    opts.forceSpaceId &&
    !args.yes &&
    builderPublicKey &&
    builderPublicKey !== opts.forceSpaceId
  ) {
    log.warning(
      `CLI is currently logged in space ${yellow(
        builderPublicKey,
      )},\nHowever the command required --spaceId=${green(opts.forceSpaceId)}`,
    );
    const confirmSwitch = await confirm({
      message: `Do you wanna switch spaces?`,
      active: "Yes",
      inactive: `Stay in current ${spaceName} space`,
    });

    if (confirmSwitch) {
      builderPublicKey = opts.forceSpaceId;
      builderPrivateKey = undefined;
      spaceName = undefined;
      userId = undefined;
    } else {
      log.warning(
        `Staying in the current space. Some other auth issues might occur.`,
      );
    }
  }

  if (isCI(args)) {
    if (!figmaAuth && opts.figmaAuth) {
      throw new Error(
        "No Figma personal token found.\nPlease set the FIGMA_PERSONAL_TOKEN environment variable.",
      );
    }
    if (!builderPrivateKey && opts.builderPrivateKey) {
      throw new Error(
        "No Builder private key found.\nPlease set the BUILDER_PRIVATE_KEY environment variable.",
      );
    }
    if (!builderPublicKey && opts.builderPublicKey) {
      throw new Error(
        "No Builder public key found.\nPlease set the BUILDER_PUBLIC_KEY environment variable.",
      );
    }
    return {
      figmaAuth,
      builderPublicKey,
      builderPrivateKey,
    };
  }
  if (opts.force || (!figmaAuth && opts.figmaAuth)) {
    figmaAuth = await getFigmaAuth(sys);
    storeCredentials(sys, {
      figmaAuth,
    });
  }
  if (
    opts.force ||
    (!builderPrivateKey && opts.builderPrivateKey) ||
    (!builderPublicKey && opts.builderPublicKey)
  ) {
    const {
      privateKey,
      spaceId,
      spaceName: name,
      userId: uId,
    } = await getBuilderAuth(sys, opts.forceSpaceId);
    if (opts.forceSpaceId && spaceId !== opts.forceSpaceId) {
      throw new Error(
        `OAuth flow returned space ${yellow(
          spaceId,
        )}, however the command specified --spaceId=${green(opts.forceSpaceId)}`,
      );
    }
    builderPublicKey = spaceId;
    builderPrivateKey = privateKey;
    spaceName = name;
    userId = uId;
    await setUserId(userId);
    await setPublicKey(sys, spaceId, undefined, false);
    storeCredentials(sys, {
      builderPublicKey,
      builderPrivateKey,
      spaceName,
      userId,
    });
  }

  if (userId) {
    sys.Sentry?.setUser({
      id: userId,
      username: process.env.USER || process.env.USERNAME || "unknown",
    });
  }
  if (spaceName || builderPublicKey) {
    sys.Sentry?.setContext("builder", {
      spaceId: builderPublicKey,
      spaceName,
      userId,
    });
  }
  return { figmaAuth, builderPublicKey, builderPrivateKey, spaceName, userId };
};

const SECRET = "RHr2ed743waC2g2ZHMEJSn6O0NQ08L";
export async function getFigmaAuth(sys: DevToolsSys): Promise<FigmaAuth> {
  const randomState = randomUUID();
  return createAuthServer<FigmaAuth>({
    sys,
    name: "Figma",
    initialPort: DEFAULT_FIGMA_PORT,
    requestListener: async (port, req, res, resolve, reject) => {
      const url = new URL(req.url || "", `http://localhost:${port}`);
      if (url.pathname === "/figma-connect") {
        const code = url.searchParams.get("code");
        const state = url.searchParams.get("state");
        const clientId = "nrGCqw6TLl95QJhVDi097A";
        const clientSecret = SECRET;

        const base64Encoded = Buffer.from(
          `${clientId}:${clientSecret}`,
        ).toString("base64");
        const authHeader = `Basic ${base64Encoded}`;

        if (code && randomState === state) {
          const figmaRes = await safeFetch(
            "https://api.figma.com/v1/oauth/token",
            {
              method: "POST",
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                Authorization: authHeader,
              },
              body: new URLSearchParams({
                redirect_uri: `http://localhost:${port}/figma-connect`,
                code,
                grant_type: "authorization_code",
              }).toString(),
            },
          );
          if (!figmaRes.ok) {
            reject(new Error("Failed to get figma access token"));
          }
          const data = await figmaRes.json();
          res.end(
            OAUTH_DONE_HTML.replace(
              "{{MESSAGE}}",
              "The CLI has authenticated correctly with Figma",
            ),
            () => {
              resolve({
                ...data,
                oauth: true,
              });
            },
          );
        }
      }
    },
    onListen: (port) => {
      const url = new URL("https://www.figma.com/oauth");
      url.searchParams.set("client_id", "nrGCqw6TLl95QJhVDi097A");
      url.searchParams.set(
        "redirect_uri",
        `http://localhost:${port}/figma-connect`,
      );
      url.searchParams.set(
        "scope",
        "files:read,file_dev_resources:read,file_variables:read",
      );
      url.searchParams.set("state", randomState);
      url.searchParams.set("response_type", "code");
      return url.href;
    },
  });
}

export async function getBuilderCodegenUsage(
  builderPublicKey: string,
  builderPrivateKey: string,
): Promise<BuilderCodegenUsage> {
  const url = new URL("https://cdn.builder.io/api/v1/usage/codegen-usage");
  url.searchParams.set("apiKey", builderPublicKey);

  const res = await safeFetch(url, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${builderPrivateKey!}`,
    },
  });
  if (!res.ok) {
    throw new Error(
      "Failed to retrieve codegen usage, are you logged in to proper Builder space?",
    );
  }
  const results = await res.json();
  return results;
}

export async function getBuilderAuth(
  sys: DevToolsSys,
  preferSpaceId?: string,
): Promise<BuilderAuth> {
  const host = await sys.getDeviceId();
  return createAuthServer<BuilderAuth>({
    sys,
    name: "Builder.io",
    initialPort: DEFAULT_BUILDER_PORT,
    requestListener: async (port, req, res, resolve) => {
      const url = new URL(req.url || "", `http://localhost:${port}`);
      if (url.pathname === BUILDER_AUTH_RETURN_PATH) {
        res.end(
          OAUTH_DONE_HTML.replace(
            "{{MESSAGE}}",
            "The CLI has authenticated correctly with Builder.io",
          ),
          () => {
            resolve({
              privateKey: url.searchParams.get("p-key") || "",
              spaceId: url.searchParams.get("api-key") || "",
              spaceName: url.searchParams.get("org-name") || "",
              userId: url.searchParams.get("user-id") || "",
            });
          },
        );
      }
    },
    onListen: (port) => {
      const authUrl = new URL(`/cli-auth`, `https://builder.io`);
      authUrl.searchParams.set(`client_id`, "builder-dev-tools-cli");

      authUrl.searchParams.set(`host`, host);
      authUrl.searchParams.set(`response_type`, `code`);
      authUrl.searchParams.set(`cli`, `true`);
      if (preferSpaceId) {
        authUrl.searchParams.set("space_id", preferSpaceId);
      }

      const returnUrl = new URL(
        BUILDER_AUTH_RETURN_PATH,
        `http://localhost:${port}`,
      );
      returnUrl.searchParams.set(PLATFORM_QS, platform());
      returnUrl.searchParams.set(NODE_VERSION_QS, process.versions.node);

      const frameworks = sys.getFrameworks();
      if (frameworks.length > 0) {
        authUrl.searchParams.set(FRAMEWORK_QS, frameworks[0].name);
        returnUrl.searchParams.set(FRAMEWORK_QS, frameworks[0].name);
      }
      authUrl.searchParams.set(`redirect_url`, returnUrl.href);
      return authUrl.href;
    },
  });
}

function storeCredentials(sys: DevToolsSys, credentials: Credentials) {
  const root = sys.getAppRootDir();
  const dir = join(root, "node_modules", ".builder");
  const filepath = join(dir, "data.json");
  if (existsSync(filepath)) {
    try {
      const data = readFileSync(filepath, "utf8");
      credentials = Object.assign(JSON.parse(data).credentials, credentials);
    } catch (e) {
      sys.Sentry?.captureException(e);
    }
  } else {
    mkdirSync(dir, { recursive: true });
  }
  writeFileSync(filepath, JSON.stringify({ credentials }, null, 2));
}

function loadCredentials(sys: DevToolsSys): Credentials {
  const root = sys.getAppRootDir();
  const filepath = join(root, "node_modules", ".builder", "data.json");
  if (existsSync(filepath)) {
    try {
      const data = readFileSync(filepath, "utf8");
      return JSON.parse(data).credentials;
    } catch (e) {
      sys.Sentry?.captureException(e);
    }
  }
  return {};
}

interface AuthServerOptions<T> {
  sys: DevToolsSys;
  initialPort: number;
  name: string;
  requestListener: (
    port: number,
    req: IncomingMessage,
    res: ServerResponse,
    resolve: (value: T) => void,
    reject: (reason?: any) => void,
  ) => void;
  onListen: (port: number) => string;
}

async function createAuthServer<T>(opts: AuthServerOptions<T>) {
  const { default: open } = await import("open");
  let resolve: (value: T) => void;
  let reject: (reason?: any) => void;
  let s: ReturnType<typeof spinner> | undefined;
  const promise = new Promise<T>((re, rej) => {
    resolve = re;
    reject = rej;
  });

  let currentPort = opts.initialPort;
  let attempt = 0;

  // Create a single server instance
  const server = createServer((req, res) =>
    opts.requestListener(currentPort, req, res, resolve!, reject!),
  );

  const closeServer = () => {
    clearHooks();
    if (server) {
      if ("closeAllConnections" in server) {
        (server as any).closeAllConnections();
      }
      server.close((err) => {
        if (err) {
          opts.sys.Sentry?.captureException(err);
        }
      });
    }
  };

  process.on("uncaughtExceptionMonitor", closeServer);
  process.on("unhandledRejection", closeServer);
  process.on("SIGINT", closeServer);
  process.on("SIGTERM", closeServer);
  process.on("exit", closeServer);

  const clearHooks = () => {
    process.removeListener("uncaughtExceptionMonitor", closeServer);
    process.removeListener("unhandledRejection", closeServer);
    process.removeListener("SIGINT", closeServer);
    process.removeListener("SIGTERM", closeServer);
    process.removeListener("exit", closeServer);
  };

  const tryListen = () => {
    return new Promise<number>((resolvePort, rejectListen) => {
      // Handle the error event for the current port attempt
      const errorHandler = (err: NodeJS.ErrnoException) => {
        server.removeListener("error", errorHandler);

        if (err.code === "EADDRINUSE" && attempt < MAX_PORT_ATTEMPTS) {
          log.warning(
            `Port ${currentPort} is already in use, trying next port`,
          );
          // Port is in use, try next port
          currentPort++;
          attempt++;
          resolvePort(0); // Signal to try next port
        } else {
          rejectListen(err);
        }
      };

      // Listen for errors
      server.once("error", errorHandler);

      // Try to listen on the current port
      server.listen(currentPort, () => {
        server.removeListener("error", errorHandler);
        const addressInfo = server.address() as AddressInfo;
        resolvePort(addressInfo.port);
      });
    });
  };

  try {
    let port = 0;
    while (port === 0 && attempt < MAX_PORT_ATTEMPTS) {
      port = await tryListen();
    }

    if (port === 0) {
      throw new Error(
        `Failed to find an available port after ${MAX_PORT_ATTEMPTS} attempts`,
      );
    }

    const href = opts.onListen(port);
    log.warn(`Required authenticating with ${opts.name}\nOPEN: ${dim(href)}`);
    s = spinner();
    s.start(`Waiting for ${opts.name} authentication`);
    open(href, { wait: false });

    const result = await promise;
    if (s) {
      s.stop(`Authenticated with ${opts.name} successfully`);
    }
    return result;
  } catch (e) {
    if (s) {
      s.stop(`Failed to authenticate with ${opts.name}.`, -1);
      printHelp("auth");
    }
    opts.sys.Sentry?.captureException(e);
    closeServer();
    throw e;
  } finally {
    closeServer();
  }
}
