import type { DevToolsSys } from "../types";
import { runFigmaMigrate } from "./download";
import { runFigmaGenerate } from "./generate";
import { runFigmaPublish } from "./figma-publish";
import { getCredentials } from "./credentials";
import { printHelp } from "./help";
import { bold, bgCyan, bgGreen, white, dim } from "picocolors";
import { cancel, intro, outro } from "@clack/prompts";
import { confirm } from "@clack/prompts";
import { exit } from "./exit";
import { findPackageJson } from "../common/fs";
import { track } from "./track";
import type { CLIArgs } from ".";
import { pkgVersion } from "./version";
import { spawn } from "node:child_process";
import type { ChildProcess } from "node:child_process";
import type { SpawnOptions } from "node:child_process";

export const runFigmaCommand = async (
  sys: DevToolsSys,
  subCommand: string,
  args: CLIArgs,
) => {
  if (!args.skipInstallation && (await shouldInstallBuilder(sys))) {
    const shouldInstall =
      args.yes ||
      (await confirm({
        message:
          "Welcome to Builder.io! Would you like to set up your integration now?",
        active: "Yes, let's integrate!",
        inactive: "No, I'll do it later",
      }));
    if (shouldInstall) {
      const passed = await runCommand(
        "npm",
        ["init", "builder.io@latest", "--", "--skip-question"],
        {
          stdio: "inherit",
          cwd: sys.getAppRootDir(),
        },
      ).command;
      if (!passed) {
        cancel(
          `Auto-install failed. You can install it manually by running ${bold(
            "npm init builder.io@latest",
          )}`,
        );
        await exit(1);
      }
    } else {
      cancel(
        `Ok, you can install it later. By running ${bold(
          "npm init builder.io@latest",
        )}`,
      );
    }
  }

  if (subCommand === "migrate") {
    await runFigmaMigrate(sys, args);
  } else if (subCommand === "publish" || subCommand === "p") {
    await runFigmaPublish(sys, args);
  } else if (
    subCommand === "add" ||
    subCommand === "generate" ||
    subCommand === "gen" ||
    subCommand === "g"
  ) {
    await runFigmaGenerate(sys, args);
  } else if (subCommand === "auth") {
    console.log("");
    intro(bgCyan(bold(white(" figma auth "))) + `  ` + dim(pkgVersion));
    const { builderPublicKey: spaceId } = await getCredentials(sys, args, {
      figmaAuth: true,
      builderPrivateKey: true,
      builderPublicKey: true,
      force: true,
    });
    await track("figma auth", {
      spaceId,
    });
    outro(bgGreen(bold(white(" Done! 🎉 "))));
  } else if (subCommand === "help") {
    printHelp(args._[2]);
  }
};

async function shouldInstallBuilder(sys: DevToolsSys): Promise<boolean> {
  const frameworks = sys.getFrameworks();
  const supportedFrameworks = ["next", "angular", "@angular/core"];
  if (
    !frameworks.some((framework) =>
      supportedFrameworks.includes(framework.name),
    )
  ) {
    return false;
  }
  try {
    const pkg = await findPackageJson(sys, sys.getRepoRootDir());
    if (!pkg) {
      return false;
    }
    const hasDevTools =
      !!pkg.devDependencies?.["@builder.io/dev-tools"] ||
      !!pkg.dependencies?.["@builder.io/dev-tools"];
    if (hasDevTools) {
      return false;
    }
  } catch (error) {
    return false;
  }
  return true;
}

function runCommand(cmd: string, args: string[], options?: SpawnOptions) {
  let child: ChildProcess;

  const command = new Promise<boolean>((resolve) => {
    try {
      child = spawn(cmd, args, {
        stdio: "inherit",
        ...options,
      });

      child.on("error", (e) => {
        resolve(false);
      });

      child.on("close", (code) => {
        if (code === 0) {
          resolve(true);
        } else {
          resolve(false);
        }
      });
    } catch (e: any) {
      resolve(false);
    }
  });

  const abort = async () => {
    if (child) {
      child.kill("SIGINT");
    }
  };

  return { abort, command };
}
