import type ts from "typescript";
import type { DevToolsSys } from "../../types";

export function removeUnusedImports(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
) {
  let statements = [...sourceFile.statements];
  let madeUpdates = false;

  for (let i = sourceFile.statements.length - 1; i >= 0; i--) {
    const importDec = sourceFile.statements[i];

    if (sys.ts.isImportDeclaration(importDec) && importDec.importClause) {
      let removeImportDeclaration = false;

      const namedBindings = importDec.importClause.namedBindings;
      if (
        namedBindings &&
        sys.ts.isNamedImports(namedBindings) &&
        namedBindings.elements
      ) {
        const namedBindingsElements = [...namedBindings.elements];

        for (let j = namedBindingsElements.length - 1; j >= 0; j--) {
          const namedImportElement = namedBindingsElements[j];
          if (sys.ts.isImportSpecifier(namedImportElement)) {
            const importName = namedImportElement.name;
            if (importName && sys.ts.isIdentifier(importName)) {
              const namedImportFn = importName.text;
              if (!isIdentifierFound(sys, sourceFile, namedImportFn)) {
                namedBindingsElements.splice(j, 1);
              }
            }
          }
        }

        if (namedBindingsElements.length === 0) {
          removeImportDeclaration = true;
        } else if (
          namedBindings.elements.length !== namedBindingsElements.length
        ) {
          const updatedImport = sys.ts.factory.updateImportClause(
            importDec.importClause,
            false,
            importDec.importClause.name,
            sys.ts.factory.createNamedImports(namedBindingsElements),
          );
          const s = sys.ts.factory.updateImportDeclaration(
            importDec,
            undefined,
            updatedImport,
            importDec.moduleSpecifier,
            undefined,
          );
          statements[i] = s;
          madeUpdates = true;
        }
      } else if (
        importDec.importClause.name &&
        sys.ts.isIdentifier(importDec.importClause.name)
      ) {
        const importName = importDec.importClause.name.text;
        if (!isIdentifierFound(sys, sourceFile, importName)) {
          removeImportDeclaration = true;
        }
      }

      if (removeImportDeclaration) {
        statements.splice(i, 1);
        madeUpdates = true;
      }
    }
  }

  if (madeUpdates) {
    return sys.ts.factory.updateSourceFile(sourceFile, statements);
  }
  return sourceFile;
}

function isIdentifierFound(
  sys: DevToolsSys,
  node: ts.SourceFile,
  identifier: string,
) {
  let foundIdentifier = false;

  function visit(childNode: ts.Node) {
    if (sys.ts.isImportDeclaration(childNode)) {
      return;
    }
    if (sys.ts.isIdentifier(childNode) && childNode.text === identifier) {
      foundIdentifier = true;
      return;
    }
    sys.ts.forEachChild(childNode, visit);
  }

  sys.ts.forEachChild(node, visit);

  return foundIdentifier;
}
