import type { ModifiedFile } from "../../../types";
import type { QwikDevToolsSys } from ".";

export async function qwikEnsureBuilderSetup(sys: QwikDevToolsSys) {
  return qwikEnsureBuilderRegistry(sys);
}

export async function qwikEnsureBuilderRegistry(sys: QwikDevToolsSys) {
  const modifiedFiles: ModifiedFile[] = [];

  if (!(await sys.exists(sys.registryPath))) {
    await sys.writeFile(sys.registryPath, DEFAULT_REGISTRY);
    modifiedFiles.push({
      filePath: sys.registryPath,
      modifiedType: "create",
    });
  }

  return modifiedFiles;
}

const DEFAULT_REGISTRY = `import type { RegisteredComponent } from "@builder.io/sdk-qwik";

export const CUSTOM_COMPONENTS: RegisteredComponent[] = [
];
`;
