import type ts from "typescript";
import type {
  ComponentInfo,
  ComponentRegistry,
  SetComponentInfoOptions,
  SetComponentInputOptions,
} from "../../../types";
import type { QwikDevToolsSys } from ".";
import type { QwikComponentSource } from "./qwik-components";
import { ensureComments } from "../../../common/ast/ensure-comments";
import { ensureImport } from "../../../common/ast/ensure-imports";
import { ensureArrayStatement } from "../../../common/ast/ensure-array-statement";
import { findArrayStatementInitializer } from "../../../common/ast/utils";
import { getDisplayFilePath, getImportPath } from "../../../common/utils";
import { getQwikComponentsFromDir } from "./qwik-components";
import {
  mergeComponents,
  updateRegistry,
} from "../../../common/ast/component-registry";
import {
  normalizeImports,
  normalizeLineBreaks,
  normalizeRegisteredArrayElements,
} from "../../../common/ast/normalize-statements";
import { parseQwikComponentSourceInputs } from "./qwik-component-source-inputs";
import { parseQwikRegisteredComponent } from "./qwik-registry-parse";
import { parseQwikRegistryFromFile } from "./qwik-registry-parse";
import { removeUnusedImports } from "../../../common/ast/remove-unused-imports";
import { sourceToCode } from "../../../common/ast/transform";
import {
  mergeComponentInputs,
  updateComponentObjLiteralInfoProperties,
} from "../../../common/ast/component-info";
import { createQwikTsProgram } from "./qwik-ts-program";
import { getAppDependencies } from "../../../common/ast/app-dependencies";
import { getQwikApiKey } from "./qwik-api-key";

export async function qwikComponentRegistry(
  sys: QwikDevToolsSys,
  cmpId: string | string[] | null,
  registerCmp: boolean,
  unregisterCmp: boolean,
  setCmpInfo: SetComponentInfoOptions | null,
  setCmpInput: SetComponentInputOptions | null,
  writeRegistry: boolean,
  readAllInputTypes: boolean,
) {
  const tsProgram = await createQwikTsProgram(sys);

  const [srcCmps, parsedRegistry, qwikApiKey] = await Promise.all([
    getQwikComponentsFromDir(sys, tsProgram),
    parseQwikRegistryFromFile(sys),
    getQwikApiKey(sys),
  ]);

  const sourceComponents = srcCmps.map((c) => c.cmpInfo);
  const registeredComponents = parsedRegistry.components;

  const cmpRegistry: ComponentRegistry = {
    components: mergeComponents(sourceComponents, registeredComponents),
    registryPath: sys.registryPath,
    registryDisplayPath: getDisplayFilePath(sys, sys.registryPath),
    frameworks: sys.getFrameworks(),
    dependencies: getAppDependencies(sourceComponents),
    publicApiKey: qwikApiKey.envValue ?? undefined,
    devToolsVersion: sys.version,
  };

  if (readAllInputTypes) {
    // TODO!!!
    return cmpRegistry;
  }

  if (cmpId != null) {
    const srcCmp = srcCmps.find((c) => c.cmpInfo.id === cmpId);
    if (!srcCmp) {
      throw new Error(`Component ${cmpId} not found`);
    }

    let sourceFile = parsedRegistry.sourceFile;

    const cmpInfo = cmpRegistry.components.find((c) => c.id === cmpId)!;
    const sourceInputs = await parseQwikComponentSourceInputs(sys, srcCmp);
    console.log("sourceInputs", sourceInputs);
    mergeComponentInputs(cmpInfo, sourceInputs);

    // TODO!!
    // allow for multiple cmp ids
    cmpId = Array.isArray(cmpId) ? cmpId[0] : cmpId;

    const update = updateRegistry(
      cmpId,
      sourceComponents,
      registeredComponents,
      registerCmp,
      unregisterCmp,
      setCmpInfo,
      setCmpInput,
    );

    const {
      elements,
      elmIndex,
      statementIndex,
      statement,
      variableDeclaration,
    } = await getQwikRegisteredComponentSource(sys, sourceFile, srcCmp);

    let madeChanges = false;

    if (update.addCmpToRegistry) {
      // not already registered, append it
      elements.push(
        createQwikRegisteredComponent(sys, update.addCmpToRegistry),
      );
      madeChanges = true;
    } else if (update.removeCmpFromRegistry) {
      // already registered, remove it
      elements.splice(elmIndex, 1);
      madeChanges = true;
    } else if (update.updateRegisteredCmp) {
      // already registered, update component info
      elements[elmIndex] = updateComponentObjLiteralInfoProperties(
        sys,
        elements[elmIndex],
        update.updateRegisteredCmp,
      );
      madeChanges = true;
    }

    if (madeChanges && writeRegistry) {
      const statements = [...sourceFile.statements];
      statements[statementIndex] = sys.ts.factory.updateVariableStatement(
        statement,
        statement.modifiers,
        sys.ts.factory.updateVariableDeclarationList(
          statement.declarationList,
          [
            sys.ts.factory.updateVariableDeclaration(
              variableDeclaration,
              variableDeclaration.name,
              variableDeclaration.exclamationToken,
              variableDeclaration.type,
              sys.ts.factory.createArrayLiteralExpression(
                normalizeRegisteredArrayElements(sys, elements),
              ),
            ),
          ],
        ),
      );
      sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

      if (cmpInfo.exportType === "named") {
        sourceFile = ensureImport(sys, sourceFile, {
          namedImports: [cmpInfo.importName],
          importPath: getImportPath(sys, sys.registryPath, cmpInfo.filePath),
        });
      } else {
        sourceFile = ensureImport(sys, sourceFile, {
          defaultImport: cmpInfo.importName,
          importPath: getImportPath(sys, sys.registryPath, cmpInfo.filePath),
        });
      }

      sourceFile = ensureImport(sys, sourceFile, {
        namedImports: ["RegisteredComponent"],
        importPath: "@builder.io/sdk-qwik",
        isTypeOnly: true,
      });

      sourceFile = ensureArrayStatement(sys, sourceFile, {
        variableName: "CUSTOM_COMPONENTS",
        arrayType: "RegisteredComponent",
      });

      sourceFile = removeUnusedImports(sys, sourceFile);
      sourceFile = normalizeImports(sys, sourceFile);

      let code = sourceToCode(sys, sourceFile);
      code = normalizeLineBreaks(code);
      code = ensureComments(code, COMMENTS, "CUSTOM_COMPONENTS");
      code = await sys.formatCode(sys.registryPath, code);
      await sys.writeFile(sys.registryPath, code);
    }

    cmpRegistry.components = update.components;
  }

  return cmpRegistry;
}

function createQwikRegisteredComponent(
  sys: QwikDevToolsSys,
  cmpInfo: ComponentInfo,
) {
  return updateComponentObjLiteralInfoProperties(
    sys,
    sys.ts.factory.createObjectLiteralExpression(
      [
        sys.ts.factory.createPropertyAssignment(
          "component",
          sys.ts.factory.createIdentifier(cmpInfo.importName),
        ),
      ],
      true,
    ),
    cmpInfo,
  );
}

async function getQwikRegisteredComponentSource(
  sys: QwikDevToolsSys,
  sourceFile: ts.SourceFile,
  srcCmp: QwikComponentSource,
) {
  const s = findArrayStatementInitializer(sys, sourceFile, "CUSTOM_COMPONENTS");
  if (!s) {
    throw new Error(
      `Exported name of the Builder registry must be "CUSTOM_COMPONENTS". Please update your Builder registry file: ${sys.registryPath}`,
    );
  }

  const elements = s.arrayLiteralExp.elements.filter((ex) => {
    return sys.ts.isObjectLiteralExpression(ex);
  }) as ts.ObjectLiteralExpression[];

  let elmIndex = -1;
  for (let i = 0; i < elements.length; i++) {
    const cmp = await parseQwikRegisteredComponent(
      sys,
      sourceFile,
      elements[i],
    );
    if (cmp?.id === srcCmp.cmpInfo.id) {
      elmIndex = i;
      break;
    }
  }

  return {
    ...s,
    elements,
    elmIndex,
  };
}

const COMMENTS = [
  `This array is used to integrate custom components within Builder.`,
  `https://www.builder.io/c/docs/custom-components-intro`,
  ``,
  `These components will be found the "Custom Components"`,
  `section of Builder's visual editor.`,
  `You can also turn on "components only mode" to limit`,
  `editing to only these components.`,
  `https://www.builder.io/c/docs/guides/components-only-mode`,
];
