import {
  type DevToolsSys,
  type SPACE_KIND_VALUES,
  SPACE_KIND,
} from "../../types";
import { requestJSON } from "../node-request";

export async function hasBuilderContentViaQueryAPI(opts: {
  publicApiKey: string;
  privateAuthKey: string;
  model: string;
  pageUrl: string;
}) {
  const url = new URL(
    `https://cdn.builder.io/api/v3/query/${opts.publicApiKey}/${opts.model}`,
  );
  url.searchParams.set(`omit`, `meta.componentsUsed`);
  url.searchParams.set(`apiKey`, opts.publicApiKey);
  url.searchParams.set(`userAttributes.urlPath`, opts.pageUrl);
  url.searchParams.set(`options.page.model`, `"${opts.model}"`);
  // This is important, without which the CDN level caching kicks in and causes all
  // kinds of pain and annoyance
  url.searchParams.set(`cachebust`, `true`);

  const data: any = await requestJSON({
    url: url,
    method: "GET",
  });

  return data[opts.model];
}

export async function hasBuilderContentUrl(
  sys: DevToolsSys,
  opts: {
    publicApiKey: string;
    privateAuthKey: string;
    model: string;
    pageUrl: string;
    kind: SPACE_KIND_VALUES;
  },
) {
  // For VCP, we don't want to create content page in builder
  if (opts.kind === SPACE_KIND.VCP) {
    return true;
  }
  // https://www.builder.io/c/docs/admin-graphql-api
  const adminUrl = new URL(`https://cdn.builder.io/api/v2/admin`);

  const body = {
    query: `query {
      models {
        id
        name
      }
    }`,
  };

  sys.debug(`Get model id for model name "${opts.model}"`);

  const graphQLRsp: GraphQLResponse = await requestJSON({
    url: adminUrl,
    method: "POST",
    headers: {
      Authorization: `Bearer ${opts.privateAuthKey}`,
      "Content-Type": "application/json",
      Accept: "application/json",
    },
    body: JSON.stringify(body),
  });

  const model = graphQLRsp.data.models.find((m) => m.name === opts.model);
  if (!model) {
    sys.debug(`No model found for model name "${opts.model}"`);
    return false;
  }

  sys.debug(`Data query, modelId: "${model.id}", urlPath: "${opts.pageUrl}"`);

  const dUrl = new URL(`https://cdn.builder.io/api/v3/data`);
  dUrl.searchParams.set(`limit`, `1`);
  dUrl.searchParams.set(`cachebust`, `true`);
  dUrl.searchParams.set(`collection`, `content`);
  dUrl.searchParams.set(`query.published.$ne`, `archived`);
  dUrl.searchParams.set(`query.query.$elemMatch.$and[0].property`, `urlPath`);
  dUrl.searchParams.set(`query.query.$elemMatch.$and[0].operator`, `is`);
  dUrl.searchParams.set(`query.query.$elemMatch.$and[0].value`, opts.pageUrl);
  dUrl.searchParams.set(`query.modelId`, model.id);
  dUrl.searchParams.set(`apiKey`, opts.publicApiKey);

  const data: DataResponse = await requestJSON({
    url: dUrl,
    method: "GET",
  });

  sys.debug(`Data results (${data.results.length}): ${dUrl.href}`);

  return data.results.length > 0;
}

export async function getBuilderContent(
  sys: DevToolsSys,
  opts: {
    publicApiKey: string;
    model: string;
    pageUrl?: string;
    id?: string;
  },
) {
  const url = new URL(`https://cdn.builder.io/api/v3/content/${opts.model}`);
  url.searchParams.set(`apiKey`, opts.publicApiKey);
  url.searchParams.set(`cachebust`, `true`);
  url.searchParams.set(`cb`, Date.now().toString());

  if (opts.pageUrl) {
    url.searchParams.set(`url`, opts.pageUrl);
    sys.debug(
      `get model: "${opts.model}", pageUrl: "${opts.pageUrl}":`,
      url.href,
    );
  }
  if (opts.id) {
    url.searchParams.set(`query.id`, opts.id);
    sys.debug(`get model: "${opts.model}", id: "${opts.id}":`, url.href);
  }

  const data: { results: any[] } = await requestJSON({
    url,
    method: "GET",
  });
  sys.debug(`get content results: ${data.results.length}`);
  return data;
}

export async function writeBuilderContent(
  sys: DevToolsSys,
  opts: {
    privateAuthKey: string;
    body: string;
    model: string;
  },
) {
  const url = new URL(`https://cdn.builder.io/api/v1/write/${opts.model}`);

  sys.debug(`writing "${opts.model}" content:`, url.href);

  await requestJSON({
    url,
    method: "POST",
    headers: {
      Authorization: `Bearer ${opts.privateAuthKey}`,
    },
    body: opts.body,
  });
}

interface GraphQLResponse {
  data: {
    models: { id: string; name: string }[];
  };
}

interface DataResponse {
  results: any[];
}
