import type ts from "typescript";
import type {
  Component as BuilderComponent,
  Input as BuilderInput,
} from "@builder.io/sdk";
import type { ComponentInfo, ComponentInput, DevToolsSys } from "../../types";
import {
  expressionToValue,
  getTextOfPropertyName,
  setObjectExpressionProperty,
  valueToExpression,
} from "./convert-values";
import { normalizeBuilderInputType } from "./component-input-types";
import { isBoolean, isNumber, isString } from "../utils";

export function parseRegisteredComponentInfo(
  sys: DevToolsSys,
  cmpInfo: ComponentInfo,
  cmpObjLit: ts.ObjectLiteralExpression,
) {
  for (const p of cmpObjLit.properties) {
    if (!sys.ts.isPropertyAssignment(p)) {
      continue;
    }
    const propName = getTextOfPropertyName(sys, p);

    if (propName === "name" && sys.ts.isStringLiteral(p.initializer)) {
      cmpInfo.name = p.initializer.text;
      continue;
    }

    if (propName === "description" && sys.ts.isStringLiteral(p.initializer)) {
      cmpInfo.description = p.initializer.text;
      continue;
    }

    if (propName === "image" && sys.ts.isStringLiteral(p.initializer)) {
      cmpInfo.image = p.initializer.text;
      continue;
    }

    if (
      propName === "inputs" &&
      sys.ts.isArrayLiteralExpression(p.initializer)
    ) {
      const registeredInputs = parseRegisteredComponentInputs(
        sys,
        p.initializer,
      );
      mergeComponentInputs(cmpInfo, registeredInputs);
      continue;
    }

    if (
      propName === "meta" &&
      sys.ts.isObjectLiteralExpression(p.initializer)
    ) {
      const meta = expressionToValue(sys, p.initializer);
      if (meta && typeof meta === "object") {
        cmpInfo.meta = meta;
      }
      continue;
    }
  }

  return cmpInfo;
}

function parseRegisteredComponentInputs(
  sys: DevToolsSys,
  arr: ts.ArrayLiteralExpression,
) {
  const inputs: ComponentInput[] = [];
  for (const elm of arr.elements) {
    if (sys.ts.isObjectLiteralExpression(elm)) {
      inputs.push(parseRegisteredComponentInput(sys, elm));
    }
  }
  return inputs;
}

function parseRegisteredComponentInput(
  sys: DevToolsSys,
  obj: ts.ObjectLiteralExpression,
) {
  const builderInput = expressionToValue(sys, obj) as BuilderInput;
  const cmpInput = normalizeBuilderInput(builderInput);
  cmpInput.isRegistered = true;
  return cmpInput;
}

export function updateComponentObjLiteralInfoProperties(
  sys: DevToolsSys,
  cmpObjExp: ts.ObjectLiteralExpression,
  cmpInfo: ComponentInfo,
) {
  const builderCmp = mapDevToolsCmpToBuilderCmp(cmpInfo);

  cmpObjExp = setObjectExpressionProperty(
    sys,
    cmpObjExp,
    "name",
    builderCmp.name,
    false,
  );

  cmpObjExp = setObjectExpressionProperty(
    sys,
    cmpObjExp,
    "description",
    builderCmp.description,
  );

  cmpObjExp = setObjectExpressionProperty(
    sys,
    cmpObjExp,
    "image",
    builderCmp.image,
  );

  cmpObjExp = setObjectExpressionProperty(
    sys,
    cmpObjExp,
    "meta",
    //@ts-expect-error waiting on updated @builder.io/sdk release
    builderCmp.meta,
  );

  cmpObjExp = updateComponentObjLiteralInputsProperty(
    sys,
    cmpObjExp,
    builderCmp,
  );

  return sortComponentInfoProperties(sys, cmpObjExp);
}

function updateComponentObjLiteralInputsProperty(
  sys: DevToolsSys,
  cmpObjExp: ts.ObjectLiteralExpression,
  builderCmp: BuilderComponent,
) {
  let arrExp: ts.ArrayLiteralExpression | null = null;

  const props = cmpObjExp.properties.filter((p) => {
    return sys.ts.isPropertyAssignment(p) && sys.ts.isIdentifier(p.name);
  }) as ts.PropertyAssignment[];

  const inputsIndex = props.findIndex((p) => {
    return sys.ts.isIdentifier(p.name) && p.name.text === "inputs";
  });

  const inputs = [...(builderCmp.inputs ?? [])];

  if (inputsIndex > -1) {
    // already have an inputs property
    if (inputs.length === 0) {
      // no registered inputs, so remove the inputs property
      return setObjectExpressionProperty(sys, cmpObjExp, "inputs", null);
    }

    const inputsObj = props[inputsIndex];
    if (
      sys.ts.isPropertyAssignment(inputsObj) &&
      sys.ts.isArrayLiteralExpression(inputsObj.initializer)
    ) {
      arrExp = inputsObj.initializer;
    }
  }

  if (inputs.length === 0) {
    return cmpObjExp;
  }

  arrExp = updateComponentInputsArrayLiteralExp(
    sys,
    arrExp || sys.ts.factory.createArrayLiteralExpression(),
    inputs as ComponentInput[],
  );

  if (inputsIndex > -1) {
    props[inputsIndex] = sys.ts.factory.updatePropertyAssignment(
      props[inputsIndex],
      props[inputsIndex].name,
      arrExp,
    );
  } else {
    props.push(sys.ts.factory.createPropertyAssignment("inputs", arrExp));
  }

  return sys.ts.factory.updateObjectLiteralExpression(cmpObjExp, props);
}

function mapDevToolsCmpToBuilderCmp(cmpInfo: ComponentInfo) {
  // use the type from @builder.io/sdk to ensure we're correct
  const builderCmp: BuilderComponent = {
    name: cmpInfo.name,
    description: cmpInfo.description,
    image: cmpInfo.image,
    inputs: (cmpInfo.inputs || [])
      .filter((i) => i.isRegistered)
      .map(mapDevToolsInputToBuilderInput),
    ...(cmpInfo.meta ? { meta: { ...cmpInfo.meta } } : {}),
  };
  return builderCmp;
}

function mapDevToolsInputToBuilderInput(cmpInput: ComponentInput) {
  // use the type from @builder.io/sdk to ensure we're correct
  const builderInput: BuilderInput = {
    name: cmpInput.name,
    type: cmpInput.type,
  };

  if (
    isString(cmpInput.defaultValue) ||
    isNumber(cmpInput.defaultValue) ||
    isBoolean(cmpInput.defaultValue)
  ) {
    builderInput.defaultValue = cmpInput.defaultValue;
  }
  if (isString(cmpInput.friendlyName)) {
    builderInput.friendlyName = cmpInput.friendlyName;
  }
  if (isString(cmpInput.helperText)) {
    builderInput.helperText = cmpInput.helperText;
  }
  if (cmpInput.required === true) {
    builderInput.required = true;
  }
  if (Array.isArray(cmpInput.enum)) {
    builderInput.enum = cmpInput.enum;
  }
  if (cmpInput.hideFromUI === true) {
    builderInput.hideFromUI = true;
  }
  if (
    typeof cmpInput.meta === "object" &&
    cmpInput.meta != null &&
    Object.keys(cmpInput.meta).length > 0
  ) {
    builderInput.meta = cmpInput.meta;
  }

  return builderInput;
}

export function updateComponentInputsArrayLiteralExp(
  sys: DevToolsSys,
  arrExp: ts.ArrayLiteralExpression,
  inputs: ComponentInput[],
) {
  let objLiteralExps = arrExp.elements.filter((elm) => {
    if (!sys.ts.isObjectLiteralExpression(elm)) {
      return false;
    }

    const inputName = elm.properties.find((p) => {
      return getTextOfPropertyName(sys, p) === "name";
    }) as ts.PropertyAssignment | undefined;

    if (!inputName) {
      return false;
    }

    const inputValue = inputName.initializer;
    if (!sys.ts.isStringLiteral(inputValue)) {
      return false;
    }

    return inputs.some((i) => i.name === inputValue.text);
  }) as ts.ObjectLiteralExpression[];

  for (const input of inputs) {
    objLiteralExps = updateComponentInputObjLiteralExp(
      sys,
      objLiteralExps,
      input,
    );
  }

  objLiteralExps.sort((a, b) => {
    const aProp = a.properties.find((p) => {
      return getTextOfPropertyName(sys, p) === "name";
    }) as ts.PropertyAssignment | undefined;
    const bProp = b.properties.find((p) => {
      return getTextOfPropertyName(sys, p) === "name";
    }) as ts.PropertyAssignment | undefined;

    const aInit = aProp?.initializer;
    const bInit = bProp?.initializer;

    const aName = aInit && sys.ts.isStringLiteral(aInit) ? aInit.text : "zz";
    const bName = bInit && sys.ts.isStringLiteral(bInit) ? bInit.text : "zz";

    return aName.toLowerCase().localeCompare(bName.toLowerCase());
  });

  return sys.ts.factory.updateArrayLiteralExpression(arrExp, objLiteralExps);
}

function updateComponentInputObjLiteralExp(
  sys: DevToolsSys,
  objLiteralExps: ts.ObjectLiteralExpression[],
  input: ComponentInput,
) {
  const index = objLiteralExps.findIndex((obj) => {
    return obj.properties.some((prop) => {
      const propName = getTextOfPropertyName(sys, prop);
      return (
        sys.ts.isPropertyAssignment(prop) &&
        propName === "name" &&
        sys.ts.isStringLiteral(prop.initializer) &&
        prop.initializer.text === input.name
      );
    });
  });

  if (index > -1) {
    objLiteralExps[index] = updateComponentInputValue(
      sys,
      objLiteralExps[index],
      input,
    );
  } else {
    const newInputObj = updateComponentInputValue(
      sys,
      sys.ts.factory.createObjectLiteralExpression(
        [
          sys.ts.factory.createPropertyAssignment(
            "name",
            sys.ts.factory.createStringLiteral(input.name),
          ),
        ],
        true,
      ),
      input,
    );
    objLiteralExps.push(newInputObj);
  }

  return objLiteralExps;
}

function updateComponentInputValue(
  sys: DevToolsSys,
  objLiteralExp: ts.ObjectLiteralExpression,
  input: ComponentInput,
) {
  let propAssignments = objLiteralExp.properties.filter((p) =>
    sys.ts.isPropertyAssignment(p),
  ) as ts.PropertyAssignment[];

  const inputKeys = (Object.keys(input) as (keyof ComponentInput)[]).filter(
    (k) => k !== "isRegistered",
  );

  for (const inputKey of inputKeys) {
    propAssignments = updateComponentInputPropAssignment(
      sys,
      propAssignments,
      input,
      inputKey,
    );
  }

  propAssignments = sortComponentInputProperties(
    sys,
    propAssignments.filter((p) => {
      const propName = getTextOfPropertyName(sys, p) as any;
      return inputKeys.includes(propName);
    }),
  );

  return sys.ts.factory.updateObjectLiteralExpression(
    objLiteralExp,
    propAssignments,
  );
}

function updateComponentInputPropAssignment(
  sys: DevToolsSys,
  propAssignments: ts.PropertyAssignment[],
  input: ComponentInput,
  inputKey: keyof ComponentInput,
) {
  const propIndex = propAssignments.findIndex(
    (p) => sys.ts.isIdentifier(p.name) && p.name.text === inputKey,
  );

  if (propIndex > -1) {
    propAssignments[propIndex] = sys.ts.factory.updatePropertyAssignment(
      propAssignments[propIndex],
      propAssignments[propIndex].name,
      valueToExpression(sys, input[inputKey]),
    );
  } else {
    propAssignments.push(
      sys.ts.factory.createPropertyAssignment(
        inputKey as string,
        valueToExpression(sys, input[inputKey]),
      ),
    );
  }

  return propAssignments;
}

export function mergeComponentInputs(
  cmp: ComponentInfo,
  inputs: ComponentInput[],
) {
  for (const input of inputs) {
    const i = cmp.inputs.findIndex((i) => i.name === input.name);
    if (i > -1) {
      if (input.isRegistered || !cmp.inputs[i].isRegistered) {
        cmp.inputs[i] = input;
      }
    } else {
      cmp.inputs.push(input);
    }
  }
  cmp.acceptsChildren = componentAcceptsChildren(cmp);
}

function normalizeBuilderInput(builderInput: BuilderInput) {
  const normalizedBuilderType = normalizeBuilderInputType(builderInput.type);

  const cmpInput: ComponentInput = {
    name: builderInput.name.trim(),
    type: normalizedBuilderType.builderType,
  };

  if (
    isString(builderInput.friendlyName) &&
    builderInput.friendlyName.trim() !== builderInput.name.trim()
  ) {
    cmpInput.friendlyName = builderInput.friendlyName;
  }

  if (
    isString(builderInput.defaultValue) ||
    isNumber(builderInput.defaultValue) ||
    isBoolean(builderInput.defaultValue)
  ) {
    cmpInput.defaultValue = builderInput.defaultValue;
  }

  if (isString(builderInput.helperText)) {
    cmpInput.helperText = builderInput.helperText;
  }

  if (isBoolean(builderInput.required)) {
    cmpInput.required = builderInput.required;
  }

  if (
    typeof builderInput.meta === "object" &&
    builderInput.meta != null &&
    Object.keys(builderInput.meta).length > 0
  ) {
    cmpInput.meta = builderInput.meta;
  }

  if (Array.isArray(builderInput.enum) && builderInput.enum.length > 0) {
    cmpInput.enum = builderInput.enum;
  }

  if (builderInput.hideFromUI === true) {
    cmpInput.hideFromUI = true;
  }

  return normalizeComponentInput(cmpInput);
}

export function componentAcceptsChildren(cmp: ComponentInfo) {
  // if the component has a children prop, set acceptsChildren to true
  const hasChildrenProp = cmp.inputs.some((i) => i.name === "children");
  const hasJsxProp = cmp.inputs.some((i) => i.meta?.ts === "ReactNode");
  if (hasChildrenProp || hasJsxProp || cmp.acceptsChildren === true) {
    // set acceptsChildren to true, which will add the withChildren() wrapper
    return true;
  }
  return false;
}

export function normalizeComponentInput(input: ComponentInput) {
  if (input.name === "children") {
    input.hideFromUI = true;
    delete input.required;
  }

  if (input.type === "object" && input.defaultValue == null) {
    input.hideFromUI = true;
  }

  if (input.type === "list" && input.defaultValue == null) {
    input.hideFromUI = true;
  }

  if (input.meta) {
    let tsType = input.meta.ts;
    if (typeof tsType === "string") {
      tsType = tsType.trim();
      if (tsType.endsWith("ReactNode")) {
        tsType = "ReactNode";
      }
      input.meta.ts = tsType;

      if (tsType === "ReactNode") {
        input.hideFromUI = true;
      }
    }
  }

  return input;
}

function sortComponentInfoProperties(
  sys: DevToolsSys,
  cmpObjExp: ts.ObjectLiteralExpression,
) {
  const props = [...cmpObjExp.properties].sort((a, b) => {
    const aName = getTextOfPropertyName(sys, a) || "zz";
    const bName = getTextOfPropertyName(sys, b) || "zz";

    if (aName === "inputs") return 1;

    const aIndex = INFO_PROP_SORT.indexOf(aName);
    const bIndex = INFO_PROP_SORT.indexOf(bName);
    if (aIndex > -1) {
      if (bIndex > -1 && aIndex > bIndex) {
        return 1;
      }
      return -1;
    }

    if (bIndex > -1) {
      if (aIndex > -1 && bIndex < aIndex) {
        return -1;
      }
      return 1;
    }

    return aName.localeCompare(bName);
  });

  return sys.ts.factory.updateObjectLiteralExpression(cmpObjExp, props);
}

const INFO_PROP_SORT = ["component", "name", "description"];

function sortComponentInputProperties(
  sys: DevToolsSys,
  propAssignments: ts.PropertyAssignment[],
) {
  const props = [...propAssignments].sort((a, b) => {
    const aName = getTextOfPropertyName(sys, a) || "zz";
    const bName = getTextOfPropertyName(sys, b) || "zz";

    const aIndex = INPUT_PROP_SORT.indexOf(aName);
    const bIndex = INPUT_PROP_SORT.indexOf(bName);
    if (aIndex > -1) {
      if (bIndex > -1 && aIndex > bIndex) {
        return 1;
      }
      return -1;
    }

    if (bIndex > -1) {
      if (aIndex > -1 && bIndex < aIndex) {
        return -1;
      }
      return 1;
    }

    return aName.localeCompare(bName);
  });

  return props;
}

const INPUT_PROP_SORT = ["name", "type"];

export function componentHasAcceptChildrenProp(
  sys: DevToolsSys,
  cmpObjLit: ts.ObjectLiteralExpression,
): boolean {
  for (const p of cmpObjLit.properties) {
    if (!sys.ts.isPropertyAssignment(p) || !sys.ts.isIdentifier(p.name)) {
      continue;
    }
    const propName = getTextOfPropertyName(sys, p);
    if (propName === "canHaveChildren") {
      const { initializer } = p;
      if (initializer.kind === sys.ts.SyntaxKind.TrueKeyword) {
        return true;
      }
      return false;
    }
  }
  return false;
}
