import type { DevToolsSys } from "../../types";
import { getBuilderContent, writeBuilderContent } from "./builder-api";
import {
  DEFAULT_MODEL_NAME,
  TEMPLATE_MODEL_NAME,
  TEMPLATE_PUBLIC_API_KEY,
} from "./content-ids";

export async function createBuilderPage(
  sys: DevToolsSys,
  opts: {
    templateContentId: string;
    publicApiKey: string;
    privateAuthKey: string;
    pathname: string;
    title: string;
  },
) {
  const data = await getBuilderContent(sys, {
    model: TEMPLATE_MODEL_NAME,
    publicApiKey: TEMPLATE_PUBLIC_API_KEY,
    id: opts.templateContentId,
  });

  if (
    !Array.isArray(data?.results) ||
    data.results.length === 0 ||
    !data.results[0].data?.blocks
  ) {
    throw new Error(
      `No Builder content found for id ${opts.templateContentId}`,
    );
  }

  const blocksString = JSON.stringify(data.results[0].data.blocks);

  const writeContentData = createContentData(
    opts.publicApiKey,
    opts.pathname,
    opts.title,
    blocksString,
  );

  await writeBuilderContent(sys, {
    privateAuthKey: opts.privateAuthKey,
    model: DEFAULT_MODEL_NAME,
    body: writeContentData,
  });
}

function createContentData(
  apiKey: string,
  pathname: string,
  title: string,
  blocksString: string,
) {
  return JSON.stringify({
    name: title,
    ownerId: apiKey,
    published: "published",
    query: [
      {
        property: "urlPath",
        value: [pathname],
        operator: "is",
      },
    ],
    data: {
      title: title,
      blocksString,
    },
  });
}
