import type { EnsureConfigResult } from "../../../types";
import type { DevToolsSys } from "../..";
import { ensureImport } from "../../../common/ast/ensure-imports";
import { codeToSource, sourceToCode } from "../../../common/ast/transform";

export async function angularEnsureConfigPlugin(
  sys: DevToolsSys,
  configFilePath: string,
  configContent: string,
): Promise<EnsureConfigResult> {
  sys.debug(`Angular config updates are currently in progress...`);

  try {
    await updatePackage(sys);
  } catch (e) {
    sys.Sentry?.captureException(
      new Error("Unable to update package.json.", { cause: e }),
    );
  }

  await angularEnsureMainFileUpdate(sys);

  return {
    content: configContent,
    fileName: sys.basename(configFilePath),
    filePath: configFilePath,
    hasContentUpdates: true,
  };
}

export async function angularEnsureMainFileUpdate(sys: DevToolsSys) {
  const rootDir = sys.getAppRootDir();
  const srcDir = sys.join(rootDir, "src");

  if (!(await sys.exists(srcDir))) {
    throw new Error(
      `Could not find src directory. Searched for src in "${sys.getAppRootDir()}".`,
    );
  }

  const mainFilePath = sys.join(srcDir, "main.ts");

  const mainFileCode = await sys.readFile(mainFilePath);

  if (!mainFileCode) {
    throw new Error(`Unable to read main.ts at ${mainFilePath}`);
  }

  const updatedCode = await angularEnsureMainFileCode(sys, mainFileCode);

  await sys.writeFile(mainFilePath, updatedCode);
}

export async function angularEnsureMainFileCode(
  sys: DevToolsSys,
  code: string,
) {
  let content = code;
  let sourceFile = codeToSource(sys, code);

  let builderDevToolsFunctionCalled = false;

  for (const s of sourceFile.statements) {
    if (
      sys.ts.isExpressionStatement(s) &&
      sys.ts.isCallExpression(s.expression) &&
      sys.ts.isPropertyAccessExpression(s.expression.expression) &&
      sys.ts.isCallExpression(s.expression.expression.expression) &&
      sys.ts.isIdentifier(s.expression.expression.expression.expression) &&
      s.expression.expression.expression.expression.text === "builderDevTools"
    ) {
      builderDevToolsFunctionCalled = true;
      break;
    }
  }

  sourceFile = ensureImport(sys, sourceFile, {
    namedImports: ["builderDevTools"],
    importPath: "@builder.io/dev-tools/angular",
  });

  const builderDevToolsCode = `\nbuilderDevTools().catch((err: Error) =>
    console.error("Error starting dev tools:", err)
  );`;

  content = sourceToCode(sys, sourceFile);

  if (!builderDevToolsFunctionCalled) {
    content += builderDevToolsCode;
  }

  return content;
}

export async function updatePackage(sys: DevToolsSys) {
  const rootDir = sys.getRepoRootDir();
  const pkgJsonPath = sys.join(rootDir, "package.json");
  const pkgJsonCode = await sys.readFile(pkgJsonPath);
  if (!pkgJsonCode) {
    throw new Error(`Unable to read package.json at ${pkgJsonPath}`);
  }
  const newPkgJsonCode = packageConcurrentlyScriptUpdate(sys, pkgJsonCode);
  if (!newPkgJsonCode) {
    return;
  }
  await sys.writeFile(pkgJsonPath, newPkgJsonCode);
}

export function packageConcurrentlyScriptUpdate(
  sys: DevToolsSys,
  code: string,
): string | undefined {
  const pkgJson = JSON.parse(code);
  pkgJson.scripts = pkgJson.scripts || {};
  if (pkgJson.scripts[DEV_TOOLS_SCRIPT_NAME]) {
    return;
  }
  const firstScript =
    pkgJson.scripts["start"] || pkgJson.scripts["dev"] || "ng serve";

  if (!firstScript.includes("concurrently")) {
    const repoRootDir = sys.getRepoRootDir();
    const appRootDir = sys.getAppRootDir();
    const args =
      repoRootDir === appRootDir
        ? ""
        : ` --cwd ${sys.relative(repoRootDir, appRootDir)}`;
    const newScript = `concurrently \"${firstScript}\" \"${DEV_TOOLS_COMMAND}${args}\"`;
    pkgJson.scripts["start"] = newScript;
    pkgJson.scripts[DEV_TOOLS_SCRIPT_NAME] = newScript;
  }

  return JSON.stringify(pkgJson, null, 2) + "\n";
}

export const DEV_TOOLS_SCRIPT_NAME = "builder-dev";

export const DEV_TOOLS_COMMAND = "npx builder-dev-tools";
