import type { EnvInfo } from "../../../types";
import { getDotEnvValue, setDotEnvVar } from "../../../common/dotenv";
import type { ReactDevToolsSys } from ".";

export async function getReactApiKey(sys: ReactDevToolsSys) {
  const dotEnvFileNames = [".env"];
  const publicApiKey =
    sys.configType === "vite"
      ? REACT_VITE_BUILDER_KEY_ENV
      : REACT_WEBPACK_BUILDER_KEY_ENV;
  const env = await getDotEnvValue(sys, dotEnvFileNames, publicApiKey);
  return env;
}

export async function setReactApiKey(
  sys: ReactDevToolsSys,
  publicApiKey: string,
) {
  const env = await getReactApiKey(sys);

  const modifiedType = await setDotEnvVar(
    sys,
    env.file,
    env.envKey,
    publicApiKey,
  );

  const updatedEnv: EnvInfo = {
    ...env,
    modifiedType,
  };
  return updatedEnv;
}

export const REACT_VITE_BUILDER_KEY_ENV = `VITE_PUBLIC_BUILDER_KEY`;

export const REACT_WEBPACK_BUILDER_KEY_ENV = `YOUR_PUBLIC_BUILDER_KEY`;
