import type { Choice } from "prompts";
import prompts from "prompts";
import { log } from "@clack/prompts";
import type { DevToolsSys } from "../types";
import { estimateCodeTokens } from "./estimate-code-tokens";
import Fuse from "fuse.js";
import { dim, reset } from "picocolors";

interface FileChoice extends Choice {
  title: string;
  description?: string;
  selected?: boolean;
}

interface MultiSelectState {
  value: FileChoice[];
  hint: string;
  suggestions: FileChoice[];
  prevValue?: string[]; // Track previous selection to detect changes
}

class TokenCounter {
  private tokenCache = new Map<string, number>();

  constructor(
    private sys: DevToolsSys,
    private appRootDir: string,
  ) {}

  get(file: string): number {
    if (!this.tokenCache.has(file)) {
      const content =
        this.sys.readFileSync(this.sys.join(this.appRootDir, file)) ?? "";
      this.tokenCache.set(file, estimateCodeTokens(content));
    }
    return this.tokenCache.get(file)!;
  }

  getForVisible(file: string, isVisible: boolean): number {
    if (!isVisible) return 0;
    return this.get(file);
  }

  calculateTotal(files: FileChoice[]): number {
    return files.reduce((sum, file) => sum + this.get(file.value), 0);
  }
}

export async function interactiveSelectFiles(
  sys: DevToolsSys,
  allFiles: string[],
  appRootDir: string,
  initialFiles: string[],
  selectedFilePaths: Map<string, number>,
  MAX_TOKENS: number,
): Promise<string[]> {
  const tokenCounter = new TokenCounter(sys, appRootDir);
  const initialSelectionSet = new Set(initialFiles);

  // Initialize with just the tokens for initial files
  for (const file of initialFiles) {
    tokenCounter.get(file);
  }

  // Create choices array with initially selected files marked
  const choices: FileChoice[] = allFiles
    .map((file) => {
      const isInitiallySelected = initialFiles.includes(file);

      return {
        title: file,
        value: file,
        selected: isInitiallySelected,
      };
    })
    .sort((a, b) => {
      const aImportance = selectedFilePaths.get(a.value) ?? 0;
      const bImportance = selectedFilePaths.get(b.value) ?? 0;
      return bImportance - aImportance;
    });

  const fuse = new Fuse(choices, {
    keys: ["title"],
  });
  const selection = await prompts({
    type: "autocompleteMultiselect",
    name: "files",
    message: " Add more relevant files (optional):",
    hint: `${initialFiles.length} files selected`,
    choices,
    instructions: false,
    onState: (state: MultiSelectState) => {
      // Detect newly selected files by comparing with previous state
      if (state.prevValue) {
        const prevSelected = new Set(state.prevValue);
        const newSelections = state.value.filter(
          (choice) => !prevSelected.has(choice.value) && choice.selected,
        );

        for (const choice of newSelections) {
          if (!initialSelectionSet.has(choice.value)) {
            // This is a manual selection, set highest importance
            selectedFilePaths.set(choice.value, 4);
          }
        }
      }
      state.prevValue = [
        ...state.value
          .filter((choice) => choice.selected)
          .map((choice) => choice.value),
      ];

      // Calculate total tokens for current selection
      const currentTokens = tokenCounter.calculateTotal(
        state.value.filter((choice) => choice.selected),
      );
      const selectedCount = state.value.filter(
        (choice) => choice.selected,
      ).length;

      if (currentTokens > MAX_TOKENS) {
        state.hint = `⚠️ Selection exceeds token limit! (${currentTokens}/${MAX_TOKENS} tokens)`;
      } else {
        state.hint = `${selectedCount} files selected (${currentTokens}/${MAX_TOKENS} tokens)`;
      }

      // Update suggestions descriptions and sorting
      if (state.suggestions) {
        state.suggestions.sort((a, b) => {
          // First sort by selection status
          if (a.selected !== b.selected) {
            return a.selected ? -1 : 1;
          }
          // Then by importance if both are selected
          if (a.selected && b.selected) {
            const aImportance = selectedFilePaths.get(a.value) ?? 0;
            const bImportance = selectedFilePaths.get(b.value) ?? 0;
            if (aImportance !== bImportance) {
              return bImportance - aImportance;
            }
          }
          // Finally by tokens, preferring smaller files
          const aTokens = tokenCounter.get(a.value);
          const bTokens = tokenCounter.get(b.value);
          return aTokens - bTokens;
        });
      }
    },
    validate: (selected: FileChoice[]) => {
      const currentTokens = tokenCounter.calculateTotal(
        selected.filter((choice) => choice.selected),
      );
      return (
        currentTokens <= MAX_TOKENS ||
        "Selection exceeds token limit. Please deselect some files."
      );
    },
    suggest: async (input: string, choices: FileChoice[]) => {
      if (!input) {
        const visibleCount = Math.min(10, choices.length);
        const visibleChoices = choices
          .filter((choice) => choice.selected)
          .concat(
            choices.filter((choice) => !choice.selected).slice(0, visibleCount),
          )
          .sort((a, b) => {
            const aImportance = selectedFilePaths.get(a.value) ?? 0;
            const bImportance = selectedFilePaths.get(b.value) ?? 0;
            return bImportance - aImportance;
          });
        return visibleChoices;
      }
      return fuse.search(input).map((result) => result.item);
    },
  });

  // Handle cancellation
  if (!selection.files || !Array.isArray(selection.files)) {
    return initialFiles;
  }

  // Update importance for any newly selected files in final selection
  for (const file of selection.files) {
    if (!initialSelectionSet.has(file) && !selectedFilePaths.has(file)) {
      selectedFilePaths.set(file, 4);
    }
  }

  return selection.files;
}

export async function selectMostRelevantFile(
  sys: DevToolsSys,
  allFiles: string[],
  appRootDir: string,
  selectedFilePaths: Map<string, number>,
  MAX_TOKENS: number,
  wasBad: boolean,
): Promise<string | null> {
  const tokenCounter = new TokenCounter(sys, appRootDir);

  // Create initial choices without token counts
  const createChoice = (file: string): FileChoice => {
    return {
      title: dim(file),
      value: file,
    };
  };

  if (allFiles.length < 4) {
    return null;
  }

  const fuse = new Fuse(allFiles.map(createChoice), {
    keys: ["value"],
  });

  log.message("");
  const message = wasBad
    ? " Select a file to refine the code"
    : " Optionally select a context file (most relevant)";

  const choices = allFiles.map(createChoice).sort((a, b) => {
    const aImportance = selectedFilePaths.get(a.value) ?? 0;
    const bImportance = selectedFilePaths.get(b.value) ?? 0;
    return bImportance - aImportance;
  });
  if (wasBad) {
    choices.push({
      title: "⏭️  Skip ",
      value: "___skip___",
    });
  } else {
    choices.unshift({
      title: "⏭️  Skip ",
      value: "___skip___",
    });
  }
  const selection = await prompts({
    type: "autocomplete",
    name: "file",
    message: reset(message),
    choices,
    suggest: async (input: string, choices: FileChoice[]) => {
      let filteredChoices: FileChoice[];

      if (!input) {
        // For initial display, sort by importance and take first 10
        filteredChoices = choices;
      } else {
        // Filter by input text
        filteredChoices = fuse.search(input).map((result) => result.item);
      }

      // Only calculate tokens for displayed choices
      const validChoices: FileChoice[] = [];

      for (const choice of filteredChoices) {
        const tokens = tokenCounter.get(choice.value);
        if (tokens <= MAX_TOKENS) {
          validChoices.push(choice);
        }
      }

      if (validChoices.length === 0) {
        // Add a message if no valid files found
        return [
          {
            title: "No valid matching files",
            value: "",
            disabled: true,
          },
        ];
      }

      return validChoices;
    },
    validate: (value: string) => {
      if (!value) return "Please select a file";
      const tokens = tokenCounter.get(value);
      if (tokens > MAX_TOKENS) {
        return `File exceeds token limit of ${MAX_TOKENS} (has ${tokens} tokens)`;
      }
      return true;
    },
  });

  // Handle cancellation
  if (!selection.file || selection.file === "___skip___") {
    return null;
  }

  // Set highest importance for the selected file if not already set
  selectedFilePaths.set(selection.file, 4);

  return selection.file;
}
