import type { AngularDevToolsSys } from "./index";
import type ts from "typescript";
import type {
  AppDependency,
  ComponentInfo,
  ComponentInput,
  DevToolsSys,
} from "../../../types";
import type { AngularComponentOptions } from "./types";
import { codeToSource } from "../../../common/ast/transform";
import {
  getComponentDisplayName,
  getComponentId,
  getDisplayFilePath,
  getComponentImportPath,
} from "../../../common/utils";
import { createTsProgram, getTypeChecker } from "../../../common/typescript";
import { parseComponentSourcePropsFromNode } from "../../../common/ast/component-input-types";
import { mergeComponentInputs } from "../../../common/ast/component-info";

export async function getAngularSourceComponentsFromDir(
  sys: AngularDevToolsSys,
  tsProgram: ts.Program,
) {
  const cmps: AngularComponentSource[] = [];
  const sourceFiles = tsProgram.getSourceFiles();
  const typeChecker = tsProgram.getTypeChecker();

  await Promise.all(
    sourceFiles.map(async (sourceFile) => {
      const fileName = sys.basename(sourceFile.fileName);

      if (shouldSkipFolder(sys, sourceFile.fileName)) {
        return;
      }

      if (
        fileName.startsWith(".") ||
        fileName === "node_modules" ||
        SKIP_APP_FILES.has(fileName)
      ) {
        return;
      }

      const filePath = sys.normalize(sourceFile.fileName);
      if (filePath === sys.registryPath) {
        return;
      }

      const fileCmps = await getAngularComponentsFromSourceFile(
        sys,
        typeChecker,
        filePath,
        sourceFile,
      );
      cmps.push(...fileCmps);
    }),
  );

  return cmps.sort((a, b) => a.cmpInfo.name.localeCompare(b.cmpInfo.name));
}

const SKIP_APP_FILES: Set<string> = new Set([]);

const SKIP_FOLDERS = new Set(["node_modules"]);

function shouldSkipFolder(sys: AngularDevToolsSys, fileName: string) {
  for (const folder of SKIP_FOLDERS) {
    if (sys.dirname(fileName).includes(folder)) {
      return true;
    }
  }
  return false;
}

export async function getAngularComponentsFromPath(
  sys: DevToolsSys,
  filePath: string,
) {
  const ext = sys.extname(filePath).toLowerCase();
  if (ext === ".tsx" || ext === ".jsx" || ext === ".ts" || ext === ".js") {
    const code = await sys.readFile(filePath);
    if (code) {
      return getAngularComponentsFromCode(sys, filePath, code);
    }
  }
  return null;
}

export function getAngularComponentsFromCode(
  sys: DevToolsSys,
  filePath: string,
  code: string,
) {
  const sourceFile = codeToSource(sys, code);
  return getAngularComponentsFromSourceFile(sys, null, filePath, sourceFile);
}

export async function getAngularComponentsFromSourceFile(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker | null,
  filePath: string,
  sourceFile: ts.SourceFile,
) {
  const fileCmps: AngularComponentSource[] = [];
  const componentOptions: AngularComponentOptions = {};
  filePath = sys.normalize(filePath);

  const classStatements = sourceFile.statements.filter(
    (s) => sys.ts.isClassDeclaration(s) && isComponentDecorator(sys, s),
  );

  const angularCoreImport = sourceFile.statements.find(
    (s) =>
      sys.ts.isImportDeclaration(s) &&
      sys.ts.isStringLiteral(s.moduleSpecifier) &&
      s.moduleSpecifier.text === "@angular/core",
  );

  /**
   * input signals are typically imported as `input`, but it is possible for a
   * developer to alias that import to something else, so we account for that here.
   */
  if (angularCoreImport && sys.ts.isImportDeclaration(angularCoreImport)) {
    const namedImports = angularCoreImport.importClause?.namedBindings;
    if (namedImports && sys.ts.isNamedImports(namedImports)) {
      /**
       * propertyName is only defined if import is aliased
       * This code does not handle shadowed `input` variables, so this logic may need
       * to be improved in the future.
       */
      const inputSignal = namedImports.elements.find(
        (e) => (e.propertyName ?? e.name).text === "input",
      );
      if (inputSignal) {
        componentOptions.inputSignalImportName = inputSignal.name.text;
      }
    }
  }

  const reExportedComponents = await getExportedAngularComponentSource(
    sys,
    typeChecker,
    filePath,
    sourceFile,
  );

  const enumOptions = extractEnumOptions(sys, sourceFile);

  await Promise.all(
    classStatements.map(async (node) => {
      const sCmps = await getAngularComponentSource(
        sys,
        typeChecker,
        filePath,
        node as ts.ClassDeclaration,
        enumOptions,
        componentOptions,
      );
      fileCmps.push(...sCmps);
    }),
  );

  fileCmps.push(...reExportedComponents);

  return fileCmps;
}

export async function parseAngularComponentSourceInputs(
  sys: DevToolsSys,
  srcCmp: AngularComponentSource,
  tsProgram: ts.Program | null,
) {
  if (srcCmp.node.fnNode) {
    const { sourceFile, typeChecker } = await getTypeChecker(
      sys,
      srcCmp.cmpInfo.filePath,
      tsProgram,
    );

    const typedSrcCmps = await getAngularComponentsFromSourceFile(
      sys,
      typeChecker,
      srcCmp.cmpInfo.filePath,
      sourceFile,
    );
    const typedSrcCmp = typedSrcCmps.find(
      (t) => t.cmpInfo.id === srcCmp.cmpInfo.id,
    );

    if (!typedSrcCmp) {
      throw new Error(
        `Could not find component ${srcCmp.cmpInfo.name} in ${srcCmp.cmpInfo.filePath}`,
      );
    }

    return typedSrcCmp.cmpInfo.inputs;

    // TODO: get inputs using typechecker
    // return parseComponentSourcePropsFromNode(
    //   sys,
    //   typeChecker,
    //   typedSrcCmp.node.fnNode
    // );
  }
  return [];
}

export async function parseAngularAllComponentSourceInputs(
  sys: DevToolsSys,
  sourceComponents: AngularComponentSource[],
  components: ComponentInfo[],
) {
  // const hasUnregistered = components.some((c) => !c.isRegistered);
  // if (!hasUnregistered) {
  //   return components;
  // }

  const tsProgram = await createTsProgram(
    sys,
    components.map((c) => c.filePath),
  );
  const typeChecker = tsProgram.getTypeChecker();

  const componentsWithTypes = await Promise.all(
    components
      .filter((cmp) => {
        if (cmp.name.endsWith("Page")) {
          return false;
        }
        if (cmp.name.endsWith("Module")) {
          return false;
        }
        if (cmp.name === "AppComponent") {
          return false;
        }
        return true;
      })
      .map(async (cmp) => {
        if (!cmp.isRegistered) {
          const sourceFile = tsProgram.getSourceFile(cmp.filePath);
          if (sourceFile) {
            const typedSrcCmps = await getAngularComponentsFromSourceFile(
              sys,
              typeChecker,
              cmp.filePath,
              sourceFile,
            );
            const typedSrcCmp = typedSrcCmps.find(
              (t) => t.cmpInfo.id === cmp.id,
            );
            if (typedSrcCmp) {
              cmp.inputs = parseComponentSourcePropsFromNode(
                sys,
                typeChecker,
                typedSrcCmp.node.fnNode,
              );
              const sourceCmp = sourceComponents.find(
                (s) => s.cmpInfo.id === cmp.id,
              );
              if (sourceCmp) {
                const sourceInputs = await parseAngularComponentSourceInputs(
                  sys,
                  sourceCmp,
                  tsProgram,
                );
                mergeComponentInputs(cmp, sourceInputs);
              }
            }
          }
        }
        return cmp;
      }),
  );

  return componentsWithTypes;
}

async function getAngularComponentSource(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker | null,
  filePath: string,
  node: ts.ClassDeclaration,
  enumOptions: Record<string, string[]>,
  componentOptions: AngularComponentOptions,
): Promise<AngularComponentSource[]> {
  const srcCmps: AngularComponentSource[] = [];
  const relFilePath = sys.relative(sys.getAppRootDir(), filePath);
  const importPath = getComponentImportPath(sys, filePath);

  const cmpName = node.name?.text;
  if (!cmpName) {
    return srcCmps;
  }
  let isDefaultExport = false;

  if (node.modifiers) {
    isDefaultExport = node.modifiers.some(
      (m) => m.kind === sys.ts.SyntaxKind.DefaultKeyword,
    );
  }

  const exportName = isDefaultExport ? "default" : cmpName;
  const exportType = isDefaultExport ? "default" : "named";

  const componentInputs: ComponentInput[] = [];

  node.members.forEach((member) => {
    /**
     * Inputs in Angular are either property declarations:
     * @Input() foo = true; // using decorator
     * bar = input(false); // using signal
     *
     * or getters/setters:
     * @Input()
     * get baz(): boolean {
     *   return this._baz;
     * }
     * set baz(value: boolean): {
     *   this._baz = value;
     * }
     */
    if (
      sys.ts.isPropertyDeclaration(member) ||
      sys.ts.isGetAccessor(member) ||
      sys.ts.isSetAccessor(member)
    ) {
      const decorators = sys.ts.getDecorators(member) ?? [];
      const inputDecorator = decorators.find((d) => isInputDecorator(sys, d));

      const usingDecorator =
        inputDecorator !== undefined && sys.ts.isIdentifier(member.name);
      const usingSignal =
        componentOptions?.inputSignalImportName !== undefined &&
        sys.ts.isIdentifier(member.name) &&
        isInputSignal(sys, member, componentOptions.inputSignalImportName);

      if (usingDecorator || usingSignal) {
        let inputType = "any";
        if (sys.ts.isSetAccessor(member)) {
          // a setter must have exactly one parameter
          inputType = getTypeText(sys, member.parameters[0].type);
        } else if (member.type && typeChecker) {
          const typeNode = typeChecker.getTypeAtLocation(member.type);
          const typeString = typeChecker.typeToString(typeNode);
          inputType = typeString;
        } else {
          inputType = getTypeText(sys, member.type);
        }

        const input: ComponentInput = {
          name: member.name.text,
          type: enumOptions[inputType] ? "string" : inputType,
        };

        if (enumOptions[inputType]) {
          input.enum = enumOptions[inputType];
        }

        componentInputs.push(input);
      }
    }
  });

  srcCmps.push({
    cmpInfo: {
      id: await getComponentId(sys, filePath, cmpName),
      displayFilePath: getDisplayFilePath(sys, filePath),
      name: getComponentDisplayName(cmpName),
      inputs: componentInputs,
      exportName,
      exportType,
      importName: cmpName,
      filePath,
      relFilePath,
      importPath,
      meta: {
        ...getComponentDecoratorInfo(sys, node),
      },
      framework: "angular",
    },
    node: {
      name: cmpName,
      fnNode: node,
      deps: undefined,
    },
  });

  return srcCmps;
}

async function getExportedAngularComponentSource(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker | null,
  filePath: string,
  sourceFile: ts.SourceFile,
): Promise<AngularComponentSource[]> {
  const srcCmps: AngularComponentSource[] = [];
  const relFilePath = sys.relative(sys.getAppRootDir(), filePath);
  const importPath = getComponentImportPath(sys, filePath);

  for (const s of sourceFile.statements) {
    if (
      !(
        sys.ts.isExportDeclaration(s) &&
        s.exportClause &&
        sys.ts.isNamedExports(s.exportClause)
      )
    ) {
      continue;
    }

    for (const exportSpecifier of s.exportClause.elements) {
      // const cmpName = exportSpecifier.name?.getText();

      const cmpName = exportSpecifier.name?.text;
      if (!cmpName) {
        continue;
      }
      const exportName = cmpName;
      const exportType = "named";

      const componentInputs: ComponentInput[] = [];

      // Resolve the type of the exported component
      if (typeChecker) {
        const symbol = typeChecker.getSymbolAtLocation(exportSpecifier.name);
        if (symbol) {
          const type = typeChecker.getDeclaredTypeOfSymbol(symbol);
          const classMembers = typeChecker.getPropertiesOfType(type);
          classMembers.forEach((member) => {
            const declarations = member.getDeclarations();

            if (declarations) {
              declarations.forEach((declaration) => {
                if (sys.ts.isPropertyDeclaration(declaration)) {
                  const type = typeChecker.getTypeAtLocation(declaration);

                  const input: any = {
                    name: member.getName(),
                    type: getTypeName(sys, typeChecker, type),
                  };

                  componentInputs.push(input);
                }
              });
            }
          });
        }
      }

      const exportDeclaration =
        sys.ts.isExportSpecifier(exportSpecifier) && exportSpecifier.parent
          ? (exportSpecifier.parent.parent as ts.ExportDeclaration)
          : undefined;
      let modulePath;

      if (exportDeclaration) {
        const moduleSpecifier = exportDeclaration.moduleSpecifier;

        if (moduleSpecifier && sys.ts.isStringLiteral(moduleSpecifier)) {
          const symbol = typeChecker?.getSymbolAtLocation(moduleSpecifier);
          if (symbol && symbol.declarations && symbol.declarations.length > 0) {
            const declaration = symbol.declarations[0];
            if (
              !sys.ts.isSourceFile(declaration) ||
              (sys.ts.isSourceFile(declaration) &&
                declaration.fileName.includes("node_modules"))
            ) {
              modulePath = moduleSpecifier.text;
            }
          }
        }
      }

      srcCmps.push({
        cmpInfo: {
          id: await getComponentId(sys, filePath, cmpName, modulePath),
          displayFilePath: getDisplayFilePath(sys, filePath),
          name: getComponentDisplayName(cmpName),
          inputs: componentInputs,
          exportName,
          exportType,
          importName: cmpName,
          filePath,
          relFilePath,
          importPath: modulePath ? modulePath : importPath,
          externalImportPath: modulePath,
          framework: "angular",
        },
        node: {
          name: cmpName,
          fnNode: exportSpecifier,
          deps: undefined,
        },
      });
    }
  }

  return srcCmps;
}

function isInputDecorator(sys: DevToolsSys, decorator: ts.Decorator): boolean {
  return decoratorNameIs(sys, decorator, "Input");
}

/**
 * Returns `true` if a class element is using the Angular input() signal.
 * Returns `false` otherwise.
 */
function isInputSignal(
  sys: DevToolsSys,
  member: ts.ClassElement,
  inputSignalName: string,
): boolean {
  return (
    sys.ts.isPropertyDeclaration(member) &&
    member.initializer !== undefined &&
    sys.ts.isCallExpression(member.initializer) &&
    sys.ts.isIdentifier(member.initializer.expression) &&
    member.initializer.expression.text === inputSignalName
  );
}

function decoratorNameIs(
  sys: DevToolsSys,
  decorator: ts.Decorator,
  name: string,
): boolean {
  if (sys.ts.isCallExpression(decorator.expression)) {
    const callExpression = decorator.expression;
    if (sys.ts.isIdentifier(callExpression.expression)) {
      return callExpression.expression?.text === name;
    }
  }
  return false;
}

function isComponentDecorator(
  sys: DevToolsSys,
  node: ts.ClassDeclaration,
): boolean {
  const decorators = sys.ts.getDecorators(node);
  const hasComponentDecorator = decorators?.some(
    (decorator) =>
      sys.ts.isCallExpression(decorator.expression) &&
      sys.ts.isIdentifier(decorator.expression.expression) &&
      decorator.expression.expression.text === "Component",
  );
  return Boolean(hasComponentDecorator);
}

function getTypeText(sys: DevToolsSys, type: ts.TypeNode | undefined): string {
  if (!type) {
    return "any";
  }
  let inputType = "any";
  switch (type.kind) {
    case sys.ts.SyntaxKind.StringKeyword:
      inputType = "string";
      break;
    case sys.ts.SyntaxKind.NumberKeyword:
      inputType = "number";
      break;
    case sys.ts.SyntaxKind.BooleanKeyword:
      inputType = "boolean";
      break;
    case sys.ts.SyntaxKind.TypeReference:
      if (
        sys.ts.isTypeReferenceNode(type) &&
        sys.ts.isIdentifier(type.typeName)
      ) {
        inputType = type.typeName.text;
      }
      break;
  }
  return inputType.includes("|") ? "string" : inputType;
}

function extractEnumOptions(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
): Record<string, string[]> {
  const enumOptions: Record<string, string[]> = {};

  sys.ts.forEachChild(sourceFile, (node) => {
    if (
      sys.ts.isTypeAliasDeclaration(node) &&
      sys.ts.isUnionTypeNode(node.type) &&
      node.type.types.every(
        (t) =>
          sys.ts.isLiteralTypeNode(t) &&
          sys.ts.isStringLiteral((t as ts.LiteralTypeNode).literal),
      )
    ) {
      enumOptions[node.name.text] = node.type.types.map(
        (t) => ((t as ts.LiteralTypeNode).literal as ts.StringLiteral).text,
      );
    }
  });

  return enumOptions;
}

export interface AngularComponentSourceNode {
  name: string;
  fnNode: ts.Node;
  deps: AppDependency[] | undefined;
}

export interface AngularComponentSource {
  cmpInfo: ComponentInfo;
  node: AngularComponentSourceNode;
}

function getTypeName(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker,
  type: ts.Type,
): string {
  let typeName = typeChecker.typeToString(type);

  // Check if the type is 'any' and provide a more detailed type if available
  if (typeName === "any") {
    const symbol = type.getSymbol();
    if (symbol) {
      const declarations = symbol.getDeclarations();
      if (declarations && declarations.length > 0) {
        const declaration = declarations[0];
        if (sys.ts.isVariableDeclaration(declaration)) {
          // Try to get the type from variable declarations
          const typeAnnotation = declaration.type;
          if (typeAnnotation) {
            typeName = typeChecker.typeToString(
              typeChecker.getTypeAtLocation(typeAnnotation),
            );
          }
        }
      }
    }
  }

  return typeName;
}

function getComponentDecoratorInfo(
  sys: DevToolsSys,
  node: ts.ClassDeclaration,
) {
  const {
    getDecorators,
    isCallExpression,
    isIdentifier,
    isObjectLiteralExpression,
    isPropertyAssignment,
    isStringLiteral,
    SyntaxKind,
  } = sys.ts;

  let selector, standalone;
  const classDecorators = getDecorators(node);
  classDecorators?.forEach((decorator) => {
    if (decoratorNameIs(sys, decorator, "Component")) {
      const { expression } = decorator;
      if (isCallExpression(expression)) {
        const [arg] = expression.arguments;
        if (isObjectLiteralExpression(arg)) {
          arg.properties.forEach((prop) => {
            if (isPropertyAssignment(prop) && isIdentifier(prop.name)) {
              if (
                prop.name.escapedText === "selector" &&
                isStringLiteral(prop.initializer)
              ) {
                selector = prop.initializer.text;
              }
              if (prop.name.escapedText === "standalone") {
                standalone = prop.initializer.kind === SyntaxKind.TrueKeyword;
              }
            }
          });
        }
      }
    }
  });

  return { selector, standalone };
}
