import {
  SDK_VERSIONS,
  type ComponentInfo,
  type ComponentRegistry,
  type SetComponentInfoOptions,
  type SetComponentInputOptions,
  type UpdateRegistry,
} from "../../../types";
import type { ReactDevToolsSys } from "./index";
import {
  createReactRegisteredComponent,
  updateReactRegisteredComponent,
} from "./react-component-info";
import { createTsProgram } from "../../../common/typescript";
import { ensureImport } from "../../../common/ast/ensure-imports";
import { sourceToCode } from "../../../common/ast/transform";
import { findArrayStatementInitializer } from "../../../common/ast/utils";
import { getReactSourceComponentsFromDir } from "./react-components";
import {
  getDisplayFilePath,
  getImportPath,
  isMapperFile,
} from "../../../common/utils";
import {
  normalizeImports,
  normalizeLineBreaks,
  normalizeRegisteredArrayElements,
} from "../../../common/ast/normalize-statements";
import {
  parseReactRegistryFromFile,
  parseReactRegisteredComponentSdkGen2,
} from "./react-registry-parse";
import {
  parseReactAllComponentSourceInputs,
  parseReactComponentSourceInputs,
  type ReactComponentSource,
} from "../react/react-components";
import { removeUnusedImports } from "../../../common/ast/remove-unused-imports";
import {
  mergeComponents,
  updateRegistry,
} from "../../../common/ast/component-registry";
import {
  mergeComponentInputs,
  updateComponentObjLiteralInfoProperties,
} from "../../../common/ast/component-info";
import { createReactTsProgram } from "../react/react-ts-program";
import { getAppDependencies } from "../../../common/ast/app-dependencies";
import { getReactApiKey } from "./react-api-key";
import type ts from "typescript";
import { ensureArrayStatement } from "../../../common/ast/ensure-array-statement";
import { hasTsconfig } from "../../../common/fs";

export async function reactComponentRegistry(
  sys: ReactDevToolsSys,
  cmpId: string | string[] | null,
  registerCmp: boolean,
  unregisterCmp: boolean,
  setCmpInfo: SetComponentInfoOptions | null,
  setCmpInput: SetComponentInputOptions | null,
  writeRegistry: boolean,
  readAllInputTypes: boolean,
) {
  const tsProgram = await createReactTsProgram(sys);

  const [srcCmps, parsedRegistry, _typescriptEnabled, reactApiKey] =
    await Promise.all([
      getReactSourceComponentsFromDir(sys, tsProgram),
      parseReactRegistryFromFile(sys),
      hasTsconfig(sys),
      getReactApiKey(sys),
    ]);

  const sourceComponents = srcCmps.map((c) => c.cmpInfo);
  let registeredComponents = parsedRegistry.components;

  const cmpRegistry: ComponentRegistry = {
    components: mergeComponents(sourceComponents, registeredComponents),
    registryPath: sys.registryPath,
    registryDisplayPath: getDisplayFilePath(sys, sys.registryPath),
    frameworks: sys.getFrameworks(),
    dependencies: getAppDependencies(sourceComponents),
    publicApiKey: reactApiKey.envValue ?? undefined,
    devToolsVersion: sys.version,
  };

  if (readAllInputTypes) {
    cmpRegistry.components = await parseReactAllComponentSourceInputs(
      sys,
      cmpRegistry.components,
    );
    return cmpRegistry;
  }

  let madeChanges = false;
  let sourceFile = parsedRegistry.sourceFile;

  const cmpIds = Array.isArray(cmpId)
    ? cmpId
    : typeof cmpId === "string"
      ? [cmpId]
      : [];

  if (cmpIds.length > 0) {
    const updateCmps = cmpIds.map((cmpId) => {
      const srcCmp = srcCmps.find((c) => c.cmpInfo.id === cmpId);
      if (!srcCmp) {
        throw new Error(`Component ${cmpId} not found`);
      }
      return srcCmp;
    });

    const srcFilePaths = Array.from(
      new Set(updateCmps.map((c) => c.cmpInfo.filePath)),
    );
    const tsProgram = await createTsProgram(sys, srcFilePaths);

    for (const srcCmp of updateCmps) {
      const cmpInfo = cmpRegistry.components.find(
        (c) => c.id === srcCmp.cmpInfo.id,
      )!;
      const sourceInputs = await parseReactComponentSourceInputs(
        sys,
        srcCmp,
        tsProgram,
        sys.registryPath === srcCmp.cmpInfo.filePath ||
          isMapperFile(srcCmp.cmpInfo.filePath),
      );
      mergeComponentInputs(cmpInfo, sourceInputs);

      const srcCmpIndex = sourceComponents.findIndex(
        (c) => c.id === srcCmp.cmpInfo.id,
      );
      sourceComponents[srcCmpIndex].inputs = sourceInputs;

      const update = updateRegistry(
        srcCmp.cmpInfo.id,
        sourceComponents,
        registeredComponents,
        registerCmp,
        unregisterCmp,
        setCmpInfo,
        setCmpInput,
      );

      if (sys.sdkVersion === SDK_VERSIONS.gen2) {
        const {
          elements,
          elmIndex,
          statementIndex,
          statement,
          variableDeclaration,
        } = await getReactRegisteredComponentSourceSdkGen2(
          sys,
          sourceFile,
          srcCmp,
        );

        if (update.addCmpToRegistry) {
          // not already registered, append it
          elements.push(
            createReactRegisteredComponentSdkGen2(sys, update.addCmpToRegistry),
          );
          madeChanges = true;
        } else if (update.removeCmpFromRegistry) {
          // already registered, remove it
          elements.splice(elmIndex, 1);
          madeChanges = true;
        } else if (update.updateRegisteredCmp) {
          // already registered, update component info
          const element = updateComponentObjLiteralInfoProperties(
            sys,
            elements[elmIndex],
            update.updateRegisteredCmp,
          );
          const identifier = sys.ts.factory.createIdentifier(
            update.updateRegisteredCmp.importName,
          );

          elements[elmIndex] = sys.ts.factory.updateObjectLiteralExpression(
            element,
            [
              sys.ts.factory.createPropertyAssignment("component", identifier),
              ...element.properties.slice(1),
            ],
          );
          madeChanges = true;
        }

        if (madeChanges && writeRegistry) {
          const statements = [...sourceFile.statements];
          statements[statementIndex] = sys.ts.factory.updateVariableStatement(
            statement,
            statement.modifiers,
            sys.ts.factory.updateVariableDeclarationList(
              statement.declarationList,
              [
                sys.ts.factory.updateVariableDeclaration(
                  variableDeclaration,
                  variableDeclaration.name,
                  variableDeclaration.exclamationToken,
                  variableDeclaration.type,
                  sys.ts.factory.createArrayLiteralExpression(
                    normalizeRegisteredArrayElements(sys, elements),
                  ),
                ),
              ],
            ),
          );

          sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

          sourceFile = ensureReactComponentImportStatements(
            sys,
            update,
            sourceFile,
          );

          sourceFile = ensureImport(sys, sourceFile, {
            namedImports: ["RegisteredComponent"],
            importPath: "@builder.io/sdk-react",
            isTypeOnly: true,
          });

          sourceFile = ensureArrayStatement(sys, sourceFile, {
            variableName: "CUSTOM_COMPONENTS",
            arrayType: "RegisteredComponent",
          });

          cmpRegistry.components = update.components;
          registeredComponents = update.components.filter(
            (c) => c.isRegistered,
          );
        }
      } else {
        let statements = [...sourceFile.statements];

        if (update.addCmpToRegistry) {
          // not already registered, append it
          statements.push(
            createReactRegisteredComponent(sys, update.addCmpToRegistry),
          );
          madeChanges = true;
        } else if (update.removeCmpFromRegistry) {
          // already registered, remove it
          statements.splice(update.nodeIndex, 1);
          madeChanges = true;
        } else if (update.updateRegisteredCmp) {
          // already registered, update component info
          statements[update.nodeIndex] = updateReactRegisteredComponent(
            sys,
            statements[update.nodeIndex],
            update.updateRegisteredCmp,
          );
          madeChanges = true;
        }

        if (madeChanges && writeRegistry) {
          sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

          sourceFile = ensureImport(sys, sourceFile, {
            namedImports: ["Builder", "builder"],
            importPath: "@builder.io/react",
          });

          sourceFile = ensureReactComponentImportStatements(
            sys,
            update,
            sourceFile,
          );

          if (cmpInfo?.acceptsChildren) {
            sourceFile = ensureImport(sys, sourceFile, {
              namedImports: ["withChildren"],
              importPath: "@builder.io/react",
            });
          }

          cmpRegistry.components = update.components;
          registeredComponents = update.components.filter(
            (c) => c.isRegistered,
          );
        }
      }
    }
  }

  if (madeChanges && writeRegistry) {
    sourceFile = removeUnusedImports(sys, sourceFile);
    sourceFile = normalizeImports(sys, sourceFile);

    let code = sourceToCode(sys, sourceFile);
    code = normalizeLineBreaks(code);

    code = await sys.formatCode(sys.registryPath, code);
    await sys.writeFile(sys.registryPath, code);
  }

  return cmpRegistry;
}

async function getReactRegisteredComponentSourceSdkGen2(
  sys: ReactDevToolsSys,
  sourceFile: ts.SourceFile,
  srcCmp: ReactComponentSource,
) {
  const s = findArrayStatementInitializer(sys, sourceFile, "CUSTOM_COMPONENTS");
  if (!s) {
    throw new Error(
      `Exported name of the Builder registry must be "CUSTOM_COMPONENTS". Please update your Builder registry file: ${sys.registryPath}`,
    );
  }

  const elements = s.arrayLiteralExp.elements.filter((ex) => {
    return sys.ts.isObjectLiteralExpression(ex);
  }) as ts.ObjectLiteralExpression[];

  let elmIndex = -1;
  for (let i = 0; i < elements.length; i++) {
    const cmp = await parseReactRegisteredComponentSdkGen2(
      sys,
      sourceFile,
      elements[i],
    );
    if (cmp?.id === srcCmp.cmpInfo.id) {
      elmIndex = i;
      break;
    }
  }

  return {
    ...s,
    elements,
    elmIndex,
  };
}

function createReactRegisteredComponentSdkGen2(
  sys: ReactDevToolsSys,
  cmpInfo: ComponentInfo,
) {
  const identifier = sys.ts.factory.createIdentifier(cmpInfo.importName);

  return updateComponentObjLiteralInfoProperties(
    sys,
    sys.ts.factory.createObjectLiteralExpression(
      [
        sys.ts.factory.createPropertyAssignment("component", identifier),
        ...(cmpInfo.acceptsChildren
          ? [
              sys.ts.factory.createPropertyAssignment(
                "canHaveChildren",
                sys.ts.factory.createTrue(),
              ),
            ]
          : []),
      ],
      true,
    ),
    cmpInfo,
  );
}

function ensureReactComponentImportStatements(
  sys: ReactDevToolsSys,
  update: UpdateRegistry,
  sourceFile: ts.SourceFile,
) {
  const regCmp = update.addCmpToRegistry || update.updateRegisteredCmp;
  if (regCmp) {
    const importPath = regCmp.externalImportPath
      ? regCmp.externalImportPath
      : getImportPath(sys, sys.registryPath, regCmp.filePath);
    if (regCmp.exportType === "default") {
      sourceFile = ensureImport(sys, sourceFile, {
        defaultImport: regCmp.importName,
        importPath,
      });
    } else if (regCmp.exportType === "named") {
      sourceFile = ensureImport(sys, sourceFile, {
        namedImports: [regCmp.importName],
        importPath,
      });
    }
  }

  return sourceFile;
}
