// Helper to extract signature info from file content
export function extractSignatureInfo(content: string): {
  contentId?: string;
  sessionKey?: string;
  snippetId?: string;
} {
  const matches = content.match(
    /builder\.io\/content\/([^?]+)\?codeSession=([^&]+)&sid=([^\s]+)/,
  );
  if (!matches) return {};
  return {
    contentId: matches[1],
    sessionKey: matches[2],
    snippetId: matches[3],
  };
}

export async function getAllProjectFiles(basePath: string): Promise<string[]> {
  const ig = getIgnorePatterns(basePath);
  const files = await glob("**/*", {
    cwd: basePath,
    ignore: {
      ignored(p) {
        return ig(p.relative());
      },
    },
    nodir: true,
  });
  return files;
}

export async function findBuilderFiles(
  basePath: string,
  targetContentId: string,
  targetSessionKey: string,
): Promise<FileNode[]> {
  const files = await glob("**/*", {
    cwd: basePath,
    ignore: ["node_modules/**"],
    nodir: true,
  });

  const builderFiles: FileNode[] = [];

  for (const file of files) {
    const fullPath = resolve(basePath, file);
    try {
      const content = readFileSync(fullPath, "utf-8");
      if (content.includes("This code was generated by Builder.io")) {
        const sigInfo = extractSignatureInfo(content);

        // Skip if we're looking for specific content/session and this doesn't match
        if (targetContentId && targetSessionKey) {
          if (
            sigInfo.contentId !== targetContentId ||
            sigInfo.sessionKey !== targetSessionKey
          ) {
            continue;
          }
        }

        builderFiles.push({
          path: file,
          code: removeBuilderSignature(content),
          name: file.split("/").pop() || "",
          timestamp: statSync(fullPath).mtime.getTime(),
          snippetId: sigInfo.snippetId,
        });
      }
    } catch (error) {
      console.error(`Error reading file ${fullPath}:`, error);
    }
  }

  return builderFiles;
}

function isMatchingBuilderFile(content: string, snippet: Snippet): boolean {
  const sigInfo = extractSignatureInfo(content);
  return (
    sigInfo.contentId === snippet.contentId &&
    sigInfo.sessionKey === snippet.sessionKey
  );
}

export function getIgnorePatterns(basePath: string): (path: string) => boolean {
  // Default patterns that should always be ignored
  const defaultPatterns = [
    "*.png",
    "*.jpg",
    "*.jpeg",
    "*.gif",
    "*.webp",
    "*.yml",
    "*.yaml",
    "*.snap",
    "*.liquid",
    ".git",
    ".DS_Store",
    ".vscode",
    "node_modules",
    ".next",
    "coverage",
    "example",
    "public",
    "yarn.lock",
    "pnpm-lock.yaml",
    "__snapshots__",
    "package-lock.json",
    // Exclude any file with .mapper extension
    "*.mapper.tsx",
    "*.mapper.ts",
    "*.mapper.js",
    "*.mapper.jsx",
    // Exclude builder-registry files regardless of extension
    "builder-registry.*",
  ];

  const ig = ignore().add(defaultPatterns);

  function tryAddIgnoreFile(path: string) {
    try {
      const ignorePath = resolve(basePath, path);
      if (existsSync(ignorePath)) {
        const gitignoreContent = readFileSync(ignorePath, "utf8");
        ig.add(gitignoreContent);
      }
    } catch (error) {
      console.debug(`No ${path} found or error reading it:`, error);
    }
  }
  // Try to read .gitignore
  tryAddIgnoreFile(".gitignore");
  tryAddIgnoreFile(".prettierignore");
  tryAddIgnoreFile(".builderignore");

  return (path: string): boolean => {
    const relativePath = relative(basePath, path);
    if (!relativePath) return false;
    // ignore package handles forward slashes only
    const normalizedPath = relativePath.split(sep).join("/");
    return ig.ignores(normalizedPath);
  };
}
export function watchDirectory(
  basePath: string,
  syncInfo: SyncInfo,
  onChange: (updatedSyncInfo: SyncInfo) => void,
) {
  const ig = getIgnorePatterns(basePath);

  const watcher = chokidar.watch(basePath, {
    ignored: ig,
    ignoreInitial: true,
    awaitWriteFinish: {
      stabilityThreshold: 500,
      pollInterval: 600,
    },
    ignorePermissionErrors: true,
    followSymlinks: false,
    atomic: 600,
  });

  const handleFileUpdate = async (fullPath: string) => {
    const relativePath = relative(basePath, fullPath);
    try {
      const content = readFileSync(fullPath, "utf-8");
      if (isMatchingBuilderFile(content, syncInfo.snippet)) {
        const existingFile = syncInfo.writtenFiles.find(
          (f) => f.path === relativePath,
        );
        if (existingFile) {
          existingFile.code = removeBuilderSignature(content);
          existingFile.timestamp = Date.now();
        } else {
          // New file appeared with matching signature
          syncInfo.writtenFiles.push({
            path: relativePath,
            code: removeBuilderSignature(content),
            timestamp: Date.now(),
            name: relativePath.split("/").pop() || "",
          });
        }
        syncInfo.timeStamp = Date.now();
        onChange(syncInfo);
      }
    } catch (error) {
      console.debug(`Error reading file ${fullPath}:`, error);
    }
  };

  // Set up event handlers
  watcher
    .on("add", async (path) => {
      log.info(`File added: ${path}`);
      await handleFileUpdate(path);
    })
    .on("change", async (path) => {
      log.info(`File changed: ${path}`);
      await handleFileUpdate(path);
    })
    .on("unlink", (path) => {
      log.info(`File deleted: ${path}`);
      const relativePath = relative(basePath, path);
      syncInfo.writtenFiles = syncInfo.writtenFiles.filter(
        (f) => f.path !== relativePath,
      );
      syncInfo.timeStamp = Date.now();
      onChange(syncInfo);
    })
    .on("unlinkDir", (path) => {
      log.info(`Folder deleted: ${path}`);
      const relativePath = relative(basePath, path);
      syncInfo.writtenFiles = syncInfo.writtenFiles.filter(
        (f) => !f.path.startsWith(relativePath),
      );
      syncInfo.timeStamp = Date.now();
      onChange(syncInfo);
    })
    .on("ready", () => {
      console.debug("Initial scan complete. Ready for changes.");
    })
    .on("error", (error) => {
      console.error(`Watcher error: ${error}`);
    });

  // Handle file moves/renames
  watcher.on("raw", async (event: string, path, details: any) => {
    // todo: handle windows
    if (event === "rename") {
      log.info(`File renamed: ${details?.oldPath} -> ${path}`);
      const oldPath = details?.oldPath
        ? relative(basePath, details.oldPath)
        : undefined;
      const newPath = path ? relative(basePath, path) : undefined;

      if (oldPath && newPath) {
        const fileInfo = syncInfo.writtenFiles.find((f) => f.path === oldPath);
        if (fileInfo) {
          fileInfo.path = newPath;
          syncInfo.timeStamp = Date.now();
          onChange(syncInfo);
        }
      }
    }
  });

  // Return cleanup function
  const cleanup = async () => {
    await watcher.close();
  };

  process.on("SIGINT", cleanup);
  process.on("SIGTERM", cleanup);

  return cleanup;
}
export async function setupSyncServer(
  sys: DevToolsSys,
  initialSyncInfo?: SyncInfo,
) {
  console.log("");
  intro(bgMagenta(bold(white(" sync server "))) + `  ` + dim(pkgVersion));

  sys.debug("builder sync server init");
  const devTools = await createDevTools(sys);
  let syncInfo = initialSyncInfo;
  let cleanupWatcher = () => Promise.resolve();

  if (syncInfo) {
    setTimeout(() => {
      cleanupWatcher = watchDirectory(
        sys.getAppRootDir(),
        syncInfo!,
        (updated) => {
          syncInfo = updated;
        },
      );
    }, 1000);
  }

  await createDevToolsServer({
    ...devTools,
    ensureFigmaImportPage: () => {
      return Promise.reject("Not supported");
    },
    getPastSyncInfo: ({ sessionKey, since }) => {
      if (!syncInfo || sessionKey !== syncInfo.snippet.sessionKey) {
        return null;
      }
      return {
        ...syncInfo,
        hasChanged: syncInfo.timeStamp > since,
      };
    },
    resyncSnippet: async (data) => {
      await cleanupWatcher(); // Clean up existing watcher

      const newSyncInfo = await syncCommand({
        path: syncInfo?.pathInput,
        snippet: data.snippet,
        cwd: sys.getAppRootDir(),
      });

      if (newSyncInfo) {
        syncInfo = newSyncInfo;
        cleanupWatcher = watchDirectory(
          sys.getAppRootDir(),
          syncInfo,
          (updated) => {
            syncInfo = updated;
          },
        );
      }

      return syncInfo;
    },

    getClientId: () => "builder-sync-cli",

    closeAppServer: async () => {
      await cleanupWatcher();
    },

    restartAppServer: async () => {
      sys.debug("restart server");
    },

    enableAppWatch: async (enable) => {
      return enable;
    },

    ...sys,
  });
}

export async function syncCommand(
  opts: AddCliOptions,
): Promise<SyncInfo | undefined> {
  const id = opts.snippetId;
  let snippet = opts.snippet || null;

  if (!snippet && id) {
    snippet = await fetchSnippet(id);
  }

  if (!snippet) {
    cancel(`No snippet found for id ${id}`);
    return await exit(1);
  }

  const cwd = process.cwd();
  const appRootDir = findAppRootDir(cwd) ?? cwd;

  // Check for existing files with same signature
  const existingFiles = await findBuilderFiles(
    appRootDir,
    snippet.contentId,
    snippet.sessionKey,
  );
  const matchingFiles = existingFiles.filter(
    (f) => f.snippetId === snippet!.id,
  );
  log.info(
    `Found ${matchingFiles.length} matching files for snippet ${snippet.id} in existing files ${existingFiles.length}`,
  );

  if (matchingFiles.length > 0) {
    // Found snippet files pre-synced, create syncInfo without writing
    const latestTimestamp = Math.max(
      ...matchingFiles.map((f) => f.timestamp || 0),
    );
    return {
      snippet,
      writtenFiles: matchingFiles,
      pathInput: findCommonPath(matchingFiles.map((f) => f.path)),
      timeStamp: latestTimestamp,
    };
  }

  const rootPath = suggestRootPath(appRootDir, snippet.framework || "react");
  const pathInput =
    opts.path ||
    findCommonPath(existingFiles.map((f) => f.path)) ||
    (await text({
      message: "Where should we copy the files?",
      initialValue: rootPath,
      validate(value) {
        if (value.length === 0) {
          return `File path is required!`;
        }
        if (value[0] !== ".") return "Please enter a relative path.";
      },
    }));
  if (isCancel(pathInput)) {
    cancel("Cancelled sync");
    process.exit(0);
  }

  // remove older snippet files for in favor of new ones
  for (const file of existingFiles) {
    const fullPath = resolve(appRootDir, file.path);
    try {
      unlinkSync(fullPath);
      log.info(`Deleted old file: ${fullPath}`);

      const parentDir = dirname(fullPath);
      if (readdirSync(parentDir).length === 0) {
        rmdirSync(parentDir);
        log.info(`Deleted empty folder: ${parentDir}`);
      }
    } catch (error) {
      log.error(`Error cleaning up file ${fullPath}: ${error}`);
    }
  }

  // Write new files and create syncInfo
  const writtenFiles = await writeFiles(
    appRootDir,
    snippet,
    snippet.files,
    pathInput,
    true,
  );
  log.success(`All files have been written successfully.`);
  const latestTimestamp = Math.max(
    ...writtenFiles.map((f) => f.timestamp || 0),
  );
  return {
    snippet,
    writtenFiles: writtenFiles.map((f) => ({
      ...f,
      code: removeBuilderSignature(f.code),
    })),
    pathInput,
    timeStamp: latestTimestamp || Date.now(),
  };
}

function removeBuilderSignature(str: string) {
  if (!str) {
    return str;
  }

  // For HTML-style comments
  const htmlSignatureRegex =
    /<!--\s*This code was generated by Builder\.io\s*-->\s*(?:<!--\s*https:\/\/builder\.io\/content\/[a-f0-9]+\?codeSession=[a-f0-9]+&sid=[a-f0-9]+\s*-->\s*)?/;

  // For code-style comments
  const codeSignatureRegex =
    /\/\*\*\s*\n\s*\* This code was generated by Builder\.io\s*\n\s*\* https:\/\/builder\.io\/content\/[a-f0-9]+\?codeSession=[a-f0-9]+&sid=[a-f0-9]+\s*\n\s*\*\/\s*/;

  // Try removing HTML-style signature first
  let cleaned = str
    .replace(htmlSignatureRegex, "")
    .replace(codeSignatureRegex, "");
  // Remove any leading newlines that might be left over
  cleaned = cleaned.replace(/^\n+/, "");
  return cleaned;
}

function findCommonPath(paths: string[]): string {
  if (paths.length === 0) {
    return "";
  }
  if (paths.length === 1) {
    return `./${paths[0].slice(0, paths[0].lastIndexOf("/") + 1)}`;
  }
  // Split paths into segments
  const segments = paths.map((path) => path.split("/"));
  const minLength = Math.min(...segments.map((seg) => seg.length));
  let commonSegments: string[] = [];
  for (let i = 0; i < minLength; i++) {
    const segment = segments[0][i];
    if (segments.every((path) => path[i] === segment)) {
      commonSegments.push(segment);
    } else {
      break;
    }
  }

  // Join common segments back into a path
  return `./${commonSegments.join("/")}`;
}
