import type {
  CreateDevToolsOptions,
  DevToolsAdapter,
  DevToolsSys,
} from "../../../types";
import { getBuilderCache, setBuilderCache } from "../../../common/cache";
import { getNextApiKey, setNextApiKey } from "./next-api-key";
import { nextComponentRegistry } from "./next-registry";
import {
  nextEnsureBuilderSetup,
  nextEnsureFigmaImportPageSetup,
} from "./next-ensure-builder-setup";
import { SDK_VERSIONS } from "../../../types";
import { detectReactSdkVersion } from "../react/react-builder-sdk-setup";
import { findBuilderRegistry, hasTsconfig } from "../../../common/fs";

export async function createNextDevTools(sys: CreateDevToolsOptions) {
  const nextSys = await createNextDevToolsSys(sys);

  const devTools: DevToolsAdapter = {
    builderPageOptions: async () => {
      return {
        templateContentId: "3de2f13211234071a4b19a17b4fbc5a7",
        pathname: "/builder-demo",
        title: "Builder + Next.js Demo Page",
      };
    },
    ensureBuilderSetup: () => {
      return nextEnsureBuilderSetup(nextSys);
    },
    ensureFigmaImportPage: () => {
      return nextEnsureFigmaImportPageSetup(nextSys);
    },

    getRegistryPath: () => {
      return nextSys.registryPath;
    },
    getRegistry: (opts) => {
      return nextComponentRegistry(
        nextSys,
        null,
        false,
        false,
        null,
        null,
        false,
        !!opts?.readAllInputTypes,
      );
    },
    addExternalPackage: (pkgName: string) => {
      nextSys.addExternalPackage(pkgName);
    },
    loadComponent: async (opts) => {
      const r = await nextComponentRegistry(
        nextSys,
        opts.cmpId,
        false,
        false,
        null,
        null,
        false,
        false,
      );
      return {
        ...r,
        component: r.components.find((c) => c.id === opts.cmpId)!,
      };
    },
    registerComponent: (opts) => {
      return nextComponentRegistry(
        nextSys,
        opts.cmpId,
        true,
        false,
        null,
        null,
        true,
        false,
      );
    },
    setRegisteredComponentInfo: (opts) => {
      return nextComponentRegistry(
        nextSys,
        opts.cmpId,
        false,
        false,
        opts,
        null,
        true,
        false,
      );
    },
    setRegisteredComponentInput: (opts) => {
      return nextComponentRegistry(
        nextSys,
        opts.cmpId,
        false,
        false,
        null,
        opts,
        true,
        false,
      );
    },
    unregisterComponent: (opts) => {
      return nextComponentRegistry(
        nextSys,
        opts.cmpId,
        false,
        true,
        null,
        null,
        true,
        false,
      );
    },
    getPublicApiKey: () => {
      return getNextApiKey(nextSys);
    },
    setPublicApiKey: (opts) => {
      return setNextApiKey(nextSys, opts.publicApiKey);
    },
    getDependencies: (opts) => {
      if (opts.sdkVersion === SDK_VERSIONS.gen2) {
        return [
          { name: "@builder.io/dev-tools" },
          { name: "@builder.io/sdk-react" },
        ];
      }
      return [
        { name: "@builder.io/dev-tools" },
        { name: "@builder.io/react" },
        { name: "@builder.io/sdk" },
      ];
    },
    getDevRunCommand: () => {
      return "dev";
    },
    getCache: () => getBuilderCache(nextSys),
    setCache: (cache) => setBuilderCache(nextSys, cache),
  };

  return devTools;
}

export async function createNextDevToolsSys(sys: DevToolsSys) {
  // https://nextjs.org/docs/pages/building-your-application/configuring/src-directory
  const rootDir = sys.getAppRootDir();
  const srcDir = sys.join(rootDir, "src");

  const nextConfigPath = await getNextConfigPath(sys);
  const typescriptEnabled = await hasTsconfig(sys);

  const nextFramework = sys.getFrameworks().find((f) => f.name === "next");
  const features = {
    asyncParams:
      nextFramework?.version?.major !== undefined &&
      nextFramework.version.major >= 15,
  };

  const externalPackages: Record<string, string> = {};
  const nextSys: NextDevToolsSys = {
    ...sys,
    addExternalPackage: (pkgName: string) => {
      externalPackages[sys.join(rootDir, pkgName, "index.ts")] =
        `export * from "${pkgName}";`;
    },
    readFileSync: (path: string) =>
      externalPackages[path] ?? sys.readFileSync(path),

    existsSync: (path: string) =>
      !!externalPackages[path] || sys.existsSync(path),

    readdirSync: (path: string) => {
      const realFiles = sys.readdirSync(path);
      if (path === rootDir) {
        return [
          ...realFiles,
          ...Object.keys(externalPackages).map((p) =>
            p.slice(rootDir.length + 1),
          ),
        ];
      }
      return realFiles;
    },
    readdir: async (path: string) => {
      const realFiles = await sys.readdir(path);
      if (path === rootDir) {
        return [
          ...realFiles,
          ...Object.keys(externalPackages).map((p) =>
            p.slice(rootDir.length + 1),
          ),
        ];
      }
      return realFiles;
    },
    appDir: null,
    pagesDir: null,
    srcDir: null,
    builderComponentPath: "",
    componentsDir: "",
    registryPath: "",
    nextConfigPath,
    typescriptEnabled,
    features,
  };

  if (sys.sdkVersion) {
    nextSys.sdkVersion = sys.sdkVersion;
  } else {
    nextSys.sdkVersion = await detectReactSdkVersion(nextSys);
  }

  if (await sys.exists(srcDir)) {
    // src dir
    nextSys.srcDir = srcDir;
    nextSys.componentsDir = sys.join(srcDir, "components");

    const srcAppDir = sys.join(srcDir, "app");
    if (await sys.exists(srcAppDir)) {
      nextSys.appDir = srcAppDir;
    }

    const srcPagesDir = sys.join(srcDir, "pages");
    if (await sys.exists(srcPagesDir)) {
      nextSys.pagesDir = srcPagesDir;
    }

    nextSys.registryPath = await findBuilderRegistry(sys, srcDir);
  } else {
    // no src dir
    nextSys.componentsDir = sys.join(rootDir, "components");
    nextSys.registryPath = await findBuilderRegistry(sys, rootDir);
  }

  nextSys.builderComponentPath = sys.join(
    nextSys.componentsDir,
    `builder.${typescriptEnabled ? "tsx" : "jsx"}`,
  );

  if (!nextSys.appDir) {
    const appDir = sys.join(rootDir, "app");
    if (await sys.exists(appDir)) {
      nextSys.appDir = appDir;
    }
  }

  if (!nextSys.pagesDir) {
    const pagesDir = sys.join(rootDir, "pages");
    if (await sys.exists(pagesDir)) {
      nextSys.pagesDir = pagesDir;
    }
  }

  if (!nextSys.appDir && !nextSys.pagesDir && !sys.ignoreMissingConfig) {
    throw new Error(
      `Could not find Next.js "app" or "pages" directory. Searched in "${rootDir}".`,
    );
  }

  return nextSys;
}

async function getNextConfigPath(sys: DevToolsSys) {
  const nextConfigTsPath = sys.join(sys.getAppRootDir(), "next.config.ts");
  if (await sys.exists(nextConfigTsPath)) {
    return nextConfigTsPath;
  }

  const nextConfigMjsPath = sys.join(sys.getAppRootDir(), "next.config.mjs");
  if (await sys.exists(nextConfigMjsPath)) {
    return nextConfigMjsPath;
  }

  const nextConfigJsPath = sys.join(sys.getAppRootDir(), "next.config.js");
  if (await sys.exists(nextConfigJsPath)) {
    return nextConfigJsPath;
  }

  throw new Error(
    `Could not find Next.js config file. Searched for "next.config.ts", "next.config.mjs" and "next.config.js" in "${sys.getAppRootDir()}".`,
  );
}

/**
 * Any Next.js-specific features that are enabled. This can be used to adjust how
 * Dev Tools integrates with Next.js apps based on the installed version of
 * Next.js.
 */
interface NextDevToolsFeatures {
  /**
   * If `true`, Next.js params will be awaited before accessed.
   * Only applies to Next.js 15+
   */
  asyncParams: boolean;
}

export interface NextDevToolsSys extends DevToolsSys {
  appDir: string | null;
  pagesDir: string | null;
  srcDir: string | null;
  componentsDir: string;
  builderComponentPath: string;
  registryPath: string;
  nextConfigPath: string;
  typescriptEnabled: boolean;
  addExternalPackage: (pkgName: string) => void;
  features: NextDevToolsFeatures;
}
