import type ts from "typescript";
import type { QwikDevToolsSys } from "./index";
import type { ComponentInfo } from "../../../types";
import { codeToSource } from "../../../common/ast/transform";
import {
  getComponentDisplayName,
  getComponentId,
  getComponentDisplayNameFromFilePath,
  getComponentImportNameFilePath,
  getDisplayFilePath,
  getComponentImportPath,
} from "../../../common/utils";
import {
  getExportedStatements,
  type ExportedStatement,
} from "../../../common/ast/exported-statements";

export async function getQwikComponentsFromDir(
  sys: QwikDevToolsSys,
  tsProgram: ts.Program,
) {
  const cmps: QwikComponentSource[] = [];
  const sourceFiles = tsProgram.getSourceFiles();
  const typeChecker = tsProgram.getTypeChecker();

  await Promise.all(
    sourceFiles.map(async (sourceFile) => {
      const fileName = sys.basename(sourceFile.fileName);
      if (fileName.startsWith(".")) {
        return;
      }
      if (QWIK_SKIP.has(fileName)) {
        return;
      }
      const filePath = sys.normalize(sourceFile.fileName);
      if (filePath === sys.registryPath) {
        return;
      }
      const fileCmps = await getQwikComponentsFromSourceFile(
        sys,
        typeChecker,
        filePath,
        sourceFile,
      );
      cmps.push(...fileCmps);
    }),
  );

  return cmps;
}

export async function getQwikComponentsFromPath(
  sys: QwikDevToolsSys,
  filePath: string,
) {
  const ext = sys.extname(filePath).toLowerCase();
  if (ext === ".tsx") {
    const code = await sys.readFile(filePath);
    if (code) {
      return getQwikComponentsFromCode(sys, filePath, code);
    }
  }
  return null;
}

export function getQwikComponentsFromCode(
  sys: QwikDevToolsSys,
  filePath: string,
  code: string,
) {
  const sourceFile = codeToSource(sys, code);
  return getQwikComponentsFromSourceFile(sys, null, filePath, sourceFile);
}

export async function getQwikComponentsFromSourceFile(
  sys: QwikDevToolsSys,
  typeChecker: ts.TypeChecker | null,
  filePath: string,
  sourceFile: ts.SourceFile,
) {
  filePath = sys.normalize(filePath);
  const fileCmps: QwikComponentSource[] = [];

  const exports = getExportedStatements(sys, sourceFile);

  await Promise.all(
    exports.map(async (exp) => {
      const sCmp = await getQwikComponentSource(
        sys,
        typeChecker,
        filePath,
        exp,
      );
      if (sCmp) {
        fileCmps.push(sCmp);
      }
    }),
  );

  return fileCmps;
}

async function getQwikComponentSource(
  sys: QwikDevToolsSys,
  _typeChecker: ts.TypeChecker | null,
  filePath: string,
  exp: ExportedStatement,
): Promise<QwikComponentSource | null> {
  const relFilePath = sys.relative(sys.getAppRootDir(), filePath);
  const importPath = getComponentImportPath(sys, filePath);

  if (sys.ts.isVariableStatement(exp.node)) {
    for (const declaration of exp.node.declarationList.declarations) {
      const cmpExp = getQwikComponentExpression(sys, declaration.initializer);
      if (cmpExp && sys.ts.isIdentifier(declaration.name)) {
        const cmpName = declaration.name.text;
        return {
          cmpInfo: {
            id: await getComponentId(sys, filePath, cmpName),
            displayFilePath: getDisplayFilePath(sys, filePath),
            name: getComponentDisplayName(cmpName),
            inputs: [],
            exportName: exp.isDefaultExport ? "default" : cmpName,
            exportType: exp.isDefaultExport ? "default" : "named",
            importName: cmpName,
            filePath,
            relFilePath,
            importPath,
            framework: "qwik",
          },
          node: cmpExp,
        };
      }
    }
  } else if (sys.ts.isCallExpression(exp.node)) {
    const cmpExp = getQwikComponentExpression(sys, exp.node);
    if (cmpExp) {
      return {
        cmpInfo: {
          id: await getComponentId(sys, filePath, "default"),
          displayFilePath: getDisplayFilePath(sys, filePath),
          name: getComponentDisplayNameFromFilePath(sys, filePath),
          inputs: [],
          exportName: "default",
          exportType: "default",
          importName: getComponentImportNameFilePath(sys, filePath),
          filePath,
          relFilePath,
          importPath,
          framework: "qwik",
        },
        node: cmpExp,
      };
    }
  }
  return null;
}

function getQwikComponentExpression(
  sys: QwikDevToolsSys,
  exp: ts.Expression | undefined,
) {
  if (exp && sys.ts.isCallExpression(exp)) {
    if (
      sys.ts.isIdentifier(exp.expression) &&
      exp.expression.text === "component$" &&
      exp.arguments.length > 0
    ) {
      const arg = exp.arguments[0];
      if (sys.ts.isArrowFunction(arg) || sys.ts.isFunctionExpression(arg)) {
        const srcNode: QwikComponentSourceNode = {
          callExpression: exp,
          fnNode: arg,
        };
        return srcNode;
      }
    }
  }
  return null;
}

export interface QwikComponentSourceNode {
  callExpression: ts.CallExpression;
  fnNode: ts.Node;
}

export interface QwikComponentSource {
  cmpInfo: ComponentInfo;
  node: QwikComponentSourceNode;
}

const QWIK_SKIP = new Set([
  "node_modules",
  "layout.tsx",
  "404.tsx",
  "router-head.tsx",
]);
