import type { DevToolsSys } from "../types";

export async function getBuilderCache(sys: DevToolsSys) {
  const cachePath = getCachePath(sys);
  const contents = await sys.readFile(cachePath);
  if (typeof contents === "string") {
    return JSON.parse(contents);
  }
  return {};
}

export async function setBuilderCache(
  sys: DevToolsSys,
  cache: Record<string, any>,
) {
  cache = typeof cache === "object" && cache != null ? cache : {};
  const cachePath = getCachePath(sys);
  await sys.writeFile(cachePath, JSON.stringify(cache ?? {}, null, 2));
}

function getCachePath(sys: DevToolsSys) {
  const rootDir = sys.getAppRootDir();
  return sys.join(rootDir, "node_modules", ".builder", "builder-cache.json");
}
