import type ts from "typescript";
import type { DevToolsSys } from "../../types";

export function codeToSource(sys: DevToolsSys, code: string | null = "") {
  return sys.ts.createSourceFile(
    "module.tsx",
    typeof code === "string" ? code : "",
    sys.ts.ScriptTarget.Latest,
    undefined,
  );
}

export function codeToStatement(sys: DevToolsSys, code: string | null = "") {
  const sourceFile = codeToSource(sys, code?.trim());
  if (sourceFile.statements.length === 0) {
    throw new Error(`No statements found in code: ${code}`);
  }
  return sourceFile.statements[0];
}

export function sourceToCode(
  sys: DevToolsSys,
  node: ts.Node | null | undefined,
  removeComments?: boolean,
) {
  if (node) {
    const printer = sys.ts.createPrinter({
      newLine: sys.ts.NewLineKind.LineFeed,
      removeComments,
    });
    if (sys.ts.isSourceFile(node)) {
      return printer.printFile(node).trim() + "\n";
    }

    return (
      printer
        .printNode(
          sys.ts.EmitHint.Unspecified,
          node,
          sys.ts.createSourceFile("module.tsx", "", sys.ts.ScriptTarget.Latest),
        )
        .trim() + "\n"
    );
  }
  return "";
}
