import type ts from "typescript";
import type { DevToolsSys, ModuleFormat } from "../../types";

export function getExportedStatements(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  moduleType: ModuleFormat = "esm",
) {
  if (moduleType === "cjs") {
    return getCjsExportedStatements(sys, sourceFile);
  } else {
    return getEsmExportedStatements(sys, sourceFile);
  }
}

function getEsmExportedStatements(sys: DevToolsSys, sourceFile: ts.SourceFile) {
  const exports: ExportedStatement[] = [];

  for (const s of sourceFile.statements) {
    if (sys.ts.isExportDeclaration(s)) {
      if (s.exportClause && sys.ts.isNamedExports(s.exportClause)) {
        for (const elm of s.exportClause.elements) {
          if (s.moduleSpecifier) {
            exports.push({
              isDefaultExport: false,
              statement: s,
              node: elm,
            });
          }
          if (sys.ts.isIdentifier(elm.name)) {
            const identifierName = elm.name.text;
            for (const s2 of sourceFile.statements) {
              if (sys.ts.isVariableStatement(s2)) {
                for (const d of s2.declarationList.declarations) {
                  if (
                    sys.ts.isIdentifier(d.name) &&
                    d.name.text === identifierName
                  ) {
                    exports.push({
                      isDefaultExport: false,
                      statement: s2,
                      node: s2,
                    });
                  }
                }
              } else if (sys.ts.isFunctionDeclaration(s2)) {
                if (s2.name && s2.name.text === identifierName) {
                  exports.push({
                    isDefaultExport: false,
                    statement: s2,
                    node: s2,
                  });
                }
              }
            }
          }
        }
      } else if (!s.exportClause && s.moduleSpecifier) {
        // Handle "export * from 'module'" case
        exports.push({
          isDefaultExport: false,
          statement: s,
          node: s,
        });
      }
      continue;
    }

    if (sys.ts.isExportAssignment(s)) {
      const isDefaultExport = s.flags === sys.ts.NodeFlags.None;

      const exportedIdentifier = s.expression;
      if (sys.ts.isIdentifier(exportedIdentifier)) {
        const identifierName = exportedIdentifier.text;
        for (const s2 of sourceFile.statements) {
          if (sys.ts.isVariableStatement(s2)) {
            for (const d of s2.declarationList.declarations) {
              if (
                sys.ts.isIdentifier(d.name) &&
                d.name.text === identifierName
              ) {
                exports.push({ isDefaultExport, statement: s2, node: s2 });
              }
            }
          }
          if (sys.ts.isFunctionDeclaration(s2)) {
            if (s2.name?.text === identifierName) {
              exports.push({ isDefaultExport, statement: s2, node: s2 });
            }
          }
        }
      } else if (
        sys.ts.isArrowFunction(exportedIdentifier) ||
        sys.ts.isCallExpression(exportedIdentifier) ||
        sys.ts.isFunctionDeclaration(exportedIdentifier) ||
        sys.ts.isObjectLiteralExpression(exportedIdentifier)
      ) {
        exports.push({
          isDefaultExport,
          statement: s,
          node: exportedIdentifier,
        });
      }
      continue;
    }

    if (
      (sys.ts.isVariableStatement(s) || sys.ts.isFunctionDeclaration(s)) &&
      s.modifiers
    ) {
      if (s.modifiers.some((m) => m.kind === sys.ts.SyntaxKind.ExportKeyword)) {
        exports.push({
          isDefaultExport: s.modifiers.some(
            (m) => m.kind === sys.ts.SyntaxKind.DefaultKeyword,
          ),
          statement: s,
          node: s,
        });
        continue;
      }
    }
  }

  return exports;
}

function getCjsExportedStatements(sys: DevToolsSys, sourceFile: ts.SourceFile) {
  const exports: ExportedStatement[] = [];

  for (const s of sourceFile.statements) {
    if (
      sys.ts.isExpressionStatement(s) &&
      sys.ts.isBinaryExpression(s.expression) &&
      sys.ts.isPropertyAccessExpression(s.expression.left)
    ) {
      const left = s.expression.left;
      const right = s.expression.right;

      if (!sys.ts.isIdentifier(left.expression)) {
        continue;
      }

      if (left.expression.text === "module" && left.name.text === "exports") {
        // module.exports = ...

        if (sys.ts.isIdentifier(right)) {
          // module.exports = identifier;
          const identifierName = right.text;

          for (const s2 of sourceFile.statements) {
            if (sys.ts.isVariableStatement(s2)) {
              for (const d of s2.declarationList.declarations) {
                if (
                  sys.ts.isIdentifier(d.name) &&
                  d.name.text === identifierName
                ) {
                  exports.push({
                    isDefaultExport: true,
                    statement: s2,
                    node: s2,
                  });
                }
              }
            } else if (sys.ts.isFunctionDeclaration(s2)) {
              if (s2.name && s2.name.text === identifierName) {
                exports.push({
                  isDefaultExport: true,
                  statement: s2,
                  node: s2,
                });
              }
            }
          }
        } else {
          exports.push({
            isDefaultExport: true,
            statement: s,
            node: right,
          });
        }

        continue;
      }
    }
  }

  return exports;
}

export interface ExportedStatement {
  isDefaultExport: boolean;
  statement: ts.Statement;
  node: ts.Node;
}
