import type { DevToolsSys, EnvInfo } from "../../../types";
import { getDotEnvValue, setDotEnvVar } from "../../../common/dotenv";

export async function getRemixApiKey(sys: DevToolsSys) {
  const dotEnvFileNames = [".env"];

  const env = await getDotEnvValue(
    sys,
    dotEnvFileNames,
    REMIX_BUILDER_API_KEY_ENV,
  );
  return env;
}

export async function setRemixApiKey(sys: DevToolsSys, publicApiKey: string) {
  const env = await getRemixApiKey(sys);

  const modifiedType = await setDotEnvVar(
    sys,
    env.file,
    env.envKey,
    publicApiKey,
  );

  const updatedEnv: EnvInfo = {
    ...env,
    modifiedType,
  };
  return updatedEnv;
}

export const REMIX_BUILDER_API_KEY_ENV = `PUBLIC_BUILDER_KEY`;
