import type {
  CreateDevToolsOptions,
  DevToolsAdapter,
  DevToolsSys,
} from "../../../types";
import { getBuilderCache, setBuilderCache } from "../../../common/cache";
import { getAngularApiKey, setAngularApiKey } from "./angular-api-key";
import { angularComponentRegistry } from "./angular-registry";
import {
  angularEnsureBuilderSetup,
  angularEnsureFigmaImportSetup,
} from "./angular-ensure-builder-setup";
import { findBuilderRegistry, hasTsconfig } from "../../../common/fs";

export async function createAngularDevTools(sys: CreateDevToolsOptions) {
  const angularSys = await createAngularDevToolsSys(sys);

  const externalPackages: Record<string, string> = {};
  const devTools: DevToolsAdapter = {
    builderPageOptions: async () => {
      return {
        templateContentId: "f0b8016ac92e474699293211a268ebaa",
        pathname: "/builder-demo",
        title: "Builder + Angular Demo Page",
      };
    },
    ensureBuilderSetup: () => {
      return angularEnsureBuilderSetup(angularSys);
    },
    ensureFigmaImportPage: () => {
      return angularEnsureFigmaImportSetup(angularSys);
    },

    getRegistryPath: () => {
      return angularSys.registryPath;
    },
    getRegistry: (opts) => {
      return angularComponentRegistry(
        angularSys,
        null,
        false,
        false,
        null,
        null,
        false,
        !!opts?.readAllInputTypes,
      );
    },
    addExternalPackage: async (pkgName: string) => {
      if (!externalPackages[pkgName]) {
        externalPackages[pkgName] = `export * from "${pkgName}";`;
      }
    },
    loadComponent: async (opts) => {
      const r = await angularComponentRegistry(
        angularSys,
        opts.cmpId,
        false,
        false,
        null,
        null,
        false,
        false,
      );
      return {
        ...r,
        component: r.components.find((c) => c.id === opts.cmpId)!,
      };
    },
    registerComponent: (opts) => {
      return angularComponentRegistry(
        angularSys,
        opts.cmpId,
        true,
        false,
        null,
        null,
        true,
        false,
      );
    },
    setRegisteredComponentInfo: (opts) => {
      return angularComponentRegistry(
        angularSys,
        opts.cmpId,
        false,
        false,
        opts,
        null,
        true,
        false,
      );
    },
    setRegisteredComponentInput: (opts) => {
      return angularComponentRegistry(
        angularSys,
        opts.cmpId,
        false,
        false,
        null,
        opts,
        true,
        false,
      );
    },
    unregisterComponent: (opts) => {
      return angularComponentRegistry(
        angularSys,
        opts.cmpId,
        false,
        true,
        null,
        null,
        true,
        false,
      );
    },
    getPublicApiKey: () => {
      return getAngularApiKey(angularSys);
    },
    setPublicApiKey: (opts) => {
      return setAngularApiKey(angularSys, opts.publicApiKey);
    },
    getDependencies: (_opts) => {
      return [
        { name: "@builder.io/dev-tools" },
        { name: "@builder.io/sdk-angular" },
        { name: "concurrently" }, // For running dev tools server and angular dev server concurrently
      ];
    },
    getDevRunCommand: () => {
      return "builder-dev";
    },
    getCache: () => getBuilderCache(angularSys),
    setCache: (cache) => setBuilderCache(angularSys, cache),
  };

  return devTools;
}

export async function createAngularDevToolsSys(sys: DevToolsSys) {
  const angularConfigPath = await getAngularConfigPath(sys);
  const rootDir = sys.getAppRootDir();
  const srcDir = sys.join(rootDir, "src");

  const typescriptEnabled = await hasTsconfig(sys);

  const appDir = sys.join(srcDir, "app");

  const registryPath = await findBuilderRegistry(sys, appDir);

  const angularSys: AngularDevToolsSys = {
    ...sys,
    srcDir: "",
    appDir: "",
    componentsDir: "",
    registryPath,
    angularConfigPath,
    typescriptEnabled,
    builderPageComponentPath: "",
    hasAppModule: false, // If app.module is not present components will be standalone
  };

  if (!(await sys.exists(srcDir))) {
    throw new Error(
      `Could not find src directory. Searched for src in "${sys.getAppRootDir()}".`,
    );
  }

  angularSys.srcDir = srcDir;
  angularSys.appDir = appDir;
  angularSys.componentsDir = sys.join(angularSys.appDir, "components");

  angularSys.builderPageComponentPath = sys.join(
    angularSys.componentsDir,
    `builder-page.component.${typescriptEnabled ? "ts" : "js"}`,
  );

  const appModuleFileName = `app.module.${typescriptEnabled ? "ts" : "js"}`;
  const appModulePath = sys.join(angularSys.appDir, appModuleFileName);

  if (await sys.exists(appModulePath)) {
    angularSys.hasAppModule = true;
  }

  return angularSys;
}

async function getAngularConfigPath(sys: DevToolsSys) {
  const configPath = sys.join(sys.getAppRootDir(), "angular.json");
  if (await sys.exists(configPath)) {
    return configPath;
  }

  const projectPath = sys.join(sys.getAppRootDir(), "project.json");
  if (await sys.exists(projectPath)) {
    return projectPath;
  }

  if (sys.ignoreMissingConfig) {
    return "";
  }
  throw new Error(
    `Could not find Angular config file. Searched for angular.json & project.json in "${sys.getAppRootDir()}".`,
  );
}

export interface AngularDevToolsSys extends DevToolsSys {
  appDir: string;
  srcDir: string;
  componentsDir: string;
  registryPath: string;
  angularConfigPath: string;
  typescriptEnabled: boolean;
  builderPageComponentPath: string;
  hasAppModule: boolean;
}
