import { transpileFile, transpileModule } from "../common/typescript";
import { readConfigFile } from "../node/node-sys";
import type {
  ApiResponse,
  ApiRequest,
  DevToolsServerContext,
  SPACE_KIND_VALUES,
  DevToolsSys,
} from "../types";
import { connectBuilder, validateBuilder } from "./builder-connect";

export async function handleDevApiRequest(
  ctx: DevToolsServerContext,
  apiReq: ApiRequest,
): Promise<ApiResponse> {
  const result: ApiResponse = {
    type: apiReq.type,
  };

  switch (apiReq.type) {
    case "getCache": {
      result.data = await ctx.getCache();
      break;
    }
    case "setCache": {
      await ctx.setCache(apiReq.data);
      break;
    }
    case "connectBuilder": {
      result.data = await connectBuilder(
        ctx,
        apiReq.data.publicApiKey,
        apiReq.data.privateAuthKey,
        apiReq.data.kind as SPACE_KIND_VALUES,
      );
      break;
    }
    case "enableDevTools": {
      result.data = enableDevTools(ctx, apiReq.data.enabled);
      break;
    }
    case "getRegistry": {
      result.data = await ctx.getRegistry(apiReq.data);
      break;
    }
    case "launchEditor": {
      result.data = await ctx.launchEditor(apiReq.data);
      break;
    }
    case "validateBuilder": {
      result.data = await validateBuilder(ctx);
      break;
    }
    case "loadComponent": {
      result.data = await ctx.loadComponent(apiReq.data);
      break;
    }
    case "registerComponent": {
      result.data = await ctx.registerComponent(apiReq.data);
      ctx.serverShouldRestart = true;
      break;
    }
    case "setComponentInfo": {
      result.data = await ctx.setRegisteredComponentInfo(apiReq.data);
      ctx.serverShouldRestart = true;
      break;
    }
    case "setComponentInput": {
      result.data = await ctx.setRegisteredComponentInput(apiReq.data);
      ctx.serverShouldRestart = true;
      break;
    }
    case "unregisterComponent": {
      result.data = await ctx.unregisterComponent(apiReq.data);
      ctx.serverShouldRestart = true;
      break;
    }
    case "getFrameworks": {
      result.data = ctx.getFrameworks();
      break;
    }
    case "readdir": {
      if (!isValidFileRequest(ctx, apiReq.path)) {
        result.errors = [`Invalid file request: ${apiReq.path}`];
      } else {
        result.data = await ctx.readdir(apiReq.path, true);
      }

      break;
    }
    case "readFile": {
      if (!isValidFileRequest(ctx, apiReq.path)) {
        result.errors = [`Invalid file request: ${apiReq.path}`];
      } else {
        result.data = await ctx.readFile(apiReq.path);
      }
      break;
    }
    case "writeFile": {
      if (!isValidFileRequest(ctx, apiReq.path)) {
        result.errors = [`Invalid file request: ${apiReq.path}`];
      } else {
        await ctx.writeFile(apiReq.path, apiReq.content);
      }
      break;
    }
    case "ensureFigmaImportPage": {
      result.data = await ctx.ensureFigmaImportPage();
      break;
    }
    case "transileFile": {
      result.data = await transpileFile(ctx, apiReq);
      break;
    }
    case "transileModule": {
      result.data = transpileModule(ctx, apiReq);
      break;
    }
    case "getPastSyncInfo": {
      result.data = await ctx.getPastSyncInfo?.(apiReq.data);
      break;
    }
    case "resyncSnippet": {
      result.data = await ctx.resyncSnippet?.(apiReq.data);
      break;
    }
    case "localConfig": {
      result.data = await readConfigFile();
      break;
    }
    default: {
      result.errors = [`Unknown request type: ${JSON.stringify(apiReq)}`];

      // exhaustiveness check
      // this will cause a compile error if we forget to handle a request type
      const _exhaustiveCheck: never = apiReq;
      _exhaustiveCheck;
    }
  }

  return result;
}

function isValidFileRequest(sys: DevToolsSys, path: string) {
  if (!path) {
    return false;
  }

  if (path.includes("..")) {
    return false;
  }

  path = path.replace(/\\/g, "/");

  const parts = path.split("/");
  const last = parts[parts.length - 1];
  if (last.length > 0) {
    let ext = last.split(".").pop();
    if (ext) {
      ext = `.${ext}`.toLocaleLowerCase();
      ext = ext.split("?")[0];
      if (!EXT_WHITELIST.includes(ext)) {
        return false;
      }
    }
  }
  if (!validatePath(sys, path)) {
    return false;
  }
  return false;
}

function validatePath(sys: DevToolsSys, userPath: string) {
  if (!userPath) {
    return false;
  }

  // Normalize and resolve the paths to ensure they are absolute and clean
  userPath = sys.normalize(sys.resolve(userPath));

  const rootDir = sys.getAppRootDir();

  if (!userPath.startsWith(rootDir) && !userPath.includes("node_modules")) {
    return false;
  }
  return true;
}

const EXT_WHITELIST = [
  ".js",
  ".jsx",
  ".ts",
  ".tsx",
  ".json",
  ".css",
  ".scss",
  ".sass",
  ".less",
  ".html",
  ".htm",
  ".xml",
  ".svg",
  ".png",
  ".jpg",
  ".jpeg",
  ".gif",
  ".ico",
  ".webp",
  ".woff",
  ".woff2",
  ".ttf",
  ".eot",
  ".mp4",
  ".webm",
  ".ogg",
  ".md",
  ".txt",
  ".csv",
  ".pdf",
];

function enableDevTools(
  ctx: DevToolsServerContext,
  isDevToolsEnabled: boolean,
) {
  if (!isDevToolsEnabled && ctx.serverShouldRestart) {
    // devtools no longer showing
    // and they've made file changes while it was opened
    // so we should restart the server now
    setTimeout(() => {
      console.log(`\nRestarting server after Builder Devtools update...\n`);
      ctx.restartAppServer();
    }, 10);
  }

  ctx.enableAppWatch(!isDevToolsEnabled);

  return {
    enabled: isDevToolsEnabled,
    platform: ctx.platform(),
  };
}
