import type { DevToolsSys } from "../types";

export interface Package {
  name: string;
  subPackages: string[];
}

export type DependencyStructure = Package[];

export async function findAllDependencies(
  sys: DevToolsSys,
  rootDir: string,
): Promise<DependencyStructure> {
  const packages: DependencyStructure = [];
  const processedPackages: Set<string> = new Set();

  async function processPackage(packageDir: string): Promise<Package | null> {
    const packageJsonPath = sys.join(packageDir, "package.json");

    try {
      const hasPackageJson = await sys.exists(packageJsonPath);
      if (!hasPackageJson) {
        return null;
      }
      const packageJsonContent = await sys.readFile(packageJsonPath);
      if (packageJsonContent === null) {
        throw new Error(`Failed to read package.json at ${packageJsonPath}`);
      }
      const packageJson = JSON.parse(packageJsonContent);

      if (processedPackages.has(packageJson.name)) {
        return null; // Skip if already processed
      }
      processedPackages.add(packageJson.name);

      const currentPackage: Package = {
        name: packageJson.name,
        subPackages: [],
      };

      // Check for subpackages within the current package
      const packageContents = await sys.readdir(packageDir);
      for (const item of packageContents) {
        if (
          item !== "node_modules" &&
          (await sys.stat(sys.join(packageDir, item))).isDirectory()
        ) {
          currentPackage.subPackages.push(item);
        }
      }

      packages.push(currentPackage);
      return currentPackage;
    } catch (error) {
      sys.Sentry?.captureException(error, {
        level: "warning",
      });
      return null;
    }
  }

  // Process main package dependencies
  try {
    const mainPackageJsonPath = sys.join(rootDir, "package.json");
    const hasPackageJson = await sys.exists(mainPackageJsonPath);
    if (hasPackageJson) {
      const mainPackageJsonContent = await sys.readFile(mainPackageJsonPath);
      if (mainPackageJsonContent !== null) {
        const mainPackageJson = JSON.parse(mainPackageJsonContent);
        const allDependencies = {
          ...mainPackageJson.dependencies,
          ...mainPackageJson.devDependencies,
        };

        for (const depName of Object.keys(allDependencies)) {
          const depPath = sys.join(rootDir, "node_modules", depName);
          await processPackage(depPath);
        }

        // Process workspaces if present
        if (mainPackageJson.workspaces) {
          for (const workspace of mainPackageJson.workspaces) {
            const workspacePaths = await sys.readdir(
              sys.join(rootDir, workspace),
            );
            for (const workspacePath of workspacePaths) {
              const fullWorkspacePath = sys.join(
                rootDir,
                workspace,
                workspacePath,
              );
              await processPackage(fullWorkspacePath);
            }
          }
        }
      }
    }
  } catch (error) {
    sys.Sentry?.captureException(error, {
      level: "warning",
    });
  }

  return packages;
}
