import type { BuildToolConfig, DevToolsSys } from "../types";
import { detectFrameworks } from "./detect-frameworks";
import { normalizeLineBreaks } from "../common/ast/normalize-statements";
import { nextEnsureConfigPlugin } from "./adapters/next/next-ensure-config-plugin";
import { remixEnsureConfigPlugin } from "./adapters/remix/remix-ensure-config-plugin";
import { viteEnsureConfigPlugin } from "./adapters/vite/vite-ensure-config-plugin";
import { webpackEnsureConfigPlugin } from "./adapters/webpack/webpack-ensure-config-plugin";
import { angularEnsureConfigPlugin } from "./adapters/angular/angular-ensure-config-plugin";

const BUILD_TOOLS = [
  {
    id: "next",
    configFileNames: ["next.config.ts", "next.config.js", "next.config.mjs"],
    ensureConfigPlugin: nextEnsureConfigPlugin,
  },
  {
    id: "vite",
    configFileNames: ["vite.config.ts", "vite.config.js"],
    ensureConfigPlugin: viteEnsureConfigPlugin,
  },
  {
    id: "remix",
    configFileNames: ["remix.config.ts", "remix.config.js"],
    ensureConfigPlugin: remixEnsureConfigPlugin,
  },
  {
    id: "webpack",
    configFileNames: ["webpack.config.ts", "webpack.config.js"],
    ensureConfigPlugin: webpackEnsureConfigPlugin,
  },
  {
    id: "angular",
    configFileNames: ["angular.json", "project.json"],
    ensureConfigPlugin: angularEnsureConfigPlugin,
  },
];

export async function findBuildToolConfig(sys: DevToolsSys) {
  const fsRoot = sys.resolve("/");

  for (const b of BUILD_TOOLS) {
    for (const fileName of b.configFileNames) {
      let dir = sys.getAppRootDir(); // Instead of using sys.cwd() use the root dir
      for (let i = 0; i < 10; i++) {
        try {
          const filePath = sys.join(dir, fileName);
          const content = sys.readFileSync(filePath);
          if (typeof content === "string") {
            const buildToolConfig: BuildToolConfig = {
              id: b.id,
              filePath,
              content,
            };
            return buildToolConfig;
          }

          if (dir === fsRoot) {
            break;
          }
          dir = sys.dirname(dir);
        } catch (e: any) {
          const message = e?.message ?? "";
          // Check next config file if current one doesn't exist
          if (message.includes("Invalid path")) {
            break;
          } else {
            throw e;
          }
        }
      }
    }
  }

  const frameworks = await detectFrameworks(sys);

  if (frameworks.some((f) => f.name === "next")) {
    const filePath = sys.join(sys.getAppRootDir(), `next.config.js`);
    await sys.writeFile(filePath, DEFAULT_NEXT_CONFIG);
    const nextConfig: BuildToolConfig = {
      id: "next",
      filePath,
      content: DEFAULT_NEXT_CONFIG,
    };
    return nextConfig;
  }

  return null;
}

export async function ensureConfigPlugin(sys: DevToolsSys) {
  const config = await findBuildToolConfig(sys);
  if (!config) {
    throw new Error(
      `Unable to find config file, such as next.config.js or vite.config.js.`,
    );
  }

  const b = BUILD_TOOLS.find((b) => b.id === config!.id)!;

  const result = await b.ensureConfigPlugin(
    sys,
    config.filePath,
    config.content,
  );

  if (result.hasContentUpdates) {
    result.content = normalizeLineBreaks(result.content);
    result.content = await sys.formatCode(result.filePath, result.content);
    await sys.writeFile(config.filePath, result.content);
  }

  return result;
}

const DEFAULT_NEXT_CONFIG = `
/** @type {import('next').NextConfig} */
const nextConfig = {
}
 
module.exports = nextConfig
`.trimStart();
