import { execSync } from "node:child_process";
import { estimateCodeTokens } from "../common/estimate-code-tokens";
import type { DevToolsSys } from "../types";
import path from "node:path";

const MAXFILES = 50;
const MAX_TOKENS = 100_000;
const MIN_FILES = 2;
const HIGH_IMPORTANCE_THRESHOLD = 2; // Files with importance >= 2 are considered high importance

export interface GitRankingParams {
  sys: DevToolsSys;
  appRootDir: string;
  allFiles: string[];
  files: string[];
  hiddenFiles: string[];
  selectedFilePaths: Map<string, number>;
  promptRelevantFiles: string[];
}

export async function processGitBasedRanking({
  sys,
  appRootDir,
  allFiles,
  files,
  hiddenFiles,
  selectedFilePaths,
  promptRelevantFiles,
}: GitRankingParams) {
  let totalTokens = 0;
  const skippedLargeFiles: string[] = [];

  // Get git-based file ranking
  const gitFiles = await getGitModifiedFiles(sys, appRootDir);
  const rankedFiles = allFiles
    .map((file) => {
      const baseImportance = shouldIncludeFile(file, {
        foundFiles: [],
        allFiles,
        selectedFilePaths,
        cwd: sys.cwd(),
        appRootDir,
        fallbackImportance: 1,
      });

      // Only include files that pass shouldIncludeFile rules
      if (baseImportance === 0) return null;

      const gitImportance = calculateGitImportance(
        file,
        gitFiles,
        baseImportance,
        promptRelevantFiles,
      );

      return {
        file,
        importance: Math.max(baseImportance, gitImportance),
      };
    })
    .filter(
      (item): item is { file: string; importance: number } =>
        item !== null && item.importance > 0,
    )
    .sort((a, b) => b.importance - a.importance);

  // Take top files up to MAXFILES
  const highImportanceCount = rankedFiles.filter(
    (item) => item.importance >= HIGH_IMPORTANCE_THRESHOLD,
  ).length;

  const filesWithMetadata = rankedFiles.map(({ file, importance }) => {
    const { tokens } = getFileMetadata(sys, appRootDir, file);
    const maxTokens = calculateMaxFileTokens(importance, highImportanceCount);
    return { file, importance, tokens, maxTokens };
  });

  // Sort files considering both importance and size
  filesWithMetadata.sort((a, b) => {
    // If one file exceeds its token limit and the other doesn't, prioritize the one within limit
    const aExceedsLimit = a.tokens > a.maxTokens;
    const bExceedsLimit = b.tokens > b.maxTokens;
    if (aExceedsLimit !== bExceedsLimit) return aExceedsLimit ? 1 : -1;

    // If both files are within limits or both exceed them,
    // compare importance first, then prefer smaller files if importance is equal
    if (a.importance !== b.importance) return b.importance - a.importance;
    return a.tokens - b.tokens;
  });

  // Process files in ranked order
  for (const { file, importance, tokens, maxTokens } of filesWithMetadata) {
    if (tokens > maxTokens) {
      skippedLargeFiles.push(file);
      continue;
    }

    if (totalTokens + tokens <= MAX_TOKENS && files.length < MAXFILES) {
      files.push(file);
      totalTokens += tokens;
      selectedFilePaths.set(file, importance);
    } else if (shouldIncludeHiddenFile(sys, file)) {
      hiddenFiles.push(file);
    }
  }

  // Update selectedFilePaths with final importance scores
  rankedFiles.forEach(({ file, importance }) => {
    selectedFilePaths.set(file, clampImportance(importance));
  });

  // If we haven't met minimum file count, try to include truncated versions of important skipped files
  if (files.length < MIN_FILES && skippedLargeFiles.length > 0) {
    for (const file of skippedLargeFiles) {
      const content = sys.readFileSync(sys.join(appRootDir, file)) ?? "";
      const importance =
        rankedFiles.find((f) => f.file === file)?.importance ?? 1;
      const maxTokens = calculateMaxFileTokens(importance, highImportanceCount);

      // Take first portion of file up to calculated token limit
      const truncatedContent = content.slice(0, maxTokens * 4); // Rough estimate of chars per token
      const truncatedTokens = estimateCodeTokens(truncatedContent);

      if (
        totalTokens + truncatedTokens <= MAX_TOKENS &&
        files.length < MAXFILES
      ) {
        files.push(file);
        totalTokens += truncatedTokens;
        // Reduce importance for truncated files
        selectedFilePaths.set(file, Math.max(1, importance - 1));
      }
    }
  }

  // Add remaining files to hiddenFiles
  allFiles.forEach((file) => {
    if (!files.includes(file) && shouldIncludeHiddenFile(sys, file)) {
      hiddenFiles.push(file);
    }
  });
}

export function shouldIncludeFile(
  inputFile: string,
  ctx: {
    foundFiles: string[];
    allFiles: string[];
    selectedFilePaths: Map<string, number>;
    cwd: string;
    appRootDir: string;
    fallbackImportance: number | 0;
  },
): number | 0 {
  const {
    foundFiles,
    allFiles,
    selectedFilePaths,
    cwd,
    appRootDir,
    fallbackImportance,
  } = ctx;

  let relativeCwd = "";
  // Normalize for windows users
  const file = inputFile.replace(/\\/g, "/");

  if (cwd !== appRootDir) {
    // Normalize path separators to forward slashes
    relativeCwd = path.relative(appRootDir, cwd).replace(/\\/g, "/");
  }

  const subPackages = new Set(
    allFiles
      .map((f) => {
        if (f.includes("/package.json")) {
          return f.replace(/\/package.json$/, "");
        }
      })
      .filter((f): f is string => typeof f === "string"),
  );

  const defaultImportance = selectedFilePaths.get(file) ?? 0;
  const parts = file.split("/");
  const filename = parts[parts.length - 1];
  const isInCwd = relativeCwd ? file.startsWith(relativeCwd + "/") : true;
  let importance = defaultImportance || fallbackImportance;

  // Check if file is in a sub-package
  const fileDir = parts.slice(0, -1).join("/");
  const isInSubPackage = Array.from(subPackages).some((pkg) =>
    fileDir.startsWith(pkg),
  );

  // Handle config files
  const isCommonConfig =
    filename === "package.json" ||
    filename === "tsconfig.json" ||
    filename === "tailwind.config.js" ||
    filename === "tailwind.config.ts" ||
    filename === "next.config.js" ||
    filename === "next.config.ts";

  if (isCommonConfig) {
    if (!isInSubPackage && (parts.length === 1 || isInCwd)) {
      importance = Math.max(importance, 3);
    } else {
      importance = Math.max(importance, 1.5);
    }
  }

  // Handle layout and page files
  const isLayout = filename === "layout.tsx" || filename === "page.tsx";
  if (isLayout) {
    if (isInCwd) {
      importance = Math.max(importance, 3);
    } else {
      importance = Math.max(importance, 2);
    }
  }

  // Handle search results
  if (foundFiles.includes(file)) {
    importance++;
  } else if (!isInCwd) {
    importance -= 0.5;
  }

  // Handle src directories
  const srcIndex = parts.indexOf("src");
  if (srcIndex !== -1) {
    const cwdParts = relativeCwd.split(/[/\\]/);
    const hasCwdSrc = allFiles.some((f) => {
      const fParts = f.split("/");
      return (
        f.startsWith(relativeCwd + "/") &&
        fParts.indexOf("src") === cwdParts.length
      );
    });

    if (hasCwdSrc && srcIndex !== cwdParts.length) {
      importance -= 0.5;
    }
  }

  if (isInSubPackage && !defaultImportance) {
    importance -= 0.5;
  }

  return clampImportance(importance);
}

export interface GitFileInfo {
  filePath: string;
  lastModified: Date;
  frequency: number;
  commitIds: string[]; // Track commits containing this file
  relatedFiles: Set<string>; // Files modified in same commits
}

export function getFileMetadata(
  sys: DevToolsSys,
  appRootDir: string,
  file: string,
) {
  const content = sys.readFileSync(sys.join(appRootDir, file)) ?? "";
  const tokens = estimateCodeTokens(content);
  return { tokens, content };
}

export function calculateMaxFileTokens(
  fileImportance: number,
  highImportanceCount: number,
) {
  if (highImportanceCount === 0) {
    return Math.floor(MAX_TOKENS * 0.15); // Default to 15% if no high importance files
  }

  // Base allocation considering number of high importance files
  const baseAllocation = Math.floor(MAX_TOKENS / (highImportanceCount + 2)); // +2 for buffer

  // Adjust allocation based on file importance
  if (fileImportance >= HIGH_IMPORTANCE_THRESHOLD) {
    return Math.min(
      Math.floor(MAX_TOKENS * 0.3), // Cap at 30%
      baseAllocation * 1.5, // Give high importance files 50% more tokens
    );
  } else {
    return Math.min(
      Math.floor(MAX_TOKENS * 0.15), // Cap at 15%
      baseAllocation * 0.75, // Give lower importance files 25% fewer tokens
    );
  }
}

/**
 * Get recently modified files using git commands with commit relationship tracking
 */
export async function getGitModifiedFiles(
  sys: DevToolsSys,
  appRootDir: string,
  commitCount = 10,
): Promise<Map<string, GitFileInfo>> {
  const fileInfoMap = new Map<string, GitFileInfo>();

  try {
    // Find git root directory
    const gitRootDir = execSync("git rev-parse --show-toplevel", {
      cwd: appRootDir,
      encoding: "utf-8",
      stdio: "pipe",
    })
      .trim()
      .replace(/\\/g, "/");

    // Calculate relative path from git root to appRootDir
    const relativeAppPath = path
      .relative(gitRootDir, appRootDir)
      .replace(/\\/g, "/");

    // Get files and their commits
    const gitLogCommand = `git log -n ${commitCount} --name-only --format="%H"`;
    const logOutput = execSync(gitLogCommand, {
      cwd: appRootDir,
      encoding: "utf-8",
      stdio: "pipe",
    }).split("\n");

    let currentCommit: string | null = null;
    const filesInCommit: string[] = [];

    // Process log output to build commit relationships
    for (const line of logOutput) {
      if (!line) continue;

      if (line.match(/^[0-9a-f]{40}$/)) {
        // This is a commit hash
        if (currentCommit && filesInCommit.length) {
          // Process files from previous commit
          const normalizedFiles = filesInCommit
            .map((file) => normalizeFilePath(file, relativeAppPath))
            .filter((file) => file !== null) as string[];
          if (normalizedFiles.length > 0) {
            updateFileRelationships(
              fileInfoMap,
              normalizedFiles,
              currentCommit,
            );
          }
        }
        currentCommit = line;
        filesInCommit.length = 0;
      } else {
        // This is a file path
        filesInCommit.push(line);
      }
    }

    // Process last commit
    if (currentCommit && filesInCommit.length) {
      const normalizedFiles = filesInCommit
        .map((file) => normalizeFilePath(file, relativeAppPath))
        .filter((file) => file !== null) as string[];
      if (normalizedFiles.length > 0) {
        updateFileRelationships(fileInfoMap, normalizedFiles, currentCommit);
      }
    }

    // Get uncommitted changes
    const uncommittedCommand = "git status --porcelain";
    const uncommittedFiles = execSync(uncommittedCommand, {
      cwd: appRootDir,
      encoding: "utf-8",
      stdio: "pipe",
    })
      .split("\n")
      .filter(Boolean)
      .map((line) => line.slice(3))
      .map((file) => normalizeFilePath(file, relativeAppPath))
      .filter((file): file is string => file !== null);

    // Add uncommitted files
    for (const file of uncommittedFiles) {
      if (!fileInfoMap.has(file)) {
        fileInfoMap.set(file, {
          filePath: file,
          lastModified: new Date(),
          frequency: 1,
          commitIds: [],
          relatedFiles: new Set(),
        });
      }
    }

    // Update last modified dates
    for (const [file, info] of fileInfoMap) {
      try {
        const gitLogDateCommand = `git log -1 --format=%cd --date=iso ${file}`;
        const lastModifiedStr = execSync(gitLogDateCommand, {
          cwd: appRootDir,
          encoding: "utf-8",
          stdio: "pipe",
        }).trim();

        if (lastModifiedStr) {
          info.lastModified = new Date(lastModifiedStr);
        }
      } catch (error) {
        // File might be new
        info.lastModified = new Date();
      }
    }

    return fileInfoMap;
  } catch (error) {
    sys.Sentry?.captureException(new Error("Error getting git history:"), {
      extra: {
        error,
      },
    });
    return new Map();
  }
}

/**
 * Normalizes a file path from git root to be relative to appRootDir
 * Returns null if the file is outside appRootDir
 */
function normalizeFilePath(
  file: string,
  relativeAppPath: string,
): string | null {
  // Normalize path separators
  file = file.replace(/\\/g, "/");

  // If appRootDir is the git root
  if (!relativeAppPath || relativeAppPath === ".") {
    return file;
  }

  // If file is within appRootDir, remove the relative path prefix
  if (file.startsWith(relativeAppPath + "/")) {
    return file.slice(relativeAppPath.length + 1);
  }

  // If file is outside appRootDir
  return null;
}

/**
 * Update file relationships based on files modified in the same commit
 */
export function updateFileRelationships(
  fileInfoMap: Map<string, GitFileInfo>,
  files: string[],
  commitId: string,
) {
  // Create or update file info for each file
  for (const file of files) {
    if (!fileInfoMap.has(file)) {
      fileInfoMap.set(file, {
        filePath: file,
        lastModified: new Date(),
        frequency: 1,
        commitIds: [commitId],
        relatedFiles: new Set(),
      });
    } else {
      const info = fileInfoMap.get(file)!;
      info.frequency++;
      info.commitIds.push(commitId);
    }
  }

  // Update related files
  for (const file of files) {
    const info = fileInfoMap.get(file)!;
    files.forEach((relatedFile) => {
      if (relatedFile !== file) {
        info.relatedFiles.add(relatedFile);
      }
    });
  }
}

/**
 * Calculate importance based on git history and relevant paths
 */
export function calculateGitImportance(
  file: string,
  gitFiles: Map<string, GitFileInfo>,
  baseImportance: number,
  relevantPaths: string[],
): number {
  for (const relevantPath of relevantPaths) {
    // Check if file is in or is the relevant path
    if (file === relevantPath) {
      return 4;
    }
  }
  const fileInfo = gitFiles.get(file);
  if (!fileInfo) return baseImportance;

  // Time-based score
  const now = new Date();
  const daysSinceModified =
    (now.getTime() - fileInfo.lastModified.getTime()) / (1000 * 60 * 60 * 24);
  const timeScore = Math.max(0, 1 - daysSinceModified / 30);

  // Frequency score
  const frequencyScore = Math.min(1, fileInfo.frequency / 5);

  // Directory relationship score
  const fileDir = path.dirname(file);
  let dirScore = 0;
  for (const [otherFile] of gitFiles) {
    if (path.dirname(otherFile) === fileDir) {
      dirScore += 0.2;
    }
  }
  dirScore = Math.min(1, dirScore);

  // Relevant paths score
  let relevanceScore = 0;
  for (const relevantPath of relevantPaths) {
    // Check if file is in or is the relevant path
    if (file.startsWith(relevantPath + "/")) {
      relevanceScore = 1.5;
      break;
    }
  }

  // Commit relationship score
  let relationshipScore = 0;
  if (fileInfo.relatedFiles.size > 0) {
    const relatedToRelevant = Array.from(fileInfo.relatedFiles).some(
      (relatedFile) =>
        relevantPaths.some(
          (relevantPath) =>
            relatedFile === relevantPath ||
            relatedFile.startsWith(relevantPath + "/"),
        ),
    );
    if (relatedToRelevant) {
      relationshipScore = 0.8; // High score for files modified with relevant files
    }
  }

  // Combine scores
  const gitScore =
    (timeScore +
      frequencyScore +
      dirScore +
      relevanceScore +
      relationshipScore) /
    5;
  const finalImportance = Math.max(baseImportance, 1 + gitScore * 3);

  return finalImportance;
}

export function shouldIncludeHiddenFile(sys: DevToolsSys, file: string) {
  if (file.includes(".test.")) {
    return false;
  }
  if (file.includes(".spec.")) {
    return false;
  }
  if (file.includes(".unit.")) {
    return false;
  }
  if (file.includes(".e2e.")) {
    return false;
  }
  if (file.includes("test")) {
    return false;
  }
  if (file.includes("tests")) {
    return false;
  }
  if (sys.basename(file).startsWith(".")) {
    return false;
  }
  return true;
}

function clampImportance(value: number) {
  return Math.min(3, Math.max(0, value));
}
