import type {
  DevToolsSys,
  Framework,
  PackageJSON,
  SemanticVersion,
} from "../types";
import { findPackageJson } from "../common/fs";
import { getPackageManager } from "../common/utils";

export async function detectFrameworks(sys: DevToolsSys) {
  const frameworks: Framework[] = [];

  const pkgJson = await findPackageJson(sys, sys.getRepoRootDir());
  if (pkgJson) {
    const depFrameworks = await detectFrameworksFromPackageJson(pkgJson, sys);
    frameworks.push(...depFrameworks);
  }

  return frameworks;
}

export async function detectTurbopack(sys: DevToolsSys) {
  const pkgJson = await findPackageJson(sys, sys.getRepoRootDir());
  if (!pkgJson?.scripts?.dev) {
    return false;
  }

  const devCommand = pkgJson.scripts.dev;
  return devCommand.includes("--turbopack");
}

export async function detectFrameworksFromPackageJson(
  pkgJson: PackageJSON,
  sys: DevToolsSys,
) {
  const frameworks: Framework[] = [];
  if (pkgJson && typeof pkgJson === "object") {
    const depNames = new Set(
      Object.keys({
        ...pkgJson.dependencies,
        ...pkgJson.devDependencies,
      }),
    );

    for (const depName of FRAMEWORKS) {
      if (depNames.has(depName)) {
        if (!frameworks.some((f) => f.name === depName)) {
          const payload: Framework = { name: depName };
          if (VERSION_RESOLVER.has(depName)) {
            payload.version = await VERSION_RESOLVER.get(depName)!(sys);
          }
          frameworks.push(payload);
        }
      }
    }
  }
  return frameworks;
}

/**
 * Given a fragment of a semantic version string, return the integer form
 * or undefined if the input is invalid.
 * @param versionFragment - A fragment of the semantic version string
 * such as "5" in "1.2.5"
 */
function parseSemanticVersionNumber(versionFragment: string) {
  const parsed = parseInt(versionFragment);

  return Number.isNaN(parsed) ? undefined : parsed;
}

/**
 * Resolvers to get the installed JS Framework version. This is useful when Dev Tools
 * needs to do things differently based on JS Framework versions (such as to account
 * for breaking changes across major releases.)
 */
const VERSION_RESOLVER = new Map<
  string,
  (sys: DevToolsSys) => Promise<SemanticVersion | undefined>
>([
  [
    "next",
    async (sys: DevToolsSys) => {
      if (!sys.spawnSync) {
        return;
      }
      const packageManager = getPackageManager();
      const command = packageManager === "npm" ? "npx" : packageManager;

      const { stdout } = sys.spawnSync(command, ["next", "-v"], {
        encoding: "utf8",
      });

      if (!stdout) {
        return;
      }

      const nextInfo = stdout.match(/(Next.js v)([\d.]+)/g);
      if (!nextInfo) {
        return;
      }
      const version = nextInfo[0].split("Next.js v")[1];
      const [major, minor, patch] = version.split(".");

      return {
        major: parseSemanticVersionNumber(major),
        minor: parseSemanticVersionNumber(minor),
        patch: parseSemanticVersionNumber(patch),
      };
    },
  ],
]);

/**
 * Order it so more specific frameworks are first
 * and more general ones are last
 */
const FRAMEWORKS: string[] = [
  // toolkit frameworks
  "@shopify/remix-oxygen",
  "@shopify/hydrogen",

  // meta frameworks
  "@builder.io/qwik-city",
  "@remix-run/react",
  "@sveltejs/kit",
  "astro",
  "gatsby",
  "next",
  "nuxt",

  // frameworks
  "@builder.io/qwik",
  "@angular/core",
  "react",
  "solid-js",
  "svelte",
  "vue",
];
