import { DEV_TOOLS_CLIENT_SCRIPT_PATH } from "../common/constants";
import { version } from "./index";

export function getClientScript(devToolsServerUrl: string) {
  try {
    if (
      typeof devToolsServerUrl !== "string" ||
      devToolsServerUrl.trim().length === 0
    ) {
      console.error(`Missing devtool server url`);
      return ``;
    }

    const url = new URL(DEV_TOOLS_CLIENT_SCRIPT_PATH, devToolsServerUrl);
    url.searchParams.set("v", version);
    const v = JSON.stringify(version);

    let c = `/* Builder Devtools v${version} */\n`;
    c += `(() => {\n`;

    c += `  if (typeof document !== "undefined") {\n`;
    c += `    let devToolsScript = document.head.querySelector("script[data-builder-dev-tools]");\n`;
    c += `    if (!devToolsScript) {\n`;
    c += `      devToolsScript = document.createElement("script");\n`;
    c += `      devToolsScript.setAttribute("src", ${JSON.stringify(url)});\n`;
    c += `      devToolsScript.setAttribute("data-builder-dev-tools", ${v});\n`;
    c += `      devToolsScript.setAttribute("async", "");\n`;
    c += `      document.head.appendChild(devToolsScript);\n`;
    c += `    }\n`;
    c += `  }\n`;

    c += `})();\n`;

    return c;
  } catch (e: any) {
    console.error(`Failed to create devtools client script: ${e.message || e}`);
    return ``;
  }
}
