import type { DevToolsServerContext, DevToolsHttpServer } from "../types";
import { createServer as createHttpServer, request } from "node:http";
import { handleDevRequest } from "./request-handler";
import { DEV_TOOLS_SERVER_CLOSE_PATH } from "../common/constants";

export async function createDevToolsHttpServer(
  ctx: DevToolsServerContext,
): Promise<DevToolsHttpServer> {
  if (globalThis.__builderDevToolsServer) {
    // reuse existing devtools server
    ctx.debug(`reusing existing devtools server`);
    globalThis.__builderDevToolsServer.setContext(ctx);
    return globalThis.__builderDevToolsServer;
  }

  const port = await setupDevToolsPort(ctx);

  const server = createHttpServer((request, response) => {
    handleDevRequest(ctx, server, request, response);
  });

  const shutdownServer = () => {
    return new Promise<void>((resolve, reject) => {
      if (server.listening) {
        ctx.debug(`closing devtools server on port ${port}`);
        server.close((err) => {
          ctx = null as any;
          if (err) {
            ctx.debug(`error closing devtools server on port ${port}`);
            reject(err);
          } else {
            if (ctx) {
              ctx.debug(`closed devtools server on port ${port}`);
            }
            resolve();
          }
        });
      } else {
        ctx.debug(`devtools server on port ${port} not listening`);
        resolve();
      }
    });
  };

  process.once("SIGTERM", shutdownServer);
  process.once("SIGINT", shutdownServer);
  if (process.env.CI !== "true") {
    process.stdin.on("end", shutdownServer);
  }

  globalThis.__builderDevToolsServer = {
    url: new URL(`http://localhost:${port}`).href,
    port,
    setContext: (newCtx) => {
      ctx.debug(`set devtools context on server ${port}`);
      ctx = newCtx;
    },
    close: async () => {
      globalThis.__builderDevToolsServer = null;
      await shutdownServer();
    },
  };

  return new Promise((resolve) => {
    server.listen(port, () => {
      ctx.debug(`started devtools server on port ${port}`);
      resolve(globalThis.__builderDevToolsServer!);
    });
  });
}

function setupDevToolsPort(ctx: DevToolsServerContext) {
  return new Promise<number>((resolve) => {
    const port = ctx.port;

    try {
      const options = {
        hostname: "localhost",
        port,
        path: DEV_TOOLS_SERVER_CLOSE_PATH,
        method: "GET",
      };

      request(options, (res) => {
        res.on("end", () => {
          ctx.debug(`${DEV_TOOLS_SERVER_CLOSE_PATH} - Response ended`);
          resolve(port);
        });
      })
        .on("error", (error) => {
          ctx.debug(`${DEV_TOOLS_SERVER_CLOSE_PATH} - No response ${error}`);
          resolve(port);
        })
        .end();
    } catch (e) {
      ctx.debug(`${DEV_TOOLS_SERVER_CLOSE_PATH} - Error ${e}`);
      resolve(port);
    }
  });
}

declare const globalThis: {
  __builderDevToolsServer?: DevToolsHttpServer | null;
};
