import { SDK_VERSIONS, type ModifiedFile } from "../../../types";
import type { ReactDevToolsSys } from ".";
import { reactComponentRegistry } from "./react-registry";
import { getImportPath } from "../../../common/utils";
import {
  REACT_VITE_BUILDER_KEY_ENV,
  REACT_WEBPACK_BUILDER_KEY_ENV,
} from "./react-api-key";

export async function reactEnsureBuilderSetup(sys: ReactDevToolsSys) {
  const modifiedFiles: ModifiedFile[] = [];

  const counterCmpPath = await reactEnsureCounterComponent(sys, modifiedFiles);
  await reactEnsureBuilderRegistry(sys, modifiedFiles, counterCmpPath);

  if (!(await sys.exists(sys.builderComponentPath))) {
    const code = await sys.formatCode(
      sys.builderComponentPath,
      getReactAppBuilderComponent(sys, sys.builderComponentPath),
    );
    await sys.writeFile(sys.builderComponentPath, code);
    modifiedFiles.push({
      filePath: sys.builderComponentPath,
      modifiedType: "create",
    });
  }

  return modifiedFiles;
}

export async function reactEnsureFigmaImportSetup(sys: ReactDevToolsSys) {
  const modifiedFiles: ModifiedFile[] = [];

  await reactEnsureBuilderRegistry(sys, modifiedFiles);

  const routeFileName = `figma-imports.${
    sys.typescriptEnabled ? "tsx" : "jsx"
  }`;
  const routePath = sys.join(sys.componentsDir, routeFileName);

  if (!(await sys.exists(routePath))) {
    const code = await sys.formatCode(
      routePath,
      getReactAppBuilderComponent(sys, routePath, "figma-imports"),
    );
    await sys.writeFile(routePath, code);
    modifiedFiles.push({
      filePath: routePath,
      modifiedType: "create",
    });
  }

  return modifiedFiles;
}

function getReactAppBuilderComponent(
  sys: ReactDevToolsSys,
  routePath: string,
  modelName: string = "page",
) {
  const ts = sys.typescriptEnabled;

  if (sys.sdkVersion === SDK_VERSIONS.gen2) {
    return `
  import React from "react";
  import {
      Content,
      fetchOneEntry,
      getBuilderSearchParams,
      isPreviewing,
    ${ts ? "type BuilderContent," : ""}
    } from "@builder.io/sdk-react";
  import { CUSTOM_COMPONENTS } from ${JSON.stringify(
    getImportPath(sys, routePath, sys.registryPath),
  )};

  // Builder Public API Key set in .env file
  const BUILDER_API_KEY = ${
    sys.configType === "vite"
      ? `import.meta.env.${REACT_VITE_BUILDER_KEY_ENV};`
      : `${REACT_WEBPACK_BUILDER_KEY_ENV};`
  }
  const MODEL_NAME = "${modelName}";

  export default function BuilderPage() {
  const [notFound, setNotFound] = React.useState(false);
  const [content, setContent] = React.useState${
    ts ? "<BuilderContent | null>" : ""
  }(null);

  // get the page content from Builder
  React.useEffect(() => {
    fetchOneEntry({
      model: MODEL_NAME,
      apiKey: BUILDER_API_KEY,
      userAttributes: {
        urlPath: window.location.pathname,
      },
      options: getBuilderSearchParams(new URL(location.href).searchParams),
    })
      .then((content) => {
        if (content) {
          setContent(content);
        }
        setNotFound(!content);
      })
      .catch((err) => {
        console.log("Oops: ", err);
      });
  }, []);

  // If no page is found, return
  // a 404 page from your code.
  if (notFound && !isPreviewing()) {
    return <div>404 Page Not Found</div>;
  }

  // return the page when found
  return (
  <>
    {/* Render the Builder page */}
    <Content
      content={content}
      model={MODEL_NAME}
      apiKey={BUILDER_API_KEY}
      customComponents={CUSTOM_COMPONENTS}
    />
  </>
  );
}
    `;
  }

  return `
import React from "react";
import { BuilderComponent, builder, useIsPreviewing } from "@builder.io/react"; 
import ${JSON.stringify(getImportPath(sys, routePath, sys.registryPath))};

// Builder Public API Key set in .env file
${
  sys.configType === "vite"
    ? `builder.init(import.meta.env.${REACT_VITE_BUILDER_KEY_ENV}${
        ts ? "!" : ""
      })`
    : `builder.init("${REACT_WEBPACK_BUILDER_KEY_ENV}")`
}

export default function BuilderPage() {
  const isPreviewingInBuilder = useIsPreviewing();
  const [notFound, setNotFound] = React.useState(false);
  const [content, setContent] = React.useState(null);

  // get the page content from Builder
  React.useEffect(() => {
    async function fetchContent() {
      const content = await builder
        .get("${modelName}", {
          url: window.location.pathname,
        })
        .promise();

      setContent(content);
      setNotFound(!content);

      // if the page title is found, 
      // set the document title
      if (content?.data.title) {
        document.title = content.data.title;
      }
    }
    fetchContent();
  }, []);
  
  if (content === null) {
    return;
  }
  // If no page is found, return 
  // a 404 page from your code.
  if (notFound && !isPreviewingInBuilder) {
    return <div>404 Page Not Found</div>;
  }

  // return the page when found
  return (
    <>
      {/* Render the Builder page */}
      <BuilderComponent model="${modelName}" content={content} />
    </>
  );
}
`.trimStart();
}

async function reactEnsureBuilderRegistry(
  sys: ReactDevToolsSys,
  modifiedFiles: ModifiedFile[],
  counterCmpPath?: string,
) {
  if (!(await sys.exists(sys.registryPath))) {
    if (sys.sdkVersion === SDK_VERSIONS.gen2) {
      const code = getBuilderRegistryReactGen2(sys);
      await sys.writeFile(sys.registryPath, code);
      // else it is gen1
    } else {
      await sys.writeFile(sys.registryPath, "");
    }

    modifiedFiles.push({
      filePath: sys.registryPath,
      modifiedType: "create",
    });
  }

  if (!counterCmpPath) {
    return;
  }

  const registry = await reactComponentRegistry(
    sys,
    null,
    false,
    false,
    null,
    null,
    false,
    false,
  );

  const counterCmp = registry.components.find((c) => {
    return c.filePath === counterCmpPath;
  });
  if (!counterCmp) {
    return;
  }

  await reactComponentRegistry(
    sys,
    counterCmp.id,
    true,
    false,
    null,
    null,
    true,
    false,
  );
}

async function reactEnsureCounterComponent(
  sys: ReactDevToolsSys,
  modifiedFiles: ModifiedFile[],
) {
  const ts = sys.typescriptEnabled;
  const ext = ts ? "tsx" : "jsx";

  const counterCmpDir = sys.join(sys.componentsDir, `Counter`);
  const counterCmpPath = sys.join(counterCmpDir, `Counter.${ext}`);
  const counterStylesPath = sys.join(counterCmpDir, `styles.module.css`);

  const { component, styles } = getCounterComponent(sys);

  if (!(await sys.exists(counterCmpPath))) {
    const code = await sys.formatCode(counterCmpPath, component);
    await sys.writeFile(counterCmpPath, code);
    modifiedFiles.push({
      filePath: counterCmpPath,
      modifiedType: "create",
    });
  }

  if (!(await sys.exists(counterStylesPath))) {
    await sys.writeFile(counterStylesPath, styles);
    modifiedFiles.push({
      filePath: counterStylesPath,
      modifiedType: "create",
    });
  }

  return counterCmpPath;
}

function getCounterComponent(sys: ReactDevToolsSys) {
  const ts = sys.typescriptEnabled;
  const component = `
"use client"
import React, { useState } from "react";
import styles from "./styles.module.css";
${
  ts
    ? `
interface CounterProps {
  initialCount?: number;
}`
    : ``
}

function Counter({ initialCount = 99 }${ts ? `: CounterProps` : ``}) {
  const [count, setCount] = useState(initialCount);

  const increment = () => {
    setCount((prevCount) => prevCount + 1);
  };

  const decrement = () => {
    setCount((prevCount) => prevCount - 1);
  };

  return (
    <div className={styles.counter}>
      <button className={styles.btn} onClick={decrement}>
        -
      </button>
      <span className={styles.count}>{count}</span>
      <button className={styles.btn} onClick={increment}>
        +
      </button>
    </div>
  );
}

export default Counter;
`.trimStart();

  const styles = `
.counter {
  margin: 32px auto;
  display: flex;
  width: 100%;
  max-width: 190px;
}

.btn {
  width: 42px;
  font-size: 32px;
  font-weight: bold;
  background-color: #1c6bd1;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  line-height: 1.4;
}

.btn:hover {
  opacity: 0.8;
}

.count {
  flex: 1;
  font-size: 42px;
  text-align: center;
}
`.trimStart();

  return { component, styles };
}

function getBuilderRegistryReactGen2(sys: ReactDevToolsSys) {
  if (sys.typescriptEnabled) {
    return `import type { RegisteredComponent } from "@builder.io/sdk-react";
    
export const CUSTOM_COMPONENTS: RegisteredComponent[] = [
];`;
  }

  return `export const CUSTOM_COMPONENTS = [
  ];
  `;
}
