import type {
  CreateDevToolsOptions,
  DevToolsAdapter,
  DevToolsSys,
} from "../../../types";
import { getBuilderCache, setBuilderCache } from "../../../common/cache";
import { getQwikApiKey, setQwikApiKey } from "./qwik-api-key";
import { qwikComponentRegistry } from "./qwik-registry";
import { qwikEnsureBuilderSetup } from "./qwik-ensure-builder-setup";
import { findBuilderRegistry, recursiveFindDir } from "../../../common/fs";

export async function createQwikCityDevTools(sys: CreateDevToolsOptions) {
  const qwikSys = await createQwikDevToolsSys(sys);

  const devTools: DevToolsAdapter = {
    builderPageOptions: async () => {
      return {
        templateContentId: "dc70f1d979ad416d8fd357137eb9ddfb",
        pathname: "/",
        title: "Builder + Qwik Demo Page",
      };
    },
    ensureBuilderSetup: () => {
      return qwikEnsureBuilderSetup(qwikSys);
    },
    ensureFigmaImportPage: async () => {
      return [];
    },
    getRegistryPath: () => {
      return qwikSys.registryPath;
    },
    getRegistry: (opts) => {
      return qwikComponentRegistry(
        qwikSys,
        null,
        false,
        false,
        null,
        null,
        false,
        !!opts?.readAllInputTypes,
      );
    },
    addExternalPackage: async (_pkgName: string) => {},
    loadComponent: async (opts) => {
      const r = await qwikComponentRegistry(
        qwikSys,
        opts.cmpId,
        false,
        false,
        null,
        null,
        false,
        false,
      );
      return {
        ...r,
        component: r.components.find((c) => c.id === opts.cmpId)!,
      };
    },
    registerComponent: (opts) => {
      return qwikComponentRegistry(
        qwikSys,
        opts.cmpId,
        true,
        false,
        null,
        null,
        true,
        false,
      );
    },
    setRegisteredComponentInfo: (opts) => {
      return qwikComponentRegistry(
        qwikSys,
        opts.cmpId,
        false,
        false,
        opts,
        null,
        true,
        false,
      );
    },
    setRegisteredComponentInput: (opts) => {
      return qwikComponentRegistry(
        qwikSys,
        opts.cmpId,
        false,
        false,
        null,
        opts,
        true,
        false,
      );
    },
    unregisterComponent: (opts) => {
      return qwikComponentRegistry(
        qwikSys,
        opts.cmpId,
        false,
        true,
        null,
        null,
        true,
        false,
      );
    },
    getPublicApiKey: () => {
      return getQwikApiKey(qwikSys);
    },
    setPublicApiKey: (opts) => {
      return setQwikApiKey(qwikSys, opts.publicApiKey);
    },
    getDependencies: (_opts) => {
      return [
        { name: "@builder.io/dev-tools" },
        { name: "@builder.io/sdk-qwik" },
      ];
    },
    getDevRunCommand: () => {
      return "dev";
    },
    getCache: () => getBuilderCache(qwikSys),
    setCache: (cache) => setBuilderCache(qwikSys, cache),
  };

  return devTools;
}

export async function createQwikDevToolsSys(sys: DevToolsSys) {
  const componentsDir = await recursiveFindDir(
    sys,
    sys.getAppRootDir(),
    "components",
  );
  if (!componentsDir) {
    throw new Error(
      `Could not find components directory within project: ${sys.getAppRootDir()}`,
    );
  }

  const registryPath = await findBuilderRegistry(sys, componentsDir);
  const envPath = sys.join(sys.getAppRootDir(), ".env");

  const qwikSys: QwikDevToolsSys = {
    ...sys,
    componentsDir,
    registryPath,
    envPath,
  };
  return qwikSys;
}

export interface QwikDevToolsSys extends DevToolsSys {
  componentsDir: string;
  registryPath: string;
  envPath: string;
}
