import { red } from "picocolors";
import { fetch as fetchPolyfill } from "undici";
import { exit } from "./exit";
import { yellow } from "picocolors";
import { version as pkgVersion } from "../../../package.json";
import process from "node:process";

export const safeFetch: typeof globalThis.fetch = async (
  input: RequestInfo | URL,
  init?: RequestInit,
) => {
  const fetchFn =
    "fetch" in globalThis ? globalThis.fetch : (fetchPolyfill as any);
  try {
    const response = await fetchFn(input, {
      ...init,
      headers: {
        ...init?.headers,
        ...getUserAgent(),
      },
    });
    if (!response.ok) {
      if (response.status === 429) {
        const json = await response.json();
        const severity = json.severity ?? "high";
        const message =
          json.message ?? "Rate limit exceeded. Please try again later.";
        console.log(); // line break
        if (severity === "high") {
          response.error(red(`${response.status} ${message}\n\n`));
        } else {
          response.warn(yellow(`${response.status} ${message}\n\n`));
        }
        return await exit(1);
      }
    }
    return response;
  } catch (error: unknown) {
    const url =
      typeof input === "string"
        ? input
        : input instanceof URL
          ? input.toString()
          : input.url;
    const method = init?.method || "GET";
    const errorMessage = error instanceof Error ? error.message : String(error);
    const enhancedError = new Error(
      `Fetch failed for ${method} ${url}: ${errorMessage}`,
    );
    if (error instanceof Error) {
      enhancedError.cause = error;
    }
    (enhancedError as any).requestContext = {
      url,
      method,
      headers: init?.headers,
    };
    throw enhancedError;
  }
};

function getUserAgent() {
  // Map Node.js platform names to User-Agent friendly names
  const platformMap: Record<string, string> = {
    darwin: "Macintosh",
    win32: "Windows NT",
    linux: "Linux",
    android: "Android",
    ios: "iPhone",
  };

  const platform = platformMap[process.platform] || "Unknown";
  const osDetails = process.platform === "win32" ? "10.0" : "";

  return {
    "User-Agent": `Mozilla/5.0 (${platform}${
      osDetails ? "; " + osDetails : ""
    }) builder-cli/${pkgVersion}`,
    "Sec-Ch-Ua-Platform": platform,
  };
}
