import type { DevToolsServerContext } from "../types";
import { getLocalAppId } from "../common/utils";

export async function getClientScript(ctx: DevToolsServerContext) {
  return updateClientRuntimeVariables(ctx, CLIENT_SCRIPT);
}

export async function getConnectedStepHtml(ctx: DevToolsServerContext) {
  return updateClientRuntimeVariables(ctx, CONNECTED_STEP_HTML);
}

async function updateClientRuntimeVariables(
  ctx: DevToolsServerContext,
  code: string,
) {
  const localAppId = await getLocalAppId(ctx);
  const frameworks = ctx.getFrameworks();
  return code
    .replace(/__LOCAL_APP_ID__/g, localAppId)
    .replace(/__DEV_TOOLS_URL__/g, ctx.devToolsServerUrl)
    .replace(/__CLIENT_ID__/g, ctx.getClientId())
    .replace(/__FRAMEWORK__/g, frameworks.length ? frameworks[0].name : "");
}

declare const CLIENT_SCRIPT: string;

declare const CONNECTED_STEP_HTML: string;
