import { intro, log, outro } from "@clack/prompts";
import { bold, bgCyan, bgGreen, white, dim, yellow, cyan } from "picocolors";

import { track } from "./track";
import { pkgVersion } from "./version";
import { builderNpxPackage } from "../common/utils";

export function printHelp(subcommand?: string) {
  track("cli help", { subcommand });

  console.log("");
  intro(bgCyan(bold(white(" help "))) + `  ` + dim(pkgVersion));

  switch (subcommand) {
    case "add":
    case "generate":
    case "gen":
    case "g":
      intro(
        `${cyan("→")} ${bold("Generate Figma Components")}

${bgGreen(white(` npx ${builderNpxPackage()} figma generate URL [URL ...] `))}

Start the auto-mapping wizard for the Figma Component URLs pasted in.

${yellow("Usage:")}
  Get the URL by right-clicking on the Figma Component and selecting
  "Copy Link". Be sure to copy the URL from the main component.

${yellow("Arguments:")}
  ${cyan("--figmaToken")}     Figma access token for authentication
  ${cyan("--spaceId")}        Builder.io space ID to use
  ${cyan("--componentName")}  Name of the component to generate
  ${cyan("--mappingOutput")}  Output file name for the mapping
  ${cyan("--ci")}             Run in CI mode without interactive prompts
  ${cyan("--force")}          Force operation even if there are warnings
  ${cyan("--skipInstallation")} Skip the package installation step
  ${cyan("--yes")}            Auto-confirm all prompts`,
      );
      break;
    case "auth":
      log.info(
        `${cyan("→")} ${bold("Authentication")}

${bgGreen(white(` npx ${builderNpxPackage()} auth `))}

Re-authenticate with Figma and Builder.io.

${yellow("Environment Variables:")}
  ${cyan("BUILDER_PUBLIC_KEY")}   Your Builder.io public API key
  ${cyan("BUILDER_PRIVATE_KEY")}  Your Builder.io private API key
  ${cyan("FIGMA_PERSONAL_TOKEN")} Your Figma personal access token

${yellow("Non-Interactive Authentication:")}
  Environment variables can be used to skip the interactive authentication process.
  This is especially useful for CI/CD pipelines or automated workflows.

  Example using environment variables:
  ${dim('export BUILDER_PRIVATE_KEY="your-private-key"')}
  ${dim('export FIGMA_PERSONAL_TOKEN="your-figma-token"')}
  ${dim(`npx ${builderNpxPackage()} figma generate <url> --ci`)}

${yellow("Arguments:")}
  ${cyan("--figmaToken")}     Figma access token for authentication
  ${cyan("--spaceId")}        Builder.io space ID to use
  ${cyan(
    "--force",
  )}          Force re-authentication even if already authenticated

${yellow("Learn More:")}
  • Builder.io API keys: https://www.builder.io/c/docs/using-your-api-key#managing-private-keys
  • Figma access token: https://help.figma.com/hc/en-us/articles/8085703771159-Manage-personal-access-tokens
  • CI setup guide: https://www.builder.io/c/docs/integrating-builder-figma`,
      );
      break;

    case "publish":
      log.info(
        `${cyan("→")} ${bold("Publish Mappings")}

${bgGreen(white(` npx ${builderNpxPackage()} figma publish `))}

Publish your Figma Mappings to Builder.io.

${yellow("Arguments:")}
  ${cyan("--spaceId")}        Builder.io space ID to use
  ${cyan("--ci")}             Run in CI mode without interactive prompts
  ${cyan("--force")}          Force publish even if there are warnings
  ${cyan("--verbose")}        Print output as JSON
  ${cyan("--dryrun")}         Run without making changes
  ${cyan("--yes")}            Auto-confirm all prompts`,
      );
      break;

    case "migrate":
      log.info(
        `${cyan("→")} ${bold("Migrate Mappings")}

${bgGreen(white(` npx ${builderNpxPackage()} figma migrate `))}

Migrate your existing Figma Mappings into the repo as local files.

${yellow("Description:")}
  Downloads existing Figma Mappings from Builder.io and saves them locally.

${yellow("Arguments:")}
  ${cyan("--spaceId")}        Builder.io space ID to use
  ${cyan("--force")}          Force migration even if there are warnings
  ${cyan("--yes")}            Auto-confirm all prompts`,
      );
      break;

    case "code":
      log.info(
        `${cyan("→")} ${bold("Code Generation")}

${bgGreen(white(` npx ${builderNpxPackage()} code --url URL `))}

Generate or modify code based on Figma designs.

${yellow("Arguments:")}
  ${cyan("--url")}            URL to start completion from
  ${cyan("--spaceId")}        Builder.io space ID to use
  ${cyan("--prompt")}         Prompt text for non-interactive mode
  ${cyan(
    "--mode",
  )}           Generation mode - either 'exact' for precise matches or 'creative' for more flexibility
  ${cyan("--cwd")}            Working directory to run commands from

${yellow("Configuration Files:")}
  ${cyan(
    ".builderignore",
  )}   Add patterns to exclude files from being included in code generation
  ${cyan(
    ".builderrules",
  )}    Add custom instructions that will be injected into the LLM prompt
  ${cyan(
    ".cursorrules",
  )}     Cursor's settings are automatically supported for consistent behavior`,
      );
      break;

    case "indexing":
      log.info(
        `${cyan("→")} ${bold("Code Indexing")}

${bgGreen(white(` npx ${builderNpxPackage()} code indexing `))}

Index your codebase to improve code generation results.

${yellow("Arguments:")}
  ${cyan("--spaceId")}        Builder.io space ID to use
  ${cyan("--cwd")}            Working directory to run commands from`,
      );
      break;

    default:
      log.info(`${bold("Available Commands:")}\n`);

      const commands = [
        {
          command: "generate URL [URL ...]",
          description: "Start the auto-mapping wizard for Figma Component URLs",
        },
        {
          command: "publish",
          description: "Publish your Figma Mappings to Builder.io",
        },
        {
          command: "migrate",
          description:
            "Migrate existing Figma Mappings into the repo as local files",
        },
        {
          command: "auth",
          description: "Re-authenticate with Figma and Builder.io",
        },
        {
          command: "code",
          description: "Generate or modify code based on Figma designs",
        },
        {
          command: "indexing",
          description: "Index your codebase to improve code generation results",
        },
      ];

      commands.forEach(({ command, description }) => {
        log.info(
          `${cyan("→")} ${bgGreen(
            white(` npx ${builderNpxPackage()} figma ${command} `),
          )}
   ${dim(description)}\n`,
        );
      });

      log.info(
        `\nUse ${cyan(
          `npx ${builderNpxPackage()} figma help <command>`,
        )} for detailed help about a command`,
      );
  }

  outro("bye 👋");
}
