import type { ModifiedFile } from "../../../types";
import type { AngularDevToolsSys } from ".";
import { angularComponentRegistry } from "./angular-registry";
import { getImportPath } from "../../../common/utils";
import { ANGULAR_BUILDER_API_KEY_ENV } from "./angular-api-key";
import { angularUpdateAppModule } from "./angular-app-module-imports";
import { angularAddRoute } from "./angular-app-routes-update";
import { angularFixTsConfig } from "./angular-fix-tsconfig";

export async function angularEnsureBuilderSetup(sys: AngularDevToolsSys) {
  const modifiedFiles: ModifiedFile[] = [];

  const counterCmpPath = await angularEnsureCounterComponent(
    sys,
    modifiedFiles,
  );

  modifiedFiles.push(...(await angularEnsureBuilderRegistry(sys)));

  await angularEnsureCounterRegistered(sys, counterCmpPath);

  const builderCmpName = "BuilderPage";

  if (!(await sys.exists(sys.builderPageComponentPath))) {
    const code = await sys.formatCode(
      sys.builderPageComponentPath,
      getAngularBuilderComponent(
        sys,
        builderCmpName,
        sys.builderPageComponentPath,
        "builder-page",
        "page",
        true,
      ),
    );
    await sys.writeFile(sys.builderPageComponentPath, code);
    modifiedFiles.push({
      filePath: sys.builderPageComponentPath,
      modifiedType: "create",
    });
  }

  // Update app.module file if it exists
  if (sys.hasAppModule) {
    //Update app module with Counter
    await angularUpdateAppModule(sys, "Counter", counterCmpPath);

    //Update app module with BuilderPage
    const appModulePath = await angularUpdateAppModule(
      sys,
      builderCmpName,
      sys.builderPageComponentPath,
    );

    if (appModulePath) {
      modifiedFiles.push({ filePath: appModulePath, modifiedType: "update" });
    }
  }
  await angularAddRoute(
    sys,
    "**",
    builderCmpName,
    `./components/builder-page.component`,
  );

  return modifiedFiles;
}

export async function angularEnsureFigmaImportSetup(sys: AngularDevToolsSys) {
  const modifiedFiles: ModifiedFile[] = [];

  modifiedFiles.push(...(await angularEnsureBuilderRegistry(sys)));

  const figmaImportsCmpName = "FigmaImportsPage";

  const fileName = `figma-imports.component`;
  const routePath = sys.join(
    sys.componentsDir,
    `${fileName}.${sys.typescriptEnabled ? "ts" : "js"}`,
  );

  if (!(await sys.exists(routePath))) {
    const code = await sys.formatCode(
      routePath,
      getAngularBuilderComponent(
        sys,
        figmaImportsCmpName,
        routePath,
        "figma-imports",
        "figma-imports",
        false,
      ),
    );
    await sys.writeFile(routePath, code);
    modifiedFiles.push({
      filePath: routePath,
      modifiedType: "create",
    });
  }

  // Update app.module file if it exists
  if (sys.hasAppModule) {
    const appModulePath = await angularUpdateAppModule(
      sys,
      figmaImportsCmpName,
      routePath,
    );

    if (appModulePath) {
      modifiedFiles.push({ filePath: appModulePath, modifiedType: "update" });
    }
  }
  await angularAddRoute(
    sys,
    "figma-imports",
    figmaImportsCmpName,
    `./components/${fileName}`,
  );
  await angularFixTsConfig(sys);
  return modifiedFiles;
}

export async function angularEnsureBuilderRegistry(sys: AngularDevToolsSys) {
  const modifiedFiles: ModifiedFile[] = [];

  if (!(await sys.exists(sys.registryPath))) {
    await sys.writeFile(sys.registryPath, getAngularBuilderRegistry(sys));
    modifiedFiles.push({
      filePath: sys.registryPath,
      modifiedType: "create",
    });
  }

  return modifiedFiles;
}

async function angularEnsureCounterRegistered(
  sys: AngularDevToolsSys,
  counterCmpPath: string,
) {
  const registry = await angularComponentRegistry(
    sys,
    null,
    false,
    false,
    null,
    null,
    false,
    false,
  );

  const counterCmp = registry.components.find((c) => {
    return c.filePath === counterCmpPath;
  });
  if (!counterCmp) {
    return;
  }

  await angularComponentRegistry(
    sys,
    counterCmp.id,
    true,
    false,
    null,
    null,
    true,
    false,
  );
}

function getAngularBuilderComponent(
  sys: AngularDevToolsSys,
  cmpName: string,
  cmpPath: string,
  pageName: string,
  modelName: string,
  add404: boolean,
) {
  const isStandAloneComponent = !sys.hasAppModule;
  const registryImportPath = getImportPath(sys, cmpPath, sys.registryPath);
  const envPath = sys.join(
    sys.getAppRootDir(),
    "src",
    "environments",
    "environment.ts",
  );
  const environmentImportPath = getImportPath(sys, cmpPath, envPath);

  const template = add404
    ? `    <ng-container *ngIf="content || isPreviewing; else notFound">
      <builder-content
        [model]="model"
        [content]="content"
        [apiKey]="apiKey"
        [customComponents]="customComponents"
      ></builder-content>
    </ng-container>

    <ng-template #notFound>
      <div>404 - Content not found</div>
    </ng-template>`
    : `    <builder-content
      [model]="model"
      [content]="content"
      [apiKey]="apiKey"
      [customComponents]="customComponents"
    ></builder-content>`;

  return `
import { Component, Input } from '@angular/core';
import { fetchOneEntry, type BuilderContent${
    add404 ? ", isPreviewing" : ""
  } } from '@builder.io/sdk-angular';
${
  isStandAloneComponent
    ? "import { Content } from '@builder.io/sdk-angular';"
    : ""
}
${
  isStandAloneComponent ? "import { CommonModule } from '@angular/common';" : ""
}
import { environment } from ${JSON.stringify(environmentImportPath)};
import { CUSTOM_COMPONENTS } from ${JSON.stringify(registryImportPath)};

@Component({
  selector: 'app-${pageName}',
  ${isStandAloneComponent ? "standalone: true," : ""}
  ${isStandAloneComponent ? "imports: [Content, CommonModule]," : ""}
  template: \`
${template}
  \`,
})
export class ${cmpName} {
  
  ${add404 ? "isPreviewing = isPreviewing();\n" : ""}
  @Input() model = '${modelName}';

  apiKey = environment.${ANGULAR_BUILDER_API_KEY_ENV};
  ${
    sys.typescriptEnabled
      ? `
  content: BuilderContent | null = null;
`
      : `
  content = null;
`
  }

  customComponents = CUSTOM_COMPONENTS;

  async ngOnInit() {
    const urlPath = window.location.pathname || '/';

    const builderContent = await fetchOneEntry({
      model: this.model,
      apiKey: this.apiKey,
      userAttributes: {
        urlPath,
      },
    });

    if (!builderContent) {
      return;
    }

    this.content = builderContent;
  }
}

  `.trimStart();
}

function getAngularBuilderRegistry(sys: AngularDevToolsSys) {
  if (sys.typescriptEnabled) {
    return `import type { RegisteredComponent } from "@builder.io/sdk-angular";
    
export const CUSTOM_COMPONENTS: RegisteredComponent[] = [
];`;
  }

  return `export const CUSTOM_COMPONENTS = [
  ];
  `;
}

async function angularEnsureCounterComponent(
  sys: AngularDevToolsSys,
  modifiedFiles: ModifiedFile[],
) {
  const ts = sys.typescriptEnabled;
  const ext = ts ? "ts" : "js";

  const counterCmpPath = sys.join(
    sys.componentsDir,
    `counter.component.${ext}`,
  );

  const hasCounterCmp = await sys.exists(counterCmpPath);

  sys.debug(`angularEnsureCounterComponent, hasCounterCmp: ${hasCounterCmp}`);

  const component = getCounterComponent(sys);

  if (!hasCounterCmp) {
    const code = await sys.formatCode(counterCmpPath, component);
    await sys.writeFile(counterCmpPath, code);
    modifiedFiles.push({
      filePath: counterCmpPath,
      modifiedType: "create",
    });
  }

  return counterCmpPath;
}

function getCounterComponent(sys: AngularDevToolsSys) {
  const component = `
import { Component, Input } from '@angular/core';

@Component({
  selector: 'app-counter',
  ${!sys.hasAppModule ? "standalone: true," : ""}
  template: \`
    <div class="counter">
      <button class="btn" (click)="decrement()">-</button>
      <span class="count">{{ count }}</span>
      <button class="btn" (click)="increment()">+</button>
    </div>
  \`,
  styles: [
    \`
      .counter {
        margin: 32px auto;
        display: flex;
        width: 100%;
        max-width: 190px;
      }

      .btn {
        width: 42px;
        font-size: 32px;
        font-weight: bold;
        background-color: #1c6bd1;
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        line-height: 1.4;
      }

      .btn:hover {
        opacity: 0.8;
      }

      .count {
        flex: 1;
        font-size: 42px;
        text-align: center;
        margin: 0 8px;
      }
    \`,
  ],
})
export class Counter {
  @Input() initialCount: number = 99;
  count: number;

  constructor() {
    this.count = this.initialCount;
  }

  ngOnInit(): void {
    this.count = this.initialCount;
  }

  increment(): void {
    this.count += 1;
  }

  decrement(): void {
    this.count -= 1;
  }
}

`.trimStart();

  return component;
}
