import {
  type DevToolsServerContext,
  type ConnectedBuilder,
  type ValidatedBuilder,
  type SPACE_KIND_VALUES,
  type ModifiedFile,
  SPACE_KIND,
} from "../types";
import { createBuilderPage } from "../common/builder/content-generation";
import {
  // getBuilderContent,
  hasBuilderContentUrl,
  hasBuilderContentViaQueryAPI,
} from "../common/builder/builder-api";
import { DEFAULT_MODEL_NAME } from "../common/builder/content-ids";

export async function connectBuilder(
  ctx: DevToolsServerContext,
  publicApiKey: string,
  privateAuthKey: string,
  kind: SPACE_KIND_VALUES,
) {
  ctx.debug(`connect builder with public key: ${publicApiKey}`);

  ctx.publicApiKey = publicApiKey;

  // ctx.kind = kind as SPACE_KIND_VALUES | null;

  const closeServerPromise = ctx.closeAppServer();

  const apiKey = await ctx.setPublicApiKey({ publicApiKey });

  ctx.debug(`set public key: process.env.${apiKey.envKey}=${publicApiKey}`);
  process.env[apiKey.envKey] = publicApiKey;

  const frameworkPageOpts = await ctx.builderPageOptions();

  ctx.debug(
    `builder model: ${DEFAULT_MODEL_NAME}, path: ${frameworkPageOpts.pathname}, template: ${frameworkPageOpts.templateContentId}`,
  );

  let hasContent = await hasBuilderContentUrl(ctx, {
    model: DEFAULT_MODEL_NAME,
    pageUrl: frameworkPageOpts.pathname,
    publicApiKey,
    privateAuthKey,
    kind,
  });

  if (!hasContent) {
    await createBuilderPage(ctx, {
      templateContentId: frameworkPageOpts.templateContentId,
      pathname: frameworkPageOpts.pathname,
      title: frameworkPageOpts.title,
      publicApiKey,
      privateAuthKey,
    });

    // If we just created content, wait a few seconds for it to be available
    // Also, hit the same URL that the Builder SDK calls, but with cachebust
    // to avoid any CDN level caching issues. Once that is available, it should
    // generally be safe to proceed. This solves most of the 404 issues on initial
    // launch
    let MAX_RETRIES = 5;
    let retries = 0;
    while (!hasContent && retries < MAX_RETRIES) {
      await new Promise((resolve) => setTimeout(resolve, 1000));
      let content = await hasBuilderContentViaQueryAPI({
        model: DEFAULT_MODEL_NAME,
        pageUrl: frameworkPageOpts.pathname,
        publicApiKey,
        privateAuthKey,
      });
      retries++;
      if (content && content.length > 0) {
        hasContent = true;
      }
    }
  }

  await closeServerPromise;

  ctx.debug(`ensure builder setup`);

  let modifiedFiles: ModifiedFile[] = [];

  // For VCP, we need to ensure figma import page is setup
  if (kind === SPACE_KIND.VCP) {
    modifiedFiles = await ctx.ensureFigmaImportPage();
  } else {
    modifiedFiles = await ctx.ensureBuilderSetup();
  }

  if (apiKey.modifiedType) {
    modifiedFiles.push({
      filePath: apiKey.file,
      modifiedType: apiKey.modifiedType,
    });
  }

  modifiedFiles.forEach((m) => {
    m.displayFilePath = ctx.relative(ctx.getAppRootDir(), m.filePath);
  });
  modifiedFiles.sort((a, b) => a.filePath.localeCompare(b.filePath));

  if (modifiedFiles.length > 0) {
    ctx.debug(
      `modified files: ${modifiedFiles
        .map((m) => m.displayFilePath)
        .join(", ")}`,
    );
    await ctx.restartAppServer();
    await new Promise((resolve) => setTimeout(resolve, 500));
  } else {
    ctx.debug(`no modified files`);
  }

  const connectedBuilder: ConnectedBuilder = {
    success: true,
    pathname: frameworkPageOpts.localePathname || frameworkPageOpts.pathname,
    modifiedFiles,
    platform: ctx.platform(),
    kind,
  };

  if (kind === SPACE_KIND.VCP) {
    connectedBuilder.pathname = "/";
  }

  ctx.debug(`connected builder: ${connectedBuilder.pathname}`);

  return connectedBuilder;
}

export async function validateBuilder(ctx: DevToolsServerContext) {
  const frameworkPageOpts = await ctx.builderPageOptions();

  ctx.debug(
    `validateBuilder, pathname: ${frameworkPageOpts.pathname}, localePathname: ${frameworkPageOpts.localePathname}, isValid: ${ctx.isValid}`,
  );

  const result: ValidatedBuilder = {
    isValid: ctx.isValid,
    pathname: frameworkPageOpts.localePathname || frameworkPageOpts.pathname,
    platform: ctx.platform(),
  };

  if (ctx.isValid) {
    return result;
  }

  // get the builder public api key from the .env file
  const envInfo = await ctx.getPublicApiKey();
  if (!envInfo.envValue) {
    ctx.debug(`missing public api key`);
    return result;
  }

  ctx.debug(`public api key: ${envInfo.envValue}, ${envInfo.file}`);

  // remember the valid app credentials
  ctx.publicApiKey = envInfo.envValue;

  // For now, skipping validation of homepage content as it is not needed for the VCP.

  // it's possible that the builder auth is setup, but they still don't have a homepage somehow
  // double check if this builder account already has a homepage created
  // const b = await getBuilderContent(ctx, {
  //   model: DEFAULT_MODEL_NAME,
  //   publicApiKey: envInfo.envValue,
  // });
  // if (b.results.length === 0) {
  //   ctx.debug(`missing homepage content`);
  //   // there is no homepage content created yet for the valid public api key
  //   // we don't have their private key, so let's redirect to the auth flow
  //   // so we can get their private key and create the homepage for them
  //   return result;
  // }

  // awesome, we're all set
  // the public key is saved correctly in the .env file
  // and they have a homepage created

  // set isValid to true so we don't have to validate again
  ctx.debug(`builder connection is valid`);
  result.isValid = true;
  ctx.isValid = true;

  return result;
}
