import { SDK_VERSIONS, type ModifiedFile } from "../../../types";
import type { RemixDevToolsSys } from ".";
import { remixComponentRegistry } from "./remix-registry";
import { REMIX_BUILDER_API_KEY_ENV } from "./remix-api-key";
import { getImportPath } from "../../../common/utils";

// https://www.builder.io/c/docs/quickstart

export async function remixEnsureBuilderSetup(sys: RemixDevToolsSys) {
  const modifiedFiles: ModifiedFile[] = [];

  const counterCmpPath = await remixEnsureCounterComponent(sys, modifiedFiles);
  modifiedFiles.push(...(await remixEnsureBuilderRegistry(sys)));

  await remixEnsureCounterRegistered(sys, counterCmpPath);
  await remixEnsureAppRoute(sys, modifiedFiles);

  return modifiedFiles;
}

export async function remixEnsureFigmaImportSetup(sys: RemixDevToolsSys) {
  const modifiedFiles: ModifiedFile[] = [];

  modifiedFiles.push(...(await remixEnsureBuilderRegistry(sys)));

  await remixEnsureFigmaImportRoute(sys, modifiedFiles);

  return modifiedFiles;
}

async function remixEnsureFigmaImportRoute(
  sys: RemixDevToolsSys,
  modifiedFiles: ModifiedFile[],
) {
  let routeFileName = `figma-imports.${sys.typescriptEnabled ? "tsx" : "jsx"}`;
  if (sys.hasLocaleParam) {
    sys.debug(`remixEnsureFigmaImportRoute, hasLocaleParam`);
    routeFileName = `($locale).${routeFileName}`;
  }

  const routePath = sys.join(sys.routesDir, routeFileName);
  const hasRoute = await sys.exists(routePath);

  sys.debug(`remixEnsureFigmaImportRoute, hasRoute: ${hasRoute}`);

  const code = await sys.formatCode(
    routePath,
    getRemixAppRoute({ sys, routePath, modelName: "figma-imports" }),
  );
  await sys.writeFile(routePath, code);
  modifiedFiles.push({
    filePath: routePath,
    modifiedType: hasRoute ? "update" : "create",
  });
}

async function remixEnsureAppRoute(
  sys: RemixDevToolsSys,
  modifiedFiles: ModifiedFile[],
) {
  let routeFileName = `$.${sys.typescriptEnabled ? "tsx" : "jsx"}`;
  if (sys.hasLocaleParam) {
    sys.debug(`remixEnsureAppRoute, hasLocaleParam`);
    routeFileName = `($locale).${routeFileName}`;
  }

  if (sys.sdkVersion === SDK_VERSIONS.gen2) {
    routeFileName = `($slug)._index.${sys.typescriptEnabled ? "tsx" : "jsx"}`;
  }

  const routePath = sys.join(sys.routesDir, routeFileName);
  const hasRoute = await sys.exists(routePath);

  sys.debug(`remixEnsureAppRoute, hasRoute: ${hasRoute}`);

  const code = await sys.formatCode(
    routePath,
    getRemixAppRoute({ sys, routePath, modelName: "page" }),
  );
  await sys.writeFile(routePath, code);
  modifiedFiles.push({
    filePath: routePath,
    modifiedType: hasRoute ? "update" : "create",
  });
}

function getRemixAppRoute({
  sys,
  routePath,
  modelName,
}: {
  sys: RemixDevToolsSys;
  routePath: string;
  modelName: "page" | "figma-imports";
}) {
  const ts = sys.typescriptEnabled;

  const registryImportPath = getImportPath(sys, routePath, sys.registryPath);

  let loaderArgsImport = ``;
  let loaderArgsType = ``;
  if (ts) {
    loaderArgsImport = `import type { LoaderArgs } from '@remix-run/server-runtime';`;
    loaderArgsType = `: LoaderArgs`;
  }

  let envVar = `process.env.${REMIX_BUILDER_API_KEY_ENV}`;
  let usesContext = ``;
  if (sys.getFrameworks().some((f) => f.name === "@shopify/remix-oxygen")) {
    // hydrogen uses the context object in the loader
    envVar = `context.env.${REMIX_BUILDER_API_KEY_ENV}`;
    usesContext = `, context`;
  }
  if (ts) {
    envVar += `!`;
  }

  if (sys.sdkVersion === SDK_VERSIONS.gen2) {
    return `

import {
  Content,
  fetchOneEntry,
  getBuilderSearchParams,
  isPreviewing,
} from "@builder.io/sdk-react";
${ts ? `import type { LoaderFunctionArgs } from "@remix-run/node";` : ``}
import { useLoaderData } from "@remix-run/react";
import { fetch as webFetch } from "@remix-run/web-fetch";
import { CUSTOM_COMPONENTS } from ${JSON.stringify(registryImportPath)};

export const loader = async ({ params, request${usesContext} }${
      ts ? ": LoaderFunctionArgs" : ""
    }) => {
  const builderApiKey = ${envVar};
  const url = new URL(request.url);
  const urlPath = \`/\${params["slug"] || ""}\`;

  const page = await fetchOneEntry({
    model: "${modelName}",
    apiKey: builderApiKey,
    options: getBuilderSearchParams(url.searchParams),
    userAttributes: { urlPath },
    fetch: webFetch,
  });

  if (!page && !isPreviewing(url.search)) {
    throw new Response("Page Not Found", {
      status: 404,
      statusText: "Page not found in Builder.io",
    });
  }

  return { page, builderApiKey };
};

// Define and render the page.
export default function Page() {
  const { page, builderApiKey } = useLoaderData${ts ? "<typeof loader>" : ""}();
  // Render the page content from Builder.io
  return (
    <Content
      model="${modelName}"
      apiKey={builderApiKey}
      content={page}
      customComponents={CUSTOM_COMPONENTS}
    />
  );
}
    `;
  }

  return `
import { BuilderComponent, builder } from "@builder.io/react";
import { useLoaderData } from "@remix-run/react";
import { CUSTOM_COMPONENTS } from ${JSON.stringify(registryImportPath)};
${loaderArgsImport}

// Fetch contents of the page
export const loader = async ({ params, request, context }${loaderArgsType}) => {
  // Initialize the Builder client and pass in your Public API Key
  const builderApiKey = ${envVar};
  builder.init(builderApiKey);

  // Fetch data content from Builder.io based on the URL path
  const page = await builder
    .get("${modelName}", {
      userAttributes: {
        urlPath: "/" + params["*"],
      },
      locale: params.locale,
    })
    .toPromise();

  // Verify the user is previewing or editing in Builder
  const isPreviewing = new URL(request.url).searchParams.has("builder.preview");

  // If the page is not found and the user is not previewing, throw a 404.
  // The CatchBoundary component will catch the error
  if (!page && !isPreviewing) {
    throw new Response("Page Not Found", {
      status: 404,
      statusText:
        "We couldn't find this page, please check your url path and if the page is published on Builder.io.",
    });
  }

  return { page, builderApiKey };
};

// Define and render the page.
export default function Page() {
  // Use the useLoaderData hook to get the Page data from "loader" above.
  const { page, builderApiKey } = useLoaderData${ts ? `<typeof loader>` : ``}();

  // Render the page content from Builder.io
  return (
    <BuilderComponent
      customComponents={CUSTOM_COMPONENTS}
      apiKey={builderApiKey}
      model="${modelName}"
      content={page}
    />
  );
}

${
  ts
    ? `
declare global {
  interface Env {
    ${REMIX_BUILDER_API_KEY_ENV}: string;
  }
}
`
    : ``
}
`;
}

async function remixEnsureCounterComponent(
  sys: RemixDevToolsSys,
  modifiedFiles: ModifiedFile[],
) {
  const ts = sys.typescriptEnabled;
  const ext = ts ? "tsx" : "jsx";

  const counterCmpDir = sys.join(sys.componentsDir, `Counter`);
  const counterCmpPath = sys.join(counterCmpDir, `Counter.${ext}`);
  const counterStylesPath = sys.join(counterCmpDir, `styles.module.css`);

  const { component, styles } = getCounterComponent(sys);

  const hasCounterCmp = await sys.exists(counterCmpPath);

  sys.debug(`remixEnsureCounterComponent, hasCounterCmp: ${hasCounterCmp}`);

  if (!hasCounterCmp) {
    const code = await sys.formatCode(counterCmpPath, component);
    await sys.writeFile(counterCmpPath, code);
    modifiedFiles.push({
      filePath: counterCmpPath,
      modifiedType: "create",
    });
  }

  const hasCounterStyles = await sys.exists(counterStylesPath);

  sys.debug(
    `remixEnsureCounterComponent, hasCounterStyles: ${hasCounterStyles}`,
  );

  if (!hasCounterStyles) {
    await sys.writeFile(counterStylesPath, styles);
    modifiedFiles.push({
      filePath: counterStylesPath,
      modifiedType: "create",
    });
  }

  return counterCmpPath;
}

function getCounterComponent(sys: RemixDevToolsSys) {
  const ts = sys.typescriptEnabled;
  const component = `
import { useState } from "react";
import styles from "./styles.module.css";
${
  ts
    ? `
interface CounterProps {
  initialCount?: number;
}`
    : ``
}

function Counter({ initialCount = 99 }${ts ? `: CounterProps` : ``}) {
  const [count, setCount] = useState(initialCount);

  const increment = () => {
    setCount((prevCount) => prevCount + 1);
  };

  const decrement = () => {
    setCount((prevCount) => prevCount - 1);
  };

  return (
    <div className={styles.counter}>
      <button className={styles.btn} onClick={decrement}>
        -
      </button>
      <span className={styles.count}>{count}</span>
      <button className={styles.btn} onClick={increment}>
        +
      </button>
    </div>
  );
}

export default Counter;
`.trimStart();

  const styles = `
.counter {
  margin: 32px auto;
  display: flex;
  width: 100%;
  max-width: 190px;
  color: #1c6bd1;
}

.btn {
  width: 42px;
  font-size: 32px;
  font-weight: bold;
  background-color: #1c6bd1;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  line-height: 1.4;
}

.btn:hover {
  opacity: 0.8;
}

.count {
  flex: 1;
  font-size: 42px;
  text-align: center;
}
`.trimStart();

  return { component, styles };
}

async function remixEnsureCounterRegistered(
  sys: RemixDevToolsSys,
  counterCmpPath: string,
) {
  const registry = await remixComponentRegistry(
    sys,
    null,
    false,
    false,
    null,
    null,
    false,
    false,
  );

  const counterCmp = registry.components.find((c) => {
    return c.filePath === counterCmpPath;
  });
  if (!counterCmp) {
    return;
  }

  await remixComponentRegistry(
    sys,
    counterCmp.id,
    true,
    false,
    null,
    null,
    true,
    false,
  );
}

export async function remixEnsureBuilderRegistry(sys: RemixDevToolsSys) {
  const modifiedFiles: ModifiedFile[] = [];

  if (!(await sys.exists(sys.registryPath))) {
    await sys.writeFile(sys.registryPath, getRemixBuilderRegistry(sys));
    modifiedFiles.push({
      filePath: sys.registryPath,
      modifiedType: "create",
    });
  }

  return modifiedFiles;
}

function getRemixBuilderRegistry(sys: RemixDevToolsSys) {
  const isSdkGen2 = sys.sdkVersion === SDK_VERSIONS.gen2;
  if (sys.typescriptEnabled) {
    return `import type { RegisteredComponent } from ${
      isSdkGen2 ? `"@builder.io/sdk-react"` : `"@builder.io/react"`
    };

export const CUSTOM_COMPONENTS: RegisteredComponent[] = [
];`;
  }

  return `export const CUSTOM_COMPONENTS = [
  ];
  `;
}
