import type { UserContext } from "$/ai-utils";

export const runFigmaPublish = async (sys: DevToolsSys, args: CLIArgs) => {
  const userContext = await getUserContext(sys);
  sys.Sentry?.setContext("userContext", userContext);

  await track("figma publish started", {});

  const interactive = !args.yes || !args.skipInstallation;
  if (interactive) {
    if (!process.stdout.isTTY && process.env.FORCE_TTY !== "1") {
      await track("tty error", {});

      cancel(
        "This command requires a TTY-enabled terminal. \n see docs for more information on how to run non-interactive.\nIf you are using Windows Git Bash, try a different terminal or wrap the command with winpty.",
      );
      return await exit(1);
    }
  }
  const ciMode = isCI(args);

  console.log("");
  intro(bgCyan(bold(white(" figma publish "))) + `  ` + dim(pkgVersion));

  await checkProjectRoot(sys, interactive);

  const mappingFiles = await findAllMappingFiles(sys);

  const { figmaBuilderLinks, figmaLinksToResolve } =
    await findMappingsFromFiles({
      mappingFiles,
      sys,
    });

  // TODO: comment out for now, we will need to fix some typechecker issues to re-enable
  // if (diagnostics.length > 0) {
  //   for (const d of diagnostics) {
  //     log.error(
  //       red(
  //         `TypeScript errors in ${dim(
  //           sys.relative(sys.getAppRootDir(), d.file?.fileName ?? ""),
  //         )}:\n`,
  //       ) + diagnostics.map((d) => ` • ${formatDiagnostic(d)}`).join("\n"),
  //     );
  //   }

  //   await track("figma publish type error", {
  //     mappingFiles: mappingFiles.length,
  //     figmaBuilderLinks: figmaBuilderLinks.length,
  //   });
  //   if (args.force !== true) {
  //     log.message("");
  //     cancel(
  //       `Found some TypeScript errors in the mapping files. Please fix the issues and try again, or use the ${bold(
  //         "--force",
  //       )} flag to publish regardless.`,
  //     );
  //     return await exit(1);
  //   } else {
  //     log.warn(
  //       "Local mappings contain some type errors, but --force flag was used, skipping.",
  //     );
  //   }
  // }

  const {
    builderPrivateKey: privateKey,
    builderPublicKey: spaceId,
    userId,
    spaceName,
    figmaAuth,
  } = await getCredentials(sys, args, {
    forceSpaceId: args.spaceId,
    builderPrivateKey: true,
    builderPublicKey: true,
    figmaAuth: figmaLinksToResolve.length > 0,
  });

  if (figmaAuth && figmaLinksToResolve.length > 0) {
    await inPlaceResolveFigmaURLs(
      sys,
      args,
      figmaAuth,
      figmaBuilderLinks,
      figmaLinksToResolve,
    );
  }

  const { filteredLinks, skippedLinks } = filterMappings(figmaBuilderLinks);

  if (skippedLinks.length > 0) {
    await track("figma publish user error", {
      mappingFiles: mappingFiles.length,
      filteredLinks: filteredLinks.length,
      skippedLinks: skippedLinks.length,
    });
    if (args.force !== true) {
      log.message("");
      cancel(
        `Found some errors in the mapping files. Please fix the issues and try again, or use the ${bold(
          "--force",
        )} flag to publish regardless.`,
      );
      return await exit(1);
    } else {
      log.warn(
        "Local mappings contain some type errors, but --force flag was used, skipping.",
      );
    }
  }

  let devTools: DevTools | undefined;
  try {
    devTools = await createDevTools(sys);
  } catch (e) {
    sys.Sentry?.captureException(e, { level: "warning" });
  }

  const extraContext = spaceName
    ? `${bold(spaceName)} ${dim("(" + spaceId + ")")}`
    : `space id "${bold(spaceId)}"`;

  if (filteredLinks.length === 0) {
    log.warning(
      `No mappings found. Publishing will remove all existing mapping in space: ${extraContext}.`,
    );
    const confirmPublish =
      ciMode ||
      args.yes ||
      (await confirm({
        message: `Do you want to continue?`,
        active: "Publish",
        inactive: "Cancel",
      }));
    if (isCancel(confirmPublish) || !confirmPublish) {
      cancel("Publish cancelled");
      return await exit(1);
    }
  } else {
    const confirmPublish =
      ciMode ||
      args.yes ||
      (await select({
        message: `Do you want to publish the found mappings to ${extraContext}?`,
        options: [
          {
            label: "Publish",
            hint: "Existing mappings will be overwritten",
            value: true,
          },
          {
            label: "Cancel",
            value: false,
          },
        ],
      }));
    if (isCancel(confirmPublish) || !confirmPublish) {
      cancel("Publish cancelled");
      return await exit(1);
    }
  }
  let s = spinner();
  const repoInfo = await sys.getRepoInfo();
  if (!args.dryrun) {
    s.start("Publishing Figma Mappings");
    await publishFigmaMappings(sys, {
      spaceId: spaceId!,
      userId: userId!,
      privateKey: privateKey!,
      figmaBuilderLinks: filteredLinks,
      repoInfo,
      userContext,
    });
    s.stop(
      filteredLinks.length > 0
        ? `${filteredLinks.length} mappings uploaded`
        : "All mappings removed from the Builder space",
    );
  }

  if (args.verbose || args.dryrun) {
    const highlight = createEmphasize(common);

    log.info(
      highlight.highlight("json", JSON.stringify(figmaBuilderLinks, null, 2))
        .value,
    );
  }

  await setPublicKey(sys, spaceId!, devTools, true);

  log.success(bgGreen(bold(white(" Done! 🎉 "))));

  const command =
    {
      react: "npm run start",
      angular: "npm run start",
      vue: "npm run dev",
      svelte: "npm run dev",
      next: "npm run dev",
      remix: "npm run dev",
    }[devTools?.framework ?? "react"] ?? "npm run start";

  outro(
    dim("You can now start the dev server:\n\n   ") +
      bgBlack(white(bold(` ${command} `))) +
      "\n\n   " +
      dim(
        "Then, go back to Figma and generate code again to see your mapped components.",
      ),
  );

  await track("figma publish success", {
    spaceId,
    mappingFiles: mappingFiles.length,
    figmaBuilderLinks: figmaBuilderLinks.length,
  });
};

async function parseFile(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  typeChecker: ts.TypeChecker,
): Promise<FigmaBuilderLink[]> {
  const figmaBuilderLinks: FigmaBuilderLink[] = [];
  const nodes: ts.Node[] = [sourceFile];
  const fileLoc = sys.relative(sys.getAppRootDir(), sourceFile.fileName);
  while (nodes.length > 0) {
    const node = nodes.shift()!;
    if (isFigmaMapperCall(node, sourceFile)) {
      const link = parseCallExpression(sys, node, sourceFile, typeChecker);
      const { line, character } = sourceFile.getLineAndCharacterOfPosition(
        node.getStart(),
      );
      if (link) {
        link.loc = `${fileLoc}:${line + 1}:${character + 1}`;
        figmaBuilderLinks.push(link);
      }
    }
    nodes.push(...node.getChildren(sourceFile));
  }
  return figmaBuilderLinks;
}

export const FIGMA_CONNECT_CALL = `figmaMapping`;

function parseCallExpression(
  sys: DevToolsSys,
  node: ts.CallExpression,
  sourceFile: ts.SourceFile,
  typeChecker: ts.TypeChecker,
): FigmaBuilderLink | null {
  const [arg] = node.arguments;
  if (!arg) {
    throw new Error("Invalid figmaMapping call, no arguments found");
  }
  if (!ts.isObjectLiteralExpression(arg)) {
    throw new Error(
      "Invalid figmaMapping call, argument is not an object literal",
    );
  }
  const properties = arg.properties;
  if (!properties) {
    throw new Error("Invalid figmaMapping call, argument has no properties");
  }
  let figmaKey: string = "";
  let inputMapper: string | undefined;
  let originalInputMapper: string | undefined;
  let builderName: string = "-";
  let figmaName: string = sys
    .basename(sourceFile.fileName)
    .replace(/\.mapper\.[tsxjm]+$/, "");
  let exportType: ExportType | undefined;
  let importName: string | undefined;
  let importPath: string | undefined;
  let figmaUrl: string | undefined;
  if (node.typeArguments && node.typeArguments.length > 0) {
    const name = extractFigmaComponentName(
      node.typeArguments[0].getText(sourceFile),
    );
    if (name) {
      figmaName = name;
    }
  }
  for (const prop of properties) {
    const name = prop.name?.getText(sourceFile);
    if (!name) {
      throw new Error("Invalid figmaMapping call, property has no name");
    }

    switch (name) {
      case "url": {
        if (!("initializer" in prop)) {
          throw new Error("Invalid figmaMapping call, url has no initializer");
        }
        const initializer = prop.initializer;
        if (!ts.isStringLiteral(initializer)) {
          throw new Error(
            "Invalid figmaMapping call, componentKey is not a string literal",
          );
        }
        figmaUrl = initializer.text;
        break;
      }
      case "componentKey": {
        if (!("initializer" in prop)) {
          throw new Error(
            "Invalid figmaMapping call, componentKey has no initializer",
          );
        }
        const initializer = prop.initializer;

        if (!ts.isStringLiteral(initializer)) {
          throw new Error(
            "Invalid figmaMapping call, componentKey is not a string literal",
          );
        }
        figmaKey = initializer.text;
        break;
      }
      case "mapper": {
        if (ts.isMethodDeclaration(prop)) {
          if (prop.parameters[0].type) {
            const type = prop.parameters[0].type.getText(sourceFile);
            const name = extractFigmaComponentName(type);
            if (name) {
              figmaName = name;
            }
          }
          function getExportStatement(identifier: ts.Identifier) {
            // use typechecker to get import statement of node.tagName
            const symbol = typeChecker.getSymbolAtLocation(identifier);
            const declaration = symbol?.getDeclarations()?.[0];
            if (declaration) {
              const importDeclaration =
                findParentImportDeclaration(declaration);

              if (importDeclaration?.importClause) {
                const name =
                  importDeclaration.importClause.name?.getText(sourceFile);
                if (name === builderName) {
                  exportType = "default";
                } else if (importDeclaration.importClause.namedBindings) {
                  exportType = "named";
                }
                importName = name;
                if (ts.isStringLiteral(importDeclaration.moduleSpecifier)) {
                  importPath = importDeclaration.moduleSpecifier.text;
                }
                return true;
              }
            }
            return false;
          }
          function visitor(node: ts.Node) {
            if (
              (ts.isJsxOpeningElement(node) ||
                ts.isJsxSelfClosingElement(node)) &&
              ts.isIdentifier(node.tagName)
            ) {
              builderName = node.tagName.getText(sourceFile);
              getExportStatement(node.tagName);
              return;
            } else if (
              // html template literal
              ts.isTaggedTemplateExpression(node) &&
              ts.isIdentifier(node.tag) &&
              node.tag.escapedText === "html"
            ) {
              if (ts.isTemplateExpression(node.template)) {
                const found = node.template.templateSpans.find(
                  (a) =>
                    ts.isIdentifier(a.expression) &&
                    isCapitalized(a.expression.text) &&
                    getExportStatement(a.expression),
                );
                if (found && ts.isIdentifier(found.expression)) {
                  builderName = found.expression.text;
                  return;
                }
              }
              const match = node.template
                .getText(sourceFile)
                .match(/^`\s*<([^\s]+)/);
              if (match) {
                builderName = match[1];
                return;
              }
            } else {
              ts.forEachChild(node, visitor);
            }
          }
          ts.forEachChild(prop, visitor);

          originalInputMapper = prop
            .getText(sourceFile)
            .replace(/^mapper/, "function Mapper");
          inputMapper = transpileInputMapper(originalInputMapper);
          break;
        }
      }
      case "designTokenMapper": {
        if (ts.isMethodDeclaration(prop)) {
          originalInputMapper = prop
            .getText(sourceFile)
            .replace(/^designTokenMapper/, "function Mapper");
          inputMapper = transpileInputMapper(originalInputMapper);
          figmaKey = "design-tokens";
          break;
        }
      }
      case "genericMapper": {
        if (ts.isMethodDeclaration(prop)) {
          originalInputMapper = prop
            .getText(sourceFile)
            .replace(/^genericMapper/, "function Mapper");
          inputMapper = transpileInputMapper(originalInputMapper);
          figmaKey = "*";
          break;
        }
      }
    }
  }

  if (!figmaKey && !figmaUrl) {
    throw new Error("Invalid figmaMapping call, no componentKey or url found");
  }
  if (!figmaKey && !inputMapper) {
    throw new Error("Invalid figmaMapping call, no mapper found");
  }
  if (!figmaKey && !originalInputMapper) {
    throw new Error("Invalid figmaMapping call, no original mapper found");
  }

  return {
    builderName,
    figmaName,
    figmaKey,
    figmaUrl,
    inputMapper,
    exportType,
    importName,
    importPath,
    originalInputMapper,
    source: "cli",
  };
}

function isCapitalized(name: string): boolean {
  return name.charAt(0) === name.charAt(0).toUpperCase();
}
function transpileInputMapper(originalInputMapper: string): string {
  const inputMapper = ts.transpile(originalInputMapper, {
    target: ts.ScriptTarget.Latest,
    allowNonTsExtensions: true,
    moduleResolution: ts.ModuleResolutionKind.NodeJs,
    module: ts.ModuleKind.CommonJS,
    noImplicitAny: false,
    esModuleInterop: true,
    noUnusedLocals: false,
    noUnusedParameters: false,
    jsx: ts.JsxEmit.React,
    jsxFactory: "React.createElement",
    jsxFragmentFactory: "React.Fragment",
    allowJs: true,
    checkJs: true,
    strict: true,
    suppressImplicitAnyIndexErrors: true,
    typeRoots: ["node_modules/@types"],
    inlineSources: true,
    inlineSourceMap: true,
  });
  return inputMapper;
}

function findParentImportDeclaration(
  declaration: ts.Declaration,
): ts.ImportDeclaration | undefined {
  let current = declaration;
  while (current) {
    if (ts.isImportDeclaration(current)) {
      return current;
    }
    current = current.parent as ts.Declaration;
  }
}
function isFigmaMapperCall(
  node: ts.Node,
  sourceFile: ts.SourceFile,
): node is ts.CallExpression {
  return (
    ts.isCallExpression(node) &&
    node.expression.getText(sourceFile) === FIGMA_CONNECT_CALL
  );
}

export async function findAllMappingFiles(sys: DevToolsSys) {
  const skipFolders = ["node_modules", "dist", "build", "public"];
  const files = await sys.readdirRecursive(sys.getAppRootDir(), skipFolders);
  return files.filter((file) => isMapperFile(file));
}

function extractFigmaComponentName(name: string) {
  const componentNameMatch = name.match(/^Figma(\w+)Props$/);
  if (componentNameMatch && typeof componentNameMatch[1] === "string") {
    return componentNameMatch[1];
  }
  return undefined;
}

export async function setPublicKey(
  sys: DevToolsSys,
  publicKey: string,
  devTools: DevTools | undefined,
  ensureFigmaImportPage: boolean = false,
) {
  try {
    if (!devTools) {
      devTools = await createDevTools(sys);
    }
    await devTools.setPublicApiKey({
      publicApiKey: publicKey,
    });
    if (ensureFigmaImportPage) {
      await devTools.ensureFigmaImportPage();
    }
    return true;
  } catch (e) {
    sys.Sentry?.captureException(e, {
      level: "warning",
    });
    return false;
  }
}

export interface PublishedMapping {
  repoInfo: RepoInfo | undefined;
  figmaBuilderLinks: FigmaBuilderLink[];
  spaceId: string;
  privateKey: string;
  userId: string;
  userContext: UserContext;
}

const publishFigmaMappings = async (
  sys: DevToolsSys,
  data: PublishedMapping,
) => {
  const url = new URL(`${apiAirV2Host}/mappings/publish`);
  url.searchParams.set("apiKey", data.spaceId);
  url.searchParams.set("userId", data.userId);

  const res = await safeFetch(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${data.privateKey}`,
    },
    body: JSON.stringify({
      remoteUrl: data.repoInfo?.remoteUrl,
      defaultBranch: data.repoInfo?.defaultBranch,
      currentBranch: data.repoInfo?.currentBranch,
      commit: data.repoInfo?.commit,
      figmaBuilderLinks: data.figmaBuilderLinks,
      spaceKind: sys.kind,
      userContext: data.userContext,
    }),
  });
  if (!res.ok) {
    throw new Error("Failed to publish Figma mappings");
  }
  return true;
};

export async function findMappingsFromFiles({
  sys,
  mappingFiles,
  typeCheck,
}: {
  mappingFiles: string[];
  sys: DevToolsSys;
  typeCheck?: boolean;
}): Promise<{
  figmaBuilderLinks: FigmaBuilderLink[];
  diagnostics: ts.Diagnostic[];
  figmaLinksToResolve: string[];
}> {
  sys.Sentry?.setContext("findMappings", {
    mappingFiles,
  });

  if (!mappingFiles.length) {
    return {
      figmaBuilderLinks: [],
      figmaLinksToResolve: [],
      diagnostics: [],
    };
  }
  const figmaBuilderLinks: FigmaBuilderLink[] = [];
  const totalDiagnostics: ts.Diagnostic[] = [];
  const tsProgram = await createTsProgram(sys, mappingFiles);
  const typeChecker = tsProgram.getTypeChecker();
  const allDiagnostics: ts.Diagnostic[] = [
    ...tsProgram.getSyntacticDiagnostics(),
    ...(typeCheck ? tsProgram.getSemanticDiagnostics() : []),
  ].filter(filterDiagnostic);

  for (const file of mappingFiles) {
    try {
      const sourceFile = tsProgram.getSourceFile(file);
      if (!sourceFile) {
        sys.Sentry?.captureException(new Error(`Could not find source file`), {
          extra: {
            file,
            sourceFiles: tsProgram.getSourceFiles().map((a) => a.fileName),
          },
          level: "warning",
        });
        continue;
      }
      const fileName = sourceFile?.fileName;
      if (fileName) {
        const diagnostics = allDiagnostics.filter((a) => {
          return a.file?.fileName === fileName;
        });
        if (diagnostics.length > 0) {
          totalDiagnostics.push(...diagnostics);
        }
      }

      const links = await parseFile(sys, sourceFile, typeChecker);
      figmaBuilderLinks.push(...links);
    } catch (e) {
      sys.Sentry?.captureException(e);
      return await exit(1);
    }
  }

  const figmaLinksToResolve = figmaBuilderLinks
    .filter((l) => l.figmaUrl && !l.figmaKey)
    .map((l) => l.figmaUrl!);

  return {
    figmaBuilderLinks,
    figmaLinksToResolve,
    diagnostics: totalDiagnostics,
  };
}

function filterMappings(links: FigmaBuilderLink[]) {
  const seenFigmaKeys = new Set<string>();
  const filteredLinks: FigmaBuilderLink[] = [];
  const skippedLinks: [FigmaBuilderLink, "duplicate" | "no-key"][] = [];
  for (const l of links) {
    let isDuplicate = false;
    if (!l.figmaKey) {
      skippedLinks.push([l, "no-key"]);
      continue;
    }
    if (l.figmaKey) {
      isDuplicate ||= seenFigmaKeys.has(l.figmaKey);
      seenFigmaKeys.add(l.figmaKey);
    } else if (l.figmaUrl) {
      // only check for duplicated urls if no key is provided
      isDuplicate ||= seenFigmaKeys.has(l.figmaUrl);
      seenFigmaKeys.add(l.figmaUrl);
    }
    if (isDuplicate) {
      skippedLinks.push([l, "duplicate"]);
    } else {
      filteredLinks.push(l);
    }
  }

  if (filteredLinks.length > 0) {
    const filteredLinksMessage =
      `Found ${filteredLinks.length} mappings:\n` +
      filteredLinks
        .map((l) => {
          let isDuplicate = false;
          if (l.figmaKey) {
            isDuplicate ||= seenFigmaKeys.has(l.figmaKey);
            seenFigmaKeys.add(l.figmaKey);
          } else if (l.figmaUrl) {
            // only check for duplicated urls if no key is provided
            isDuplicate ||= seenFigmaKeys.has(l.figmaUrl);
            seenFigmaKeys.add(l.figmaUrl);
          }

          let text = "";
          switch (l.figmaKey) {
            // add icon
            case "design-tokens":
              text = "  " + cyan(`❇︎ Design tokens mapper`);
              break;
            case "*": // generic mapper
              text = "  " + yellow(`● Generic mapper`);
              break;
            default:
              text = `  ${magenta(
                "❖" + " " + getFigmaComponentName(l.figmaName),
              )}  ${dim("→")}  ${
                l.builderName === "-" ? "-" : "<" + l.builderName + ">"
              }`;
              break;
          }
          if (l.loc) {
            text = `${text} in ${dim(l.loc)}`;
          }
          return text;
        })
        .join("\n");

    log.info(filteredLinksMessage);
  }
  if (skippedLinks.length > 0) {
    const skippedLinksMessage = skippedLinks
      .map(([l, type]) => {
        let text = "";
        switch (l.figmaKey) {
          // add icon
          case "design-tokens":
            text += cyan(`❇︎ Design tokens mapper`);
            break;
          case "*": // generic mapper
            text += yellow(`● Generic mapper`);
            break;
          default:
            text += `${magenta(
              "❖" + " " + getFigmaComponentName(l.figmaName),
            )}`;
            break;
        }
        text = (l.loc ? bgRed(`${text} in ${l.loc}`) : text) + "\n";
        switch (type) {
          case "duplicate":
            text += bold(`Component mapping is duplicateds`);
            break;
          case "no-key":
            text += `Could not resolve the "url" to a figma component key.\n${dim(
              l.figmaUrl,
            )} is not a COMPONENT or COMPONENT_SET`;
            break;
        }
        return text;
      })
      .join("\n");
    log.error(skippedLinksMessage);
  }

  return {
    filteredLinks,
    skippedLinks,
  };
}
