import type {
  CreateDevToolsOptions,
  DevToolsAdapter,
  DevToolsSys,
} from "../../../types";
import { getBuilderCache, setBuilderCache } from "../../../common/cache";
import { getReactApiKey, setReactApiKey } from "./react-api-key";
import { reactComponentRegistry } from "./react-registry";
import {
  reactEnsureBuilderSetup,
  reactEnsureFigmaImportSetup,
} from "./react-ensure-builder-setup";
import { SDK_VERSIONS } from "../../../types";
import { detectReactSdkVersion } from "./react-builder-sdk-setup";
import { findBuilderRegistry, hasTsconfig } from "../../../common/fs";

export async function createReactDevTools(sys: CreateDevToolsOptions) {
  const reactSys = await createReactDevToolsSys(sys);

  const devTools: DevToolsAdapter = {
    builderPageOptions: async () => {
      return {
        templateContentId: "2e36438034894a8abf279ea9e6d3596f",
        pathname: "/builder-demo",
        title: "Builder + React Demo Page",
      };
    },
    ensureBuilderSetup: () => {
      return reactEnsureBuilderSetup(reactSys);
    },
    ensureFigmaImportPage: () => {
      return reactEnsureFigmaImportSetup(reactSys);
    },

    getRegistryPath: () => {
      return reactSys.registryPath;
    },
    getRegistry: (opts) => {
      return reactComponentRegistry(
        reactSys,
        null,
        false,
        false,
        null,
        null,
        false,
        !!opts?.readAllInputTypes,
      );
    },
    addExternalPackage: (pkgName: string) => {
      reactSys.addExternalPackage(pkgName);
    },
    loadComponent: async (opts) => {
      const r = await reactComponentRegistry(
        reactSys,
        opts.cmpId,
        false,
        false,
        null,
        null,
        false,
        false,
      );
      return {
        ...r,
        component: r.components.find((c) => c.id === opts.cmpId)!,
      };
    },
    registerComponent: (opts) => {
      return reactComponentRegistry(
        reactSys,
        opts.cmpId,
        true,
        false,
        null,
        null,
        true,
        false,
      );
    },
    setRegisteredComponentInfo: (opts) => {
      return reactComponentRegistry(
        reactSys,
        opts.cmpId,
        false,
        false,
        opts,
        null,
        true,
        false,
      );
    },
    setRegisteredComponentInput: (opts) => {
      return reactComponentRegistry(
        reactSys,
        opts.cmpId,
        false,
        false,
        null,
        opts,
        true,
        false,
      );
    },
    unregisterComponent: (opts) => {
      return reactComponentRegistry(
        reactSys,
        opts.cmpId,
        false,
        true,
        null,
        null,
        true,
        false,
      );
    },
    getPublicApiKey: () => {
      return getReactApiKey(reactSys);
    },
    setPublicApiKey: (opts) => {
      return setReactApiKey(reactSys, opts.publicApiKey);
    },
    getDependencies: (opts) => {
      if (opts.sdkVersion === SDK_VERSIONS.gen2) {
        return [
          { name: "@builder.io/dev-tools" },
          { name: "@builder.io/sdk-react" },
        ];
      }
      return [
        { name: "@builder.io/dev-tools" },
        { name: "@builder.io/react" },
        { name: "@builder.io/sdk" },
      ];
    },
    getDevRunCommand: () => {
      return "dev";
    },
    getCache: () => getBuilderCache(reactSys),
    setCache: (cache) => setBuilderCache(reactSys, cache),
  };

  return devTools;
}

export async function createReactDevToolsSys(sys: DevToolsSys) {
  const reactConfigPath = await getReactConfigPath(sys);
  const rootDir = sys.getAppRootDir();
  const srcDir = sys.join(rootDir, "src");

  const typescriptEnabled = await hasTsconfig(sys);

  const externalPackages: Record<string, string> = {};

  const reactSys: ReactDevToolsSys = {
    ...sys,
    srcDir: null,
    builderComponentPath: "",
    componentsDir: "",
    registryPath: "",
    reactConfigPath,
    typescriptEnabled,
    configType: "",
    addExternalPackage: (pkgName: string) => {
      externalPackages[sys.join(rootDir, pkgName, "index.ts")] =
        `export * from "${pkgName}";`;
    },
    readFileSync: (path: string) =>
      externalPackages[path] ?? sys.readFileSync(path),

    existsSync: (path: string) =>
      !!externalPackages[path] || sys.existsSync(path),

    readdirSync: (path: string) => {
      const realFiles = sys.readdirSync(path);
      if (path === rootDir) {
        return [
          ...realFiles,
          ...Object.keys(externalPackages).map((p) =>
            p.slice(rootDir.length + 1),
          ),
        ];
      }
      return realFiles;
    },
    readdir: async (path: string) => {
      const realFiles = await sys.readdir(path);
      if (path === rootDir) {
        return [
          ...realFiles,
          ...Object.keys(externalPackages).map((p) =>
            p.slice(rootDir.length + 1),
          ),
        ];
      }
      return realFiles;
    },
  };

  if (sys.sdkVersion) {
    reactSys.sdkVersion = sys.sdkVersion;
  } else {
    reactSys.sdkVersion = await detectReactSdkVersion(reactSys);
  }

  if (reactConfigPath.includes("webpack.config")) {
    reactSys.configType = "webpack";
  }

  if (reactConfigPath.includes("vite.config")) {
    reactSys.configType = "vite";
  }

  if (await sys.exists(srcDir)) {
    // src dir
    reactSys.srcDir = srcDir;
    reactSys.componentsDir = sys.join(srcDir, "components");

    reactSys.registryPath = await findBuilderRegistry(sys, srcDir);

    reactSys.builderComponentPath = sys.join(
      reactSys.srcDir,
      `builder-page.${typescriptEnabled ? "tsx" : "jsx"}`,
    );
  } else {
    // no src dir
    reactSys.componentsDir = sys.join(rootDir, "components");
    reactSys.registryPath = await findBuilderRegistry(sys, rootDir);
    reactSys.builderComponentPath = sys.join(
      rootDir,
      `builder-page.${typescriptEnabled ? "tsx" : "jsx"}`,
    );
  }

  return reactSys;
}

async function getReactConfigPath(sys: DevToolsSys) {
  const configs = [
    "vite.config.ts",
    "vite.config.js",
    "webpack.config.js",
    "webpack.config.ts",
  ];
  for (const configName of configs) {
    const configPath = sys.join(sys.getAppRootDir(), configName);
    if (await sys.exists(configPath)) {
      return configPath;
    }
  }

  if (sys.ignoreMissingConfig) {
    return "";
  }
  throw new Error(
    `Could not find React config file. Searched for ${configs.join(
      ", ",
    )} in "${sys.getAppRootDir()}".`,
  );
}

export interface ReactDevToolsSys extends DevToolsSys {
  srcDir: string | null;
  componentsDir: string;
  builderComponentPath: string;
  registryPath: string;
  reactConfigPath: string;
  typescriptEnabled: boolean;
  configType: "webpack" | "vite" | "";
  addExternalPackage: (pkgName: string) => void;
}
