import type ts from "typescript";
import type { ComponentInfo } from "../../../types";
import type { AngularDevToolsSys } from "./index";
import { codeToSource } from "../../../common/ast/transform";
import {
  findArrayStatementInitializer,
  getFilePathFromIdentifier,
} from "../../../common/ast/utils";
import {
  getComponentDisplayName,
  getComponentId,
  getComponentImportPath,
  sortComponents,
} from "../../../common/utils";
import { getTextOfPropertyName } from "../../../common/ast/convert-values";
import { parseRegisteredComponentInfo } from "../../../common/ast/component-info";
import { angularEnsureBuilderRegistry } from "./angular-ensure-builder-setup";
import { getAngularComponentsFromPath } from "./angular-components";

export async function parseAngularRegistryFromFile(sys: AngularDevToolsSys) {
  await angularEnsureBuilderRegistry(sys);
  const code = await sys.readFile(sys.registryPath);
  if (code) {
    return parseAngularRegistryFromCode(sys, code);
  }
  return { components: [], sourceFile: codeToSource(sys, "") };
}

export async function parseAngularRegistryFromCode(
  sys: AngularDevToolsSys,
  code: string,
) {
  const sourceFile = codeToSource(sys, code);
  return parseRegistryFromSource(sys, sourceFile);
}

export async function parseRegistryFromSource(
  sys: AngularDevToolsSys,
  sourceFile: ts.SourceFile,
) {
  const cmps: ComponentInfo[] = [];
  const s = findArrayStatementInitializer(sys, sourceFile, "CUSTOM_COMPONENTS");

  if (s) {
    await Promise.all(
      s.arrayLiteralExp.elements.map(async (cmpElm, i) => {
        const cmp = await parseAngularRegisteredComponent(
          sys,
          sourceFile,
          cmpElm,
        );
        if (cmp) {
          cmp.nodeIndex = i;
          cmps.push(cmp);
        }
      }),
    );
  }

  return { sourceFile, components: sortComponents(cmps) };
}

export async function parseAngularRegisteredComponent(
  sys: AngularDevToolsSys,
  sourceFile: ts.SourceFile,
  cmpObjLit: ts.Node,
) {
  if (!sys.ts.isObjectLiteralExpression(cmpObjLit)) {
    return null;
  }

  let cmp: ComponentInfo = {
    id: "",
    name: "",
    displayFilePath: "",
    exportName: "",
    importName: "",
    filePath: "",
    relFilePath: "",
    importPath: "",
    inputs: [],
    framework: "angular",
  };

  for (const p of cmpObjLit.properties) {
    if (!sys.ts.isPropertyAssignment(p) || !sys.ts.isIdentifier(p.name)) {
      continue;
    }
    const propName = getTextOfPropertyName(sys, p);

    if (propName === "component") {
      let identifierName: string;
      const { initializer } = p;

      if (sys.ts.isIdentifier(initializer)) {
        identifierName = initializer.text;
      } else if (sys.ts.isCallExpression(initializer)) {
        const arg = initializer.arguments[0];
        if (sys.ts.isIdentifier(arg)) {
          identifierName = arg.text;
        }
      }

      //@ts-ignore
      if (!identifierName) {
        continue;
      }

      if (cmp.name === "") {
        cmp.name = getComponentDisplayName(identifierName);
      }
      cmp.importName = identifierName;

      const i = await getFilePathFromIdentifier(
        sys,
        sys.registryPath,
        sourceFile,
        identifierName,
      );
      if (!i) {
        continue;
      }

      cmp.filePath = i.filePath;
      cmp.relFilePath = i.relFilePath;

      if (await sys.exists(i.filePath)) {
        cmp.importPath = getComponentImportPath(sys, i.filePath);
      } else {
        // If path doesn't exist, it's an external module
        cmp.externalImportPath = i.importPath;
        cmp.importPath = cmp.externalImportPath;
      }

      cmp.displayFilePath = i.displayFilePath;
      cmp.exportType = i.exportType;

      if (cmp.externalImportPath) {
        cmp.exportName = identifierName;
      } else {
        const cmps = await getAngularComponentsFromPath(sys, i.filePath);
        if (cmps && cmps.length > 0) {
          if (cmps.length === 1) {
            cmp.exportName = cmps[0].cmpInfo.exportName;
          } else {
            const namedCmp = cmps.find(
              (c) => c.cmpInfo.exportName === identifierName,
            );
            if (namedCmp) {
              cmp.exportName = namedCmp.cmpInfo.exportName;
            }
          }
        }
      }

      continue;
    }
  }

  cmp = parseRegisteredComponentInfo(sys, cmp, cmpObjLit);

  if (cmp.filePath && cmp.exportName) {
    cmp.id = await getComponentId(
      sys,
      cmp.filePath,
      cmp.exportName,
      cmp.externalImportPath,
    );
  }

  return cmp;
}
