import {
  log,
  text,
  isCancel,
  spinner,
  confirm,
  intro,
  cancel,
} from "@clack/prompts";
import { dirname, join, resolve } from "node:path";
import {
  existsSync,
  mkdirSync,
  writeFileSync,
  readFileSync,
  statSync,
} from "node:fs";
import {
  createDevToolsNodeSys,
  findAppRootDir,
  findPackageJsonDir,
} from "../../node/node-sys";
import { prettierFormat } from "../prettier";
import { bold, bgCyan, white, dim } from "picocolors";
import type {
  AddCliOptions,
  FileNode,
  FolderNode,
  Snippet,
  SyncInfo,
} from "../../types";
import { exit } from "../exit";
import { safeFetch } from "../fetch";
import { pkgVersion } from "../version";

export async function addCommand(
  opts: AddCliOptions,
): Promise<SyncInfo | undefined> {
  console.log("");
  intro(bgCyan(bold(white(" add "))) + `  ` + dim(pkgVersion));

  const id = opts.snippetId;
  if (!id && !opts.snippet) {
    cancel("SnippetId expected. npx builder.io add <snippetId>");
    return await exit(1);
  }

  const s = spinner();
  let snippet = opts.snippet || null;
  if (!snippet && id) {
    s.start(`Fetching snippet ${id}`);
    snippet = await fetchSnippet(id);
    s.stop(`Fetched snippet ${id}`);
  }

  if (!snippet) {
    cancel(`No snippet found for id ${id}`);
    return await exit(1);
  }

  const cwd = process.cwd();
  const appRootDir = findAppRootDir(cwd) ?? cwd;
  if (snippet.files?.length > 0) {
    const rootPath = suggestRootPath(appRootDir, snippet.framework || "react");
    const pathInput =
      opts.path ||
      (await text({
        message: "Where should we copy the files?",
        initialValue: rootPath,
        validate(value) {
          if (value.length === 0) {
            return `File path is required!`;
          }
          if (value[0] !== ".") return "Please enter a relative path.";
        },
      }));
    if (isCancel(pathInput)) {
      cancel("Cancelled sync");
      return await exit(0);
    }
    const writtenFiles = await writeFiles(
      appRootDir,
      snippet,
      snippet.files,
      pathInput,
      false,
    );
    log.success(`All files have been written successfully.`);
    return {
      snippet,
      pathInput,
      writtenFiles,
      timeStamp: Date.now(),
    };
  } else {
    let { code, suggestedName, framework } = snippet;
    if (framework) {
      // new code saved, we started recently saving that
      suggestedName = await suggestComponentPath(appRootDir, framework, code);
    }
    suggestedName ??= `./src/components/MyComponent.jsx`;

    let formatted = addBuilderSignature(
      snippet,
      removeCodeDelimiters(snippet.code),
      snippet.framework,
    );
    try {
      if (snippet.framework === "html") {
        formatted = await prettierFormat(formatted, "html");
      } else if (snippet.framework === "angular") {
        formatted = await prettierFormat(formatted, "angular");
      } else if (snippet.framework === "vue") {
        formatted = await prettierFormat(formatted, "vue");
      } else if (snippet.framework === "svelte") {
        formatted = await prettierFormat(formatted, "html");
      } else {
        formatted = await prettierFormat(formatted, "typescript");
      }
    } catch (e) {
      // ignore crashed prettier
    }

    let pathInput: string | symbol = opts.path!;

    if (!pathInput) {
      try {
        const sys = await createDevToolsNodeSys({
          cwd: process.cwd(),
        });
        const frameworks = sys.getFrameworks();

        if (frameworks.length > 0) {
          let projectFramework: string;

          if (frameworks.some((f) => f.name.includes("qwik"))) {
            projectFramework = "qwik";
          } else if (frameworks.some((f) => f.name.includes("react"))) {
            projectFramework = "react";
          } else {
            projectFramework = frameworks[0].name;
          }

          if (snippet.framework && snippet.framework !== projectFramework) {
            const shouldContinue = await confirm({
              message: `This snippet is for ${snippet.framework}, but you are using ${projectFramework}. Continue?`,
              initialValue: false,
            });
            if (isCancel(shouldContinue) || !shouldContinue) {
              console.log(
                `Please go back to builder and change the output framework to match your project.`,
              );
              console.log(); // line break
              return await exit(0);
            }
          }
        }
      } catch (e) {
        // probably in a project that isn't supported by dev tools
      }

      pathInput = await text({
        message: "Where should we copy the code?",
        initialValue: suggestedName?.startsWith("./")
          ? suggestedName
          : `./${suggestedName}`,
        validate(value) {
          if (value.length === 0) {
            return `File path is required!`;
          }
          if (value[0] !== ".") return "Please enter a relative path.";
        },
      });
      if (isCancel(pathInput)) {
        console.log(``);
        return await exit(0);
      }
    }

    const componentFilePath = resolve(appRootDir, pathInput);
    log.info(`Writing code to ${componentFilePath}`);
    mkdirSync(dirname(componentFilePath), { recursive: true });
    writeFileSync(componentFilePath, formatted);
  }
}

export function fetchSnippet(id: string): Promise<Snippet | null> {
  return safeFetch(`https://cdn.builder.io/api/v1/snippet/${id}`).then(
    (res) => {
      if (res.status === 404) {
        return null;
      }
      return res.json();
    },
  );
}

function removeCodeDelimiters(str: string) {
  return str.replace(/^```jsx\s?|\s?```$/g, "").trim();
}

function addBuilderSignature(
  snippet: Snippet,
  str: string,
  framework?: string,
  addSessionLink = false,
) {
  if (!framework) {
    return str;
  }

  const baseComment = "This code was generated by Builder.io";
  const link = addSessionLink
    ? `https://builder.io/content/${snippet.contentId}?codeSession=${snippet.sessionKey}&sid=${snippet.id}`
    : "";

  const isHtmlLike = ["vue", "html", "svelte"].includes(
    framework.toLowerCase(),
  );
  const commentLines = [baseComment, link].filter(Boolean);

  const comments = isHtmlLike
    ? commentLines.map((line) => `<!-- ${line} -->`).join("\n")
    : `/**\n${commentLines.map((line) => ` * ${line}`).join("\n")}\n */`;

  return `${comments}\n${str}`;
}

async function suggestComponentPath(
  appRootDir: string,
  framework: string,
  content: string,
) {
  let path;
  const componentName = detectComponentName(content, framework.toLowerCase());

  switch (framework.toLowerCase()) {
    case "react":
      const isTypescript = await detectTypescript(appRootDir);
      path = `components/${componentName || "MyComponent"}.${
        isTypescript ? "tsx" : "jsx"
      }`;
      break;
    case "angular":
      path = `app/components/${componentName || "my-component"}.component.ts`;
      break;
    case "vue":
      path = `components/${componentName || "MyComponent"}.vue`;
      break;
    case "svelte":
      path = `components/${componentName || "MyComponent"}.svelte`;
      break;
    case "html":
      path = `${componentName || "my-design"}.html`;
      break;
    case "qwik":
      path = `components/${componentName || "MyComponent"}.tsx`;
      break;
    default:
      path = `components/${componentName || "MyComponent"}.tsx`;
  }

  path = existsSync("./src") ? `./src/${path}` : `./${path}`;

  return path;
}

async function detectTypescript(appRootDir: string) {
  if (existsSync(join(appRootDir, "./tsconfig.json"))) {
    return true;
  }

  const packageJsonDir = findPackageJsonDir(appRootDir);
  if (packageJsonDir) {
    if (existsSync(join(packageJsonDir, "./package.json"))) {
      const packageJSON = readFileSync(
        join(packageJsonDir, "./package.json"),
        "utf-8",
      );
      return packageJSON.includes("typescript");
    }
  }
  return false;
}

function detectComponentName(content: string, framework: string) {
  const frameworkNaming: Record<string, RegExp> = {
    vue: /name:\s*'([^']+)'/,
    react: /function (\w+)\([\w\W]+\)/,
    qwik: /export const (\w+) = component\$/,
    angular: /selector:\s*'([^']+)'/,
  };
  if (!frameworkNaming[framework]) {
    return null;
  }
  const match = content.match(frameworkNaming[framework]);

  if (framework === "react" && !match?.[1]) {
    // if no match for react, let's try to find a const Component = () => {} declaration
    const match = content.match(/const (\w+)[\w\W]+=\s*\(/);
    return match?.[1] || null;
  }

  return match?.[1] || null;
}

export async function writeFiles(
  appRootDir: string,
  snippet: Snippet,
  files: Array<FileNode | FolderNode>,
  rootPath: string,
  addSessionLink: boolean,
) {
  let results: Array<FileNode> = [];
  for (const item of files) {
    const itemPath = combinePaths(rootPath, item.path);
    const fullPath = resolve(appRootDir, itemPath);

    if ("code" in item) {
      const formatted = await formatCode(
        snippet,
        item.code,
        getParserForFile(item.path),
        addSessionLink,
      );
      mkdirSync(dirname(fullPath), { recursive: true });
      writeFileSync(fullPath, formatted);
      results.push({
        ...item,
        path: itemPath,
        code: formatted,
        timestamp: statSync(fullPath).mtime.getTime(),
      });
      log.info(`Writing file to ${fullPath}`);
    } else {
      // It's a FolderNode
      mkdirSync(fullPath, { recursive: true });
      results = results.concat(
        await writeFiles(
          appRootDir,
          snippet,
          item.files,
          itemPath,
          addSessionLink,
        ),
      );
    }
  }
  return results;
}

function combinePaths(rootPath: string, itemPath: string): string {
  // Normalize paths by removing leading and trailing slashes
  const normalizedRoot = rootPath.replace(/^\.?\/+|\/+$/g, "");
  const normalizedItem = itemPath.replace(/^\.?\/+|\/+$/g, "");

  // Split the paths into segments
  const rootSegments = normalizedRoot.split("/").filter(Boolean);
  const itemSegments = normalizedItem.split("/").filter(Boolean);

  // Find the last common segment
  let lastCommonIndex = -1;
  for (let i = 0; i < rootSegments.length; i++) {
    const index = itemSegments.indexOf(rootSegments[i]);
    if (index !== -1) {
      lastCommonIndex = index;
    }
  }

  // Combine the paths
  const resultSegments = [
    ...rootSegments,
    ...itemSegments.slice(lastCommonIndex + 1),
  ];

  return resultSegments.join("/");
}

function getParserForFile(filePath: string): string {
  const extension = filePath.split(".").pop()?.toLowerCase();
  switch (extension) {
    case "html":
    case "svelte":
      return "html";
    case "vue":
      return "vue";
    case "ts":
    case "tsx":
    case "js":
    case "jsx":
      return "typescript";
    case "css":
      return "css";
    case "json":
      return "json";
    default:
      return "typescript";
  }
}

async function formatCode(
  snippet: Snippet,
  code: string,
  parser: string,
  addSessionLink: boolean,
): Promise<string> {
  let codeToFormat = code;
  try {
    codeToFormat = await prettierFormat(code, parser);
  } catch (e) {
    // ignore crashed prettier
  }
  return addSessionLink
    ? addBuilderSignature(snippet, codeToFormat, parser, addSessionLink)
    : codeToFormat;
}

export function suggestRootPath(appRootDir: string, framework: string) {
  const hasSrcFolder = existsSync(join(appRootDir, "src"));
  const srcPrefix = hasSrcFolder ? "src/" : "";

  switch (framework.toLowerCase()) {
    case "angular":
      return `./${srcPrefix}app/components/`;
    case "html":
      return `./`;
    default:
      return `./${srcPrefix}components/`;
  }
}
