import type { DevToolsSys, EnvInfo } from "../types";

export async function getDotEnvValue(
  sys: DevToolsSys,
  dotEnvFileNames: string[],
  envKey: string,
) {
  const envInfo: EnvInfo = {
    envKey,
    envValue: null,
    file: "",
  };

  for (const dotEnvFileName of dotEnvFileNames) {
    const envPath = sys.join(sys.getAppRootDir(), dotEnvFileName);
    const envVars = await parseDotEnvFile(sys, envPath);
    if (envVars) {
      const value = envVars[envKey];
      if (
        typeof value === "string" &&
        value.length > 0 &&
        value !== "YOUR_API_KEY"
      ) {
        envInfo.envValue = value;
        envInfo.file = envPath;
        return envInfo;
      }
    }
  }

  envInfo.file = sys.join(
    sys.getAppRootDir(),
    dotEnvFileNames[dotEnvFileNames.length - 1],
  );
  return envInfo;
}

export async function setDotEnvValue(
  sys: DevToolsSys,
  dotEnvFileNames: string[],
  envKey: string,
  envValue: string,
) {
  const env = await getDotEnvValue(sys, dotEnvFileNames, envKey);

  const modifiedType = await setDotEnvVar(sys, env.file, envKey, envValue);

  const envInfo: EnvInfo = {
    envKey,
    envValue,
    file: env.file,
    modifiedType,
  };

  return envInfo;
}

export async function parseDotEnvFile(sys: DevToolsSys, envPath: string) {
  const envContent = await sys.readFile(envPath);
  if (typeof envContent === "string") {
    return parseDotEnvContent(envContent);
  }
  return null;
}

export function parseDotEnvContent(envContent: string) {
  const envVars: Record<string, string> = {};

  // Convert line breaks to same format
  const lines = envContent.replace(/\r\n?/gm, "\n");

  let match: RegExpExecArray | null;
  while ((match = DOTENV_LINE.exec(lines)) != null) {
    const key = match[1];

    // Default undefined or null to empty string
    let value = match[2] || "";

    // Remove whitespace
    value = value.trim();

    // Check if double quoted
    const maybeQuote = value[0];

    // Remove surrounding quotes
    value = value.replace(/^(['"`])([\s\S]*)\1$/gm, "$2");

    // Expand newlines if double quoted
    if (maybeQuote === '"') {
      value = value.replace(/\\n/g, "\n");
      value = value.replace(/\\r/g, "\r");
    }

    // Add to object
    envVars[key] = value;
  }

  return envVars;
}

const DOTENV_LINE =
  /(?:^|^)\s*(?:export\s+)?([\w.-]+)(?:\s*=\s*?|:\s+?)(\s*'(?:\\'|[^'])*'|\s*"(?:\\"|[^"])*"|\s*`(?:\\`|[^`])*`|[^#\r\n]+)?\s*(?:#.*)?(?:$|$)/gm;

export async function setDotEnvVar(
  sys: DevToolsSys,
  envPath: string,
  key: string,
  value: string,
): Promise<"create" | "update" | null> {
  const comment = `# https://www.builder.io/c/docs/using-your-api-key`;
  let envContent = await sys.readFile(envPath);

  // check if we already have an .env file
  if (envContent) {
    // read the existing .env file
    if (envContent.includes(key)) {
      // existing .env has a builder api key already, update its value
      if (!envContent.includes(value)) {
        // existing .env has a builder api key, but it's not the same as the one we have
        sys.debug(`Replace "${key}" in ${envPath}`);
        envContent = envContent.replace(
          new RegExp(`${key}=.*`),
          `${key}=${value}`,
        );
        await sys.writeFile(envPath, envContent);
        return "update";
      } else {
        sys.debug(`"${key}" with "${value}" already set in ${envPath}`);
      }
    } else {
      // existing .env does not have a builder api key, append the key/value
      sys.debug(`Append "${key}" to ${envPath}`);
      envContent += `\n\n${comment}\n${key}=${value}\n\n`;
      await sys.writeFile(envPath, envContent);
      return "update";
    }
  } else {
    // create a new .env file since it doesn't exist yet
    sys.debug(`Creating new .env file with "${key}" at ${envPath}`);
    envContent = [comment, `${key}=${value}`, ``].join("\n");
    await sys.writeFile(envPath, envContent);
    return "create";
  }

  return null;
}
