import type ts from "typescript";
import type { DevToolsSys } from "../..";
import type { EnsureConfigResult } from "../../../types";
import { codeToSource, sourceToCode } from "../../../common/ast/transform";
import { getExportedStatements } from "../../../common/ast/exported-statements";
import { ensureImport } from "../../../common/ast/ensure-imports";
import { detectTurbopack } from "../../detect-frameworks";

export async function nextEnsureConfigPlugin(
  sys: DevToolsSys,
  configFilePath: string,
  configContent: string,
) {
  try {
    let sourceFile = codeToSource(sys, configContent);

    const result: EnsureConfigResult = {
      content: configContent,
      filePath: configFilePath,
      fileName: sys.basename(configFilePath),
      hasContentUpdates: false,
    };

    const isTurbopack = await detectTurbopack(sys);
    if (isTurbopack) {
      console.warn(
        `Turbopack does not support the Builder.io devtools cli. Please disable Turbopack or manually integrate Builder.io into your Next.js project.`,
      );
      return result;
    }

    const withFn = getWithFnVariableName(sys, sourceFile);
    if (withFn) {
      return result;
    }

    const cjsExports = getExportedStatements(sys, sourceFile, "cjs");
    const esmExports = getExportedStatements(sys, sourceFile);
    if (cjsExports.length !== 1 && esmExports.length !== 1) {
      throw new Error(`Expected an export from file`);
    }

    if (esmExports.length === 1) {
      const esmDefaultExport = esmExports[0];
      if (!esmDefaultExport.isDefaultExport) {
        throw new Error(`Expected a default export from file`);
      }

      const exportAssignment = esmDefaultExport.statement;
      if (!sys.ts.isVariableStatement(exportAssignment)) {
        throw new Error(
          `Unexpected export type in ${configFilePath}\n` +
            `Found config content:\n${configContent}\n\n` +
            `The config file must follow one of these formats:\n` +
            `1. const nextConfig = { /* config */ }\n` +
            `   export default nextConfig\n\n` +
            `2. const nextConfig = (phase, config) => ({ /* config */ })\n` +
            `   export default nextConfig\n\n` +
            `3. export default { /* config */ }\n\n` +
            `Current export type: ${
              exportAssignment.kind
                ? sys.ts.SyntaxKind[exportAssignment.kind]
                : "none"
            }`,
        );
      }

      const configVariableStatement = esmDefaultExport.statement;
      if (sys.ts.isVariableStatement(configVariableStatement)) {
        // export default config

        const declarations = [
          ...configVariableStatement.declarationList.declarations,
        ];
        if (declarations.length !== 1) {
          throw new Error(`Expected a single variable declaration`);
        }

        const configDeclaration = declarations[0];
        if (!configDeclaration.initializer) {
          throw new Error(`Expected an initializer on the declaration`);
        }

        const configInitializer = configDeclaration.initializer;
        if (
          sys.ts.isObjectLiteralExpression(configInitializer) ||
          sys.ts.isCallExpression(configInitializer)
        ) {
          // const config = {}
          // export default config

          const fnVariableName = "BuilderDevTools()";

          const statements = [...sourceFile.statements];
          const index = statements.indexOf(configVariableStatement);

          statements[index] = sys.ts.factory.updateVariableStatement(
            configVariableStatement,
            configVariableStatement.modifiers,
            sys.ts.factory.updateVariableDeclarationList(
              configVariableStatement.declarationList,
              [
                sys.ts.factory.updateVariableDeclaration(
                  configDeclaration,
                  configDeclaration.name,
                  configDeclaration.exclamationToken,
                  configDeclaration.type,
                  sys.ts.factory.createCallExpression(
                    sys.ts.factory.createIdentifier(fnVariableName),
                    undefined,
                    [configInitializer],
                  ),
                ),
              ],
            ),
          );

          sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

          sourceFile = ensureImport(sys, sourceFile, {
            defaultImport: "BuilderDevTools",
            importPath: "@builder.io/dev-tools/next",
          });

          result.content = sourceToCode(sys, sourceFile);
          result.hasContentUpdates = true;

          return result;
        }

        throw new Error(
          `Unexpected initializer on the variable statement in file\n` +
            `Found config content:\n${configContent}\n\n` +
            `The config variable must be initialized with either:\n` +
            `1. An object literal: const nextConfig = { /* config */ }\n` +
            `2. A function call: const nextConfig = withSomePlugin({ /* config */ })\n\n` +
            `Current initializer type: ${
              configInitializer?.kind
                ? sys.ts.SyntaxKind[configInitializer.kind]
                : "none"
            }\n` +
            `Expected types: ObjectLiteralExpression or CallExpression`,
        );
      }
    }

    const defaultExport = cjsExports[0];
    if (!defaultExport.isDefaultExport) {
      throw new Error(
        `Expected a default export from ${configFilePath}\n` +
          `Found config content:\n${configContent}\n` +
          `Detected export type: ${defaultExport?.statement.kind ?? "none"}\n` +
          `Valid formats:\n` +
          `1. module.exports = { ... }\n` +
          `2. module.exports = (phase, config) => { return { ... } }\n` +
          `3. export default { ... }`,
      );
    }

    if (
      sys.ts.isObjectLiteralExpression(defaultExport.node) ||
      sys.ts.isCallExpression(defaultExport.node)
    ) {
      const moduleExportsStatement = defaultExport.statement;
      if (!sys.ts.isExpressionStatement(moduleExportsStatement)) {
        throw new Error(
          `Expected an expression statement for the default export`,
        );
      }

      const moduleExports = moduleExportsStatement.expression;
      if (!sys.ts.isBinaryExpression(moduleExports)) {
        throw new Error(`Expected a binary expression for the default export`);
      }

      // module.exports = {}
      const w = ensureWithFnVariableName(sys, sourceFile);
      sourceFile = w.sourceFile;

      const statements = [...sourceFile.statements];
      const index = statements.indexOf(moduleExportsStatement);
      statements[index] = sys.ts.factory.updateExpressionStatement(
        moduleExportsStatement,
        sys.ts.factory.updateBinaryExpression(
          moduleExports,
          moduleExports.left,
          moduleExports.operatorToken,
          sys.ts.factory.createCallExpression(
            sys.ts.factory.createIdentifier(w.withFnVariableName),
            undefined,
            [moduleExports.right],
          ),
        ),
      );
      sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

      result.content = sourceToCode(sys, sourceFile);
      result.hasContentUpdates = true;
      return result;
    }

    const nextConfigVariableStatement = defaultExport.statement;
    if (sys.ts.isVariableStatement(nextConfigVariableStatement)) {
      // module.exports = nextConfig

      const declarations = [
        ...nextConfigVariableStatement.declarationList.declarations,
      ];
      if (declarations.length !== 1) {
        throw new Error(`Expected a single variable declaration`);
      }

      const nextConfigDeclaration = declarations[0];
      if (!nextConfigDeclaration.initializer) {
        throw new Error(`Expected an initializer on the declaration`);
      }

      const nextConfigInitializer = nextConfigDeclaration.initializer;
      if (
        sys.ts.isObjectLiteralExpression(nextConfigInitializer) ||
        sys.ts.isCallExpression(nextConfigInitializer)
      ) {
        // const nextConfig = {}
        // module.exports = nextConfig

        const w = ensureWithFnVariableName(sys, sourceFile);
        sourceFile = w.sourceFile;

        const statements = [...sourceFile.statements];
        const index = statements.indexOf(nextConfigVariableStatement);

        statements[index] = sys.ts.factory.updateVariableStatement(
          nextConfigVariableStatement,
          nextConfigVariableStatement.modifiers,
          sys.ts.factory.updateVariableDeclarationList(
            nextConfigVariableStatement.declarationList,
            [
              sys.ts.factory.updateVariableDeclaration(
                nextConfigDeclaration,
                nextConfigDeclaration.name,
                nextConfigDeclaration.exclamationToken,
                nextConfigDeclaration.type,
                sys.ts.factory.createCallExpression(
                  sys.ts.factory.createIdentifier(w.withFnVariableName),
                  undefined,
                  [nextConfigInitializer],
                ),
              ),
            ],
          ),
        );

        sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

        result.content = sourceToCode(sys, sourceFile);
        result.hasContentUpdates = true;
        return result;
      }

      throw new Error(
        `Unexpected initializer on the variable statement in ${configFilePath}\n` +
          `Found config content:\n${configContent}\n\n` +
          `The config variable must be initialized with either:\n` +
          `1. An object literal: const nextConfig = { /* config */ }\n` +
          `2. A function call: const nextConfig = withSomePlugin({ /* config */ })\n\n` +
          `Current initializer type: ${nextConfigInitializer.kind}\n` +
          `Expected types: ${sys.ts.SyntaxKind.ObjectLiteralExpression} or ${sys.ts.SyntaxKind.CallExpression}`,
      );
    }

    if (sys.ts.isArrowFunction(defaultExport.node)) {
      const arrowFn = defaultExport.node;

      const moduleExportsStatement = defaultExport.statement;
      if (!sys.ts.isExpressionStatement(moduleExportsStatement)) {
        throw new Error(
          `Expected an expression statement for the default export`,
        );
      }

      const moduleExports = moduleExportsStatement.expression;
      if (!sys.ts.isBinaryExpression(moduleExports)) {
        throw new Error(`Expected a binary expression for the default export`);
      }

      const configFn = defaultExport.node;
      const fnBody = configFn.body;
      if (!sys.ts.isBlock(fnBody)) {
        throw new Error(`Expected a block body on the next config function`);
      }

      const fnStatements = [...fnBody.statements];
      const rtnStatementIndex = fnStatements.findIndex((s) =>
        sys.ts.isReturnStatement(s),
      );
      if (rtnStatementIndex === -1) {
        throw new Error(
          `Expected a return statement on the next config function`,
        );
      }
      const rtnStatement = fnStatements[
        rtnStatementIndex
      ] as ts.ReturnStatement;

      // module.exports = (phase, defaultConfig) => {
      //   return defaultConfig;
      // };

      const w = ensureWithFnVariableName(sys, sourceFile);
      sourceFile = w.sourceFile;

      fnStatements[rtnStatementIndex] = sys.ts.factory.updateReturnStatement(
        rtnStatement,
        sys.ts.factory.createCallExpression(
          sys.ts.factory.createIdentifier(w.withFnVariableName),
          undefined,
          [rtnStatement.expression!],
        ),
      );

      const statements = [...sourceFile.statements];
      const index = statements.indexOf(moduleExportsStatement);

      statements[index] = sys.ts.factory.updateExpressionStatement(
        moduleExportsStatement,
        sys.ts.factory.updateBinaryExpression(
          moduleExports,
          moduleExports.left,
          moduleExports.operatorToken,
          sys.ts.factory.updateArrowFunction(
            arrowFn,
            arrowFn.modifiers,
            arrowFn.typeParameters,
            arrowFn.parameters,
            arrowFn.type,
            arrowFn.equalsGreaterThanToken,
            sys.ts.factory.updateBlock(fnBody, fnStatements),
          ),
        ),
      );
      sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

      result.content = sourceToCode(sys, sourceFile);
      result.hasContentUpdates = true;
      return result;
    }

    throw new Error(`Unexpected export type`);
  } catch (e) {
    sys.Sentry?.captureException(e, {
      extra: {
        configFilePath,
        configContent,
      },
      level: "warning",
    });
    throw e;
  }
}

function getWithFnVariableName(sys: DevToolsSys, sourceFile: ts.SourceFile) {
  for (const s of sourceFile.statements) {
    if (!sys.ts.isVariableStatement(s)) {
      continue;
    }
    for (const d of s.declarationList.declarations) {
      if (!d.initializer) {
        continue;
      }
      if (!sys.ts.isCallExpression(d.initializer)) {
        continue;
      }
      const requireExp = d.initializer.expression;
      if (!sys.ts.isCallExpression(requireExp)) {
        continue;
      }
      if (!sys.ts.isIdentifier(requireExp.expression)) {
        continue;
      }
      if (requireExp.expression.text !== "require") {
        continue;
      }
      const requirePath = requireExp.arguments[0];
      if (!requirePath || !sys.ts.isStringLiteral(requirePath)) {
        continue;
      }
      if (requirePath.text !== "@builder.io/dev-tools/next") {
        continue;
      }
      if (!sys.ts.isIdentifier(d.name)) {
        continue;
      }
      return d.name.text;
    }
  }
  return null;
}

function ensureWithFnVariableName(sys: DevToolsSys, sourceFile: ts.SourceFile) {
  let withFnVariableName = getWithFnVariableName(sys, sourceFile);

  if (!withFnVariableName) {
    withFnVariableName = `withBuilderDevTools`;

    sourceFile = sys.ts.factory.updateSourceFile(sourceFile, [
      sys.ts.factory.createVariableStatement(
        undefined,
        sys.ts.factory.createVariableDeclarationList(
          [
            sys.ts.factory.createVariableDeclaration(
              withFnVariableName,
              undefined,
              undefined,
              sys.ts.factory.createCallExpression(
                sys.ts.factory.createCallExpression(
                  sys.ts.factory.createIdentifier("require"),
                  undefined,
                  [
                    sys.ts.factory.createStringLiteral(
                      "@builder.io/dev-tools/next",
                    ),
                  ],
                ),
                undefined,
                [],
              ),
            ),
          ],
          sys.ts.NodeFlags.Const,
        ),
      ),
      ...sourceFile.statements,
    ]);
  }

  return { withFnVariableName, sourceFile };
}
