import type { DevToolsServerContext } from "../types";
import {
  BUILDER_AUTH_RETURN_PATH,
  FRAMEWORK_QS,
  PLATFORM_QS,
  NODE_VERSION_QS,
} from "../common/constants";
import { platform } from "node:os";

export async function getBuilderAuthConnectUrl(
  ctx: DevToolsServerContext,
  previewUrl: string,
) {
  const authUrl = new URL(`/cli-auth`, `https://builder.io`);
  authUrl.searchParams.set(`client_id`, ctx.getClientId());

  authUrl.searchParams.set(`host`, await ctx.getDeviceId());
  authUrl.searchParams.set(`response_type`, `code`);
  authUrl.searchParams.set(`cli`, `true`);

  const returnUrl = new URL(BUILDER_AUTH_RETURN_PATH, ctx.devToolsServerUrl);
  returnUrl.searchParams.set(PLATFORM_QS, platform());
  returnUrl.searchParams.set(NODE_VERSION_QS, process.versions.node);

  const frameworks = ctx.getFrameworks();
  if (frameworks.length > 0) {
    authUrl.searchParams.set(FRAMEWORK_QS, frameworks[0].name);
    returnUrl.searchParams.set(FRAMEWORK_QS, frameworks[0].name);
  }

  authUrl.searchParams.set(`redirect_url`, returnUrl.href);
  authUrl.searchParams.set(`preview_url`, previewUrl);

  return authUrl.href;
}
