import { cancel, log } from "@clack/prompts";
import { safeFetch } from "./fetch";
import { exit } from "./exit";
import * as Sentry from "@sentry/node";
import { apiAirV2Host } from "./constants";
import {
  getCredentials,
  type Credentials,
  type FigmaAuth,
} from "./credentials";
import type { DevToolsSys, ExportType } from "../types";
import type { CLIArgs } from "./index";
import type { FigmaComponentInfo, FigmaComponentInput } from "$/ai-utils";

export interface FigmaBuilderLink {
  builderName: string;
  figmaName: string;
  figmaKey: string;
  figmaUrl?: string;
  inputMapper?: string;
  originalInputMapper?: string;
  exportType?: ExportType;
  importName?: string;
  importPath?: string;
  source: string;
  loc?: string;
}

type FigmaAPIOpts = {
  auth: {
    access_token: string;
    oauth: boolean;
  };
  params?: Record<string, any>;
};
export const REMOVE_EMOJI = /[^\x00-\x7F]/g;

export const parseFigmaURL = (str: string) => {
  try {
    const url = new URL(str);
    if (!str.startsWith("https://www.figma.com/design/")) {
      return null;
    }
    const nodeId = url.searchParams.get("node-id")?.replaceAll("-", ":");
    const fileID = url.pathname.split("/")[2];
    if (nodeId && fileID) {
      return { fileID, nodeId };
    }
    return null;
  } catch {
    return null;
  }
};

const baseFigmaAPI = "https://api.figma.com/v1/";
const figmaApi = async (
  sys: DevToolsSys,
  args: CLIArgs,
  path: string,
  { auth, params }: FigmaAPIOpts,
) => {
  const url = new URL(`${baseFigmaAPI}${path}`),
    headers: Record<string, string> = {};
  if (auth.oauth) {
    headers.Authorization = `Bearer ${auth.access_token}`;
  } else {
    headers["X-FIGMA-TOKEN"] = auth.access_token;
  }

  if (params) {
    for (const key in params) {
      if (params[key] != null) {
        url.searchParams.set(key, params[key].toString());
      }
    }
  }

  const res = await safeFetch(url, {
    headers,
  });
  if (!res.ok) {
    if (res.status === 403) {
      log.error(
        "Seems like figma auth is expired or it's invalid, let's try to",
      );
      const credentials = await getCredentials(sys, args, {
        force: true,
        figmaAuth: true,
      });
      return figmaApi(sys, args, path, {
        auth: credentials.figmaAuth!,
        params,
      });
    }
    throw new Error(`Figma REST ${res.statusText}: ${await res.text()}`);
  }
  return res.json();
};

export const getFigmaNodeData = async (
  sys: DevToolsSys,
  args: CLIArgs,
  auth: {
    access_token: string;
    oauth: boolean;
  },
  fileId: string,
  nodeIds: string,
  depth?: number,
) => {
  // curl -H 'X-FIGMA-TOKEN: <personal access token>' 'https://api.figma.com/v1/files/:file_key/nodes?ids=1:34'
  const data = await figmaApi(sys, args, `files/${fileId}/nodes`, {
    auth,
    params: {
      ids: nodeIds,
      depth,
    },
  });

  return data.nodes;
};

export function getFigmaComponentName(name: string) {
  name = name.replace(REMOVE_EMOJI, "");
  name = name.replace(/[\W\d]/g, " ");
  name = name.trim();
  name = camelize(name);
  // capilize first letter
  name = name.charAt(0).toUpperCase() + name.slice(1);
  return name;
}

function camelize(str: string) {
  return str
    .replace(/(?:^\w|[A-Z]|\b\w)/g, function (word, index) {
      return index === 0 ? word.toLowerCase() : word.toUpperCase();
    })
    .replace(/\s+/g, "");
}

const componentKeyRegex = /^[a-f0-9]{40}$/;

export async function getImportDataFromToken(
  credentials: Credentials,
  token: string,
  verbose: boolean,
): Promise<(readonly [string, FigmaComponentInfo])[]> {
  const json = JSON.stringify({
    token,
  });
  const url = new URL(`${apiAirV2Host}/mappings/figma-components/get`);
  if (credentials.builderPublicKey) {
    url.searchParams.set("apiKey", credentials.builderPublicKey);
  }
  if (credentials.userId) {
    url.searchParams.set("userId", credentials.userId);
  }
  if (verbose) {
    console.log("FETCH", url, json);
  }
  const headers: Record<string, string> = {
    "Content-Type": "application/json",
  };
  if (credentials.builderPrivateKey) {
    headers.Authorization = `Bearer ${credentials.builderPrivateKey}`;
  }
  const res = await safeFetch(url, {
    method: "POST",
    body: json,
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${credentials.builderPrivateKey!}`,
    },
  });
  if (!res.ok) {
    throw new Error("Failed to generate completion:\n" + (await res.text()));
  }
  const responseJson = await res.json();
  if (!Array.isArray(responseJson.figmaComponents)) {
    throw new Error("Invalid response from Figma API");
  }
  return responseJson.figmaComponents.map((a: any) => ["", a] as const);
}

export function needsFigmaAuth(urls: string[]) {
  return urls.some((url) => !!parseFigmaURL(url));
}

export async function getFigmaNodeDataFromURLs(
  sys: DevToolsSys,
  args: CLIArgs,
  figmaAuth:
    | {
        access_token: string;
        oauth: boolean;
      }
    | undefined,
  builderAuth: {
    privateKey: string;
    spaceId: string;
  },
  urls: string[],
): Promise<(readonly [string, FigmaComponentInfo])[]> {
  const importData = await Promise.all(
    urls.map(async (url) => {
      let fileId, nodeId;
      if (url.match(componentKeyRegex)) {
        try {
          if (figmaAuth) {
            let figmaRestApiData;
            try {
              // Fall back to Figma components API, requires that the component be published and that the user has access to it
              figmaRestApiData = await figmaApi(
                sys,
                args,
                `components/${url}`,
                {
                  auth: figmaAuth,
                },
              );
              fileId = figmaRestApiData.meta.file_key;
              nodeId = figmaRestApiData.meta.node_id;
            } catch (e) {
              log.warning(`Failed to fetch component from Figma API.`);
            }
          }
        } catch (e) {
          Sentry.captureException(e);
          throw e;
        }
      }

      if (!fileId || !nodeId) {
        // Parse figma link URL
        // TODO: Return all components in selection
        const parsedFile = parseFigmaURL(url);
        if (!parsedFile) {
          cancel(
            "Invalid Figma URL, use Cmd+L while selecting a figma component to get a link to selection.\n   " +
              "Or make sure the command is copied correctly from the Figma plugin and the same builder space is being used.",
          );
          return await exit(1);
        }
        if (parsedFile) {
          ({ fileID: fileId, nodeId } = parsedFile);
        }
      }

      if (!figmaAuth) {
        return [];
      }

      // Figma link or published component fileId and nodeId
      const figmaRestApiData = await getFigmaNodeData(
        sys,
        args,
        figmaAuth,
        fileId,
        nodeId,
      );

      const info = await getComponentInfo(figmaRestApiData[nodeId], nodeId);
      if (info) {
        return [[url, info]] as const;
      }
      return [];
    }),
  );

  return importData.flat().filter(nonNull);
}

async function getComponentInfo(
  figmaRestApiData: any,
  nodeId: string,
): Promise<FigmaComponentInfo | undefined> {
  const nodeDocument = figmaRestApiData.document;
  if (
    nodeDocument.type !== "COMPONENT" &&
    nodeDocument.type !== "COMPONENT_SET"
  ) {
    let message = `Seems like the pasted URL points to a ${nodeDocument.type} node, not a COMPONENT or COMPONENT_SET.`;
    if (nodeDocument.type === "INSTANCE") {
      message += `\nDouble click and select "Go to main component", then copy the URL to the figma COMPONENT`;
    }
    cancel(message);
    return await exit(1);
  }

  const componentKey =
    figmaRestApiData.components[nodeId]?.key ??
    figmaRestApiData.componentSets[nodeId]?.key;
  if (!componentKey) {
    cancel("Pasted URL does not correspond to a component or component set");
    return await exit(1);
  }
  figmaRestApiData.document.key = componentKey;

  return computeComponentInfo(nodeId, nodeDocument);
}

function computeComponentInfo(
  figmaKeyOrId: string,
  mainComponent: ComponentNode | ComponentSetNode,
) {
  const { inputs, nameMap } =
    getInputsSlotsFromComponentPropertyDefinitions(mainComponent);

  const figmaNodeInfo: FigmaComponentInfo = {
    type: mainComponent.type,
    name: mainComponent.name,

    jsx:
      mainComponent.type === "COMPONENT_SET"
        ? printTreeJSX(mainComponent.children[0], nameMap, 0, 0)
        : printTreeJSX(mainComponent, nameMap, 0, 0),
    inputs: inputs,
    key: mainComponent.key,
    description: mainComponent.description,
    documentationLinks: [],
    instanceId: figmaKeyOrId.includes(":") ? figmaKeyOrId : mainComponent.id,
    documentName: "",
    tree: "",
  };

  return figmaNodeInfo;
}

const printTreeJSX = (
  node: SceneNode,
  nameMap: (key: string) => string,
  _index: number,
  nuSiblings: number,
  level = -1,
): string => {
  const spaces = "  ".repeat(Math.max(level + 1, 0));

  let output = "";
  const type = node.type;
  let shouldWalk =
    type === "GROUP" || type === "FRAME" || (level < 0 && "children" in node);
  let instance = "";
  const emit = level >= 0;
  if (emit) {
    let props = "";
    if (node.type === "INSTANCE") {
      if (
        node.componentPropertyReferences &&
        node.componentPropertyReferences.mainComponent
      ) {
        output += `${spaces}{figma["${nameMap(
          node.componentPropertyReferences.mainComponent,
        )}"]}\n`;
        return output;
      } else {
        const cmp = undefined as any;
        if (false) {
          instance = getFigmaComponentName(cmp.name);
          if (cmp.name.startsWith(".")) {
            shouldWalk = true;
          } else if (nuSiblings === 1) {
            shouldWalk = true;
          }
        } else {
          instance = node.type;
        }
      }
    }
    if (instance === "") {
      // if ('layoutMode' in node && node.layoutMode === 'HORIZONTAL') {
      //   instance = 'hframe';
      // } else if ('layoutMode' in node && node.layoutMode === 'VERTICAL') {
      //   instance = 'vframe';
      // } else {
      // }
      instance = node.type;
    }
    if (node.type !== "TEXT" || !node.autoRename) {
      props += ` name="${node.name}"`;
    }
    if (
      node.componentPropertyReferences &&
      node.componentPropertyReferences.visible
    ) {
      props += ` visible={figma["${nameMap(
        node.componentPropertyReferences.visible,
      )}"]}`;
    }
    output += `${spaces}<${instance}${props}`;
  }
  if (node.type === "TEXT") {
    if (emit) {
      output += `>\n`;
    }
    if (
      node.componentPropertyReferences &&
      node.componentPropertyReferences.characters
    ) {
      output += `${spaces}  {figma["${nameMap(
        node.componentPropertyReferences.characters,
      )}"]}\n`;
    } else {
      output += `${spaces}  ${node.characters}\n`;
    }
    if (emit) {
      output += `${spaces}</${instance}>\n`;
    }
  } else if (shouldWalk && "children" in node && node.children.length > 0) {
    if (emit) {
      output += `>\n`;
    }
    let index = 0;
    for (const child of node.children) {
      output += printTreeJSX(
        child,
        nameMap,
        index,
        node.children.length,
        level + 1,
      );
      index++;
    }
    if (emit) {
      output += `${spaces}</${instance}>\n`;
    }
  } else if (emit) {
    output += `/>\n`;
  }
  return output;
};

function getInputsSlotsFromComponentPropertyDefinitions(
  mainComponent: ComponentSetNode | ComponentNode,
) {
  const inputs: FigmaComponentInput[] = [];
  let cmpPropDefinitions: ComponentPropertyDefinitions = {};
  try {
    cmpPropDefinitions = mainComponent.componentPropertyDefinitions ?? {};
  } catch (e) {
    // TODO: Show a nice warning in UI for users
    console.error("Invalid Component Property Definitions", e);
  }

  const components =
    mainComponent.type === "COMPONENT_SET"
      ? (mainComponent.children as ComponentNode[])
      : [mainComponent];

  const groups = new Map();
  const instances: (InstanceNode | TextNode)[] = [];
  const ids = Object.keys(cmpPropDefinitions);
  const nameMap = computeInputNames(ids);

  for (const component of components) {
    const instancesOriginal = findAllWithCriteria(component, {
      types: ["INSTANCE", "TEXT"],
    });
    for (const instance of instancesOriginal) {
      instances.push(instance);
      const refs = instance.componentPropertyReferences;
      if (refs) {
        if (refs.visible) {
          if (refs.characters) {
            groups.set(refs.characters, nameMap(refs.visible));
          }
          if (refs.mainComponent) {
            groups.set(refs.mainComponent, nameMap(refs.visible));
          }
        }
      }
    }
  }

  for (const id of ids) {
    const cmpPropDefinition = cmpPropDefinitions[id];

    switch (cmpPropDefinition.type) {
      case "TEXT": {
        // text component property
        inputs.push({
          id,
          name: nameMap(id),
          value: cmpPropDefinition.defaultValue,
          type: "string",
          baseType: "text",
          isDefault: true,
          ref: groups.get(id),
        });
        break;
      }

      case "BOOLEAN": {
        // boolean component property
        inputs.push({
          id,
          name: nameMap(id),
          value: !!cmpPropDefinition.defaultValue,
          baseType: "boolean",
          isDefault: true,
          type: "boolean",
        });
        break;
      }

      case "VARIANT": {
        // variant component property
        inputs.push({
          id,
          name: nameMap(id),
          value: cmpPropDefinition.defaultValue,
          type:
            cmpPropDefinition.variantOptions
              ?.map((a) => `'${a}'`)
              .join(" | ") ?? "string",
          baseType: "variant",
          variantOptions: cmpPropDefinition.variantOptions ?? [],
          isDefault: true,
          ref: groups.get(id),
        });
        break;
      }

      case "INSTANCE_SWAP": {
        // instance swap component property
        inputs.push({
          id,
          name: nameMap(id),
          value: String(cmpPropDefinition.defaultValue),
          type: "React.ReactNode",
          baseType: "slot",
          isDefault: true,
          ref: groups.get(id),
        });
        break;
      }
    }
  }

  return { inputs, nameMap };
}

function computeInputNames(ids: string[]) {
  const nameMap = new Map<string, string>();
  const nameSetted = new Map<string, string>();
  ids.forEach((id) => {
    const n = getMainComponentPropName(id);
    if (nameSetted.has(n)) {
      if (nameMap.has(id)) {
        nameMap.set(nameSetted.get(n)!, nameSetted.get(n)!);
        nameMap.delete(n);
      }
      nameMap.set(id, id);
    } else {
      nameMap.set(id, n);
      nameSetted.set(n, id);
    }
  });
  return (key: string) => {
    const value = nameMap.get(key) ?? getMainComponentPropName(key);
    if (!value) {
      throw new Error("No value for key " + key);
    }
    return value;
  };
}

function getMainComponentPropName(propId: string) {
  if (typeof propId === "string") {
    // Name#12:3234
    return propId
      .replace(/#\d+:\d+$/, "")
      .replace(REMOVE_EMOJI, "")
      .trim();
  }
  return "";
}

function findAllWithCriteria(
  node: SceneNode,
  criteria: { types: string[] },
): (InstanceNode | TextNode)[] {
  const types = criteria.types;

  const nodes = [];
  if (types.includes(node.type)) {
    nodes.push(node);
  }
  if ("children" in node) {
    nodes.push(
      ...node.children.flatMap((n) => findAllWithCriteria(n, criteria)),
    );
  }
  return nodes as any;
}

export async function inPlaceResolveFigmaURLs(
  sys: DevToolsSys,
  args: CLIArgs,
  figmaAuth: FigmaAuth,
  figmaBuilderLinks: FigmaBuilderLink[],
  figmaLinksToResolve: string[],
) {
  if (figmaLinksToResolve.length > 0) {
    const resolvedFigmaKeys = await resolveFigmaUrls(
      sys,
      args,
      figmaAuth,
      figmaLinksToResolve,
    );
    for (const l of figmaBuilderLinks) {
      if (l.figmaUrl && !l.figmaKey) {
        const resolvedKey = resolvedFigmaKeys[l.figmaUrl];
        if (resolvedKey) {
          l.figmaKey = resolvedKey;
        }
      }
    }
  }
}

const resolveFigmaUrls = async (
  sys: DevToolsSys,
  args: CLIArgs,
  figmaAuth: FigmaAuth | undefined,
  figmaUrls: string[],
) => {
  if (!figmaAuth) {
    return {};
  }
  const files = new Map<string, string[]>();
  const mappedLinks: Record<string, string | undefined> = {};

  figmaUrls.forEach((url) => {
    const parsedFile = parseFigmaURL(url);
    if (!parsedFile) {
      sys.Sentry?.captureException(
        new Error(
          "Invalid Figma URL, use Cmd+L while selecting a figma component to get a link to selection",
        ),
        {
          extra: {
            figmaUrl: url,
          },
          level: "warning",
        },
      );
      return;
    }
    let ids = files.get(parsedFile.fileID);
    if (!ids) {
      ids = [];
      files.set(parsedFile.fileID, ids);
    }
    if (!ids.includes(parsedFile.nodeId)) {
      ids.push(parsedFile.nodeId);
    }
    mappedLinks[url] = `${parsedFile.fileID}/${parsedFile.nodeId}`;
  });

  await Promise.all(
    Array.from(files.entries()).map(async ([fileID, nodeIds]) => {
      const nodes = await getFigmaNodeData(
        sys,
        args,
        figmaAuth,
        fileID,
        nodeIds.join(","),
        0,
      );
      for (const nodeId of nodeIds) {
        const nodeData = nodes[nodeId];
        const componentKey =
          nodeData.components[nodeId]?.key ??
          nodeData.componentSets[nodeId]?.key;
        if (!componentKey) {
          sys.Sentry?.withScope((scope) => {
            scope.addAttachment({
              data: JSON.stringify(nodes, null, 2),
              filename: `${fileID}.json`,
            });
            sys.Sentry?.captureException(
              new Error(`Cannot resolve figma component key for NodeID`),
              {
                extra: {
                  nodeId,
                  fileID,
                },
                level: "warning",
              },
            );
          });
        }
        const key = `${fileID}/${nodeId}`;
        for (const url of figmaUrls) {
          if (mappedLinks[url] === key) {
            mappedLinks[url] = componentKey;
          }
        }
      }
    }),
  );
  return mappedLinks;
};

function nonNull<T>(value: T | null | undefined): value is T {
  return value != null;
}
