import type { DevToolsSys, FileChangeCallback, FileChangeInfo } from "../types";
import { createPath } from "./path";
import { detectFrameworks } from "../core/detect-frameworks";

export async function createDevToolsSys(opts: CreateDevToolsSysOptions) {
  const getRootDir = opts.getRepoRootDir ? opts.getRepoRootDir : () => "/";
  const cwd = opts.cwd ? opts.cwd : () => "/";

  const path = createPath({ cwd, ...opts });
  const fs = new Map<string, string>();

  const normalize = (p: string) => path.normalize(path.resolve(cwd(), p));

  const isDir = (p: string) => {
    for (const [fileName] of fs) {
      if ((fileName + "/").startsWith(p)) {
        return true;
      }
    }
    if (p === getRootDir()) {
      return true;
    }
    return false;
  };

  const existsSync = (p: string) => {
    p = normalize(p);
    if (fs.has(p)) {
      return true;
    }
    return isDir(p);
  };

  const readdirSync = (p: string) => {
    p = normalize(p);
    const dir = p === getRootDir() ? p : p + "/";
    const partCount = dir.split("/").length - 1;
    const items: string[] = [];

    for (const [dirItem] of fs) {
      const dirParts = dirItem.split("/").slice(0, partCount);
      const dirItemTest = dirParts.join("/") + "/";

      if (dirItemTest === dir) {
        const item = dirItem.split("/")[partCount];
        if (item && !items.includes(item)) {
          items.push(item);
        }
      }
    }

    return items.sort();
  };

  const readdirRecursive = async (p: string, skipFolders: string[] = []) => {
    p = normalize(p);
    const dir = p === getRootDir() ? p : p + "/";
    const items: string[] = [];
    const skipFoldersSet = new Set(skipFolders);

    for (const [dirItem] of fs) {
      if (dirItem.startsWith(dir)) {
        // Get the relative path from the search directory
        const relativePath = dirItem.slice(dir.length);
        // Skip if any parent folder is in skipFolders
        const shouldSkip = relativePath
          .split("/")
          .some((part) => skipFoldersSet.has(part));
        if (!shouldSkip) {
          items.push(dirItem);
        }
      }
    }

    return items.sort();
  };

  const readFileSync = (p: string) => {
    p = normalize(p);
    const content = fs.get(p);
    return typeof content === "string" ? content : null;
  };

  const statSync = (p: string) => {
    p = normalize(p);
    return {
      isFile: () => fs.has(p),
      isDirectory: () => !fs.has(p) && isDir(p),
    };
  };

  const hash = async (str: string) => {
    if (typeof crypto !== "undefined") {
      const encoder = new TextEncoder();
      const data = encoder.encode(str);
      const hash = await crypto.subtle.digest("SHA-256", data);
      return Array.from(new Uint8Array(hash))
        .map((b) => b.toString(16).padStart(2, "0"))
        .join("");
    }
    let hash = 0;
    for (let i = 0; i < str.length; i++) {
      const chr = str.charCodeAt(i);
      hash = (hash << 5) - hash + chr;
      hash |= 0;
    }
    return Number(Math.abs(hash)).toString(36);
  };

  const onChangeCallbacks = new Set<FileChangeCallback>();

  const sys: DevToolsSys = {
    getRepoRootDir: getRootDir,
    getAppRootDir: getRootDir,
    getCwdDir: cwd,
    cwd,
    exists: async (p) => existsSync(p),
    existsSync,
    readdir: async (p) => readdirSync(p),
    readdirSync,
    readdirRecursive,
    readFile: async (p) => readFileSync(p),
    readFileSync,
    stat: async (p) => statSync(p),
    statSync,
    writeFile: async (p, content) => {
      p = normalize(p);
      fs.set(p, content);

      const fileInfo: FileChangeInfo = {
        path: p,
        basename: path.basename(p),
        dirname: path.dirname(p),
        extname: path.extname(p),
      };

      for (const onChangeCallback of onChangeCallbacks) {
        await onChangeCallback(fileInfo);
      }
    },
    unlink: async (p) => {
      p = normalize(p);
      fs.delete(p);
    },
    unlinkSync: (p) => {
      p = normalize(p);
      fs.delete(p);
    },
    hash,
    getDeviceId: async () => {
      if (typeof navigator !== "undefined") {
        return hash(navigator.userAgent);
      }
      return ``;
    },
    getRepoInfo: async () => {
      return {
        remoteUrl: "",
        defaultBranch: "",
        currentBranch: "",
        commit: "",
      };
    },
    formatCode: async (_, code) => {
      return code;
    },
    on: (eventName: "change", callback: FileChangeCallback) => {
      if (eventName === "change") {
        onChangeCallbacks.add(callback);
      }
    },
    off: (eventName: "change", callback: FileChangeCallback) => {
      if (eventName === "change") {
        onChangeCallbacks.delete(callback);
      }
    },
    debug: (...args: []) => {
      console.debug(`[builder-dev-tools]`, ...args);
    },
    launchEditor: async () => {},
    platform: () => {
      return {
        runtime: "",
        os: "",
      };
    },
    getFrameworks: () => [],
    ...path,
    ...opts,
    version: "0.0.0",
    sdkVersion: null,
    kind: null,
    magicast: undefined,
    spawnSync: undefined,
    Sentry: undefined,
  };

  const frameworks = await detectFrameworks(sys);
  sys.getFrameworks = opts.getFrameworks
    ? opts.getFrameworks
    : () => frameworks;
  return sys;
}

export interface CreateDevToolsSysOptions extends Partial<DevToolsSys> {
  /**
   * TypeScript compiler instance.
   */
  ts: any;
}

declare const ts: any;
