import type ts from "typescript";
import type { DevToolsSys, ExportType } from "../../types";
import { getDisplayFilePath } from "../utils";

export function getImportPathFromIdentifier(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  identifierName: string,
) {
  for (const s of sourceFile.statements) {
    if (sys.ts.isImportDeclaration(s) && s.importClause) {
      if (
        s.importClause.namedBindings &&
        sys.ts.isNamedImports(s.importClause.namedBindings)
      ) {
        for (const elm of s.importClause.namedBindings.elements) {
          if (
            sys.ts.isIdentifier(elm.name) &&
            elm.name.text === identifierName
          ) {
            if (sys.ts.isStringLiteral(s.moduleSpecifier)) {
              return {
                importPath: s.moduleSpecifier.text,
                exportType: "named" as ExportType,
              };
            }
          }
        }
      }
      if (s.importClause.name && s.importClause.name.text === identifierName) {
        if (sys.ts.isStringLiteral(s.moduleSpecifier)) {
          return {
            importPath: s.moduleSpecifier.text,
            exportType: "default" as ExportType,
          };
        }
      }
    }
  }
  return null;
}

export async function getFilePathFromIdentifier(
  sys: DevToolsSys,
  containerPath: string,
  sourceFile: ts.SourceFile,
  identifierName: string,
) {
  // Traverse the source file to find the import statement and import path
  const i = getImportPathFromIdentifier(sys, sourceFile, identifierName);
  if (i) {
    const filePath = await resolveComponentPath(
      sys,
      sys.normalize(sys.resolve(sys.dirname(containerPath), i.importPath)),
    );
    const relFilePath = sys.relative(sys.getAppRootDir(), filePath);
    return {
      displayFilePath: getDisplayFilePath(sys, filePath),
      filePath,
      relFilePath,
      importPath: i.importPath,
      exportType: i.exportType as ExportType,
    };
  }
  return null;
}

export function findVariableStatement(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  variableName: string,
) {
  for (let i = 0; i < sourceFile.statements.length; i++) {
    const statement = sourceFile.statements[i];
    if (sys.ts.isVariableStatement(statement)) {
      for (const declaration of statement.declarationList.declarations) {
        if (
          sys.ts.isIdentifier(declaration.name) &&
          declaration.name.text === variableName
        ) {
          return { statement, statementIndex: i };
        }
      }
    }
  }
  return null;
}

export function findArrayStatementInitializer(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  variableName: string,
) {
  const s = findVariableStatement(sys, sourceFile, variableName);
  if (s && s.statement.declarationList.declarations.length > 0) {
    const declaration = s.statement.declarationList.declarations[0];
    if (
      declaration.initializer &&
      sys.ts.isArrayLiteralExpression(declaration.initializer)
    ) {
      return {
        statement: s.statement,
        statementIndex: s.statementIndex,
        variableDeclaration: declaration,
        arrayLiteralExp: declaration.initializer,
      };
    }
  }
  return null;
}

export function hasJsxNode(sys: DevToolsSys, n: ts.Node | undefined) {
  if (n) {
    if (isJsxNode(sys, n)) {
      return true;
    }
    let foundJsx = false;
    n.forEachChild((c) => {
      if (hasJsxNode(sys, c)) {
        foundJsx = true;
      }
    });
    if (foundJsx) {
      return true;
    }
  }
  return false;
}

function isJsxNode(sys: DevToolsSys, n: ts.Node) {
  return (
    sys.ts.isJsxElement(n) ||
    sys.ts.isJsxOpeningElement(n) ||
    sys.ts.isJsxOpeningFragment(n) ||
    sys.ts.isJsxSelfClosingElement(n) ||
    sys.ts.isJsxFragment(n) ||
    sys.ts.isJsxChild(n)
  );
}

export async function resolveComponentPath(sys: DevToolsSys, filePath: string) {
  const ext = sys.extname(filePath).toLowerCase();
  if (ext === "" || !RESOLVE_EXTS.includes(ext)) {
    for (const tryExt of RESOLVE_EXTS) {
      const checkPath = filePath + tryExt;
      if (await sys.exists(checkPath)) {
        return checkPath;
      }
    }

    for (const tryExt of RESOLVE_EXTS) {
      const checkPath = sys.join(filePath, "index" + tryExt);
      if (await sys.exists(checkPath)) {
        return checkPath;
      }
    }
  }
  return filePath;
}

export const RESOLVE_EXTS = [".tsx", ".jsx", ".ts", ".js"];
