import type { DevToolsSys, FileChangeCallback, FileChangeInfo } from "../types";
import {
  access,
  mkdir,
  readdir,
  readFile,
  stat,
  unlink,
  writeFile,
} from "node:fs/promises";
import { createHash } from "node:crypto";
import {
  existsSync,
  readdirSync,
  readFileSync,
  statSync,
  unlinkSync,
} from "node:fs";
import { homedir, hostname } from "node:os";
import path from "node:path";
import type { Options as PrettierOptions } from "prettier";
import prettier from "prettier";
import ts from "typescript";
import launchEditor from "launch-editor";
import { detectFrameworks } from "../core/detect-frameworks";
import { spawnSync } from "node:child_process";
import * as magicast from "magicast";
import * as Sentry from "@sentry/node";
import { pkgVersion } from "../cli/version";

interface DevToolsNodeSysOptions {
  cwd: string;
  skipValidate?: boolean;
  ignoreMissingConfig?: boolean;
}

async function readdirRecursive(
  dir: string,
  skipFolders: Set<string>,
): Promise<string[]> {
  const entries = await readdir(dir, { withFileTypes: true });
  const files: string[] = [];

  for (const entry of entries) {
    const fullPath = path.join(dir, entry.name);

    if (entry.isDirectory()) {
      if (!skipFolders.has(entry.name)) {
        const subFiles = await readdirRecursive(fullPath, skipFolders);
        files.push(...subFiles);
      }
    } else {
      files.push(fullPath);
    }
  }

  return files;
}

export async function createDevToolsNodeSys(
  opts: DevToolsNodeSysOptions,
): Promise<DevToolsSys> {
  const onChangeCallbacks = new Set<FileChangeCallback>();

  const debug = (...args: any[]) => {
    if (process.env.DEBUG) {
      const d = new Date();
      const ts = `${String(d.getMinutes()).padStart(2, "0")}:${String(
        d.getSeconds(),
      ).padStart(2, "0")}.${String(d.getMilliseconds()).padStart(3, "0")}`;
      // eslint-disable-next-line no-console
      console.debug(ts, `[builder-dev-tools]`, ...args);
    }
  };

  const hash = async (str: string) => {
    const hash = createHash("md5");
    hash.update(str);
    return hash.digest("hex");
  };
  const rootDir = path.normalize(opts.cwd);
  const repoRootDir = findPackageJsonDir(path.normalize(opts.cwd)) ?? rootDir;
  const appRootDir = findAppRootDir(path.normalize(opts.cwd)) ?? repoRootDir;

  const nodeFs: DevToolsSys = {
    ...path,
    getCwdDir: () => rootDir,
    getRepoRootDir: () => repoRootDir,
    getAppRootDir: () => appRootDir,
    cwd: () => process.cwd(),
    exists: async (p) => {
      try {
        await access(p);
        return true;
      } catch (e) {
        return false;
      }
    },
    magicast,
    existsSync: (p) => {
      // TODO: This is causing error for react vite, need to investigate
      // validatePath(opts, p);
      return existsSync(p);
    },
    readdir: async (p, absolutePaths) => {
      const items = await readdir(p);
      if (absolutePaths) {
        return items.map((i) => path.join(p, i));
      }
      return items;
    },
    readdirRecursive: async (p, skipFolders) => {
      return readdirRecursive(p, new Set(skipFolders));
    },
    readdirSync: (p) => {
      return readdirSync(p);
    },
    readFile: async (p) => {
      try {
        const content = await readFile(p, "utf-8");
        return content;
      } catch (e: any) {
        if (e.code === "ENOENT") {
          return null;
        }
        debug(e, p);
        throw e;
      }
    },
    readFileSync(p) {
      try {
        return readFileSync(p, "utf-8");
      } catch (e: any) {
        if (e.code === "ENOENT") {
          return null;
        }
        debug(e, p);
        throw e;
      }
    },
    spawnSync,
    stat: async (p) => {
      const s = await stat(p);
      return s;
    },
    statSync: (p) => {
      return statSync(p);
    },
    unlinkSync: (p) => {
      unlinkSync(p);
    },
    unlink: async (p) => {
      await unlink(p);
    },
    writeFile: async (p, contents) => {
      p = path.normalize(p);
      const fileInfo: FileChangeInfo = {
        path: p,
        basename: path.basename(p),
        dirname: path.dirname(p),
        extname: path.extname(p),
      };

      await mkdir(path.dirname(p), { recursive: true });
      await writeFile(p, contents);

      for (const onChangeCallback of onChangeCallbacks) {
        await onChangeCallback(fileInfo);
      }
    },
    hash,
    getDeviceId: () => hash(`${hostname()}:${homedir()}`),
    formatCode: async (filePath, code) => {
      let userOpts: PrettierOptions | null = null;
      try {
        userOpts = await prettier.resolveConfig(filePath);
      } catch (e) {
        //
      }

      try {
        return prettier.format(code, {
          ...userOpts,
          filepath: filePath,
        });
      } catch (e) {
        // possible the prettier internal parser throws an error
        // in that case, just return the original code
      }
      return code;
    },
    on: (eventName: "change", callback: FileChangeCallback) => {
      if (eventName === "change") {
        onChangeCallbacks.add(callback);
      }
    },
    off: (eventName: "change", callback: FileChangeCallback) => {
      if (eventName === "change") {
        onChangeCallbacks.delete(callback);
      }
    },
    debug,
    launchEditor: async (file) => {
      try {
        let filePath = file.filePath;
        if (typeof file.line === "number") {
          filePath += `:${file.line}`;
          if (typeof file.column === "number") {
            filePath += `:${file.column}`;
          }
        }

        launchEditor(filePath, "code", (pathFile, e) => {
          Sentry.captureException(
            new Error(`Failed to launch editor for ${e}`),
            {
              extra: {
                pathFile,
              },
            },
          );
        });
      } catch (e: any) {
        Sentry.captureException(
          new Error("Failed to launch editor", { cause: e }),
        );
      }
    },
    platform: () => {
      return { runtime: "node", os: process.platform };
    },
    getRepoInfo: async () => {
      const rootDir = path.normalize(opts.cwd);
      return {
        remoteUrl: getGitRemoteURL(rootDir),
        defaultBranch: getGitRepoDefaultBranchName(rootDir),
        commit: getGitCommit(rootDir),
        currentBranch: getCurrentBranchName(rootDir),
      };
    },
    getFrameworks: () => [],
    ts,
    Sentry,
    version: pkgVersion,
    sdkVersion: null,
    ignoreMissingConfig: opts.ignoreMissingConfig,
    kind: null,
  };

  const frameworks = await detectFrameworks(nodeFs);
  nodeFs.getFrameworks = () => frameworks;

  return nodeFs;
}

function getConfigPath() {
  const homeDirPath = homedir();
  return path.join(homeDirPath, ".config", "builder", "dev-tools.json");
}

export function configHasProperty(contents: Record<string, any>, key: string) {
  return typeof contents === "object" && contents != null && key in contents;
}

export async function readConfigFile() {
  const configPath = getConfigPath();
  try {
    const contents = await readFile(configPath, "utf-8");
    if (typeof contents === "string") {
      return JSON.parse(contents);
    }
  } catch (e: any) {
    if (e.code === "ENOENT") {
      return null;
    }
  }
  return null;
}

export async function writeConfigFile(contents: Record<string, any>) {
  const configPath = getConfigPath();
  contents = typeof contents === "object" && contents != null ? contents : {};

  await mkdir(path.dirname(configPath), { recursive: true });
  await writeFile(configPath, JSON.stringify(contents ?? {}, null, 2));
}

export function getGitRemoteURL(repoPath: string) {
  try {
    const spawn = spawnSync("git", ["config", "--get", "remote.origin.url"], {
      cwd: repoPath,
    });
    const output = spawn.stdout;
    return (output || "").toString().trim();
  } catch (error) {
    Sentry.captureException(
      new Error("Error getting git remote URL:", { cause: error }),
    );
    return "";
  }
}

/**
 * Find the default branch name (master or main) for the git repository
 */
function getGitRepoDefaultBranchName(repoPath: string) {
  const DEFAULT_BRANCH_NAME = "master";

  try {
    // Get all git branches
    const gitBranchResult = spawnSync("git", ["branch", "-r"], {
      cwd: repoPath,
    });

    if (!gitBranchResult.stdout) {
      return DEFAULT_BRANCH_NAME;
    }

    const branches = gitBranchResult.stdout
      .toString()
      .trim()
      .split("\n")
      .map((s) => s.trim());

    // Check if origin/main exists, otherwise assume master
    if (branches.includes("origin/main")) {
      return "main";
    } else {
      return DEFAULT_BRANCH_NAME;
    }
  } catch (error: any) {
    Sentry.captureException(
      new Error("Error getting git default branch name:", {
        cause: error,
      }),
    );
    return DEFAULT_BRANCH_NAME;
  }
}

function getGitCommit(repoPath: string) {
  try {
    const spawn = spawnSync("git", ["rev-parse", "HEAD"], {
      cwd: repoPath,
    });
    const output = spawn.stdout;
    return (output || "").toString().trim();
  } catch (error) {
    Sentry.captureException(
      new Error("Error getting git commit:", { cause: error }),
    );
    return "";
  }
}

function getCurrentBranchName(repoPath: string) {
  try {
    const spawn = spawnSync("git", ["branch", "--show-current"], {
      cwd: repoPath,
    });
    const output = spawn.stdout;
    return (output || "").toString().trim();
  } catch (error) {
    Sentry.captureException(
      new Error("Error getting current branch name:", { cause: error }),
    );
    return "";
  }
}

export function findPackageJsonDir(dir: string) {
  const fsRoot = path.resolve("/");

  for (let i = 0; i < 20; i++) {
    const pkgJsonPath = path.join(dir, "package.json");
    const content = existsSync(pkgJsonPath);
    if (content) {
      return dir;
    }

    if (dir === fsRoot) {
      break;
    }
    dir = path.dirname(dir);
  }

  return null;
}

export function findAppRootDir(dir: string) {
  const fsRoot = path.resolve("/");

  for (let i = 0; i < 20; i++) {
    // Package root
    if (existsSync(path.join(dir, "package.json"))) {
      return dir;
    }
    // Remix root .js
    if (existsSync(path.join(dir, "remix.config.js"))) {
      return dir;
    }
    // Remix root .js
    if (existsSync(path.join(dir, "remix.config.mjs"))) {
      return dir;
    }
    // Remix root .ts
    if (existsSync(path.join(dir, "remix.config.ts"))) {
      return dir;
    }
    // Vite root .js
    if (existsSync(path.join(dir, "vite.config.js"))) {
      return dir;
    }
    // Vite root .ts
    if (existsSync(path.join(dir, "vite.config.ts"))) {
      return dir;
    }
    // Vite root .mjs
    if (existsSync(path.join(dir, "vite.config.mjs"))) {
      return dir;
    }
    // Next root .js
    if (existsSync(path.join(dir, "next.config.js"))) {
      return dir;
    }
    // Next root .ts
    if (existsSync(path.join(dir, "next.config.ts"))) {
      return dir;
    }
    // Angular root
    if (existsSync(path.join(dir, "angular.json"))) {
      return dir;
    }
    // Monorepo root
    if (existsSync(path.join(dir, "project.json"))) {
      return dir;
    }
    // TsConfig root
    if (existsSync(path.join(dir, "tsconfig.json"))) {
      return dir;
    }
    if (dir === fsRoot) {
      break;
    }
    dir = path.dirname(dir);
  }

  return null;
}
