import type ts from "typescript";
import type { ComponentInput, DevToolsSys } from "../../types";

export function parseComponentSourcePropsFromNode(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker,
  fnNode: ts.Node | undefined,
) {
  let inputs: ComponentInput[] = [];
  if (!fnNode) {
    return inputs;
  }

  let properties: ts.Symbol[] | null = null;

  if (
    sys.ts.isArrowFunction(fnNode) ||
    sys.ts.isFunctionExpression(fnNode) ||
    sys.ts.isFunctionDeclaration(fnNode)
  ) {
    const fnParams = fnNode.parameters;
    if (fnParams.length > 0) {
      const propsNode = fnParams[0];
      let type = typeChecker.getTypeAtLocation(propsNode);
      if (type.aliasTypeArguments && type.aliasTypeArguments.length > 0) {
        type = type.aliasTypeArguments[0];
      }
      const isObject = !!(type.flags & sys.ts.TypeFlags.Object);
      if (isObject) {
        properties = type.getProperties();
      }
    }
  } else if (sys.ts.isIdentifier(fnNode)) {
    const type = typeChecker.getTypeAtLocation(fnNode);
    const classDecl = type.symbol?.valueDeclaration;

    if (classDecl && sys.ts.isClassDeclaration(classDecl)) {
      const extendClause = classDecl.heritageClauses?.find(
        (h) => h.token === sys.ts.SyntaxKind.ExtendsKeyword,
      );
      const extendTypes = extendClause?.types;
      if (extendTypes) {
        for (const extendType of extendTypes) {
          const symbol = typeChecker.getSymbolAtLocation(extendType.expression);
          if (symbol?.name === "Component") {
            if (sys.ts.isExpressionWithTypeArguments(extendType)) {
              const typeArguments = extendType.typeArguments;
              if (typeArguments) {
                const propsTypeNode = typeArguments[0];
                const propsType =
                  typeChecker.getTypeFromTypeNode(propsTypeNode);
                properties = propsType.getProperties();
              }
            }
            break;
          }
        }
      }
    }

    if (!properties) {
      const callSignatures = type.getCallSignatures();
      if (callSignatures.length > 0) {
        const params = callSignatures[0].getParameters();
        if (params.length > 0) {
          const typeOfSymbol = typeChecker.getTypeOfSymbolAtLocation(
            params[0],
            fnNode,
          );
          properties = typeOfSymbol.getProperties();
        }
      }
    }
  } else if (sys.ts.isExportSpecifier(fnNode)) {
    const symbol = typeChecker.getSymbolAtLocation(fnNode.name);
    if (symbol) {
      const type = typeChecker.getTypeOfSymbolAtLocation(symbol, fnNode);
      const callSignatures = type.getCallSignatures();
      if (callSignatures.length > 0) {
        const params = callSignatures[0].getParameters();
        if (params.length > 0) {
          const typeOfSymbol = typeChecker.getTypeOfSymbolAtLocation(
            params[0],
            fnNode,
          );
          properties = typeOfSymbol.getProperties();
        }
      }
    }
  }

  if (properties) {
    for (const symbol of properties) {
      const input = parseComponentSourceInputFromSymbol(
        sys,
        typeChecker,
        symbol,
      );

      if (input) {
        inputs.push(input);
      }
    }
  }

  inputs = inputs.filter(shouldIncludeInput);
  if (inputs.length > 0) {
    return inputs;
  }

  return parseComponentSourceInputFromJsDocs(sys, fnNode);
}

function parseComponentSourceInputFromSymbol(
  sys: DevToolsSys,
  typeChecker: ts.TypeChecker,
  symbol: ts.Symbol,
) {
  let value = symbol.valueDeclaration;
  const propName = symbol.getName();
  if (!value) {
    value = symbol.getDeclarations()?.[0];
  }
  if (value && sys.ts.isPropertySignature(value)) {
    if (propName !== "children") {
      const filename = value.getSourceFile().fileName;
      if (filename.includes("node_modules/@types/")) {
        return null;
      }
    }
    let builderInputType = "string"; // default
    let nonPrimitiveType: string | undefined = undefined;

    if (value?.type) {
      const nodeType = typeChecker.getTypeFromTypeNode(value.type);

      // get the type string
      const typeString = typeChecker.typeToString(nodeType);

      const normlizedBuilderType = normalizeBuilderInputType(typeString);
      if (!normlizedBuilderType.knownType) {
        if (
          isValidBuilderObjectType(typeString) ||
          nodeType.flags & sys.ts.TypeFlags.StringLike
        ) {
          builderInputType = "string";
        } else if (nodeType.flags & sys.ts.TypeFlags.NumberLike) {
          builderInputType = "number";
        } else if (nodeType.flags & sys.ts.TypeFlags.IndexedAccess) {
          builderInputType = "list";
        } else if (nodeType.flags & sys.ts.TypeFlags.Object) {
          builderInputType = "object";
        } else if (
          nodeType.isUnion() &&
          nodeType.types.every((t) => t.flags & sys.ts.TypeFlags.Object)
        ) {
          builderInputType = "object";
        } else if (
          nodeType.isIntersection() &&
          nodeType.types.some((t) => t.flags & sys.ts.TypeFlags.Object)
        ) {
          builderInputType = "object";
        } else {
          builderInputType = "string";
        }
      } else {
        builderInputType = normlizedBuilderType.builderType;
      }

      if (typeString !== builderInputType) {
        // if the type is not a valid builder input type
        // then we need to store it in the meta
        nonPrimitiveType = typeString;
      }
    }

    const input: ComponentInput = {
      name: propName,
      type: builderInputType,
      isRegistered: false,
    };
    if (nonPrimitiveType && value.type) {
      const resolvedType = resolveType(
        sys,
        typeChecker,
        typeChecker.getTypeFromTypeNode(value.type),
      );

      input.meta = input.meta || {};
      if (
        resolvedType &&
        resolvedType.length > 0 &&
        input.type === "string" &&
        resolvedType.every(hasQuotes)
      ) {
        input.enum = resolvedType.map(removeQuotes);
      } else {
        input.meta.ts = nonPrimitiveType;
      }
    }

    if (!value.questionToken) {
      input.required = true;
    }

    return input;
  }
  return null;
}

function parseComponentSourceInputFromJsDocs(
  sys: DevToolsSys,
  fnNode: ts.Node,
) {
  /**
   * Component description.
   *
   * @component
   * @param {Object} props
   * @param {string} props.title The title of the component.
   * @param {number} props.enabled Is the component enabled.
   * @param {number} props.count Count of items.
   */

  const inputs: ComponentInput[] = [];
  const jsDocComments = sys.ts.getJSDocCommentsAndTags(fnNode);

  for (const jsDoc of jsDocComments) {
    if (!sys.ts.isJSDoc(jsDoc) || !jsDoc.tags) {
      continue;
    }

    for (const tag of jsDoc.tags) {
      if (!sys.ts.isJSDocParameterTag(tag)) {
        continue;
      }
      const tagName = tag.tagName.text;
      if (tagName !== "param") {
        continue;
      }

      // * @param {Object} props

      const jsDocType = tag.typeExpression?.type;
      if (!jsDocType || !sys.ts.isJSDocTypeLiteral(jsDocType)) {
        continue;
      }
      const jsDocPropertyTags = jsDocType.jsDocPropertyTags;
      if (!jsDocPropertyTags) {
        continue;
      }

      for (const jsDocPropertyTag of jsDocPropertyTags) {
        if (!sys.ts.isJSDocPropertyLikeTag(jsDocPropertyTag)) {
          continue;
        }
        const paramPropName = jsDocPropertyTag.name;
        if (!sys.ts.isQualifiedName(paramPropName)) {
          continue;
        }
        const typeExp = jsDocPropertyTag.typeExpression;
        if (!typeExp) {
          continue;
        }

        // * @param {string} props.title The title of the component.

        let builderInputType = "";
        switch (typeExp.type.kind) {
          case sys.ts.SyntaxKind.StringKeyword: {
            builderInputType = "string";
            break;
          }
          case sys.ts.SyntaxKind.NumberKeyword: {
            builderInputType = "number";
            break;
          }
          case sys.ts.SyntaxKind.BooleanKeyword: {
            builderInputType = "boolean";
            break;
          }
          default: {
            // idk
            continue;
          }
        }

        const propName = paramPropName.right.text;

        const input: ComponentInput = {
          name: propName,
          type: builderInputType,
          isRegistered: false,
        };
        inputs.push(input);
      }
    }
  }

  return inputs.filter(shouldIncludeInput);
}

const VALID_OBJECT_TYPES = [
  "ClassicComponent",
  "FunctionComponent",
  "IntrinsicElements",
  "JSX",
  "ReactNode",
  "ReactElement",
  "ReactPortal",
];

function isValidBuilderObjectType(type: string) {
  if (typeof type !== "string") {
    return false;
  }

  if (VALID_OBJECT_TYPES.some((t) => type.includes(t))) {
    return true;
  }

  return false;
}

export function normalizeBuilderInputType(t: string) {
  if (typeof t === "string") {
    t = t.trim();
    if (INPUT_TYPES.some((i) => i.value === t)) {
      return {
        builderType: t,
        knownType: true,
      };
    }
  }
  // default builder component input type
  return {
    builderType: "string",
    knownType: false,
  };
}

/**
 * https://www.builder.io/c/docs/custom-components-input-types
 */
export const INPUT_TYPES = [
  { value: "boolean", text: "boolean" },
  { value: "color", text: "color (provides a color in hex or rgb)" },
  { value: "date", text: "date (same format as the Date constructor)" },
  { value: "email", text: "email" },
  { value: "file", text: "file (uploads a file and provides a url)" },
  { value: "list", text: "list (collection of items)" },
  { value: "longText", text: "longText (multiline text editor)" },
  { value: "number", text: "number" },
  { value: "object", text: "object (set of specific names and values)" },
  { value: "richText", text: "richText (provides value as html)" },
  { value: "string", text: "string" },
];

export const STRING_TYPES = [
  "color",
  "date",
  "email",
  "file",
  "longText",
  "richText",
  "string",
];
export const NUMBER_TYPES = ["number"];
export const BOOLEAN_TYPES = ["boolean"];
export const ARRAY_TYPES = ["list"];
export const OBJECT_TYPES = ["object"];

export function getPrimitiveType(t: string) {
  if (STRING_TYPES.includes(t)) {
    return "string";
  } else if (NUMBER_TYPES.includes(t)) {
    return "number";
  } else if (BOOLEAN_TYPES.includes(t)) {
    return "boolean";
  } else if (ARRAY_TYPES.includes(t)) {
    return "array";
  } else if (OBJECT_TYPES.includes(t)) {
    return "object";
  } else {
    return "string";
  }
}

function hasQuotes(text: string) {
  return /^['"].*['"]$/.test(text);
}

export function removeQuotes(text: string): string {
  if (
    (text.startsWith(`"`) || text.startsWith(`'`)) &&
    (text.endsWith(`"`) || text.endsWith(`'`))
  ) {
    return text.slice(1, -1);
  }
  return text;
}

// This is used to get enum values from the type
export const resolveType = (
  sys: DevToolsSys,
  checker: ts.TypeChecker,
  type: ts.Type,
): string[] | undefined => {
  const set = new Set<string>();
  parseDocsType(sys, checker, type, set);

  const hasTrue = set.delete("true");
  const hasFalse = set.delete("false");
  if (hasTrue || hasFalse) {
    set.add("boolean");
  }

  let parts = Array.from(set.keys()).sort();
  if (parts.length > 1) {
    parts = parts.map((p) => (p.indexOf("=>") >= 0 ? `(${p})` : p));
  }
  // This is intentional as some props have too many enums
  if (parts.length > 20) {
    return;
  } else {
    return parts;
  }
};

export const typeToString = (
  sys: DevToolsSys,
  checker: ts.TypeChecker,
  type: ts.Type,
): string => {
  const TYPE_FORMAT_FLAGS =
    sys.ts.TypeFormatFlags.InElementType | sys.ts.TypeFormatFlags.NoTruncation;

  return checker.typeToString(type, undefined, TYPE_FORMAT_FLAGS);
};

export const parseDocsType = (
  sys: DevToolsSys,
  checker: ts.TypeChecker,
  type: ts.Type,
  parts: Set<string>,
): void => {
  if (type.isUnion()) {
    (type as ts.UnionType).types.forEach((t) => {
      parseDocsType(sys, checker, t, parts);
    });
  } else {
    if (type.isLiteral()) {
      const text = typeToString(sys, checker, type);
      parts.add(text);
    }
  }
};

function shouldIncludeInput(input: ComponentInput) {
  const propName = input?.name;
  if (typeof propName !== "string" || propName === "") {
    return false;
  }

  if (propName.startsWith("aria")) {
    return false;
  }

  if (propName.startsWith("data-")) {
    return false;
  }

  if (propName.startsWith("on") && propName.length > 2) {
    // onClick, onHover, etc
    const thirdChar = propName.charAt(2);
    if (thirdChar.toUpperCase() === thirdChar) {
      return false;
    }
  }

  return !PROP_BLACKLIST.has(propName.toLowerCase());
}

/**
 * Props that we should probably not include as a possible component input
 */
const PROP_BLACKLIST = new Set(
  [
    "about",
    "accessKey",
    "accessKeyLabel",
    "asChild",
    "autoCapitalize",
    "autoCorrect",
    "autoFocus",
    "autoSave",
    "blur",
    "contentEditable",
    "contextMenu",
    "dangerouslySetInnerHTML",
    "datatype",
    "defaultChecked",
    "defaultValue",
    "dir",
    "draggable",
    "enterKeyHint",
    "focus",
    "form",
    "formAction",
    "formEncType",
    "formMethod",
    "formNoValidate",
    "formTarget",
    "inlist",
    "innerText",
    "inputMode",
    "is",
    "isContentEditable",
    "itemID",
    "itemProp",
    "itemRef",
    "itemScope",
    "itemType",
    "lang",
    "nonce",
    "offsetHeight",
    "offsetLeft",
    "offsetTop",
    "offsetWidth",
    "outerText",
    "prefix",
    "property",
    "radioGroup",
    "rel",
    "resource",
    "results",
    "rev",
    "role",
    "security",
    "slot",
    "spellCheck",
    "suppressContentEditableWarning",
    "suppressHydrationWarning",
    "tabIndex",
    "translate",
    "typeof",
    "unselectable",
    "vocab",
  ].map((s) => s.toLowerCase()),
);
