import type ts from "typescript";
import type { DevToolsSys, ModuleFormat } from "../../types";

export function ensureImport(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  opts: EnsureImportOptions,
  moduleType: ModuleFormat = "esm",
) {
  if (Array.isArray(opts.namedImports)) {
    opts.namedImports.forEach((namedImport) => {
      sourceFile = ensureNamedImport(
        sys,
        sourceFile,
        namedImport,
        opts.importPath,
        !!opts.isTypeOnly,
        moduleType,
      );
    });
  }
  if (typeof opts.defaultImport === "string") {
    sourceFile = ensureDefaultImport(
      sys,
      sourceFile,
      opts.defaultImport,
      opts.importPath,
      moduleType,
    );
  }
  return sourceFile;
}

function ensureNamedImport(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  namedImport: string,
  importPath: string,
  isTypeOnly: boolean,
  moduleType: ModuleFormat,
) {
  if (moduleType === "cjs") {
    return ensureCjsImports(sys, sourceFile, null, namedImport, importPath);
  } else {
    return ensureEsmImports(
      sys,
      sourceFile,
      null,
      namedImport,
      importPath,
      isTypeOnly,
    );
  }
}

function ensureDefaultImport(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  defaultImport: string,
  importPath: string,
  moduleType: ModuleFormat,
) {
  if (moduleType === "cjs") {
    return ensureCjsImports(sys, sourceFile, defaultImport, null, importPath);
  } else {
    return ensureEsmImports(
      sys,
      sourceFile,
      defaultImport,
      null,
      importPath,
      false,
    );
  }
}

function ensureEsmImports(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  defaultImport: string | null,
  namedImport: string | null,
  importPath: string,
  isTypeOnly: boolean,
) {
  const statements = [...sourceFile.statements];
  let lastImportIndex = 0;
  let madeNameImportChange = false;
  let madeDefaultImportChange = false;

  for (let i = 0; i < statements.length; i++) {
    const n = statements[i];
    if (!sys.ts.isImportDeclaration(n)) {
      continue;
    }
    lastImportIndex = i;
    if (!n.moduleSpecifier || !sys.ts.isStringLiteral(n.moduleSpecifier)) {
      continue;
    }
    if (n.moduleSpecifier.text !== importPath) {
      continue;
    }
    if (!!n.importClause?.isTypeOnly !== isTypeOnly) {
      continue;
    }

    const existingNamedImports: ts.ImportSpecifier[] = [];
    const namedImports = n.importClause?.namedBindings;
    if (
      namedImports &&
      sys.ts.isNamedImports(namedImports) &&
      namedImports.elements
    ) {
      existingNamedImports.push(...namedImports.elements);
    }

    if (typeof namedImport === "string") {
      if (existingNamedImports.some((e) => e.name.text === namedImport)) {
        return sourceFile;
      }

      existingNamedImports.push(
        sys.ts.factory.createImportSpecifier(
          false,
          undefined,
          sys.ts.factory.createIdentifier(namedImport),
        ),
      );
      existingNamedImports.sort((a, b) => {
        return a.name.text.localeCompare(b.name.text);
      });
      madeNameImportChange = true;
    }

    let defaultIdentifier = n.importClause ? n.importClause.name : undefined;
    if (typeof defaultImport === "string") {
      if (defaultIdentifier && defaultIdentifier.text === defaultImport) {
        return sourceFile;
      }
      defaultIdentifier = sys.ts.factory.createIdentifier(defaultImport);
      madeDefaultImportChange = true;
    }

    if (madeNameImportChange || madeDefaultImportChange) {
      statements[i] = sys.ts.factory.updateImportDeclaration(
        n,
        undefined,
        sys.ts.factory.createImportClause(
          isTypeOnly,
          defaultIdentifier,
          sys.ts.factory.createNamedImports(existingNamedImports),
        ),
        n.moduleSpecifier,
        undefined,
      );
      return sys.ts.factory.updateSourceFile(sourceFile, statements);
    }
  }

  let defaultIdentifier: ts.Identifier = undefined as any;
  let namedBindings: ts.NamedImports = undefined as any;

  if (typeof defaultImport === "string") {
    defaultIdentifier = sys.ts.factory.createIdentifier(defaultImport);
  }

  if (typeof namedImport === "string") {
    namedBindings = sys.ts.factory.createNamedImports([
      sys.ts.factory.createImportSpecifier(
        false,
        undefined,
        sys.ts.factory.createIdentifier(namedImport),
      ),
    ]);
  }

  const newImport = sys.ts.factory.createImportDeclaration(
    undefined,
    sys.ts.factory.createImportClause(
      isTypeOnly,
      defaultIdentifier,
      namedBindings,
    ),
    sys.ts.factory.createStringLiteral(importPath),
  );
  statements.splice(lastImportIndex, 0, newImport);

  return sys.ts.factory.updateSourceFile(sourceFile, statements);
}

function ensureCjsImports(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  defaultImport: string | null,
  namedImport: string | null,
  importPath: string,
) {
  const statements = [...sourceFile.statements];
  let lastRequireIndex = 0;

  for (let i = 0; i < statements.length; i++) {
    const varStatement = statements[i];
    if (!sys.ts.isVariableStatement(varStatement)) {
      continue;
    }

    for (let d = 0; d < varStatement.declarationList.declarations.length; d++) {
      const declarations = [...varStatement.declarationList.declarations];
      const decl = declarations[d];
      const requireCallExp = decl.initializer;

      if (!requireCallExp || !sys.ts.isCallExpression(requireCallExp)) {
        continue;
      }
      const requireCallExpName = requireCallExp.expression;
      if (!sys.ts.isIdentifier(requireCallExpName)) {
        continue;
      }
      if (requireCallExpName.text !== "require") {
        continue;
      }
      if (requireCallExp.arguments.length !== 1) {
        continue;
      }
      const requireCallExpArg = requireCallExp.arguments[0];
      if (!sys.ts.isStringLiteral(requireCallExpArg)) {
        continue;
      }
      lastRequireIndex = i;
      if (requireCallExpArg.text !== importPath) {
        continue;
      }

      if (sys.ts.isIdentifier(decl.name) && typeof defaultImport === "string") {
        if (decl.name.text === defaultImport) {
          return sourceFile;
        }
        declarations[d] = sys.ts.factory.updateVariableDeclaration(
          decl,
          sys.ts.factory.createIdentifier(defaultImport),
          decl.exclamationToken,
          decl.type,
          decl.initializer,
        );

        statements[i] = sys.ts.factory.updateVariableStatement(
          varStatement,
          varStatement.modifiers,
          sys.ts.factory.updateVariableDeclarationList(
            varStatement.declarationList,
            declarations,
          ),
        );
        return sys.ts.factory.updateSourceFile(sourceFile, statements);
      }

      if (
        sys.ts.isObjectBindingPattern(decl.name) &&
        typeof namedImport === "string"
      ) {
        const elements = [...decl.name.elements];

        const hasNamedBinding = elements.some((e) => {
          if (!sys.ts.isBindingElement(e) || !sys.ts.isIdentifier(e.name)) {
            return false;
          }
          return e.name.text === namedImport;
        });
        if (hasNamedBinding) {
          return sourceFile;
        }

        elements.push(
          sys.ts.factory.createBindingElement(
            undefined,
            undefined,
            namedImport,
          ),
        );

        elements.sort((a, b) => {
          if (!sys.ts.isBindingElement(a) || !sys.ts.isIdentifier(a.name)) {
            return 0;
          }
          if (!sys.ts.isBindingElement(b) || !sys.ts.isIdentifier(b.name)) {
            return 0;
          }
          return a.name.text.localeCompare(b.name.text);
        });

        declarations[d] = sys.ts.factory.updateVariableDeclaration(
          decl,
          sys.ts.factory.createObjectBindingPattern(elements),
          decl.exclamationToken,
          decl.type,
          decl.initializer,
        );

        statements[i] = sys.ts.factory.updateVariableStatement(
          varStatement,
          varStatement.modifiers,
          sys.ts.factory.updateVariableDeclarationList(
            varStatement.declarationList,
            declarations,
          ),
        );
        return sys.ts.factory.updateSourceFile(sourceFile, statements);
      }
    }
  }

  if (typeof defaultImport === "string") {
    const newRequire = sys.ts.factory.createVariableStatement(
      undefined,
      sys.ts.factory.createVariableDeclarationList(
        [
          sys.ts.factory.createVariableDeclaration(
            defaultImport,
            undefined,
            undefined,
            sys.ts.factory.createCallExpression(
              sys.ts.factory.createIdentifier("require"),
              undefined,
              [sys.ts.factory.createStringLiteral(importPath)],
            ),
          ),
        ],
        sys.ts.NodeFlags.Const,
      ),
    );
    statements.splice(lastRequireIndex, 0, newRequire);
    return sys.ts.factory.updateSourceFile(sourceFile, statements);
  }

  if (typeof namedImport === "string") {
    const newRequire = sys.ts.factory.createVariableStatement(
      undefined,
      sys.ts.factory.createVariableDeclarationList(
        [
          sys.ts.factory.createVariableDeclaration(
            sys.ts.factory.createObjectBindingPattern([
              sys.ts.factory.createBindingElement(
                undefined,
                undefined,
                namedImport,
              ),
            ]),
            undefined,
            undefined,
            sys.ts.factory.createCallExpression(
              sys.ts.factory.createIdentifier("require"),
              undefined,
              [sys.ts.factory.createStringLiteral(importPath)],
            ),
          ),
        ],
        sys.ts.NodeFlags.Const,
      ),
    );
    statements.splice(lastRequireIndex, 0, newRequire);
    return sys.ts.factory.updateSourceFile(sourceFile, statements);
  }

  return sourceFile;
}

export interface EnsureImportOptions {
  defaultImport?: string;
  namedImports?: string[];
  importPath: string;
  isTypeOnly?: boolean;
}
