import type ts from "typescript";
import type {
  DevToolsSys,
  TranspileFileOptions,
  TranspileModuleOptions,
  TranspileResult,
} from "../types";
import { codeToSource } from "./ast/transform";
import { clone } from "./utils";
import { findUpNodeModules } from "./fs";

export async function createTsProgram(
  sys: DevToolsSys,
  filePaths: string[],
  overrideTsOptions?: ts.CompilerOptions,
) {
  if (!Array.isArray(filePaths) || filePaths.length === 0) {
    throw new Error(`createTsProgram() Invalid filePaths: ${filePaths}`);
  }

  const nodeModulesPath = await findUpNodeModules(sys, filePaths[0]);
  const tsLibDir = sys.join(nodeModulesPath, "typescript", "lib");

  const existsCache = new Map<string, boolean>();

  const host: ts.CompilerHost = {
    fileExists: (p) => {
      p = sys.normalize(p);

      if (p.includes("node_modules")) {
        let nm = NODE_MODULE_CACHE.get(p);
        if (!nm) {
          try {
            const stat = sys.statSync(p);
            if (stat.isFile()) {
              nm = { exists: true, content: sys.readFileSync(p) };
            } else {
              nm = { exists: false };
            }
          } catch (e) {
            nm = { exists: false };
          }
          NODE_MODULE_CACHE.set(p, nm);
        }
        return nm.exists;
      }

      if (!existsCache.has(p)) {
        const exists = sys.existsSync(p);
        if (exists) {
          const stat = sys.statSync(p);
          existsCache.set(p, stat.isFile());
        } else {
          existsCache.set(p, false);
        }
      }

      return existsCache.get(p)!;
    },
    getCanonicalFileName: (p) => sys.normalize(p),
    getCurrentDirectory: () => sys.cwd(),
    getDefaultLibFileName: () => sys.join(tsLibDir, "lib.d.ts"),
    getDefaultLibLocation: () => tsLibDir,
    getNewLine: () => "\n",
    getSourceFile: (p) => {
      p = sys.normalize(p);

      if (p.includes("node_modules")) {
        const nm = NODE_MODULE_CACHE.get(p);
        if (nm) {
          return codeToSource(sys, nm.content);
        }
      }

      let c: string | null | undefined;
      try {
        c = sys.readFileSync(p);
      } catch {
        console.error(`Could not read: ${p}`);
        c == null;
      }
      if (c == null) {
        const fileName = sys.basename(p);
        const ext = sys.extname(fileName).toLowerCase();
        if (p.startsWith(tsLibDir) && ext === ".ts") {
          c = LIB_CACHE.get(fileName);
          if (
            c == null &&
            typeof self !== "undefined" &&
            typeof fetch === "function"
          ) {
            throw new MissingTsLibError(fileName);
          }
        }
      }

      if (p.includes("node_modules")) {
        if (c) {
          NODE_MODULE_CACHE.set(p, { exists: true, content: c });
        } else {
          NODE_MODULE_CACHE.set(p, { exists: false });
        }
      }

      return codeToSource(sys, c);
    },
    readFile: (p) => {
      const content = sys.readFileSync(p);
      return typeof content === "string" ? content : undefined;
    },
    useCaseSensitiveFileNames: () => false,
    writeFile: () => {},
  };

  while (true) {
    try {
      return createHostProgram(sys, host, filePaths, overrideTsOptions);
    } catch (e) {
      if (e instanceof MissingTsLibError) {
        await fetchLib(sys, e.libName);
        continue;
      }
      sys.Sentry?.captureException(e);
      throw new Error(
        `createTsProgram() Could not create program for: ${filePaths[0]}`,
        { cause: e },
      );
    }
  }
}

function createHostProgram(
  sys: DevToolsSys,
  host: ts.CompilerHost,
  filePaths: string[],
  overrideTsOptions?: ts.CompilerOptions,
) {
  const programOpts: ts.CreateProgramOptions = {
    rootNames: [...filePaths],
    host,
    options: {},
  };

  const tsConfigPath = sys.ts.findConfigFile(sys.dirname(filePaths[0]), (p) =>
    sys.existsSync(p),
  );

  if (tsConfigPath) {
    let options = TSCONFIG_CACHE.get(tsConfigPath);
    if (!options) {
      const configFile = sys.ts.readConfigFile(tsConfigPath, (p) => {
        const content = sys.readFileSync(p);
        return typeof content === "string" ? content : undefined;
      });

      if (!configFile.error) {
        const tsConfig = sys.ts.parseJsonConfigFileContent(
          configFile.config,
          sys.ts.sys || {
            useCaseSensitiveFileNames: false,
            readDirectory: (p, _) => sys.readdirSync(p),
            fileExists: (p) => sys.existsSync(p),
            readFile: (p) => sys.readFileSync(p) || undefined,
          },
          sys.dirname(tsConfigPath),
          undefined,
          tsConfigPath,
        );
        options = tsConfig.options;
        TSCONFIG_CACHE.set(tsConfigPath, options);
      } else {
        sys.Sentry?.captureException(
          new Error(`Error reading tsconfig for createTsProgram`),
          {
            extra: {
              error: configFile.error,
              tsConfigPath,
            },
          },
        );
      }
    }
    if (options) {
      programOpts.options = clone(options);
    }
  }

  programOpts.options.noEmit = true;
  programOpts.options.isolatedModules = false;
  programOpts.options.strict = false;
  programOpts.options.jsx = sys.ts.JsxEmit.Preserve;
  programOpts.options.allowJs = true;

  Object.assign(programOpts.options, overrideTsOptions);

  return sys.ts.createProgram(programOpts);
}

export async function getTypeChecker(
  sys: DevToolsSys,
  filePath: string,
  tsProgram: ts.Program | null,
) {
  if (!tsProgram) {
    tsProgram = await createTsProgram(sys, [filePath]);
  }

  const sourceFile = tsProgram.getSourceFile(filePath);
  if (!sourceFile) {
    throw new Error(`getTypeChecker() Could not find source file: ${filePath}`);
  }
  const typeChecker = tsProgram.getTypeChecker();
  return {
    typeChecker,
    sourceFile,
  };
}

class MissingTsLibError {
  constructor(public libName: string) {}
}

async function fetchLib(sys: DevToolsSys, libName: string) {
  const libUrl = `https://cdn.jsdelivr.net/npm/typescript@${sys.ts.version}/lib/${libName}`;

  if (!PENDING_LIB_CACHE.has(libName)) {
    PENDING_LIB_CACHE.set(
      libName,
      fetch(libUrl)
        .then((rsp) => rsp.text())
        .then((content) => {
          LIB_CACHE.set(libName, content);
        }),
    );
  }
  await PENDING_LIB_CACHE.get(libName);
  PENDING_LIB_CACHE.delete(libName);
}

const LIB_CACHE = new Map<string, string>();
const PENDING_LIB_CACHE = new Map<string, Promise<void>>();
const NODE_MODULE_CACHE = new Map<
  string,
  { exists: boolean; content?: string | null }
>();
const TSCONFIG_CACHE = new Map<string, ts.CompilerOptions>();

export async function transpileFile(
  sys: DevToolsSys,
  opts: TranspileFileOptions,
) {
  const code = await sys.readFile(opts.filePath);
  if (code == null) {
    const result: TranspileResult = {
      diagnostics: [
        {
          messageText: `Error reading: ${opts.filePath}`,
        },
      ],
      code: null,
      output: null,
    };
    return result;
  }

  return transpileModule(sys, {
    code,
    filePath: opts.filePath,
    compilerOptions: opts.compilerOptions,
  });
}

export function transpileModule(
  sys: DevToolsSys,
  opts: TranspileModuleOptions,
) {
  const result: TranspileResult = {
    diagnostics: [],
    code: opts.code,
    output: null,
  };

  try {
    const output = sys.ts.transpileModule(opts.code, {
      compilerOptions: opts.compilerOptions,
      fileName: opts.filePath ?? "module.tsx",
      reportDiagnostics: true,
    });

    output.diagnostics?.forEach((d) => {
      result.diagnostics.push({
        messageText: String(d.messageText),
      });
    });

    result.output = output.outputText;
  } catch (e) {
    console.error(`transpileModule`, e);
    result.diagnostics.push({
      messageText: String(e),
    });
  }

  return result;
}
