import type {
  DevToolsServer,
  DevToolsServerContext,
  DevToolsServerOptions,
} from "../types";
import { createDevToolsHttpServer } from "./dev-tools-http-server";
import { version } from "./index";

export async function createDevToolsServer(opts: DevToolsServerOptions) {
  const ctx: DevToolsServerContext = {
    getPastSyncInfo: () => null,
    resyncSnippet: () => null,
    ...opts,
    devToolsServerUrl: "",
    isValid: false,
    publicApiKey: "",
    serverShouldRestart: false,
    port: opts.port || 5273,
    ignoreMissingConfig: false,
  };

  const frameworks = opts
    .getFrameworks()
    .map((f) => f.name)
    .join(", ");

  ctx.debug(`create devtools ${version} [${frameworks}]`);

  const httpServer = await createDevToolsHttpServer(ctx);

  ctx.devToolsServerUrl = httpServer.url;
  ctx.port = httpServer.port;

  const devToolsServer: DevToolsServer = {
    getUrl: () => ctx.devToolsServerUrl,
  };

  return devToolsServer;
}
