import type {
  ComponentInfo,
  ComponentInput,
  SetComponentInfoOptions,
  SetComponentInputOptions,
} from "../../types";
import { clone, isBoolean, isNumber, isString, sortComponents } from "../utils";
import {
  componentAcceptsChildren,
  normalizeComponentInput,
} from "./component-info";

export function updateRegistry(
  cmpId: string,
  srcCmps: ComponentInfo[],
  regCmps: ComponentInfo[],
  registerCmp: boolean,
  unregisterCmp: boolean,
  setCmpInfo: SetComponentInfoOptions | null,
  setCmpInput: SetComponentInputOptions | null,
) {
  let addCmpToRegistry: ComponentInfo | null = null;
  let removeCmpFromRegistry: ComponentInfo | null = null;
  let updateRegisteredCmp: ComponentInfo | null = null;
  let nodeIndex = -1;

  srcCmps = clone(srcCmps);
  regCmps = clone(regCmps);

  const srcCmpInfo = srcCmps.find((c) => c.id === cmpId)!;
  const regCmpInfo = regCmps.find((c) => c.id === cmpId);

  if (registerCmp) {
    // lets register this component
    if (!regCmpInfo) {
      // component info not already registered, append it using src info
      addCmpToRegistry = {
        ...clone(srcCmpInfo),
        inputs: clone(srcCmpInfo.inputs).map((i) => {
          i.isRegistered = true;
          return i;
        }),
      };
      nodeIndex = regCmps.length;
      regCmps.push(addCmpToRegistry);
    }
  } else if (
    typeof regCmpInfo?.nodeIndex === "number" &&
    regCmpInfo.nodeIndex > -1
  ) {
    // component is in the registry
    if (unregisterCmp) {
      // already registered, unregister it
      nodeIndex = regCmpInfo.nodeIndex;
      removeCmpFromRegistry = clone(regCmpInfo);
      regCmps = regCmps.filter((c) => c.id !== cmpId);
    } else if (setCmpInfo != null) {
      // already registered, update component info
      updateCmpInfoValues(regCmpInfo, setCmpInfo);
      nodeIndex = regCmpInfo.nodeIndex;
      updateRegisteredCmp = regCmpInfo;
    } else if (setCmpInput != null) {
      // already registered, update component input
      const input = regCmpInfo.inputs.find((i) => i.name === setCmpInput.name);
      if (input) {
        if (setCmpInput.registerInput === false) {
          // remove input from the registered inputs
          input.isRegistered = false;
        } else {
          // update the existing registered input
          updateCmpInputValues(input, setCmpInput);
          input.isRegistered = true;
        }
        updateRegisteredCmp = regCmpInfo;
        nodeIndex = regCmpInfo.nodeIndex;
      }
    }
  }

  return {
    addCmpToRegistry,
    removeCmpFromRegistry,
    updateRegisteredCmp,
    nodeIndex,
    components: mergeComponents(srcCmps, regCmps),
  };
}

export function mergeComponents(
  sourceComponents: ComponentInfo[],
  registeredCmps: ComponentInfo[],
) {
  const cmps = sortComponents([
    ...registeredCmps.map((c) => {
      c.isRegistered = true;
      return c;
    }),
    ...sourceComponents
      .filter((c) => !registeredCmps.some((r) => r.id === c.id))
      .map((c) => {
        c.isRegistered = false;
        return c;
      }),
  ]);

  cmps.forEach((cmp) => {
    cmp.inputs = cmp.inputs.map(normalizeComponentInput);
    cmp.inputs.sort((a, b) => {
      return a.name.toLowerCase().localeCompare(b.name.toLowerCase());
    });

    const regCmp = registeredCmps.find((c) => c.id === cmp.id);
    if (regCmp) {
      // update the array of the registered component in place
      // when updating many components in one call, we need to
      // ensure the array stays up to date with each registered cmp
      regCmp.acceptsChildren = componentAcceptsChildren(cmp);
    }

    // Make sure filePath and relFilePath are set correctly in case of external modules
    const externalCmp = sourceComponents.find(
      (c) => c.id === cmp.id && c.externalImportPath,
    );
    if (externalCmp) {
      cmp.filePath = externalCmp.filePath;
      cmp.relFilePath = externalCmp.relFilePath;
    }
  });

  return cmps;
}

function updateCmpInfoValues(
  cmpInfo: ComponentInfo,
  setCmpInfo: SetComponentInfoOptions,
) {
  if (isString(setCmpInfo.name)) {
    cmpInfo.name = setCmpInfo.name;
  } else if (setCmpInfo.name === null) {
    cmpInfo.name = cmpInfo.importName;
  }

  if (isString(setCmpInfo.description)) {
    cmpInfo.description = setCmpInfo.description;
  } else if (setCmpInfo.description === null) {
    delete cmpInfo.description;
  }

  if (isString(setCmpInfo.image)) {
    cmpInfo.image = setCmpInfo.image;
  } else if (setCmpInfo.image === null) {
    delete cmpInfo.image;
  }
}

function updateCmpInputValues(
  cmpInput: ComponentInput,
  setCmpInput: SetComponentInputOptions,
) {
  if (isString(setCmpInput.type)) {
    cmpInput.type = setCmpInput.type;
  }

  if (isString(setCmpInput.friendlyName)) {
    cmpInput.friendlyName = setCmpInput.friendlyName;
  } else if (setCmpInput.friendlyName === null) {
    delete cmpInput.friendlyName;
  }

  if (
    isString(setCmpInput.defaultValue) ||
    isNumber(setCmpInput.defaultValue) ||
    isBoolean(setCmpInput.defaultValue)
  ) {
    cmpInput.defaultValue = setCmpInput.defaultValue;
  } else if (setCmpInput.defaultValue === null) {
    delete cmpInput.defaultValue;
  }

  if (isString(setCmpInput.helperText)) {
    cmpInput.helperText = setCmpInput.helperText;
  } else if (setCmpInput.helperText === null) {
    delete cmpInput.helperText;
  }

  if (setCmpInput.required === true) {
    cmpInput.required = setCmpInput.required;
  } else if (setCmpInput.required === null || setCmpInput.required === false) {
    delete cmpInput.required;
  }
}
