import type { AngularDevToolsSys } from ".";
import JSON5 from "json5";

export async function angularFixTsConfig(sys: AngularDevToolsSys) {
  const tsConfigPath = sys.ts.findConfigFile(
    sys.getAppRootDir(),
    sys.existsSync,
  );
  if (tsConfigPath) {
    const tsConfigContent = await sys.readFile(tsConfigPath);
    if (!tsConfigContent) {
      return;
    }
    const tsConfig = JSON5.parse(tsConfigContent);
    if (!tsConfig || typeof tsConfig !== "object" || Array.isArray(tsConfig)) {
      return;
    }
    const compilerOptions = tsConfig.compilerOptions;
    if (
      !compilerOptions ||
      typeof compilerOptions !== "object" ||
      Array.isArray(compilerOptions)
    ) {
      return;
    }
    let paths = compilerOptions.paths;
    if (!paths || typeof paths !== "object" || Array.isArray(paths)) {
      compilerOptions.paths = {} as any;
      paths = compilerOptions.paths;
    }
    Object.assign(compilerOptions.paths, {
      "~/*": ["./src/*"],
    });
    await sys.writeFile(tsConfigPath, JSON.stringify(tsConfig, null, 2));
  }
}
