export function ensureComments(
  code: string,
  commentText: string[],
  lineTextAfterComments: string,
) {
  if (lineTextAfterComments.length === 0) {
    return code;
  }

  const lines = code.split("\n");
  const lineIndex = lines.findIndex((l) => l.includes(lineTextAfterComments));
  if (lineIndex === -1) {
    return code;
  }

  const hasComments = lines.some((l) => l.includes(commentText[0]));
  if (hasComments) {
    return code;
  }

  const commentLines = [
    `/**`,
    ...commentText.map((c) => ` * ${c.trim()}`),
    ` */`,
  ];
  lines.splice(lineIndex, 0, ...commentLines);

  return lines.join("\n");
}
