import type ts from "typescript";
import type { DevToolsSys } from "../..";
import type { EnsureConfigResult } from "../../../types";
import { codeToSource, sourceToCode } from "../../../common/ast/transform";
import { getExportedStatements } from "../../../common/ast/exported-statements";
import { ensureImport } from "../../../common/ast/ensure-imports";
import { getTextOfPropertyName } from "../../../common/ast/convert-values";

export async function remixEnsureConfigPlugin(
  sys: DevToolsSys,
  configFilePath: string,
  configContent: string,
) {
  try {
    let sourceFile = codeToSource(sys, configContent);

    const result: EnsureConfigResult = {
      content: configContent,
      filePath: configFilePath,
      fileName: sys.basename(configFilePath),
      hasContentUpdates: false,
    };

    const withFn = getWithFnVariableName(sys, sourceFile);
    if (withFn) {
      return result;
    }

    const cjsExports = getExportedStatements(sys, sourceFile, "cjs");
    const esmExports = getExportedStatements(sys, sourceFile);
    if (cjsExports.length !== 1 && esmExports.length !== 1) {
      throw new Error(`Expected an export from ${configFilePath}`);
    }

    const defaultExport = cjsExports[0];

    if (esmExports.length === 1) {
      const esmDefaultExport = esmExports[0];

      if (!esmDefaultExport.isDefaultExport) {
        throw new Error(`Expected a default export from ${configFilePath}`);
      }

      const exportAssignment = esmDefaultExport.statement;
      if (!sys.ts.isExportAssignment(exportAssignment)) {
        throw new Error(`Expected an export assignment from ${configFilePath}`);
      }

      if (sys.ts.isObjectLiteralExpression(exportAssignment.expression)) {
        const fnVariableName = "builderDevTools()";

        const configObj = updateConfigObjectLiteral(
          sys,
          exportAssignment.expression,
        );

        if (!configObj) {
          return result;
        }

        const configObjWithServerDependencies =
          sys.ts.factory.createCallExpression(
            sys.ts.factory.createIdentifier(fnVariableName),
            undefined,
            [configObj],
          );

        const statements = [...sourceFile.statements];
        const index = statements.indexOf(exportAssignment);
        statements[index] = sys.ts.factory.updateExportAssignment(
          exportAssignment,
          exportAssignment.modifiers,
          configObjWithServerDependencies,
        );

        sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

        sourceFile = ensureImport(sys, sourceFile, {
          defaultImport: "builderDevTools",
          importPath: "@builder.io/dev-tools/remix",
        });

        result.content = sourceToCode(sys, sourceFile);
        result.hasContentUpdates = true;

        return result;
      }
    }

    if (!defaultExport.isDefaultExport) {
      throw new Error(`Expected a default export from ${configFilePath}`);
    }

    if (
      sys.ts.isObjectLiteralExpression(defaultExport.node) ||
      sys.ts.isCallExpression(defaultExport.node)
    ) {
      const moduleExportsStatement = defaultExport.statement;
      if (!sys.ts.isExpressionStatement(moduleExportsStatement)) {
        throw new Error(
          `Expected an expression statement for the default export`,
        );
      }

      const moduleExports = moduleExportsStatement.expression;
      if (!sys.ts.isBinaryExpression(moduleExports)) {
        throw new Error(`Expected a binary expression for the default export`);
      }

      // module.exports = {}
      const w = ensureWithFnVariableName(sys, sourceFile);
      sourceFile = w.sourceFile;

      const statements = [...sourceFile.statements];
      const index = statements.indexOf(moduleExportsStatement);
      statements[index] = sys.ts.factory.updateExpressionStatement(
        moduleExportsStatement,
        sys.ts.factory.updateBinaryExpression(
          moduleExports,
          moduleExports.left,
          moduleExports.operatorToken,
          sys.ts.factory.createCallExpression(
            sys.ts.factory.createIdentifier(w.withFnVariableName),
            undefined,
            [moduleExports.right],
          ),
        ),
      );
      sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

      result.content = sourceToCode(sys, sourceFile);
      result.hasContentUpdates = true;
      return result;
    }

    const remixConfigVariableStatement = defaultExport.statement;
    if (sys.ts.isVariableStatement(remixConfigVariableStatement)) {
      // module.exports = remixConfig

      const declarations = [
        ...remixConfigVariableStatement.declarationList.declarations,
      ];
      if (declarations.length !== 1) {
        throw new Error(`Expected a single variable declaration`);
      }

      const remixConfigDeclaration = declarations[0];
      if (!remixConfigDeclaration.initializer) {
        throw new Error(`Expected an initializer on the declaration`);
      }

      const remixConfigInitializer = remixConfigDeclaration.initializer;
      if (
        sys.ts.isObjectLiteralExpression(remixConfigInitializer) ||
        sys.ts.isCallExpression(remixConfigInitializer)
      ) {
        // const remixConfig = {}
        // module.exports = remixConfig

        const w = ensureWithFnVariableName(sys, sourceFile);
        sourceFile = w.sourceFile;

        const statements = [...sourceFile.statements];
        const index = statements.indexOf(remixConfigVariableStatement);

        statements[index] = sys.ts.factory.updateVariableStatement(
          remixConfigVariableStatement,
          remixConfigVariableStatement.modifiers,
          sys.ts.factory.updateVariableDeclarationList(
            remixConfigVariableStatement.declarationList,
            [
              sys.ts.factory.updateVariableDeclaration(
                remixConfigDeclaration,
                remixConfigDeclaration.name,
                remixConfigDeclaration.exclamationToken,
                remixConfigDeclaration.type,
                sys.ts.factory.createCallExpression(
                  sys.ts.factory.createIdentifier(w.withFnVariableName),
                  undefined,
                  [remixConfigInitializer],
                ),
              ),
            ],
          ),
        );

        sourceFile = sys.ts.factory.updateSourceFile(sourceFile, statements);

        result.content = sourceToCode(sys, sourceFile);
        result.hasContentUpdates = true;
        return result;
      }

      throw new Error(`Unexpected initializer on the variable statement`);
    }

    throw new Error(`Unexpected export type`);
  } catch (e) {
    sys.Sentry?.captureException(e, {
      extra: {
        configFilePath,
        configContent,
      },
      level: "warning",
    });
    throw e;
  }
}

function getWithFnVariableName(sys: DevToolsSys, sourceFile: ts.SourceFile) {
  for (const s of sourceFile.statements) {
    if (!sys.ts.isVariableStatement(s)) {
      continue;
    }
    for (const d of s.declarationList.declarations) {
      if (!d.initializer) {
        continue;
      }
      if (!sys.ts.isCallExpression(d.initializer)) {
        continue;
      }
      const requireExp = d.initializer.expression;
      if (!sys.ts.isCallExpression(requireExp)) {
        continue;
      }
      if (!sys.ts.isIdentifier(requireExp.expression)) {
        continue;
      }
      if (requireExp.expression.text !== "require") {
        continue;
      }
      const requirePath = requireExp.arguments[0];
      if (!requirePath || !sys.ts.isStringLiteral(requirePath)) {
        continue;
      }
      if (requirePath.text !== "@builder.io/dev-tools/remix") {
        continue;
      }
      if (!sys.ts.isIdentifier(d.name)) {
        continue;
      }
      return d.name.text;
    }
  }
  return null;
}

function ensureWithFnVariableName(sys: DevToolsSys, sourceFile: ts.SourceFile) {
  let withFnVariableName = getWithFnVariableName(sys, sourceFile);

  if (!withFnVariableName) {
    withFnVariableName = `withBuilderDevTools`;

    sourceFile = sys.ts.factory.updateSourceFile(sourceFile, [
      sys.ts.factory.createVariableStatement(
        undefined,
        sys.ts.factory.createVariableDeclarationList(
          [
            sys.ts.factory.createVariableDeclaration(
              withFnVariableName,
              undefined,
              undefined,
              sys.ts.factory.createCallExpression(
                sys.ts.factory.createCallExpression(
                  sys.ts.factory.createIdentifier("require"),
                  undefined,
                  [
                    sys.ts.factory.createStringLiteral(
                      "@builder.io/dev-tools/remix",
                    ),
                  ],
                ),
                undefined,
                [],
              ),
            ),
          ],
          sys.ts.NodeFlags.Const,
        ),
      ),
      ...sourceFile.statements,
    ]);
  }

  return { withFnVariableName, sourceFile };
}

function updateConfigObjectLiteral(
  sys: DevToolsSys,
  config: ts.ObjectLiteralExpression,
) {
  const properties = [...config.properties];

  const pIndex = properties.findIndex((p) => {
    const n = getTextOfPropertyName(sys, p);
    return n === "serverDependenciesToBundle";
  });

  if (pIndex > -1) {
    const dependenciesProp = properties[pIndex];
    if (
      sys.ts.isPropertyAssignment(dependenciesProp) &&
      dependenciesProp.initializer &&
      sys.ts.isArrayLiteralExpression(dependenciesProp.initializer)
    ) {
      const serverDependenciesArr = updateServerDependenciesArray(
        sys,
        dependenciesProp.initializer as ts.ArrayLiteralExpression,
      );
      if (serverDependenciesArr) {
        properties[pIndex] = sys.ts.factory.updatePropertyAssignment(
          dependenciesProp,
          dependenciesProp.name,
          serverDependenciesArr,
        );
        return sys.ts.factory.updateObjectLiteralExpression(config, properties);
      }
    }
    return null;
  }

  const serverDependenciesArr = updateServerDependenciesArray(
    sys,
    sys.ts.factory.createArrayLiteralExpression([], true),
  );
  if (serverDependenciesArr) {
    return sys.ts.factory.updateObjectLiteralExpression(config, [
      ...properties,
      sys.ts.factory.createPropertyAssignment(
        "serverDependenciesToBundle",
        serverDependenciesArr,
      ),
    ]);
  }

  return null;
}

function updateServerDependenciesArray(
  sys: DevToolsSys,
  serverDependenciesArr: ts.ArrayLiteralExpression,
) {
  const hasServerDependencies = serverDependenciesArr.elements.some((e) => {
    if (sys.ts.isCallExpression(e) && sys.ts.isIdentifier(e.expression)) {
      return e.expression.text === "@builder.io/react";
    }
    return false;
  });

  if (hasServerDependencies) {
    return null;
  }

  return sys.ts.factory.updateArrayLiteralExpression(serverDependenciesArr, [
    ...serverDependenciesArr.elements,
    sys.ts.factory.createIdentifier(`"@builder.io/react"`),
  ]);
}
