import type {
  DevToolsAdapter,
  DevToolsSys,
  ExportedRegistry,
  MinimalComponentInfo,
} from "../types";
import { clone, normalizePathSlash } from "../common/utils";
import { compress, decompress, type Compressed } from "compress-json";

export async function exportRegistry(
  sys: DevToolsSys,
  devTools: DevToolsAdapter,
) {
  const registry = await devTools.getRegistry();

  const exportedRegistry: ExportedRegistry = {
    components: registry.components
      .filter((c) => c.isRegistered)
      .map((c) => {
        const minimal: MinimalComponentInfo = {
          filePath: normalizePathSlash(
            sys.relative(sys.getAppRootDir(), c.filePath),
          ),
          name: c.name,
          image: c.image,
          description: c.description,
          exportName: c.exportName,
          inputs: clone(c.inputs),
        };
        return clone(minimal);
      }),
    version: 1,
  };

  sys.debug(
    `exporting registry components:`,
    exportedRegistry.components.length,
  );

  const compressed = compress(exportedRegistry);

  return btoa(JSON.stringify(compressed));
}

export async function importRegistry(
  sys: DevToolsSys,
  devTools: DevToolsAdapter,
  registryToImport: string,
) {
  if (typeof registryToImport !== "string") {
    throw new Error(`Invalid registry to import`);
  }

  const registry = await devTools.getRegistry();

  const compressed = JSON.parse(atob(registryToImport)) as Compressed;

  const importedRegistry = decompress(compressed) as ExportedRegistry;

  let updatedRegistry = clone(registry);

  sys.debug(
    `importing registry components:`,
    importedRegistry.components.length,
  );

  for (const cmpToRegister of importedRegistry.components) {
    const filePath = normalizePathSlash(sys.normalize(cmpToRegister.filePath));
    const cmp = registry.components.find(
      (c) =>
        normalizePathSlash(c.filePath).endsWith(filePath) &&
        c.exportName === cmpToRegister.exportName,
    );
    if (!cmp) {
      console.error(
        `Could not find component to register: ${cmpToRegister.filePath}, ${cmpToRegister.exportName}`,
      );
      continue;
    }

    sys.debug(`register (${cmp.id})`, cmp.filePath);

    await devTools.registerComponent({
      cmpId: cmp.id,
    });

    for (const input of cmpToRegister.inputs) {
      sys.debug(`set input (${cmp.id})`);
      await devTools.setRegisteredComponentInput({
        cmpId: cmp.id,
        name: input.name,
        type: input.type,
        required: input.required,
        defaultValue: input.defaultValue,
        friendlyName: input.friendlyName,
        helperText: input.helperText,
      });
    }

    sys.debug(`set input (${cmp.id}) info`);
    const r = await devTools.setRegisteredComponentInfo({
      cmpId: cmp.id,
      name: cmpToRegister.name,
      image: cmpToRegister.image,
      description: cmpToRegister.description,
    });

    updatedRegistry = clone(r);
  }

  return updatedRegistry;
}
