import type ts from "typescript";
import type { DevToolsSys, ModuleFormat } from "../../types";
import type { ExportedStatement } from "./exported-statements";

export function getImportedStatements(
  sys: DevToolsSys,
  sourceFile: ts.SourceFile,
  moduleType: ModuleFormat = "esm",
) {
  if (moduleType === "cjs") {
    return getCjsImportedStatements(sys, sourceFile);
  } else {
    return getEsmImportedStatements(sys, sourceFile);
  }
}

// expression to match any external import, not relative or local
const externalImportRegex = /^(@\w|\w)/;

function getEsmImportedStatements(sys: DevToolsSys, sourceFile: ts.SourceFile) {
  const exports: ExportedStatement[] = [];

  for (const s of sourceFile.statements) {
    if (sys.ts.isImportDeclaration(s)) {
      if (sys.ts.isLiteralExpression(s.moduleSpecifier)) {
        if (!externalImportRegex.test(s.moduleSpecifier.text)) {
          continue;
        }
        if (s.importClause?.isTypeOnly) {
          continue;
        }
        if (s.importClause && s.importClause.namedBindings) {
          if (sys.ts.isNamedImports(s.importClause.namedBindings)) {
            for (const n of s.importClause.namedBindings.elements) {
              if (!n.isTypeOnly) {
                exports.push({
                  isDefaultExport: false,
                  statement: s,
                  node: n.name,
                });
              }
            }
          }
        }

        // handle default import
        if (s.importClause && s.importClause.name) {
          exports.push({
            isDefaultExport: true,
            statement: s,
            node: s.importClause.name,
          });
        }
      }
    }
  }

  return exports;
}

function getCjsImportedStatements(
  _sys: DevToolsSys,
  _sourceFile: ts.SourceFile,
) {
  const exports: ExportedStatement[] = [];

  return exports;
}
