import type { DevToolsSys, PackageJSON } from "../types";

export function hasTsconfig(sys: DevToolsSys) {
  const tsconfigPath = sys.join(sys.getAppRootDir(), "tsconfig.json");
  return sys.exists(tsconfigPath);
}

const skipFolders = ["node_modules", "dist", "build", "public"];

export async function findBuilderRegistry(
  sys: DevToolsSys,
  defaultFolder: string,
) {
  async function walk(
    root: string,
    depth: number,
  ): Promise<string | undefined> {
    if (depth > 10) {
      return;
    }
    const files = await sys.readdir(root);
    for (const file of files) {
      if (skipFolders.includes(file)) {
        continue;
      }
      const filePath = sys.join(root, file);
      const stat = await sys.stat(filePath);
      if (stat) {
        if (stat.isFile() && file.startsWith("builder-registry.")) {
          return filePath;
        }
        if (stat.isDirectory()) {
          const result = await walk(filePath, depth + 1);
          if (result) {
            return result;
          }
        }
      }
    }
  }
  const result = await walk(sys.getAppRootDir(), 0);
  if (!result) {
    const typescriptEnabled = await hasTsconfig(sys);

    const registryFileName = `builder-registry.${
      typescriptEnabled ? "ts" : "js"
    }`;
    return sys.join(defaultFolder, registryFileName);
  }
  return result;
}

export async function findPackageJson(sys: DevToolsSys, dir: string) {
  const fsRoot = sys.resolve("/");

  for (let i = 0; i < 20; i++) {
    const pkgJsonPath = sys.join(dir, "package.json");
    const content = await sys.readFile(pkgJsonPath);
    if (content) {
      const pkgJson = JSON.parse(content) as PackageJSON;
      return pkgJson;
    }

    if (dir === fsRoot) {
      break;
    }
    dir = sys.dirname(dir);
  }

  return null;
}

export async function findUpNodeModules(sys: DevToolsSys, p: string) {
  for (let i = 0; i < 20; i++) {
    const dir = sys.dirname(p);
    const nodeModulesDir = sys.join(dir, "node_modules");

    if (await sys.exists(nodeModulesDir)) {
      return nodeModulesDir;
    }

    if (p === sys.getAppRootDir()) {
      break;
    }
    p = sys.dirname(p);
  }

  return sys.join(sys.getAppRootDir(), "node_modules");
}

export async function recursiveFindDir(
  sys: DevToolsSys,
  dir: string,
  findDirectoryName: string,
): Promise<string | undefined> {
  const itemNames = await sys.readdir(dir);

  if (itemNames.includes(findDirectoryName)) {
    return sys.join(dir, findDirectoryName);
  }

  const results = await Promise.all(
    itemNames.map(async (itemName) => {
      if (itemName.startsWith(".") || itemName === "node_modules") {
        return;
      }

      const itemPath = sys.join(dir, itemName);
      const itemStat = await sys.stat(itemPath);
      if (itemStat.isDirectory()) {
        const foundDir = await recursiveFindDir(
          sys,
          itemPath,
          findDirectoryName,
        );
        if (foundDir) {
          return foundDir;
        }
      }
    }),
  );

  return results.find((r) => typeof r === "string");
}
